export default class CardData {
    public cardId: number = 0;
    public monsterId: number = -1;
    public curHp: number = 0;
    public special: number = 0;
    public star: number = 1;
    public exp: number = 0;
    public atk: number = 0;
    public hp: number = 0;
    public constructor(data: any = null) {
        if (data) {
            this.cardId = data.cardId || 0;
            this.monsterId = data.monsterId >= 0 ? data.monsterId : -1;
            this.curHp = data.curHp || 0;
            this.special = data.special || 0;
            this.star = data.star || 1;
            this.exp = data.exp || 0;
            this.atk = data.atk || 0;
            this.hp = data.hp || 0;
        }
    }
}