import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";
import LevelCellRuntime from "./LevelCellRuntime";
import MapCellRuntime from "./MapCellRuntime";
import MoneyUiRuntime from "./MoneyUiRuntime";

export default class ChallengeRuntime extends ui.pages.challengeUI {
    bigMapCellList: MapCellRuntime[] = [];
    showCellList: LevelCellRuntime[] = [];
    spaceX = 10;
    curMapIndex = 0;
    onAwake() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.challengeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.succeedBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rankBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.levelNumLabel.filters = [CatchRuntime.blackColorFilter];
        this.bigLevelPanel.vScrollBarSkin = null;
        this.initBigLevelUi();
        Laya.timer.frameOnce(1, this, () => {
            this.updateCurBigLevel(UserData.instance.challengeLevel[0]);
        });
        this.smallLevelPanel.hScrollBarSkin = null;
        this.initSmallLevelUi(UserData.instance.challengeLevel[0]);
        Laya.timer.frameOnce(1, this, () => {
            this.updateCurSmallLevel(UserData.instance.challengeLevel[1]);
        });
        this.initPanelUi(UserData.instance.challengeLevel[0]);
        this.curMapIndex = UserData.instance.challengeLevel[0];
        this.initMoneyPoint();
        this.judgeShowSucceedBtn();
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
    }
    static haveRed() {
        return false;
    }
    initSmallLevelUi(bigLevel: number) {
        let listData: { bigLevel: number, smallLevel: number, passReward: number[][], firstPassReward: number[][] }[] = [];
        for (let i = 0; i < GameDataManager.Instance.challengeMapData[bigLevel - 1].length; i++) {
            let data = GameDataManager.Instance.challengeMapData[bigLevel - 1][i];
            listData.push({ bigLevel: bigLevel, smallLevel: i + 1, passReward: GameDataManager.Instance.parseRewardData(data.passReward), firstPassReward: GameDataManager.Instance.parseRewardData(data.firstPassReward) });
        }
        let diff = listData.length - this.showCellList.length;
        let biggerlength = diff > 0 ? listData.length : this.showCellList.length;
        let smallerlength = diff > 0 ? this.showCellList.length : listData.length;
        let offsetX = 0;
        for (let i = 0; i < biggerlength;) {
            if (i < smallerlength) {
                //正常赋值操作
                let cell = this.showCellList[i];
                let data = listData[i];
                cell.dataSource = data;
                cell.x = offsetX;
                offsetX += (cell.width + this.spaceX);
                i++;
            } else {
                if (diff > 0) {
                    //创建新格子操作
                    let cell = this.getOneCell();
                    let data = listData[i];
                    cell.dataSource = data;
                    cell.x = offsetX;
                    offsetX += (cell.width + this.spaceX);
                    this.smallLevelPanel.addChild(cell);
                    this.showCellList.push(cell);
                    i++;
                } else {
                    //回收格子操作
                    let cell = this.showCellList[i];
                    cell.removeSelf();
                    Laya.Pool.recover("LevelCell", cell);
                    this.showCellList.splice(i, 1);
                    biggerlength = this.showCellList.length;
                }
            }
        }
    }
    initBigLevelUi() {
        let totalHeight = 0;
        for (let i = GameDataManager.Instance.challengeMapData.length - 1; i >= 0; i--) {
            let mapCell = new MapCellRuntime();
            mapCell.dataSource = { bigLevel: i + 1 };
            mapCell.y = totalHeight;
            totalHeight += mapCell.height;
            this.bigLevelPanel.addChild(mapCell);
            this.bigMapCellList.push(mapCell);
            mapCell.on(Laya.Event.CLICK, this, this.onMapCellClick, [i + 1]);
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            Laya.Tween.to(this.mapInfoPanel, { x: -Laya.stage.width }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.mapInfoPanel.visible = false;
            }));
        } else if (e.target == this.challengeBtn) {
            Laya.MouseManager.enabled = false;
            EventManager.event(EventType.GotoScene, "challenge");
        } else if (e.target == this.succeedBtn) {
            LayaTool.openScene("pages/succeed.scene", false, true, Laya.Handler.create(this, (reason: string) => {
                if (reason == "yes") {
                    this.onSucceed();
                }
            }), null, true);
        } else if (e.target == this.rankBtn) {
            LayaTool.openScene("pages/challengeRank.scene", false, true, null, null, false);
        }
    }
    updateCurBigLevel(bigLevel: number) {
        let totalHeight = 0;
        for (let i = 0; i <= GameDataManager.Instance.challengeMapData.length - bigLevel; i++) {
            totalHeight += this.bigMapCellList[i].height;
        }
        let scrollValue = totalHeight - this.bigLevelPanel.height;
        if (scrollValue < 0) scrollValue = 0;
        this.bigLevelPanel.vScrollBar.value = scrollValue;
    }
    updateCurSmallLevel(smallLevel: number) {
        let totalWidth = 0;
        for (let i = 0; i < smallLevel; i++) {
            totalWidth += (this.showCellList[i].width + this.spaceX);
        }
        let scrollValue = totalWidth - this.smallLevelPanel.width;
        if (scrollValue < 0) scrollValue = 0;
        this.smallLevelPanel.hScrollBar.value = scrollValue;
    }
    getOneCell() {
        let cell: LevelCellRuntime = Laya.Pool.getItem("LevelCell");
        if (!cell) cell = new LevelCellRuntime();
        return cell;
    }
    initPanelUi(bigLevel: number) {
        this.mapImage.skin = `newSystem/Ui_DaoGuan${bigLevel}.png`;
        this.mapName.skin = `newSystem/Ui_DaoGuanName${bigLevel}.png`;
        this.levelNumLabel.text = GameDataManager.Instance.challengeMapData[bigLevel - 1].length + "";
    }
    onMapCellClick(index: number, e: Laya.Event) {
        if (index > UserData.instance.challengeLevel[0]) {
            EventManager.event(EventType.PopTip, "先通关当前道馆");
            return;
        }
        if (this.curMapIndex != index) {
            this.curMapIndex = index;
            this.initSmallLevelUi(index);
            Laya.timer.frameOnce(1, this, () => {
                if (index < UserData.instance.challengeLevel[0]) {
                    this.updateCurSmallLevel(GameDataManager.Instance.challengeMapData[index - 1].length);
                } else {
                    this.updateCurSmallLevel(UserData.instance.challengeLevel[1]);
                }
            });
            this.initPanelUi(index);
        }
        this.mapInfoPanel.visible = true;
        Laya.Tween.to(this.mapInfoPanel, { x: 18 }, 200, Laya.Ease.sineIn);
    }
    onUserDataChange(key: string) {
        if (key == "challengeLevel") {
            this.updateBigLevelUi();
            this.updateCurBigLevel(UserData.instance.challengeLevel[0]);
            this.initSmallLevelUi(UserData.instance.challengeLevel[0]);
            this.updateCurSmallLevel(UserData.instance.challengeLevel[1]);
            this.initPanelUi(UserData.instance.challengeLevel[0]);
            this.curMapIndex = UserData.instance.challengeLevel[0];
        } else if (key == "isSucceed") {
            this.judgeShowSucceedBtn();
        }
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
    }
    updateBigLevelUi() {
        let bigLevelNum = GameDataManager.Instance.challengeMapData.length - 1;
        for (let i = bigLevelNum; i >= 0; i--) {
            let mapCell = this.bigMapCellList[bigLevelNum - i];
            mapCell.dataSource = { bigLevel: i + 1 };
        }
    }
    judgeShowSucceedBtn() {
        if (UserData.instance.highestChallengeLevel && !UserData.instance.isSucceed) {
            this.succeedBtn.visible = true;
        } else {
            this.succeedBtn.visible = false;
        }
    }
    onSucceed() {
        let gold = 0;
        let badge = 0;
        let beforeHighestLevel = [...UserData.instance.highestChallengeLevel];
        beforeHighestLevel[1]--;
        if (beforeHighestLevel[1] == 0) {
            beforeHighestLevel[0]--;
            if (beforeHighestLevel[0] - 1 >= 0) {
                beforeHighestLevel[1] = GameDataManager.Instance.challengeMapData[beforeHighestLevel[0] - 1].length;
            }
        }
        if (beforeHighestLevel[0] > 0 && beforeHighestLevel[1] > 0) {
            let bigLevel = beforeHighestLevel[0];
            for (let i = 0; i < bigLevel; i++) {
                for (let j = 0; j < GameDataManager.Instance.challengeMapData[i].length; j++) {
                    let data = GameDataManager.Instance.challengeMapData[i][j];
                    let passReward = GameDataManager.Instance.parseRewardData(data.passReward);
                    let firstPassReward = GameDataManager.Instance.parseRewardData(data.firstPassReward);
                    let rewardList = [...passReward, ...firstPassReward];
                    for (let i = 0; i < rewardList.length; i++) {
                        if (rewardList[i][0] == 1) {
                            gold += rewardList[i][1];
                        } else if (rewardList[i][0] == 5) {
                            badge += rewardList[i][1];
                        }
                    }
                }
            }
            if (gold > 0) {
                UserData.instance.gold += gold;
                UserData.instance.onDataChange("gold");
                EventManager.event(EventType.AddMoney, ["gold", gold]);
            }
            if (badge > 0) {
                UserData.instance.badge += badge;
                UserData.instance.onDataChange("badge");
                EventManager.event(EventType.AddMoney, ["badge", badge]);
            }
        } else {

        }
        UserData.instance.challengeLevel = [...UserData.instance.highestChallengeLevel];
        UserData.instance.onDataChange("challengeLevel");
        UserData.instance.isSucceed = true;
        UserData.instance.onDataChange("isSucceed");
        GameDataManager.Instance.uploadChallengeRankScore();
    }
    initMoneyPoint() {
        let badgeMoneyUi: MoneyUiRuntime = this.badgeMoneyUi;
        badgeMoneyUi.setMoneyType("badge", false);
        badgeMoneyUi.moneyIcon.localToGlobal(badgeMoneyUi.moneyPoint, false, this);
    }
    onSelect() {
        this.visible = true;
        GameDataManager.Instance.isPause = true;
        EventManager.event(EventType.PlayerStopMove);
    }
    offSelect() {
        this.visible = false;
        GameDataManager.Instance.isPause = false;
    }
}