import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import TimeTool from "../gframe-work/tool/TimeTool";
import { ui } from "../ui/layaMaxUI";
import CardBagRuntime from "./CardBagRuntime";

export default class CardRuntime extends ui.pages.cardUI {
    public static colorFilter = new Laya.ColorFilter(
        [
            0, 0, 0, 0, 0, //R
            0, 0, 0, 0, 0, //G
            0, 0, 0, 0, 0, //B
            0, 0, 0, 1, 0, //A
        ]);
    public static mask = new Laya.Image();
    data: CardData = null;
    fullHp: number = 0;
    shanSpine: Laya.Skeleton = null;
    storeLeftTime = 0;
    public set dataSource(data: any) {
        if (!data) return;
        this.data = data;
        if (!this.data["isEgg"] && this.data.monsterId == -1) return;
        this.icon.mask = CardRuntime.mask;
        this.eggTimeLeftLabel.visible = false;
        Laya.timer.clear(this, this.onTimeLoop);
        if (this.data["isEgg"]) {
            this.setBtnVideoTag();
            this.hpLabel.text = "???";
            this.atkLabel.text = "???";
            this.starLabel.text = "?";
            this.icon.skin = `main/Ui_MonsterHeadBall.png`;
            this.newTag.visible = false;
            this.setBloodProgress(1);
            this.icon.filters = null;
            this.removeEffect();
            this.redLight.alpha = 0;
            this.hurt.stop();
            this.storeLeftTime = CardBagRuntime.eggStoreTime * 1000 - (Date.now() - this.data["saveTime"]);
            if (this.storeLeftTime > 0) {
                this.eggTimeLeftLabel.visible = true;
                this.eggTimeLeftLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.storeLeftTime));
                Laya.timer.loop(1000, this, this.onTimeLoop);
            }
        } else {
            this.videoTag.visible = false;
            if (GameDataManager.Instance.newMonsterIdList.indexOf(this.data.monsterId) == -1) {
                this.newTag.visible = false;
            } else {
                this.newTag.visible = true;
            }
            this.fullHp = data.hp || this.data.hp;
            if (data.special) {
                this.fullHp += Math.round(Math.max(5, 0.1 * this.fullHp));
            }
            this.hpLabel.text = this.data.curHp + "";
            this.atkLabel.text = (data.atk || this.data.atk) + "";
            this.setStar(this.data.star);
            this.icon.skin = `monster/Ui_MonsterHead${this.data.monsterId}.png`;
            if (UserData.instance.collectMonsterIdList.indexOf(this.data.monsterId) == -1 && !this.data["forceShow"]) {
                this.icon.filters = [CardRuntime.colorFilter];
                this.removeEffect();
            } else {
                if (this.data.monsterId >= 120) {
                    this.addEffect();
                } else {
                    this.removeEffect();
                }
                this.icon.filters = null;
            }
            if (this.data.cardId > 0) {
                //是自己所拥有的卡片
            } else {
                //不是自己拥有的卡片
            }
            if (this.data.curHp == this.fullHp) {
                this.hurt.stop();
                this.redLight.alpha = 0;
            } else {
                if (!this.hurt.isPlaying) {
                    this.hurt.play(0, true);
                }
            }
            this.setBloodProgress(this.data.curHp / this.fullHp);
        }
        if ("canSelect" in this.data) {
            if (this.data["canSelect"]) {
                this.cardBg.disabled = false;
            } else {
                this.cardBg.disabled = true;
            }
        } else {
            this.cardBg.disabled = false;
        }
    }
    addEffect() {
        if (!this.shanSpine) {
            this.shanSpine = Laya.Pool.getItem("ShanSpine");
            if (!this.shanSpine) {
                this.shanSpine = new Laya.Skeleton();
                this.shanSpine.x = 70;
                this.shanSpine.y = 57;
            }
        }
        this.addChild(this.shanSpine);
        this.shanSpine.load("spine/shan.sk", Laya.Handler.create(this, () => {
            this.shanSpine.play(0, true, true, 0);
        }), 0);
    }
    removeEffect() {
        if (this.shanSpine) {
            this.shanSpine.removeSelf();
            Laya.Pool.recover("ShanSpine", this.shanSpine);
            this.shanSpine = null;
        }
    }
    private setBloodProgress(progress: number) {
        Laya.Tween.to(this.hpProgress, { width: progress * 90 }, 100, Laya.Ease.sineOut);
    }
    onTimeLoop() {
        this.storeLeftTime -= 1000;
        if (this.storeLeftTime <= 0) {
            this.removeStoreEgg();
        }
        this.eggTimeLeftLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.storeLeftTime));
    }
    removeStoreEgg() {
        this.storeLeftTime = 0;
        Laya.timer.clear(this, this.onTimeLoop);
        CardBagRuntime.removeStoreEgg(this.data["saveTime"]);
    }
    setStar(star: number) {
        if (star <= 10) {
            if (false && UserData.instance.collectMonsterIdList.indexOf(this.data.monsterId) == -1 && !this.data["forceShow"]) {
                this.starLabel.text = "?";
            } else {
                this.starLabel.text = this.data.star + "";
            }
            this.starIcon.skin = "newSystem/Ui_StarYellow.png";
        } else {
            this.starIcon.skin = "newSystem/Ui_StarPurple.png";
            this.starLabel.text = (this.data.star - 10) + "";
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}