import { ActorBase } from "../gframe-work/actor/ActorBase";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { Node, Vector3 } from "../gframe-work/pathfinding/Geometry";
import { Pathfinding } from "../gframe-work/pathfinding/Pathfinding";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";

export default class Adventurer extends ActorBase {
    endTarget: Vector3 = new Vector3();
    tempVec3: Vector3 = new Vector3();
    moveDir: Laya.Vector2 = new Laya.Vector2();
    walkDir: Laya.Vector3 = new Laya.Vector3();
    translation: Laya.Vector3 = new Laya.Vector3();
    rotation: Laya.Vector3 = new Laya.Vector3();
    forwardDir: Laya.Vector3 = new Laya.Vector3();
    targetDir: Laya.Vector3 = new Laya.Vector3();
    moveSpeed: number = 4.5;
    frameTime: number = 0;
    isMoving: boolean = false;
    lastNode: Node = null;
    pathfinding: Pathfinding = null;
    zoneId: string = "";
    rotateSpeedSection: number[] = [360, 1000];
    isTurning: boolean = false;
    bornPoint: { position: number[], rotation: number[] } = null;
    footDelay: number = 0;
    initPathfinding(pathfinding: Pathfinding, zoneId: string) {
        this.pathfinding = pathfinding;
        this.zoneId = zoneId;
        LayaTool.cloneToVec3(this.gameObject.transform.position, this.tempVec3);
        let groupId = this.pathfinding.getGroup(this.zoneId, this.tempVec3, true);
        this.lastNode = this.pathfinding.getClosestNode(this.tempVec3, this.zoneId, groupId, true);
    }
    initPosAndRot() {
        this.gameObject.transform.position = LayaTool.createVector3FromArray(this.bornPoint.position);
        this.gameObject.transform.rotation = LayaTool.createQuaternionFromArray(this.bornPoint.rotation);
    }
    onAwake() {
        super.onAwake();
        this.moveDir.x = 0;
        this.moveDir.y = -1;
    }
    onUpdate() {
        this.frameTime = Laya.timer.delta / 1000;
        if (this.isMoving) {
            this.turn();
            this.move();
            if (GameDataManager.Instance.enterMapIndex == 2) {
                this.addFoot();
            }
        }
    }
    addFoot() {
        if (this.footDelay == 0) {
            this.footDelay = 300;
            let foot = LayaTool.createSprite3D("Foot", this.gameObject.parent);
            foot.transform.position = this.gameObject.transform.position.clone();
            foot.transform.localRotation = this.gameObject.transform.localRotation.clone();
            Laya.timer.once(2500, this, () => {
                foot.removeSelf();
                Laya.Pool.recover("Foot", foot);
            });
        }
        if (this.footDelay > 0) {
            this.footDelay -= Laya.timer.delta;
            if (this.footDelay < 0) this.footDelay = 0;
        }
    }
    turn() {
        this.gameObject.transform.getForward(this.forwardDir);
        this.forwardDir.y = 0;
        this.forwardDir.x *= -1;
        this.forwardDir.z *= -1;
        this.targetDir.x = -this.moveDir.x;
        this.targetDir.z = -this.moveDir.y;
        this.targetDir.y = 0;
        let diff = MathTool.getRotateRad(this.forwardDir, this.targetDir) * MathTool.rad2Deg;
        if (diff != 0) {
            this.isTurning = true;
            let rotateSpeed = this.rotateSpeedSection[0] + (this.rotateSpeedSection[1] - this.rotateSpeedSection[0]) * Math.abs(diff) / 180;
            let frameRotateSpeed = rotateSpeed * this.frameTime * Math.sign(diff);
            if ((frameRotateSpeed > 0 && frameRotateSpeed > diff) ||
                (frameRotateSpeed < 0 && frameRotateSpeed < diff)
            ) {
                frameRotateSpeed = diff;
            }
            this.rotation.y = frameRotateSpeed;
            this.gameObject.transform.rotate(this.rotation, false, false);
        } else {
            this.isTurning = false;
        }
    }
    move() {
        this.moveSpeed = 4.5 * UserData.instance.speedBuff;
        let frameMoveSpeed = this.moveSpeed * this.frameTime;
        this.walkDir.x = -this.moveDir.x;
        this.walkDir.y = 0;
        this.walkDir.z = -this.moveDir.y;
        Laya.Vector3.normalize(this.walkDir, this.walkDir);
        Laya.Vector3.scale(this.walkDir, frameMoveSpeed, this.translation);
        let nextPosition = new Laya.Vector3();
        let curPosition = this.gameObject.transform.position;
        Laya.Vector3.add(curPosition, this.translation, nextPosition);
        LayaTool.cloneToVec3(nextPosition, this.tempVec3);
        let groupId = this.pathfinding.getGroup(this.zoneId, this.tempVec3, true);
        let node = this.pathfinding.getClosestNode(this.tempVec3, this.zoneId, groupId, true);
        if (node) {
            this.gameObject.transform.position = nextPosition;
            if (this.lastNode != node) {
                this.lastNode = node;
                EventManager.event(EventType.TargetNodeChange, this);
            }
        } else {
            this.pathfinding.init();
            this.pathfinding.clampStep(null, this.tempVec3, this.lastNode, this.zoneId, groupId, this.endTarget);
            LayaTool.cloneToLayaVec3(this.endTarget, nextPosition);
            this.gameObject.transform.position = nextPosition;
        }
        EventManager.event(EventType.UpdateSmallMap, this.gameObject.transform.position);
    }
}