import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import { ui } from "../ui/layaMaxUI";

export default class QuickClickTreasureRuntime extends ui.pages.quickClickTreasureUI {
    public static isOpen: boolean = false;
    private isFade: boolean = false;
    private isShowAd: boolean = false;
    private click_award_add: number = 0.1;
    private click_award_back: number = 0.005;
    private click_award_percent: number[] = [0.4, 0.7];
    private jumpSuccess = false;
    public successFunc: Function = null;
    private curProgress: number = 0;
    private forceTouchAdType: ForceTouchAdType = ForceTouchAdType.None;
    onAwake() {
        this.clickBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.clickBtn.on(Laya.Event.MOUSE_DOWN, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.setProgress(0);
        if (typeof wx != "undefined") {
            (wx as any).onShow(this.onShow.bind(this));
            (wx as any).onHide(this.onShow.bind(this));
        }
        this.successFunc = () => {
            let goldNum = MathTool.getRandom(200, 500);
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, goldNum, 8]);
            this.close();
        }
    }
    onDestroy() {
        Laya.timer.clearAll(this);
        if (typeof wx != "undefined") {
            (wx as any).offShow(this.onShow);
            (wx as any).offHide(this.onHide);
        }
        if (this.forceTouchAdType == ForceTouchAdType.Banner) {
            AllPlatformAdMgr.Instance.hideBannerAd();
            this.backBannerPos();
        } else if (this.forceTouchAdType == ForceTouchAdType.Custom) {
            AllPlatformAdMgr.Instance.hideCustomAd("adunit-239e866b6ff05e2f");
            this.backCustomPos();
        }
    }
    onShow() {
        console.log('onshow');
        // if (this.jumpSuccess) {
        //     if (this.successFunc) this.successFunc();
        // }
    }
    onHide() {
        console.log('onhide');
        // if (this.isShowAd) {
        //     this.jumpSuccess = true;
        // }
    }
    setParam(forceTouchAdType: ForceTouchAdType) {
        this.click_award_add = AllPlatformAdMgr.Instance.click_award_add;
        this.click_award_back = AllPlatformAdMgr.Instance.click_award_back;
        this.click_award_percent = AllPlatformAdMgr.Instance.click_award_percent;
        this.forceTouchAdType = forceTouchAdType;
    }
    onOpened(param: any): void {
        if (this.forceTouchAdType == ForceTouchAdType.Banner) {
            this.initBannerToBtnPos();
        } else if (this.forceTouchAdType == ForceTouchAdType.Custom) {
            this.initCustomToBtnPos();
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.clickBtn) {
            if (e.type == Laya.Event.MOUSE_DOWN) {
                if (this.curProgress < 1) {
                    this.curProgress += this.click_award_add;
                    this.curProgress = this.curProgress > 1 ? 1 : this.curProgress;
                    this.setProgress(this.curProgress);
                    this.isFade = false;
                    if (this.curProgress != 1) {
                        Laya.timer.once(100, this, () => {
                            this.isFade = true;
                        });
                    }
                }
                if (((this.curProgress >= this.click_award_percent[0] && this.curProgress <= this.click_award_percent[1]) || this.curProgress == 1) && !this.isShowAd) {
                    this.isShowAd = true;
                    if (this.forceTouchAdType == ForceTouchAdType.Video) {
                        AllPlatformAdMgr.Instance.showVideoAd("adunit-670d87306dc1c259", null);
                        EventManager.once(EventType.VideoAdClosed, this, (isEnded: boolean) => {
                            if (isEnded) {
                                if (this.successFunc) this.successFunc();
                            } else {
                                this.close();
                            }
                        });
                    } else if (this.forceTouchAdType == ForceTouchAdType.Custom) {
                        if (typeof wx != "undefined") {
                            AllPlatformAdMgr.Instance.showCustomAd("adunit-239e866b6ff05e2f");
                        }
                    } else if (this.forceTouchAdType == ForceTouchAdType.Banner) {
                        if (typeof wx != "undefined") {
                            AllPlatformAdMgr.Instance.showBannerAd();
                            Laya.timer.once(2000, this, () => {
                                if (this.successFunc) this.successFunc();
                            });
                        }
                    }
                }
            }
        } else if (e.target == this.closeBtn) {
            if (e.type == Laya.Event.CLICK) {
                this.close();
            }
        }
    }
    onFrameLoop() {
        if (this.isFade) {
            this.curProgress -= this.click_award_back;
            this.curProgress = this.curProgress < 0 ? 0 : this.curProgress;
            this.setProgress(this.curProgress);
        }
    }
    setProgress(progress: number) {
        this.curProgress = progress;
        this.progressBar.width = 620 * this.curProgress;
    }
    initBannerToBtnPos() {
        if (typeof wx != "undefined") {
            let globalPoint = new Laya.Point();
            globalPoint.x = this.clickBtn.width / 2;
            globalPoint.y = this.clickBtn.height / 2;
            this.clickBtn.localToGlobal(globalPoint, false);
            let top = globalPoint.y / Laya.stage.height * wx.getSystemInfoSync().screenHeight - AllPlatformAdMgr.Instance.banner_Height / 2;
            AllPlatformAdMgr.Instance.initBannerAd("adunit-f5780315db6f4ec6", top);
        }
    }
    backBannerPos() {
        AllPlatformAdMgr.Instance.initBannerAd("adunit-9379d0955f8130cb");
    }
    initCustomToBtnPos() {
        if (typeof wx != "undefined") {
            let globalPoint = new Laya.Point();
            globalPoint.x = this.clickBtn.width / 2;
            globalPoint.y = this.clickBtn.height / 2;
            this.clickBtn.localToGlobal(globalPoint, false);
            let systemInfo = wx.getSystemInfoSync();
            let style = { left: 0, top: 0, fixed: true };
            style.left = globalPoint.x / Laya.stage.width * systemInfo.screenWidth - 68 / 2;
            style.top = globalPoint.y / Laya.stage.height * systemInfo.screenHeight - 106 / 2;
            AllPlatformAdMgr.Instance.initSingleCustomAd("adunit-239e866b6ff05e2f", style);
        }
    }
    backCustomPos() {
        if (typeof wx != "undefined") {
            let systemInfo = wx.getSystemInfoSync();
            let style = { left: 0, top: 0, fixed: true };
            style.left = 0 / Laya.stage.width * systemInfo.screenWidth;
            style.top = (Laya.stage.height / 2 - 200) / Laya.stage.height * systemInfo.screenHeight;
            AllPlatformAdMgr.Instance.initSingleCustomAd("adunit-239e866b6ff05e2f", style);
        }
    }
}
export enum ForceTouchAdType {
    None,
    Banner,
    Video,
    Custom
}