import { Utils } from "./Utils";
import { AStar } from "./AStar";
import { Builder } from "./Builder";
import { Channel } from "./Channel";
import { Geometry, Node, Plane, Triangle, Vector3, Zone } from "./Geometry";

/**
 * Defines an instance of the pathfinding module, with one or more zones.
 */
class Pathfinding {
  zones: Map<string, Zone>;
  plane: Plane;
  constructor() {
    this.zones = new Map<string, Zone>();
    this.plane = new Plane();
    this.point = new Vector3();
    this.triangle = new Triangle();
    this.endPoint = new Vector3();
    this.closestPoint = new Vector3();
  }

  static createZone(geometry: any) {
    geometry = new Geometry(geometry);
    return Builder.buildZone(geometry);
  }

  setZoneData(zoneID: string, zone: Zone) {
    this.zones.set(zoneID, zone);
  }

  getRandomNode(zoneID: string, groupID: number, nearPosition: Vector3, nearRange: number) {
    if (!this.zones.get(zoneID)) return new Vector3();

    nearPosition = nearPosition || null;
    nearRange = nearRange || 0;

    const candidates: Vector3[] = [];
    const polygons = this.zones.get(zoneID).groups[groupID];

    polygons.forEach((p) => {
      if (nearPosition && nearRange) {
        if (
          Utils.distanceToSquared(nearPosition, p.centroid) <
          nearRange * nearRange
        ) {
          candidates.push(p.centroid);
        }
      } else {
        candidates.push(p.centroid);
      }
    });

    return Utils.sample(candidates) || new Vector3();
  }

  getClosestNode(position: Vector3, zoneID: string, groupID: number, checkPolygon = false) {
    const nodes = this.zones.get(zoneID).groups[groupID];
    const vertices = this.zones.get(zoneID).vertices;
    let closestNode: Node = null;
    let closestDistance = Infinity;

    nodes.forEach((node) => {
      const distance = Utils.distanceToSquared(node.centroid, position);
      if (
        distance < closestDistance &&
        (!checkPolygon || Utils.isVectorInPolygon(position, node, vertices))
      ) {
        closestNode = node;
        closestDistance = distance;
      }
    });

    return closestNode;
  }

  findPath(startPosition: Vector3, targetPosition: Vector3, zoneID: string, groupID: number) {
    const nodes = this.zones.get(zoneID).groups[groupID];
    const vertices = this.zones.get(zoneID).vertices;

    const closestNode = this.getClosestNode(
      startPosition,
      zoneID,
      groupID,
      true
    );
    const farthestNode = this.getClosestNode(
      targetPosition,
      zoneID,
      groupID,
      true
    );

    // If we can't find any node, just go straight to the target
    if (!closestNode || !farthestNode) {
      return null;
    }

    const paths = AStar.search(nodes, closestNode, farthestNode);

    const getPortalFromTo = function (a: Node, b: Node) {
      for (var i = 0; i < a.neighbours.length; i++) {
        if (a.neighbours[i] === b.id) {
          return a.portals[i];
        }
      }
    };

    // We have the corridor, now pull the rope.
    const channel = new Channel();
    channel.push(startPosition, undefined);
    for (let i = 0; i < paths.length; i++) {
      const polygon = paths[i];
      const nextPolygon = paths[i + 1];

      if (nextPolygon) {
        const portals = getPortalFromTo(polygon, nextPolygon);
        channel.push(vertices[portals[0]], vertices[portals[1]]);
      }
    }
    channel.push(targetPosition, undefined);
    channel.stringPull();

    // Return the path, omitting first position (which is already known).
    const path = channel.path.map((c) => new Vector3(c.x, c.y, c.z));
    path.shift();
    return path;
  }
  getGroup(zoneID: string, position: Vector3, checkPolygon = false) {
    if (!this.zones.get(zoneID)) return null;
    const plane = this.plane
    let closestNodeGroup = null;
    let distance = Math.pow(50, 2);
    const zone = this.zones.get(zoneID);

    for (let i = 0; i < zone.groups.length; i++) {
      const group = zone.groups[i];
      for (const node of group) {
        if (checkPolygon) {
          plane.setFromCoplanarPoints(
            zone.vertices[node.vertexIds[0]],
            zone.vertices[node.vertexIds[1]],
            zone.vertices[node.vertexIds[2]]
          );
          if (Math.abs(plane.distanceToPoint(position)) < 0.01) {
            const poly = [
              zone.vertices[node.vertexIds[0]],
              zone.vertices[node.vertexIds[1]],
              zone.vertices[node.vertexIds[2]],
            ];
            if (Utils.isPointInPoly(poly, position)) {
              return i;
            }
          }
        }
        const measuredDistance = Utils.distanceToSquared(
          node.centroid,
          position
        );
        if (measuredDistance < distance) {
          closestNodeGroup = i;
          distance = measuredDistance;
        }
      }
    }

    return closestNodeGroup;
  }

  point: Vector3;
  closestPoint: Vector3;
  endPoint: Vector3;
  triangle: Triangle;
  clampStep(startRef: Vector3, endRef: Vector3, node: Node, zoneID: string, groupID: number, endTarget: Vector3) {
    const point = this.point;
    const plane = this.plane;
    const triangle = this.triangle;

    const endPoint = this.endPoint;

    let closestNode: Node;
    let closestDistance: number;

    const vertices = this.zones.get(zoneID).vertices;
    const nodes = this.zones.get(zoneID).groups[groupID];

    const nodeQueue = [node];
    const nodeDepth = {};
    nodeDepth[node.id] = 0;

    closestNode = undefined;
    this.closestPoint.set(0, 0, 0);
    closestDistance = Infinity;

    // Project the step along the current node.
    plane.setFromCoplanarPoints(
      vertices[node.vertexIds[0]],
      vertices[node.vertexIds[1]],
      vertices[node.vertexIds[2]]
    );
    plane.projectPoint(endRef, point);
    endPoint.copy(point);

    for (
      let currentNode = nodeQueue.pop();
      currentNode;
      currentNode = nodeQueue.pop()
    ) {
      triangle.set(
        vertices[currentNode.vertexIds[0]],
        vertices[currentNode.vertexIds[1]],
        vertices[currentNode.vertexIds[2]]
      );

      triangle.closestPointToPoint(endPoint, point);
      if (point.distanceToSquared(endPoint) < closestDistance) {
        closestNode = currentNode;
        this.closestPoint.copy(point);
        closestDistance = point.distanceToSquared(endPoint);
      }

      const depth = nodeDepth[currentNode.id];
      if (depth > 2) continue;

      for (let i = 0; i < currentNode.neighbours.length; i++) {
        const neighbour = nodes[currentNode.neighbours[i]];
        //修改了neighbour为undefined的问题
        if (!neighbour || neighbour.id in nodeDepth) continue;

        nodeQueue.push(neighbour);
        nodeDepth[neighbour.id] = depth + 1;
      }
    }

    endTarget.copy(this.closestPoint);
    return closestNode;
  }
  init() {
    this.point.set(0, 0, 0);
    this.plane.constant = 0;
    this.plane.normal.set(1, 0, 0);
    this.triangle.a.set(0, 0, 0);
    this.triangle.b.set(0, 0, 0);
    this.triangle.c.set(0, 0, 0);
    this.endPoint.set(0, 0, 0);
    this.closestPoint.set(0, 0, 0);
  }
}
export { Pathfinding };
