import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class TryGamePageRuntime extends ui.pages.tryGamePageUI {
    public isTry: boolean = false;
    public jumpSuccess: boolean = false;
    public tryAppData: any;
    onAwake(): void {
        this.gameList.vScrollBarSkin = null;
        this.gameList.array = AllPlatformAdMgr.Instance.appDataList;
        this.gameList.refresh();
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        if (typeof wx != "undefined") {
            (wx as any).onShow(this.onShow.bind(this));
            (wx as any).onHide(this.onHide.bind(this));
        }
        EventManager.on(EventType.ClickGameCell, this, this.onClickGameCell);
    }
    onDestroy(): void {
        if (typeof wx != "undefined") {
            (wx as any).offShow(this.onShow.bind(this));
            (wx as any).offHide(this.onHide.bind(this));
        }
        EventManager.off(EventType.ClickGameCell, this, this.onClickGameCell);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        }
    }
    onClickGameCell(type: string, data: any) {
        if (type == "try") {
            if (typeof wx != "undefined") {
                let outTime = Date.now();
                (wx as any).navigateToMiniProgram({
                    appId: data.appId,
                    success: (res: any) => {
                        // 打开成功
                        this.isTry = true;
                        let tryPlayObj: any = localStorage.getItem("tryPlayObj");
                        if (tryPlayObj) {
                            tryPlayObj = JSON.parse(tryPlayObj);
                        } else {
                            tryPlayObj = {};
                        }
                        tryPlayObj[data.appId] = outTime;
                        localStorage.setItem("tryPlayObj", JSON.stringify(tryPlayObj));
                        console.log(res);
                    },
                    fail: (err: any) => {
                        console.error(err);
                    }
                })
            }
        } else if (type == "receive") {
            LayaTool.openScene("pages/getCard.scene", false, true, Laya.Handler.create(this, () => {
                this.gameList.refresh();
            }), [1, 500, 5]);
        }
    }
    onShow() {
        if (this.isTry) {
            this.isTry = false;
            this.jumpSuccess = false;
            this.gameList.refresh();
            this.tryAppData = null;
            Laya.timer.frameOnce(1, this, () => {
                localStorage.removeItem("tryPlayObj");
            });
        }
    }
    onHide() {
        if (this.isTry) {
            this.jumpSuccess = true;
        }
    }
    public static tryOutTime(appId: string) {
        let tryPlayObj: any = localStorage.getItem("tryPlayObj");
        if (tryPlayObj) {
            tryPlayObj = JSON.parse(tryPlayObj);
            return tryPlayObj[appId] || 0;
        } else {
            return 0;
        }
    }
}