import CardData from "../gamelogic/CardData";
import CatchSceneLogic from "../gamelogic/CatchSceneLogic";
import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import SceneManager from "../gframe-work/manager/SceneManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import TimeTool from "../gframe-work/tool/TimeTool";
import { ui } from "../ui/layaMaxUI";
import CardBagRuntime from "./CardBagRuntime";
import CardRuntime from "./CardRuntime";
import CatchRuntime from "./CatchRuntime";
import HandBookRuntime from "./HandBookRuntime";
import LotteryRuntime from "./LotteryRuntime";
import SignRuntime from "./SignRuntime";
import SpriteShopRuntime from "./SpriteShopRuntime";
import SuperEvaRuntime from "./SuperEvaRuntime";
import TaskRuntime from "./TaskRuntime";

export default class StartRuntime extends ui.pages.startUI {
    curMapIndex: number = 0;
    curCreamMapIndex: number = 0;
    maxMap: number = 0;
    isUpTrigger: boolean = false;
    panelDownX: number = 0;
    mapConfigData: any = null;
    public static mapColorFiter: Laya.ColorFilter = new Laya.ColorFilter([
        0.5, 0, 0, 0, 0, //R
        0, 0.5, 0, 0, 0, //G
        0, 0, 0.5, 0, 0, //B
        0, 0, 0, 1, 0, //A
    ]);
    niuDanTimeLimit: number = 0;
    catchBossTimeLimit: number = 0;
    catchBossActivityTime: number = 120;
    curTabIndex: number = -1;
    curCreamTabIndex: number = -1;

    selectCardDataList: CardData[] = [null, null, null];
    selectCard: CardRuntime = null;
    cloneCard: CardRuntime = null;
    /**0:空白位置 1:上面卡库的位置2：第一张卡位置3：第二张卡位置4：第三张卡位置 */
    posType: number = 0;
    selectFrom: number = 0;
    stopClick = false;
    isMove: boolean = false;
    downPoint: Laya.Point = new Laya.Point();
    isShowMapBox = true;
    lightPosList = [{ x: 111, y: 136 }, { x: 67, y: 218 }, { x: 152, y: 218 }];
    static isElite = false;
    onAwake() {
        this.judgeShowSuperEvaBtn();
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.mapList.hScrollBarSkin = null;
        this.mapList.scrollBar.rollRatio = 0;
        this.mapMonsterList.hScrollBarSkin = null;
        this.mapMonsterList.elasticEnabled = true;
        this.mapMonsterList.mouseHandler = Laya.Handler.create(this, this.onMapMonsterListMouseEvent, null, false);
        this.mapList.on(Laya.Event.MOUSE_UP, this, this.onPanelEvent);
        this.mapList.on(Laya.Event.MOUSE_OUT, this, this.onPanelEvent);
        this.mapList.on(Laya.Event.MOUSE_DOWN, this, this.onPanelEvent);
        this.settingBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.leftBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.superEveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.startBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.shopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.signBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.lotteryBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.tryGameBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.taskBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.catchBossBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.handBookBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.spriteShopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.sendBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.switchEliteBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.switchNormalBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.quickSelectBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.giftPackBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.mapTabBtn.on(Laya.Event.CLICK, this, this.onCreamTabClick, [0]);
        this.wipeBtn.on(Laya.Event.CLICK, this, this.onCreamTabClick, [1]);
        this.initMapList();
        this.curMapIndex = this.getLastMapIndex(false);
        this.curCreamMapIndex = this.getLastMapIndex(true);
        this.tweenList(this.curMapIndex, false);
        this.initRed();
        this.updateNiuDanTimeStamp();
        this.updateExpeditionLeftTime();
        this.updateCatchBossTimeStamp();
        EventManager.on(EventType.SelectCard, this, this.onSelectPageCard);
        (<CardBagRuntime>this.cardBag).visible = false;
        (<CardBagRuntime>this.cardBag).isShowStoreEgg = false;
        (<CardBagRuntime>this.cardBag).exceptFunc = this.exceptFunc.bind(this);
        (<CardBagRuntime>this.cardBag).initMonsterList();

        this.on(Laya.Event.MOUSE_MOVE, this, this.onStartPageEvent);
        this.on(Laya.Event.MOUSE_UP, this, this.onStartPageEvent);

        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OUT, this, this.onCardBagEvent);
        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OVER, this, this.onCardBagEvent);

        let cardBoxList = [this.cardBox1, this.cardBox2, this.cardBox3];
        for (let i = 0; i < cardBoxList.length; i++) {
            let cardBox = cardBoxList[i];
            cardBox.on(Laya.Event.MOUSE_OUT, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.MOUSE_OVER, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.CLICK, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.MOUSE_DOWN, this, this.onCardBoxEvent);
        }

        this.cloneCard = new CardRuntime();
        this.addChild(this.cloneCard);
        this.cloneCard.visible = false;

        this.refreshSelectCardList();
        (<CardBagRuntime>this.cardBag).monsterList.name = "startMonsterList";
        this.judgeUnlockSpriteShop();

        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.on(EventType.UpdateSuperRed, this, this.onUpdateSuperRed);
        this.forcePopSign();
    }
    judgeShowSuperEvaBtn() {
        if (UserData.instance.gameRank >= GameDataManager.Instance.openSuperEvaRank) {
            this.superEveBtn.visible = true;
        } else {
            this.superEveBtn.visible = false;
        }
    }
    updateCatchBossTimeStamp() {
        Laya.timer.clear(this, this.onCatchBossTimeLoop);
        this.catchBossTimeLimit = this.catchBossActivityTime * 1000 - (Date.now() - UserData.instance.catchBossRec.timestamp);
        if (this.catchBossTimeLimit > 0) {
            this.catchBossBtnShake.play(0, true);
            this.catchBossBtn.visible = true;
            this.catchBossTimeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.catchBossTimeLimit));
            Laya.timer.loop(1000, this, this.onCatchBossTimeLoop);
        } else {
            this.catchBossBtn.visible = false;
            this.catchBossBtnShake.stop();
        }
    }
    onCatchBossTimeLoop() {
        this.catchBossTimeLimit -= 1000;
        if (this.catchBossTimeLimit <= 0) {
            this.catchBossTimeLimit = 0;
            this.catchBossBtn.visible = false;
            this.catchBossBtnShake.stop();
            Laya.timer.clear(this, this.onCatchBossTimeLoop);
        }
        this.catchBossTimeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.catchBossTimeLimit));
    }
    updateNiuDanTimeStamp() {
        Laya.timer.clear(this, this.onNiuDanTimeLoop);
        if (UserData.instance.niuDanLeftTime > 0) {
            let timeLimit = 600;
            if (UserData.instance.niuDanLeftTime == 2) {
                timeLimit = 300;
            } else if (UserData.instance.niuDanLeftTime == 3) {
                timeLimit = 180;
            } else if (UserData.instance.niuDanLeftTime == 4) {
                timeLimit = 0;
            }
            this.niuDanTimeLimit = timeLimit * 1000 - (Date.now() - UserData.instance.niuDanTimestamp);
            this.niuDanTimeLimit = this.niuDanTimeLimit > 0 ? this.niuDanTimeLimit : 0;
            if (this.niuDanTimeLimit > 0) {
                this.shopRed.visible = false;
                this.timeLabel.visible = true;
                this.timeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.niuDanTimeLimit));
                Laya.timer.loop(1000, this, this.onNiuDanTimeLoop);
            } else {
                this.timeLabel.visible = false;
                this.shopRed.visible = true;
            }
        } else {
            this.shopRed.visible = false;
            this.timeLabel.visible = false;
        }
    }
    onNiuDanTimeLoop() {
        this.niuDanTimeLimit -= 1000;
        if (this.niuDanTimeLimit <= 0) {
            this.niuDanTimeLimit = 0;
            this.timeLabel.visible = false;
            if (UserData.instance.niuDanLeftTime > 0) {
                this.shopRed.visible = true;
            } else {
                this.shopRed.visible = false;
            }
            Laya.timer.clear(this, this.onNiuDanTimeLoop);
        }
        this.timeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.niuDanTimeLimit));
    }
    initRed() {
        this.signRed.visible = SignRuntime.haveRed();
        this.lotteryRed.visible = LotteryRuntime.haveRed();
        this.taskRed.visible = TaskRuntime.haveRed();
        this.superEveRed.visible = SuperEvaRuntime.haveRed();
        this.handBookRed.visible = HandBookRuntime.haveRed();
        this.shopBtnRed.visible = SpriteShopRuntime.haveRed();
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.off(EventType.UpdateSuperRed, this, this.onUpdateSuperRed);
        EventManager.off(EventType.SelectCard, this, this.onSelectPageCard);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.settingBtn) {
            LayaTool.openScene("pages/setting.scene", true, true, null, null);
        } else if (e.target == this.leftBtn) {
            this.changeMapIndex(-1, this.curTabIndex == 1);
        } else if (e.target == this.rightBtn) {
            this.changeMapIndex(1, this.curTabIndex == 1);
        } else if (e.target == this.superEveBtn) {
            GuideManager.finishInnerTask("点击究极进化");
            LayaTool.openScene("pages/superEva.scene", false, true, null, null);
        } else if (e.target == this.startBtn) {
            this.startGame();
        } else if (e.target == this.shopBtn) {
            LayaTool.openScene("pages/eggShop.scene", false, true, null, null);
        } else if (e.target == this.signBtn) {
            LayaTool.openScene("pages/sign.scene", true, true, null, null);
        } else if (e.target == this.lotteryBtn) {
            LayaTool.openScene("pages/lottery.scene", true, true, null, null);
        } else if (e.target == this.taskBtn) {
            LayaTool.openScene("pages/task.scene", true, true, null, null);
        } else if (e.target == this.catchBossBtn) {
            this.gotoCatchScene();
        } else if (e.target == this.handBookBtn) {
            EventManager.event(EventType.ShowHandBook, true);
        } else if (e.target == this.spriteShopBtn) {
            EventManager.event(EventType.ShowSpriteShop, true);
        } else if (e.target == this.giftPackBtn) {
            LayaTool.openScene("pages/giftPackPage.scene", false, true, null, null);
        } else if (e.target == this.sendBtn) {
            if (UserData.instance.expeditionTimeLeftList[this.curCreamMapIndex] == 0) return;
            UserData.instance.expeditionTimeLeftList[this.curCreamMapIndex] -= 1;
            UserData.instance.onDataChange("expeditionTimeLeftList");
            let cardList: CardData[] = [];
            for (let i = 0; i < this.selectCardDataList.length; i++) {
                let selectCard = this.selectCardDataList[i];
                if (selectCard) {
                    cardList.push(selectCard);
                }
            }
            if (cardList.length == 0) return;
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                this.startExpedition(cardList);
            });
        } else if (e.target == this.switchEliteBtn) {
            let lastMapIndex = this.getLastMapIndex(true);
            if (this.curCreamMapIndex > lastMapIndex) this.curCreamMapIndex = lastMapIndex;
            this.tweenList(this.curCreamMapIndex, true);
        } else if (e.target == this.switchNormalBtn) {
            let lastMapIndex = this.getLastMapIndex(false);
            if (this.curMapIndex > lastMapIndex) this.curMapIndex = lastMapIndex;
            this.tweenList(this.curMapIndex, false);
        } else if (e.target == this.quickSelectBtn) {
            this.showMapBox(false);
            this.quickSelect();
        } else if (e.target == this.tryGameBtn) {
            LayaTool.openScene("pages/tryGamePage.scene", true, true, null, null);
        }
    }
    startGame() {
        let upNum = 0;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightCardList[i]) {
                upNum++;
            }
        }
        if (upNum == 0) {
            EventManager.event(EventType.PopTip, "至少上阵一只怪物");
        } else {
            let curIndex = StartRuntime.isElite ? this.curCreamMapIndex : this.curMapIndex;
            let rank = StartRuntime.isElite ? UserData.instance.eliteGameRank : UserData.instance.gameRank;
            if (curIndex <= rank) {
                GuideManager.finishInnerTask("点击开始挑战按钮");
                Laya.MouseManager.enabled = false;
                EventManager.event(EventType.StartGame);
            } else {
                EventManager.event(EventType.PopTip, "通关上一关");
            }
        }
    }
    setMapHardLevel(level: number) {
        let hardListData = [];
        for (let i = 0; i < level; i++) {
            hardListData.push({ hardIcon: { skin: `main/Ui_GuanLv${i + 1}.png` } });
        }
        this.hardIconList.array = hardListData;
        this.hardIconList.refresh();
    }
    initMapList() {
        let mapDataList = [];
        let listIndex = 0;
        for (const key in GameDataManager.Instance.stageConfig) {
            let mapData = GameDataManager.Instance.stageConfig[key];
            mapDataList.push({ level: mapData.level, index: mapData.mapIndex, listIndex: listIndex });
            listIndex++;
        }
        this.maxMap = mapDataList.length;
        this.mapList.array = mapDataList;
        this.mapList.refresh();
    }
    onPanelEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.panelDownX = e.stageX;
        } else {
            if (e.type == Laya.Event.MOUSE_UP) this.isUpTrigger = true;
            if (e.type == Laya.Event.MOUSE_OUT && this.isUpTrigger) {
                this.isUpTrigger = false;
                return;
            }
            let dx = this.panelDownX - e.stageX;
            if (Math.abs(dx) < 200) {
                if (Math.abs(dx) != 0) {
                    if (StartRuntime.isElite) {
                        this.tweenList(this.curCreamMapIndex, true);
                    } else {
                        this.tweenList(this.curMapIndex, false);
                    }
                }
            } else {
                if (StartRuntime.isElite) {
                    let newMapIndex = this.curCreamMapIndex + Math.sign(dx);
                    if (newMapIndex >= 0 && newMapIndex < this.mapList.length) {
                        this.tweenList(newMapIndex, true);
                    }
                } else {
                    let newMapIndex = this.curMapIndex + Math.sign(dx);
                    if (newMapIndex >= 0 && newMapIndex < this.mapList.length) {
                        this.tweenList(newMapIndex, false);
                    }
                }
            }
        }
    }
    tweenList(index: number, isCream: boolean) {
        if (index == -1) return;
        if (isCream) {
            this.curCreamMapIndex = index;
            this.updateArrow(this.curCreamMapIndex);
        } else {
            this.curMapIndex = index;
            this.updateArrow(this.curMapIndex);
        }
        this.setMapData(isCream);
        let targetX = 720 * index;
        Laya.Tween.to(this.mapList.scrollBar, { value: targetX }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            this.mapList.scrollBar.stopScroll();
        }), 0, true);
    }
    setMapData(isElite: boolean) {
        let mapIndex = isElite ? this.curCreamMapIndex : this.curMapIndex;
        this.mapIndexLabel.text = (mapIndex + 1) + "";
        this.mapConfigData = isElite ? GameDataManager.Instance.creamStageConfig[mapIndex] : GameDataManager.Instance.stageConfig[mapIndex];
        GameDataManager.Instance.enterMapIndex = mapIndex;
        this.mapName.skin = `main/Ui_GuanTittle${this.mapConfigData.mapIndex + 1}.png`;
        this.setMapHardLevel(this.mapConfigData.level);
        this.setMonsterList();
        this.curTabIndex = -1;
        this.curCreamTabIndex = -1;
        this.enableElite(isElite);
        this.updateSwitchBtnState(isElite);
    }
    setMonsterList() {
        let monsterDataList: CardData[] = [];
        let monsterIdList: any[];
        monsterIdList = [...this.mapConfigData.list];
        monsterIdList.push(this.mapConfigData.bossStat.id);
        for (let i = 0; i < monsterIdList.length; i++) {
            let monsterId = monsterIdList[i];
            if ("number" == typeof monsterId && monsterId >= 0) {
                let cardData = GameDataManager.Instance.getOneCardData(monsterId);
                monsterDataList.push(cardData);
            }
        }
        monsterDataList.sort((a, b) => {
            return a.monsterId - b.monsterId;
        });
        this.mapMonsterList.array = monsterDataList;
        this.mapMonsterList.refresh();
        let collectNum = 0;
        for (let i = 0; i < monsterDataList.length; i++) {
            if (UserData.instance.collectMonsterIdList.indexOf(monsterDataList[i].monsterId) != -1) {
                collectNum++;
            }
        }
        this.collectLabel.text = `${collectNum}/${monsterDataList.length}`;
    }
    onUserDataChange(key: string) {
        if (key == "collectMonsterIdList") {
            this.setMonsterList();
        } else if (key == "gameRank") {
            this.judgeShowSuperEvaBtn();
            this.judgeUnlockElite();
            this.judgeUnlockSpriteShop();
            if (!StartRuntime.isElite) {
                let mapIndex = this.getLastMapIndex(false);
                this.tweenList(mapIndex, false);
            }
        } else if (key == "signRec") {
            this.signRed.visible = SignRuntime.haveRed();
        } else if (key == "freeLotteryTimeLeft") {
            this.lotteryRed.visible = LotteryRuntime.haveRed();
        } else if (key == "achieveTaskReceiveRec" ||
            key == "achieveTaskCountRec" ||
            key == "achieveTaskStage" ||
            key == "achieveTaskReceiveRec" ||
            key == "dayTaskReceiveRec" ||
            key == "dayTaskCountRec") {
            this.taskRed.visible = TaskRuntime.haveRed();
        } else if (key == "niuDanTimestamp") {
            this.updateNiuDanTimeStamp();
        } else if (key == "catchBossRec") {
            this.updateCatchBossTimeStamp();
        } else if (key == "cardList") {
            this.superEveRed.visible = SuperEvaRuntime.haveRed();
        } else if (key == "collectReceived" || key == "collectStage" || key == "collectMonsterIdList") {
            this.handBookRed.visible = HandBookRuntime.haveRed();
        } else if (key == "eliteRank") {
            if (StartRuntime.isElite) {
                let creamMapIndex = this.getLastMapIndex(true);
                this.tweenList(creamMapIndex, true);
            }
        } else if (key == "shopBuyRecord") {
            this.shopBtnRed.visible = SpriteShopRuntime.haveRed();
        }
    }
    updateArrow(mapIndex: number) {
        if (this.maxMap <= 1) {
            this.leftBtn.alpha = 0.5;
            this.rightBtn.alpha = 0.5;
        } else {
            if (mapIndex == 0) {
                this.leftBtn.alpha = 0.5;
                this.rightBtn.alpha = 1;
            } else if (mapIndex == this.maxMap - 1) {
                this.rightBtn.alpha = 0.5;
                this.leftBtn.alpha = 1;
            } else {
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;
            }
        }
    }
    onUpdateSuperRed() {
        this.superEveRed.visible = SuperEvaRuntime.haveRed();
    }
    gotoCatchScene() {
        this.stopBossTimeRun(true);
        GameDataManager.Instance.catchMonsterId = UserData.instance.catchBossRec.bossId;
        GameDataManager.Instance.catchMonsterData = GameDataManager.Instance.monsterData[GameDataManager.Instance.catchMonsterId];
        GameDataManager.Instance.isCatch = false;
        SceneManager.Instance.openScene3D("FightScene2", CatchSceneLogic, [true]);
    }
    stopBossTimeRun(isStop: boolean) {
        if (this.catchBossTimeLimit == 0) return;
        if (isStop) {
            Laya.timer.clear(this, this.onCatchBossTimeLoop);
        } else {
            Laya.timer.loop(1000, this, this.onCatchBossTimeLoop);
        }
    }
    onTabClick(index: number, e: Laya.Event) {
        if (index == this.curTabIndex) return;
        this.curTabIndex = index;
        if (index == 0) {
            this.mapInfoBg.skin = "main/Ui_FrameGuan.png";
            this.infoBox1.visible = true;
            this.hardBox.visible = true;
            this.infoBox2.visible = false;
            this.expeditionTimeLeftLabel.visible = false;
            this.wipeBtn.visible = false;
            this.showMapBox(true);
            this.setMonsterList();
            this.setMapHardLevel(this.mapConfigData.level);
            this.rewardBg.visible = true;
            this.setRewardList(false);
            this.clearSelectExpeditionCardList();
            GameDataManager.Instance.initGameSceneConfig(false);
            Laya.timer.frameOnce(1, this, () => {
                EventManager.event(EventType.UpdateBg, [this.mapConfigData.mapIndex, false]);
            });
        } else if (index == 1) {
            this.wipeBtn.visible = true;
            this.curCreamTabIndex = -1;
            this.onCreamTabClick(0, null);
            GameDataManager.Instance.initGameSceneConfig(true);
            Laya.timer.frameOnce(1, this, () => {
                EventManager.event(EventType.UpdateBg, [this.mapConfigData.mapIndex, true]);
            });
        }
    }
    onCreamTabClick(index: number, e: Laya.Event) {
        if (index == 1) {
            if ((UserData.instance.eliteGameRank < GameDataManager.Instance.creamStageMaxIndex && UserData.instance.eliteGameRank <= this.curCreamMapIndex) ||
                (UserData.instance.eliteGameRank == GameDataManager.Instance.creamStageMaxIndex && (UserData.instance.eliteGameRank == this.curCreamMapIndex && !UserData.instance.eliteGameAllPass))
            ) {
                EventManager.event(EventType.PopTip, "先通关当前精英关卡");
                return;
            }
        }
        if (this.curTabIndex != 1 || index == this.curCreamTabIndex) return;
        this.curCreamTabIndex = index;
        if (this.curCreamTabIndex == 0) {
            this.mapInfoBg.skin = "newSystem/Ui_FrameGuan6.png";
            this.infoBox1.visible = true;
            this.hardBox.visible = true;
            this.infoBox2.visible = false;
            this.expeditionTimeLeftLabel.visible = false;
            this.setMonsterList();
            this.setMapHardLevel(this.mapConfigData.level);
            this.rewardBg.visible = true;
            this.setRewardList(true);
            this.showMapBox(true);
        } else if (this.curCreamTabIndex == 1) {
            UserData.instance.refreshExpeditionData();
            this.receiveExpeditionReward();
            this.infoBox1.visible = false;
            let isFighting = UserData.instance.expedition[this.curCreamMapIndex] > 0;
            if (isFighting) {
                this.hardBox.visible = false;
                this.mapInfoBg.skin = "newSystem/Ui_SaoDangPic.png";
                this.infoBox2.visible = false;
                this.expeditionTimeLeftLabel.visible = true;
            } else {
                this.hardBox.visible = true;
                this.mapInfoBg.skin = "newSystem/Ui_FrameGuan2.png";
                this.infoBox2.visible = true;
                this.expeditionTimeLeftLabel.visible = false;
                this.refreshExpeditionTimeLeft();
                this.selectCardDataList = UserData.instance.expeditionCardList[this.curCreamMapIndex];
                this.refreshSelectCardList();
                this.showLight();
            }
            this.rewardBg.visible = false;
        }
    }
    refreshSelectCardList() {
        let cardList = [this.card1, this.card2, this.card3];
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            let selectCardData = this.selectCardDataList[i];
            if (selectCardData) {
                cardList[i].visible = true;
                cardList[i].dataSource = selectCardData;
            } else {
                cardList[i].visible = false;
            }
        }
    }
    onCardBagEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = 1;
        }
    }
    onStartPageEvent(e: Laya.Event) {
        if (!this.selectCard) return;
        if (e.type == Laya.Event.MOUSE_MOVE) {
            if (this.selectCard.visible) {
                this.cloneCard.visible = true;
                this.selectCard.visible = false;
                (<CardBagRuntime>this.cardBag).monsterList.scrollBar.stopScroll();
            }
            if (this.cloneCard.visible) {
                this.cloneCard.x = e.stageX - this.cloneCard.width / 2;
                this.cloneCard.y = e.stageY - this.cloneCard.height / 2;
            }
            this.isMove = true;
        } else if (e.type == Laya.Event.MOUSE_UP) {
            if (this.posType == 0 || this.posType == this.selectFrom || GameDataManager.Instance.isInFightList(this.selectCard.data)) {
                if (this.isMove && this.posType == this.selectFrom && this.posType >= 2 && this.posType <= 4) {
                    this.stopClick = true;
                    Laya.timer.frameOnce(2, this, () => {
                        this.stopClick = false;
                    });
                }
                //空白位置或者位置相同
                this.clearSelect(true);
            } else {
                let parseSelectFrom = this.selectFrom - 2;
                let parsePosType = this.posType - 2;
                if (this.posType == 1) {
                    //卡库位置
                    this.changeSelectList([this.selectCard.data], false, parseSelectFrom);
                    this.showLight();
                    this.clearSelect(false);
                } else {
                    if (this.selectFrom == 1) {
                        this.changeSelectList([this.selectCard.data], true, parsePosType);
                        this.showLight();
                        this.clearSelect(false);
                    } else {
                        let temp = this.selectCardDataList[parsePosType];
                        this.selectCardDataList[parsePosType] = this.selectCardDataList[parseSelectFrom];
                        this.selectCardDataList[parseSelectFrom] = temp;
                        this.changeSelectList([...this.selectCardDataList], true, -1);
                        this.showLight();
                        this.clearSelect(false);
                    }
                }
            }
            this.isMove = false;
        }
    }
    onSelectPageCard(list: Laya.List, e: Laya.Event, card: CardRuntime) {
        if (list != (<CardBagRuntime>this.cardBag).monsterList) return;
        if (card.cardBg.disabled) return;
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.selectFrom = 1;
            this.selectCard = card;
            this.downPoint.x = e.stageX;
            this.downPoint.y = e.stageY;
            this.cloneCard.dataSource = card.data;
        } else if (e.type == Laya.Event.CLICK) {
            let firstEmptyIndex = this.getFirstEmptyIndex();
            if (firstEmptyIndex != -1 && !GameDataManager.Instance.isInFightList(card.data)) {
                this.changeSelectList([card.data], true, firstEmptyIndex);
                this.showLight();
            }
        }
    }
    onCardBoxEvent(e: Laya.Event) {
        let cardBoxList = [this.cardBox1, this.cardBox2, this.cardBox3];
        let cardList = [this.card1, this.card2, this.card3];
        let posTypeList = [2, 3, 4];
        let cardBoxIndex = cardBoxList.indexOf(e.target as Laya.Box);
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = posTypeList[cardBoxIndex];
        } else if (e.type == Laya.Event.MOUSE_DOWN) {
            let pickCard = cardList[cardBoxIndex] as CardRuntime;
            if (!pickCard.visible) return;
            this.selectCard = pickCard;
            this.selectFrom = posTypeList[cardBoxIndex];
            this.downPoint.x = e.stageX;
            this.downPoint.y = e.stageY;
            this.cloneCard.dataSource = this.selectCard.data;
        } else if (e.type == Laya.Event.CLICK) {
            this.showMapBox(false);
            let pickCard = cardList[cardBoxIndex] as CardRuntime;
            if (this.stopClick || !pickCard.visible) return;
            this.changeSelectList([pickCard.data], false, cardBoxIndex);
            this.showLight();

        }
    }
    clearSelect(isRecovery: boolean) {
        this.cloneCard.visible = false;
        if (isRecovery) this.selectCard.visible = true;
        this.selectCard = null;
        this.selectFrom = 0;
        this.posType = 0;
        this.downPoint.x = 0;
        this.downPoint.y = 0;
    }
    getFirstEmptyIndex() {
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            if (this.selectCardDataList[i] == null) {
                return i;
            }
        }
        return -1;
    }
    exceptFunc(cardData: CardData) {
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            let selectCardData = this.selectCardDataList[i];
            if (selectCardData && cardData.cardId == selectCardData.cardId) {
                return false;
            }
        }
        return true;
    }
    changeSelectList(dataList: CardData[], isAdd: boolean, pos: number) {
        for (let i = 0; i < dataList.length; i++) {
            if (isAdd) {
                if (pos != -1) {
                    this.selectCardDataList[pos] = dataList[i];
                } else {
                    this.selectCardDataList[i] = dataList[i];
                }
            } else {
                if (pos != -1) {
                    this.selectCardDataList[pos] = null;
                } else {
                    for (let j = 0; j < this.selectCardDataList.length; j++) {
                        if (dataList[i].cardId == this.selectCardDataList[j].cardId) {
                            this.selectCardDataList[j] = null;
                            break;
                        }
                    }
                }
            }
        }
        this.refreshSelectCardList();
        (<CardBagRuntime>this.cardBag).initMonsterList();
    }
    showMapBox(isShow: boolean) {
        if (isShow == this.isShowMapBox) return;
        this.isShowMapBox = isShow;
        if (isShow) {
            this.topBox.visible = true;
            this.middleBox.visible = true;
            Laya.Tween.to(this.topBox, { alpha: 1, }, 200, Laya.Ease.sineOut);
            Laya.Tween.to(this.middleBox, { alpha: 1, }, 200, Laya.Ease.sineOut);
            Laya.Tween.to(this.cardBag, { alpha: 0, }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.cardBag.visible = false;
            }));
        } else {
            Laya.Tween.to(this.topBox, { alpha: 0, }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.topBox.visible = false;
            }));
            Laya.Tween.to(this.middleBox, { alpha: 0, }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.middleBox.visible = false;
            }));
            this.cardBag.visible = true;
            Laya.Tween.to(this.cardBag, { alpha: 1 }, 200, Laya.Ease.sineOut);
        }
    }
    updateExpeditionLeftTime() {
        Laya.timer.clear(this, this.onExpeditionTimeLoop);
        this.setExpeditionTimeLeftLabel();
        Laya.timer.loop(1000, this, this.onExpeditionTimeLoop);
    }
    setExpeditionTimeLeftLabel() {
        let expeditionMapIndex = this.curCreamMapIndex;
        let expeditionStartTime = UserData.instance.expedition[expeditionMapIndex];
        if (expeditionStartTime > 0) {
            let timeLimit = GameDataManager.Instance.eliteStageConfig[expeditionMapIndex].expeditionTime * 3600 * 1000;
            let expeditionTimeLeft = timeLimit - (Date.now() - expeditionStartTime);
            expeditionTimeLeft = expeditionTimeLeft > 0 ? expeditionTimeLeft : 0;
            this.expeditionTimeLeftLabel.text = TimeTool.convertTimeFormat(expeditionTimeLeft);
        } else {
            this.expeditionTimeLeftLabel.text = "00:00";
        }
    }
    onExpeditionTimeLoop() {
        for (let i = 0; i < UserData.instance.expedition.length; i++) {
            let expeditionMapIndex = i;
            let expeditionStartTime = UserData.instance.expedition[expeditionMapIndex];
            if (expeditionStartTime > 0) {
                let timeLimit = GameDataManager.Instance.eliteStageConfig[expeditionMapIndex].expeditionTime * 3600 * 1000;
                let expeditionTimeLeft = timeLimit - (Date.now() - expeditionStartTime);
                expeditionTimeLeft = expeditionTimeLeft > 0 ? expeditionTimeLeft : 0;
                if (expeditionTimeLeft > 0) {
                    expeditionTimeLeft -= 1000;
                    expeditionTimeLeft = expeditionTimeLeft > 0 ? expeditionTimeLeft : 0;
                }
                if (expeditionTimeLeft == 0) {
                    if (this.curTabIndex == 1 && this.curCreamTabIndex == 1 && this.curCreamMapIndex == expeditionMapIndex) {
                        this.curCreamTabIndex = -1;
                        this.onCreamTabClick(1, null);
                    } else {
                        UserData.instance.refreshExpeditionData();
                    }
                }
                if (this.curTabIndex == 1 && this.curCreamTabIndex == 1) {
                    if (this.curCreamMapIndex == expeditionMapIndex) {
                        this.expeditionTimeLeftLabel.text = TimeTool.convertTimeFormat(expeditionTimeLeft);
                    }
                }
            }
        }
    }
    enableElite(enable: boolean) {
        StartRuntime.isElite = enable;
        this.mapList.refresh();
        if (enable) {
            this.onTabClick(1, null);
        } else {
            this.onTabClick(0, null);
        }
    }
    setRewardList(isCream: boolean) {
        this.rewardBg.skin = isCream ? "newSystem/Ui_FrameGuan8.png" : "newSystem/Ui_FrameGuan7.png";
        let rewardDataList: any[];
        if (!isCream) {
            let passReward = GameDataManager.Instance.passRewardConfig[this.curMapIndex].passReward;
            let rewardList = GameDataManager.Instance.parseRewardData(passReward);
            rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Normal);
        } else {
            let passReward = GameDataManager.Instance.eliteStageConfig[this.curMapIndex].passReward;
            let rewardList = GameDataManager.Instance.parseRewardData(passReward);
            rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Cream);
        }
        let rewardUiDataList = [];
        for (let i = 0; i < rewardDataList.length; i++) {
            let uiData = { label: { text: "", visible: true }, icon: { skin: "" } };
            uiData.label.text = "X" + rewardDataList[i].num;
            if (rewardDataList[i].type == "gold") {
                uiData.icon.skin = "main/Ui_Money.png";
            } else if (rewardDataList[i].type == "badge") {
                uiData.icon.skin = "newSystem/Ui_Badge.png";
            } else if (rewardDataList[i].type == "monster") {
                uiData.icon.skin = "newSystem/Ui_MonsterBlack.png";
                uiData.label.visible = false;
            } else if (rewardDataList[i].type == "chip") {
                uiData.icon.skin = "newSystem/Ui_Chip.png";
            } else if (rewardDataList[i].type == "fightTicket") {
                uiData.icon.skin = "newSystem/Ui_Badgebig.png";
            }
            rewardUiDataList.push(uiData);
        }
        this.rewardList.array = rewardUiDataList;
        this.rewardList.refresh();
        for (let i = 0; i < this.rewardList.length; i++) {
            let cell = this.rewardList.getCell(i) as any;
            let label: Laya.Label = cell.getChildByName("label");
            label.filters = [CatchRuntime.blackColorFilter];
        }
    }
    showLight() {
        let firstEmptyIndex = this.getFirstEmptyIndex();
        if (firstEmptyIndex == -1) {
            this.light.visible = false;
            this.lightBreath.stop();
            this.lightBreath.scaleX = this.lightBreath.scaleY = 1;
        } else {
            this.light.visible = true;
            this.light.x = this.lightPosList[firstEmptyIndex].x;
            this.light.y = this.lightPosList[firstEmptyIndex].y;
            if (!this.lightBreath.isPlaying) {
                this.lightBreath.wrapMode = Laya.Animation.WRAP_PINGPONG;
                this.lightBreath.play(0, true);
            }
        }
    }
    quickSelect() {
        let cardList: CardData[] = [];
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            let cardData = UserData.instance.cardList[i];
            if (cardData.monsterId < 1003 && !GameDataManager.Instance.isInFightList(cardData)) {
                cardList.push(cardData);
            }
        }
        cardList.sort((a, b) => {
            return b.star - a.star;
        });
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            if (cardList[i]) {
                this.selectCardDataList[i] = cardList[i];
            } else {
                this.selectCardDataList[i] = null;
            }
        }
        this.refreshSelectCardList();
        (<CardBagRuntime>this.cardBag).initMonsterList();
        this.showLight();
    }
    clearSelectExpeditionCardList() {
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            this.selectCardDataList[i] = null;
        }
        this.refreshSelectCardList();
    }
    receiveExpeditionReward() {
        if (this.curTabIndex == 1 && this.curCreamTabIndex == 1) {
            let reward = UserData.instance.expeditionReward[this.curCreamMapIndex];
            if (reward > 0) {
                let expeditionReward = GameDataManager.Instance.eliteStageConfig[this.curCreamMapIndex].expeditionReward;
                let expeditionRewardList = GameDataManager.Instance.parseRewardData(expeditionReward);
                let isExtra = reward == 2;
                if (isExtra) {
                    let expeditionExtraReward = GameDataManager.Instance.eliteStageConfig[this.curCreamMapIndex].expeditionExtraReward;
                    let expeditionExtraRewardList = GameDataManager.Instance.parseRewardData(expeditionExtraReward);
                    expeditionRewardList.push(...expeditionExtraRewardList);
                }
                let rewardList = GameDataManager.Instance.countRewardList(expeditionRewardList, FightType.Cream);
                GameDataManager.Instance.receiveReward(rewardList);
                UserData.instance.expeditionReward[this.curCreamMapIndex] = 0;
                UserData.instance.onDataChange("expeditionReward");
            }
        }
    }
    refreshExpeditionTimeLeft() {
        let expeditionLeftTime = UserData.instance.expeditionTimeLeftList[this.curCreamMapIndex];
        this.expeditionLeftTimeLabel.text = expeditionLeftTime + "";
    }
    judgeUnlockSpriteShop() {
        if (UserData.instance.unlockSystem.pk == false) {
            this.spriteShopBtn.visible = false;
            let dayDiff = TimeTool.dayDiff(UserData.instance.registerTimestamp, Date.now());
            if (dayDiff >= 1 && UserData.instance.gameRank >= 3) {
                this.spriteShopBtn.visible = true;
            }
        } else {
            this.spriteShopBtn.visible = true;
        }
    }
    judgeUnlockElite() {
        if (UserData.instance.gameRank >= 3) {
            if (UserData.instance.unlockSystem1.elite == false) {
                UserData.instance.unlockSystem1.elite = true;
                UserData.instance.onDataChange("unlockSystem1");
                LayaTool.openScene("pages/helpPack.scene", false, true, null, ["elite"]);
            }
        }
    }
    startExpedition(cardList: CardData[]) {
        UserData.instance.expeditionCardList[this.curCreamMapIndex] = cardList;
        UserData.instance.expedition[this.curCreamMapIndex] = Date.now();
        UserData.instance.onDataChange("expedition");
        UserData.instance.onDataChange("expeditionCardList");
        this.curCreamTabIndex = -1;
        this.onCreamTabClick(1, null);
        this.updateExpeditionLeftTime();
    }
    forcePopSign() {
        let dayDiff = TimeTool.DateDiff(UserData.instance.registerTimestamp, Date.now());
        if (dayDiff == 0) {
            //第一天
            if (UserData.instance.dayLoginTime == 2) {
                LayaTool.openScene("pages/sign.scene", false, true, null, null);
            }
        } else {
            //第二天之后
            if (UserData.instance.dayLoginTime == 1) {
                LayaTool.openScene("pages/sign.scene", false, true, null, null);
            }
        }
    }
    changeMapIndex(changeValue: number, isCream: boolean) {
        let newMapIndex = this.curMapIndex + changeValue;
        if (isCream) {
            newMapIndex = this.curCreamMapIndex + changeValue;
        }
        if ((Math.sign(changeValue) > 0 && newMapIndex < this.mapList.length) ||
            (Math.sign(changeValue) < 0 && newMapIndex >= 0)
        ) {
            this.tweenList(newMapIndex, isCream);
        }
    }
    getLastMapIndex(isCream: boolean) {
        let mapIndex = UserData.instance.gameRank;
        if (isCream) {
            mapIndex = UserData.instance.eliteGameRank;
        }
        mapIndex = mapIndex >= this.mapList.length ? this.mapList.length - 1 : mapIndex;
        return mapIndex;
    }
    updateSwitchBtnState(isElite: boolean) {
        if (UserData.instance.gameRank < 3) {
            this.switchEliteBtn.visible = false;
            this.switchNormalBtn.visible = false;
        } else {
            this.switchEliteBtn.visible = !isElite;
            this.switchNormalBtn.visible = isElite;
        }
    }
    onMapMonsterListMouseEvent(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            let cardData: CardData = this.mapMonsterList.array[index];
            if (UserData.instance.collectMonsterIdList.indexOf(cardData.monsterId) == -1) {
                LayaTool.openScene("pages/askForCard.scene", false, true, null, [cardData]);
            }
        }
    }
}