import ResManager from "../manager/ResManager";
import { Vector3 } from "../pathfinding/Geometry";

export default class LayaTool {
    /**
     * 根据路径找到节点
     * @param parent 父节点
     * @param url 节点路径
     */
    static findNodeByUrl<T>(parent: any, url: string): T {
        let names = url.split('/');
        let node = parent;
        for (let i = 0; i < names.length; i++) {
            node = node.getChildByName(names[i]);
        }
        return node as T;
    }
    /**
     * 根据名字找到节点
     * @param parent 父节点
     * @param name 节点名字
     */
    static findNodeByName<T>(parent: any, name: string): T {
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child.name == name) {
                return <unknown>child as T
            } else {
                return this.findNodeByName(child, name);
            }
        }
    }
    /**
    * 根据类型找到节点
    * @param parent 父节点
    * @param name 节点名字
    */
    static findNodeByType<T>(parent: any, type: { new(): T; }): T {
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child instanceof type) {
                return child;
            } else {
                return this.findNodeByType(child, type);
            }
        }
    }
    /**
   * 根据类型找到所有节点
   * @param parent 父节点
   * @param name 节点名字
   */
    static findNodesByType<T>(parent: any, type: { new(): T; }): T[] {
        let nodes = [];
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            if (child instanceof type) {
                nodes.push(child);
            }
            if (child.numChildren > 0) {
                nodes = nodes.concat(this.findNodeByType(child, type));
            }
        }
        return nodes;
    }
    /**
     * 获取节点上的组件
     * @param node 节点
     */
    static getComponent<T>(node: any, componentType: { new(): T }): T {
        return node.getComponent(componentType);
    }

    static getComponents<T>(node: any, componentType: { new(): T }): T[] {
        return node.getComponents(componentType);
    }

    static getComponentInChildren<T>(node: any, componentType: { new(): T }): T {
        for (let i = 0; i < node.numChildren; i++) {
            let child = node.getChildAt(i);
            let component = this.getComponent<T>(child, componentType);
            if (component) {
                return component;
            }
            if (child.numChildren > 0) this.getComponentInChildren<T>(child, componentType);
        }
        return null;
    }

    static getComponentsInChildren<T>(node: any, componentType: { new(): T }) {
        for (let i = 0; i < node.numChildren; i++) {
            let child = node.getChildAt(i);
            let components = this.getComponents<T>(child, componentType);
            if (components) {
                return components;
            } else {
                return this.getComponentsInChildren<T>(child, componentType);
            }
        }
    }
    /**3d物体的缓动 */
    static tweenTo(target: any, props: any, duration: number, ease?: Function, complete?: Laya.Handler, delay?: number, coverBefore?: boolean, autoRecover?: boolean) {
        let tweenObj = {};
        for (const key in props) {
            if (Object.prototype.hasOwnProperty.call(props, key)) {
                tweenObj[key + 'Start'] = target.transform[key].clone();
                let diff = new Laya.Vector3();
                Laya.Vector3.subtract(props[key], target.transform[key], diff);
                tweenObj[key + 'Diff'] = diff;
            }
        }
        let tweenTarget = { value: 0 };
        let tween = Laya.Tween.to(tweenTarget, {
            value: 1, update: Laya.Handler.create(this, () => {
                if (!target || !target.transform) {
                    Laya.Tween.clear(tween);
                    return;
                }
                for (const key in props) {
                    if (Object.prototype.hasOwnProperty.call(props, key)) {
                        let now = new Laya.Vector3();
                        let scaleDiff = new Laya.Vector3();
                        Laya.Vector3.scale(tweenObj[key + 'Diff'], tweenTarget.value, scaleDiff);
                        Laya.Vector3.add(tweenObj[key + 'Start'], scaleDiff, now);
                        target.transform[key] = now;
                    }
                }
            }, null, false)
        }, duration, ease, complete, delay, coverBefore, autoRecover);
        return tween;
    }
    /**3d坐标转2d坐标 */
    private static out: Laya.Vector4 = new Laya.Vector4;
    private static vec2: Laya.Vector2 = new Laya.Vector2;
    static convert3DTo2DPosition(camera: Laya.Camera, source: Laya.Vector3) {
        this.convertWorldToCameraProjection(camera, source);
        var x: number = this.out.x, y: number = this.out.y, z: number = this.out.z;
        var w: number = this.out.w;
        if (w < camera.nearPlane) w = camera.nearPlane;
        if (w !== 1.0) {
            x = x / w;
            y = y / w;
            z = z / w;
        }
        let viewport = camera.viewport;
        this.out.x = (x + 1.0) * 0.5 * viewport.width + viewport.x;
        this.out.y = (-y + 1.0) * 0.5 * viewport.height + viewport.y;
        this.out.z = z * (viewport.maxDepth - viewport.minDepth) + viewport.minDepth;
        this.vec2.x = this.out.x / Laya.stage.clientScaleX;
        this.vec2.y = this.out.y / Laya.stage.clientScaleY;
        return this.vec2;
    }
    /**把世界坐标转换到相机的投影空间坐标 */
    static convertWorldToCameraProjection(camera: Laya.Camera, source: Laya.Vector3) {
        let matrix = camera.projectionViewMatrix;
        Laya.Vector3.transformV3ToV4(source, matrix, this.out);
        return this.out;
    }
    /**打开界面 */
    static openScene(name: string, closeOther: boolean, isDialog: boolean, closeHandler: Laya.Handler, param: any[], showEffect = true) {
        Laya.MouseManager.enabled = false;
        Laya.Scene.load(name, Laya.Handler.create(this, (scene: any) => {
            scene.width = Laya.stage.width;
            scene.height = Laya.stage.height;
            if (isDialog) {
                Laya.Dialog.manager.open(scene, closeOther, showEffect);
            } else {
                scene.open(closeOther);
            }
            Laya.MouseManager.enabled = true;
            scene.closeHandler = closeHandler;
            if (scene.setParam) scene.setParam(...param);
        }));
    }
    static lookAt(eye: Laya.Vector3, target: Laya.Vector3, up: Laya.Vector3) {
        let worldPosition = eye;
        if (Math.abs(eye.x - target.x) < Laya.MathUtils3D.zeroTolerance &&
            Math.abs(eye.y - target.y) < Laya.MathUtils3D.zeroTolerance &&
            Math.abs(eye.z - target.z) < Laya.MathUtils3D.zeroTolerance)
            return null;
        let rotation = new Laya.Quaternion();
        Laya.Quaternion.lookAt(target, worldPosition, up, rotation);
        rotation.x *= -1;
        rotation.y *= -1;
        rotation.z *= -1;
        return rotation;
    }
    static createSprite3DWithScript<T>(name: string, cls: new () => T, parent: any): T {
        let poolRes = Laya.Pool.getItem(cls.name);
        if (!poolRes) {
            let res = ResManager.getResClone<Laya.Sprite3D>(name + ".lh");
            poolRes = res.addComponent(cls as any);
            parent.addChild(res);
        } else {
            parent.addChild(poolRes.gameObject);
        }
        return poolRes;
    }
    static createSprite3D(name: string, parent: any) {
        let poolRes = Laya.Pool.getItem(name);
        if (!poolRes) {
            poolRes = ResManager.getResClone<Laya.Sprite3D>(name + ".lh");
        }
        parent.addChild(poolRes);
        return poolRes as Laya.Sprite3D;
    }
    static getVisibleByCamera(camera: Laya.Camera, mesh: Laya.MeshSprite3D | Laya.SkinnedMeshSprite3D) {
        if (mesh instanceof Laya.SkinnedMeshSprite3D) {
            return camera.boundFrustum.intersects(mesh.skinnedMeshRenderer.bounds._boundBox)
        } else {
            return camera.boundFrustum.intersects(mesh.meshRenderer.bounds._boundBox)
        }
    }
    static createVector3FromArray(arr: number[]) {
        return new Laya.Vector3(arr[0], arr[1], arr[2]);
    }
    static createQuaternionFromArray(arr: number[]) {
        return new Laya.Quaternion(arr[0], arr[1], arr[2], arr[3]);
    }
    static cloneToLayaVec3(left: Vector3, right: Laya.Vector3) {
        right.x = left.x;
        right.y = left.y;
        right.z = left.z;
    }
    static cloneToVec3(left: Laya.Vector3, right: Vector3) {
        right.x = left.x;
        right.y = left.y;
        right.z = left.z;
    }
    //t->(0,1)  stx:起始位置     kongzhiX：拉力点   endX ：终点
    static bezier(t: number, stX: number = 0, stY: number = 0, kongzhiX: number, kongzhiY: number, endX: number, endY: number): any {
        var tem = 1 - t;
        var tx = tem * tem * stX + 2 * t * tem * kongzhiX + t * t * endX
        var ty = tem * tem * stY + 2 * t * tem * kongzhiY + t * t * endY
        return { x: tx, y: ty };//返回坐标位置
    }
    static getCharLength(str: string) {
        let charList = str.split("");
        let length = 0;
        for (let i = 0; i < charList.length; i++) {
            let char = charList[i];
            if (char.charCodeAt(0) < 299) {
                length++;
            } else {
                length += 2;
            }
        }
        return length;
    }
    static getSpecifyCharLengthStr(str: string, specifyLen: number, concatWith: string) {
        let charList = str.split("");
        let length = 0;
        let newStr = "";
        for (let i = 0; i < charList.length; i++) {
            let char = charList[i];
            if (char.charCodeAt(0) < 299) {
                length++;
            } else {
                length += 2;
            }
            if (length <= specifyLen) {
                newStr += char;
            } else {
                return newStr + concatWith;
            }
        }
        return newStr;
    }
}
