import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";
import DivisionIconRuntime from "./DivisionIconRuntime";

export default class PkRuntime extends ui.pages.pkUI {
    rewardBoxWinTimeList = [1, 3, 5, 7, 10];
    rewardBoxStateList = [0, 0, 0, 0, 0];
    rewardList = [{ type: "gold", num: 100 }, { type: "chip", num: 100 }, { type: "gold", num: 100 }, { type: "chip", num: 100 }, { type: "gold", num: 20 }];
    curTabIndex = -1;
    onAwake() {
        this.matchBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goShopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rankBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.divisionBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.winTimeLabel.filters = [CatchRuntime.blackColorFilter];
        this.setRewardBoxPos();
        this._list.vScrollBarSkin = null;
        // this._list.visible = false;
        this.wxOpenDataViewer.visible = false;
        this.rankRewardList.vScrollBarSkin = null;
    }
    static haveRed() {
        return false;
    }
    onDestroy(): void {

    }
    onSelect() {
        this.curTabIndex = -1;
        this.setCurTabIndex(0);
        this.visible = true;
        GameDataManager.Instance.isPause = true;
        EventManager.event(EventType.PlayerStopMove);
        EventManager.event(EventType.ShowGoldMoneyUi, false);
    }
    offSelect() {
        this.hideRankList();
        this.visible = false;
        GameDataManager.Instance.isPause = false;
        EventManager.event(EventType.PlayerStopMove);
        EventManager.event(EventType.ShowGoldMoneyUi, true);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.matchBtn) {
            GameDataManager.Instance.pvpEnemyData = this._list.array[MathTool.getRandom(0, this._list.array.length - 1)];
            LayaTool.openScene("pages/pvpMatch.scene", false, true, null, null, false);
        } else if (e.target == this.goShopBtn) {
            EventManager.event(EventType.JumpToPage, 5);
        } else if (e.target == this.rankBtn) {
            this.setCurTabIndex(0);
        } else if (e.target == this.divisionBtn) {
            this.setCurTabIndex(1);
        }
    }
    setRewardBoxPos() {
        let rewardBoxList = [this.box1, this.box2, this.box3, this.box4, this.box5];
        let fullWinTime = this.rewardBoxWinTimeList[this.rewardBoxWinTimeList.length - 1];
        for (let i = 0; i < this.rewardBoxWinTimeList.length; i++) {
            let progress = this.rewardBoxWinTimeList[i] / fullWinTime;
            let box = rewardBoxList[i];
            box.x = progress * 500;
            box.boxIndexLabel.text = (i + 1) + "";
            box.on(Laya.Event.CLICK, this, this.onRewardBoxClick, [i]);
        }
    }
    updateRewardBoxState() {
        let curWinTime = 0;
        let rewardBoxList = [this.box1, this.box2, this.box3, this.box4, this.box5];
        for (let i = 0; i < rewardBoxList.length; i++) {
            let box = rewardBoxList[i];
            let state = 0;
            let target = this.rewardBoxWinTimeList[i];
            if (curWinTime >= target) {
                if (UserData.instance.pvpRewardBoxReceiveList[i] == 1) {
                    state = 2;
                } else {
                    state = 1;
                }
            }
            this.rewardBoxStateList[i] = state;
            this.setRewardBoxState(box, state);
        }
    }
    updateDayWinCountProgress() {
        let dayWinCount = UserData.instance.dayWinCount;
        let fullWinTime = this.rewardBoxWinTimeList[this.rewardBoxWinTimeList.length - 1];
        dayWinCount = dayWinCount > fullWinTime ? fullWinTime : dayWinCount;
        this.dayWinprogress.width = dayWinCount / fullWinTime * 500;
    }
    /**
     * 
     * @param rewardBox 
     * @param state 0:未达到1：达到未打开2：达到打开
     */
    setRewardBoxState(rewardBox: ui.pages.rewardBoxUI, state: number) {
        if (state == 0 || state == 2) {
            rewardBox.redPoint.visible = false;
        } else {
            rewardBox.redPoint.visible = true;
        }
    }
    showRankList() {
        if (typeof wx != "undefined") {
            // this.wxOpenDataViewer.postMsg({ type: "showpvp" });
        }
    }
    hideRankList() {
        if (typeof wx != "undefined") {
            // this.wxOpenDataViewer.postMsg({ type: "hidepvp" });
        }
    }
    showFakeRankList() {
        let listData = [];
        let selfData = { index: 0, avatarIP: "", UserName: "我", RankValue: UserData.instance.winCount, isMe: true };
        listData.push(selfData);
        this.addAiData(listData);
        //根据RankValue排序
        listData.sort((a, b) => {
            return b.RankValue - a.RankValue;
        });
        //增加一个用于查看的index排名
        for (let i = 0; i < listData.length; i++) {
            listData[i].index = i + 1;
        }
        this._list.array = listData;
        this._list.refresh();
    }
    setCurTabIndex(tabIndex: number) {
        if (tabIndex == this.curTabIndex) return;
        this.curTabIndex = tabIndex;
        this.setTabBtnState();
        this.rewardBox.visible = this.curTabIndex == 1;
        this.rankBox.visible = this.curTabIndex == 0;
        this.pvpBg.skin = this.curTabIndex == 0 ? "pvp/Ui_FrameFight2.png" : "pvp/Ui_FrameFight.png";
        if (tabIndex == 0) {
            // this.wxOpenDataViewer.visible = true;
            this.updateRewardBoxState();
            this.updateDayWinCountProgress();
            this.showFakeRankList();
            // this.showRankList();
        } else {
            // this.wxOpenDataViewer.visible = false;
            this.updateDivision();
            this.showRankRewardList();
        }
    }
    setTabBtnState() {
        this.rankBtn.skin = this.curTabIndex == 0 ? "pvp/Btn_FightRank2.png" : "pvp/Btn_FightRank1.png";
        this.divisionBtn.skin = this.curTabIndex == 1 ? "pvp/Btn_Fightgift2.png" : "pvp/Btn_Fightgift1.png";
    }
    onRewardBoxClick(index: number) {
        if (this.rewardBoxStateList[index] == 1) {
            UserData.instance.pvpRewardBoxReceiveList[index] = 1;
            GameDataManager.Instance.receiveReward([this.rewardList[index]]);
            this.updateRewardBoxState();
        }
    }
    showRankRewardList() {
        let rewardDataList = [];
        for (let i = 0; i < GameDataManager.Instance.pvpScoreConfig.length; i++) {
            let pvpScoreData = GameDataManager.Instance.pvpScoreConfig[i];
            if (pvpScoreData.levelReward) {
                let data = { index: i, level: pvpScoreData.level, star: pvpScoreData.star, levelReward: pvpScoreData.levelReward, levelExtraReward: pvpScoreData.levelExtraReward };
                rewardDataList.push(data);
            }
        }
        this.rankRewardList.array = rewardDataList;
        this.rankRewardList.refresh();
    }
    updateDivision() {
        let dataList = GameDataManager.Instance.pvpScoreDataList;
        let divisionData = GameDataManager.Instance.getPvpDivisionDataByScore(UserData.instance.winCount);
        (<DivisionIconRuntime>this.curDivisionIcon).setDivision(divisionData[0]);
        (<DivisionIconRuntime>this.curDivisionIcon).setStarNum(divisionData[1]);
        let nextDivisionData = [...divisionData];
        if (nextDivisionData[1] + 1 >= dataList[nextDivisionData[0] - 1].length) {
            if (nextDivisionData[0] + 1 < dataList.length) {
                nextDivisionData[0]++;
                nextDivisionData[1] = 0;
            }
        } else {
            nextDivisionData[1]++;
        }
        (<DivisionIconRuntime>this.nextDivisionIcon).setDivision(nextDivisionData[0]);
        (<DivisionIconRuntime>this.nextDivisionIcon).setStarNum(nextDivisionData[1]);
        let totalUpgradeScore = dataList[nextDivisionData[0] - 1][nextDivisionData[1] - 1].totalUpgradeScore;
        this.winTimeLabel.text = `${UserData.instance.winCount}/${totalUpgradeScore}`;
        let progress = UserData.instance.winCount / totalUpgradeScore;
        progress = progress > 1 ? 1 : progress;
        this.divisionProgress.width = progress * 250;
    }
    addAiData(listData: any[]) {
        let dataList = GameDataManager.Instance.pvpScoreDataList;
        let totalScore = dataList[dataList.length - 1][2].totalUpgradeScore;
        for (let i = 0; i < GameDataManager.Instance.aiConfig.length; i++) {
            let ai = GameDataManager.Instance.aiConfig[i];
            let otherData = { index: 0, avatarIP: ai.icon, UserName: ai.name, RankValue: MathTool.getRandom(0, totalScore), isMe: false };
            listData.push(otherData);
        }
    }
}