import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class VideoConfirmRuntime extends ui.pages.videoConfirmUI {
    videoClickFunc: Function;
    videoSuccessFunc: Function;
    diamondCost = 10;
    onAwake(): void {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.watchVideoBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.diamondBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (e.target == this.watchVideoBtn) {
            this.videoClickFunc && this.videoClickFunc();
            this.close();
        } else if (e.target == this.diamondBtn) {
            if (UserData.instance.diamond >= this.diamondCost) {
                UserData.instance.diamond -= this.diamondCost;
                UserData.instance.onDataChange("diamond");
                EventManager.event(EventType.UseMoney, "diamond");
                this.videoSuccessFunc && this.videoSuccessFunc();
                this.close();
            } else {

            }
        }
    }
    setParam(videoClickFunc: Function, videoSuccessFunc: Function) {
        this.videoClickFunc = videoClickFunc;
        this.videoSuccessFunc = videoSuccessFunc;
    }
}