import { GameDataManager } from "../gamelogic/GameDataManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class SignRuntime extends ui.pages.signUI {
    cellList: ui.pages.signCellUI[];
    private signconfig = [
        { reward: "金币", num: 100 },
        { reward: "卡片", num: 128 },
        { reward: "金币", num: 300 },
        { reward: "金币", num: 400 },
        { reward: "金币", num: 500 },
        { reward: "金币", num: 600 },
        { reward: "卡片", num: 126 },
    ]
    onAwake() {
        this.cellList = [this.cell1, this.cell2, this.cell3, this.cell4, this.cell5, this.cell6, this.cell7];
        for (let i = 0; i < this.cellList.length; i++) {
            this.cellList[i].on(Laya.Event.CLICK, this, this.onBtnClick);
        }
        this.updateSignReward();
        this.updateSignCellState();
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (e.target == this.videoReceiveBtn) {
            let day = UserData.instance.signDay;
            if (UserData.instance.signRec[day] == 1) {
                //可领取领取了;
            } else {
                //可领取没有领取;
                AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                    this.receiveReward(day, true);
                });
            }
        } else {
            let day = this.cellList.indexOf(e.target as any);
            if (day < UserData.instance.signDay) {
                //领取过了
            } else if (day == UserData.instance.signDay) {
                if (UserData.instance.signRec[day] == 1) {
                    //可领取领取了;
                } else {
                    //可领取没有领取;
                    this.receiveReward(day, false);
                }
            } else if (day > UserData.instance.signDay) {
                //没到时间
            }
        }
    }
    onOpened() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.videoReceiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    updateSignCellState() {
        for (let i = 0; i < 7; i++) {
            if (i < UserData.instance.signDay) {
                this.setDayState(i, 2);
            } else if (i == UserData.instance.signDay) {
                if (UserData.instance.signRec[i] == 1) {
                    this.setDayState(i, 2);
                } else {
                    this.setDayState(i, 1);
                }
            } else if (i > UserData.instance.signDay) {
                this.setDayState(i, 0);
            }
        }
        let day = UserData.instance.signDay;
        if (UserData.instance.signRec[day] == 1) {
            this.videoReceiveBtn.visible = false;
        } else {
            this.videoReceiveBtn.visible = true;
        }
    }
    updateSignReward() {
        for (let i = 0; i < 7; i++) {
            this.setDayReward(i);
        }
    }
    setDayState(day: number, state: number) {
        let cell = this.cellList[day];
        if (state == 2) {
            //已经领取
            cell.maskImage.visible = true;
            cell.redPoint.visible = false;
        } else if (state == 1) {
            //可以领取
            cell.maskImage.visible = false;
            cell.redPoint.visible = true;
            cell.ani1.play(0, true);
        } else if (state == 0) {
            //不可领取
            cell.maskImage.visible = false;
            cell.redPoint.visible = false;
        }
    }
    setDayReward(day: number) {
        let cell = this.cellList[day];
        let reward = this.signconfig[day];
        cell.dayImage.skin = `sign/Ui_Day${day + 1}.png`;
        if (reward.reward == "金币") {
            cell.rewardImage.skin = "count/Ui_TwoGold.png";
            cell.rewardImage.scaleX = cell.rewardImage.scaleY = 1;
            cell.goldLabel.text = "X" + reward.num;
            cell.monsterInfoBg.visible = false;
        } else if (reward.reward == "卡片") {
            cell.starNumLabel.filters = [CatchRuntime.blackColorFilter];
            cell.hpLabel.filters = [CatchRuntime.blackColorFilter];
            cell.atkLabel.filters = [CatchRuntime.blackColorFilter];
            cell.goldLabel.text = "X" + 1;
            cell.rewardImage.scaleX = cell.rewardImage.scaleY = 0.5;
            cell.rewardImage.skin = `monster/Ui_Monster${reward.num}.png`;
            cell.monsterInfoBg.visible = true;
            let cardData = GameDataManager.Instance.getOneCardData(reward.num);
            cell.starNumLabel.text = cardData.star + "";
            cell.hpLabel.text = cardData.hp + "";
            cell.atkLabel.text = cardData.atk + "";
        }
    }
    static haveRed() {
        return UserData.instance.signRec[UserData.instance.signDay] == 0;
    }
    receiveReward(day: number, isDouble: boolean) {
        let reward = this.signconfig[day];
        if (reward.reward == "金币") {
            if (isDouble) reward.num *= 2;
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, reward.num, 5]);
        } else if (reward.reward == "卡片") {
            let monsterDataList = GameDataManager.Instance.getMonsterData([reward.num]);
            let param = [true, reward.num, monsterDataList[0], false, 3];
            LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                if (isDouble) {
                    LayaTool.openScene("pages/catchResult.scene", false, true, null, param);
                }
            }), param);
        }
        UserData.instance.signRec[UserData.instance.signDay] = 1;
        UserData.instance.signTimestamp = Date.now();
        UserData.instance.onDataChange("signRec");
        UserData.instance.onDataChange("signTimestamp");
        this.updateSignCellState();
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}