import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class LuckyPackRuntime extends ui.pages.luckyPackUI {
    monsterCardData: CardData;
    moneyNum: number = 0;
    onAwake() {
        this.videoGetBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        let monsterStar = UserData.instance.gameRank + 3;
        monsterStar = monsterStar > 7 ? 7 : monsterStar;
        let monsterCardData = GameDataManager.Instance.getSpecifyStarMonsterCard([monsterStar, monsterStar], true);
        if (!monsterCardData) {
            monsterCardData = GameDataManager.Instance.getSpecifyStarMonsterCard([monsterStar, monsterStar], false);
        }
        this.starLabel.text = monsterCardData.star + "";
        this.hpLabel.text = monsterCardData.hp + "";
        this.atkLabel.text = monsterCardData.atk + "";
        this.monsterCardData = monsterCardData;
        this.monsterImage.skin = `monster/Ui_Monster${this.monsterCardData.monsterId}.png`;
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (e.target == this.videoGetBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-5e9bb5c9d90565eb", () => {
                GameDataManager.Instance.receiveReward([{ type: "gold", num: 1000 }]);
                UserData.instance.luckPackLeftTime--;
                UserData.instance.onDataChange("luckPackLeftTime");
                let monsterConfig = GameDataManager.Instance.monsterData;
                LayaTool.openScene("pages/catchResult.scene", false, true, null, [true, this.monsterCardData.monsterId, monsterConfig[this.monsterCardData.monsterId], false, 4, this.monsterCardData.special]);
                this.close();
            });
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}