import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CardBagRuntime from "./CardBagRuntime";
import CardRuntime from "./CardRuntime";

export default class ComposeRuntime extends ui.pages.composeUI {
    selectCard: CardRuntime = null;
    cloneCard: CardRuntime = null;
    /**0:空白位置1：上面2：第一张卡的位置3：第二张卡的位置 */
    posType: number = 0;
    selectFrom: number = 0;
    static composeSelectData: CardData[] = [null, null];
    composeResultData: CardData = new CardData();
    mixCost: number = 0;
    stopClick = false;
    downY: number = 0
    downX: number = 0;
    isMove: boolean = false;
    downPoint: Laya.Point = new Laya.Point();
    onAwake() {
        EventManager.on(EventType.SelectCard, this, this.onSelectPageCard);
        EventManager.on(EventType.ComposeEnd, this, this.onComposeEnd);
        (<CardBagRuntime>this.cardBag).isShowStoreEgg = true;
        (<CardBagRuntime>this.cardBag).exceptFunc = this.exceptFunc.bind(this);
        (<CardBagRuntime>this.cardBag).initMonsterList();

        this.on(Laya.Event.MOUSE_MOVE, this, this.onComposePageEvent);
        this.on(Laya.Event.MOUSE_UP, this, this.onComposePageEvent);

        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OUT, this, this.onCardBagEvent);
        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OVER, this, this.onCardBagEvent);

        this.cardBox1.on(Laya.Event.MOUSE_OUT, this, this.onComposeListEvent);
        this.cardBox1.on(Laya.Event.MOUSE_OVER, this, this.onComposeListEvent);
        this.cardBox1.on(Laya.Event.CLICK, this, this.onComposeListEvent);
        this.cardBox1.on(Laya.Event.MOUSE_DOWN, this, this.onComposeListEvent);

        this.cardBox2.on(Laya.Event.MOUSE_OUT, this, this.onComposeListEvent);
        this.cardBox2.on(Laya.Event.MOUSE_OVER, this, this.onComposeListEvent);
        this.cardBox2.on(Laya.Event.CLICK, this, this.onComposeListEvent);
        this.cardBox2.on(Laya.Event.MOUSE_DOWN, this, this.onComposeListEvent);

        this.quickSelectBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.composeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);

        this.refreshComposeList();
        (<CardRuntime>this.composeResultCard).visible = false;
        (<CardRuntime>this.card1).visible = false;
        (<CardRuntime>this.card2).visible = false;
        this.cloneCard = new CardRuntime();
        this.cloneCard.visible = false;
        this.addChild(this.cloneCard);
        this.quickSelectRed.visible = ComposeRuntime.haveRed();
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
    }
    onUserDataChange(key: string) {
        if (key == "fightCardList" || key == "cardList") {
            let isRed = ComposeRuntime.haveRed();
            this.quickSelectRed.visible = isRed;
            EventManager.event(EventType.UpdateComposeRed, isRed);
        }
    }
    clearSelect(isRecovery: boolean) {
        this.cloneCard.visible = false;
        if (isRecovery) this.selectCard.visible = true;
        this.selectCard = null;
        this.downPoint.x = 0;
        this.downPoint.y = 0;
        this.selectFrom = 0;
        this.posType = 0;
    }
    onComposeListEvent(e: Laya.Event) {
        if (e.target == this.cardBox1) {
            if (e.type == Laya.Event.MOUSE_OUT) {
                this.posType = 0;
            } else if (e.type == Laya.Event.MOUSE_OVER) {
                this.posType = 2;
            } else if (e.type == Laya.Event.MOUSE_DOWN) {
                if (!(<CardRuntime>this.card1).visible) return;
                let card = (<CardRuntime>this.card1);
                this.selectCard = card;
                this.selectFrom = 2;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.posType = 2;
                this.cloneCard.dataSource = this.selectCard.data;
                this.downY = e.stageY;
                this.downX = e.stageX;
            } else if (e.type == Laya.Event.CLICK) {
                if (this.stopClick || !(<CardRuntime>this.card1).visible) return;
                this.changeComposeList([(<CardRuntime>this.card1).data], false, 0);
            }
        } else {
            if (e.type == Laya.Event.MOUSE_OUT) {
                this.posType = 0;
            } else if (e.type == Laya.Event.MOUSE_OVER) {
                this.posType = 3;
            } else if (e.type == Laya.Event.MOUSE_DOWN) {
                if (!(<CardRuntime>this.card2).visible) return;
                let card = (<CardRuntime>this.card2);
                this.selectCard = card;
                this.selectFrom = 3;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.posType = 3;
                this.cloneCard.dataSource = this.selectCard.data;
                this.downY = e.stageY;
            } else if (e.type == Laya.Event.CLICK) {
                if (this.stopClick || !(<CardRuntime>this.card2).visible) return;
                this.changeComposeList([(<CardRuntime>this.card1).data], false, 1);
            }
        }
    }
    onCardBagEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = 1;
        }
    }
    onComposePageEvent(e: Laya.Event) {
        if (!this.selectCard) return;
        if (e.type == Laya.Event.MOUSE_MOVE) {
            console.log(e.stageX, e.stageY, this.downPoint.x, this.downPoint.y);
            if (e.stageX == this.downPoint.x && e.stageY == this.downPoint.y) return;
            this.isMove = true;
            if (this.selectCard.visible) {
                this.cloneCard.visible = true;
                this.selectCard.visible = false;
                (<CardBagRuntime>this.cardBag).monsterList.scrollBar.stopScroll();
            }
            if (this.cloneCard.visible) {
                this.cloneCard.x = e.stageX - this.cloneCard.width / 2;
                this.cloneCard.y = e.stageY - this.cloneCard.height / 2;
            }
        } else if (e.type == Laya.Event.MOUSE_UP) {
            if (this.posType == 0 || this.posType == this.selectFrom || !ComposeRuntime.isAllowCompose(this.selectCard.data)) {
                if (this.isMove && this.posType == this.selectFrom && this.posType >= 2 && this.posType <= 3) {
                    this.stopClick = true;
                    Laya.timer.frameOnce(2, this, () => {
                        this.stopClick = false;
                    });
                }
                //空白位置
                this.clearSelect(true);
            } else if (this.posType == 1) {
                //卡库位置
                if (this.selectFrom == 2) {
                    this.changeComposeList([this.selectCard.data], false, 0);
                    this.clearSelect(false);
                } else if (this.selectFrom == 3) {
                    this.changeComposeList([this.selectCard.data], false, 1);
                    this.clearSelect(false);
                }
            } else if (this.posType == 2) {
                //第一张卡的位置
                if (this.selectFrom == 1) {
                    this.changeComposeList([this.selectCard.data], true, 0);
                    this.clearSelect(true);
                } else if (this.selectFrom == 3) {
                    let temp = ComposeRuntime.composeSelectData[0];
                    ComposeRuntime.composeSelectData[0] = ComposeRuntime.composeSelectData[1];
                    ComposeRuntime.composeSelectData[1] = temp;
                    this.changeComposeList([...ComposeRuntime.composeSelectData], true, -1);
                    this.clearSelect(false);
                }
            } else if (this.posType == 3) {
                //第二张卡的位置
                if (this.selectFrom == 1) {
                    this.changeComposeList([this.selectCard.data], true, 1);
                    this.clearSelect(true);
                } else if (this.selectFrom == 2) {
                    let temp = ComposeRuntime.composeSelectData[1];
                    ComposeRuntime.composeSelectData[1] = ComposeRuntime.composeSelectData[0];
                    ComposeRuntime.composeSelectData[0] = temp;
                    this.changeComposeList([...ComposeRuntime.composeSelectData], true, -1);
                    this.clearSelect(false);
                }
            }
            this.isMove = false;
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.quickSelectBtn) {
            let subData = { page_id: 10, button_id: 11, ad_id: 2, ad_type: 4 };
            TongYiEventManager.sendEvent(10002, subData);
            ComposeRuntime.composeSelectData[0] = null;
            ComposeRuntime.composeSelectData[1] = null;
            let cardDataList: CardData[] = [];
            for (let i = 0; i < UserData.instance.cardList.length; i++) {
                let card = UserData.instance.cardList[i];
                if (ComposeRuntime.isAllowCompose(card)) {
                    cardDataList.push(card);
                }
            }
            cardDataList.sort((a, b) => {
                let dataList = GameDataManager.Instance.getMonsterData([a.monsterId, b.monsterId]);
                if (dataList[0].level == dataList[1].level) {
                    if (dataList[0].atk == dataList[1].atk) {
                        if (dataList[0].hp == dataList[1].hp) {
                            let reduceHp1 = dataList[0].hp - a.curHp;
                            let reduceHp2 = dataList[1].hp - b.curHp;
                            return reduceHp2 - reduceHp1;
                        } else {
                            return dataList[1].hp - dataList[0].hp;
                        }
                    } else {
                        return dataList[1].atk - dataList[0].atk;
                    }
                } else {
                    return dataList[0].level - dataList[1].level;
                }
            });
            let dataList: CardData[] = [];
            for (let i = 0; i < (cardDataList.length > 2 ? 2 : cardDataList.length); i++) {
                dataList.push(cardDataList[i]);
            }
            this.changeComposeList(dataList, true, -1);
        } else if (e.target == this.composeBtn) {
            let cardNum = 0;
            for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
                if (ComposeRuntime.composeSelectData[i]) {
                    cardNum++;
                }
            }
            if (cardNum < 2) {
                EventManager.event(EventType.PopTip, "合成不足");
                return;
            }
            if (GuideManager.isOpen) {
                this.startCompose();
                return;
            }
            if (UserData.instance.gold >= this.mixCost) {
                let levelLimit = UserData.instance.gameRank + 3;
                let mixLevel = GameDataManager.Instance.monsterData[this.composeResultData.monsterId].level;
                if (mixLevel > levelLimit) {
                    EventManager.event(EventType.PopTip, "合成等级超过");
                    return;
                }
                this.startCompose();
                UserData.instance.gold -= this.mixCost;
                UserData.instance.onDataChange("gold");
                TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: this.mixCost * -1, get_type: 10, last_num: UserData.instance.gold });
                EventManager.event(EventType.UseMoney, "gold");
            } else {
                LayaTool.openScene("pages/addMoney.scene", false, true, null, [2, 200]);
                EventManager.event(EventType.ShowGiftPack);
            }
        }
    }
    startCompose() {
        for (let j = 0; j < ComposeRuntime.composeSelectData.length; j++) {
            let index = UserData.instance.cardList.indexOf(ComposeRuntime.composeSelectData[j]);
            if (index != -1) UserData.instance.cardList.splice(index, 1);
        }
        this.playCardFly();
        EventManager.event(EventType.ShowComposeEffect);
        Laya.MouseManager.enabled = false;
        GuideManager.finishInnerTask("点击合成");
    }
    playCardFly() {
        Laya.stage.addChild(this.card1);
        Laya.stage.addChild(this.card2);
        let point = new Laya.Point(0, 0);
        this.cardBox1.localToGlobal(point, false);
        this.card1.x = point.x;
        this.card1.y = point.y;
        this.card1.zOrder = 1000;
        point.x = 0;
        point.y = 0;
        this.cardBox2.localToGlobal(point, false);
        this.card2.x = point.x;
        this.card2.y = point.y;
        this.card2.zOrder = 1000;
        Laya.Tween.to(this.card1, { x: Laya.stage.width / 2 - 70, y: Laya.stage.height / 2 - 174 }, 500, Laya.Ease.sineOut);
        Laya.Tween.to(this.card2, { x: Laya.stage.width / 2 - 70, y: Laya.stage.height / 2 - 174 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.card1, { alpha: 0 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.cardBox1.addChild(this.card1);
                this.card1.x = 0;
                this.card1.y = 0;
                this.card1.alpha = 1;
            }));
            Laya.Tween.to(this.card2, { alpha: 0 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.cardBox2.addChild(this.card2);
                this.card2.x = 0;
                this.card2.y = 0;
                this.card2.alpha = 1;
                this.clearCompose();
            }));
        }), 250);
    }
    onComposeEnd() {
        Laya.MouseManager.enabled = true;
        EventManager.event(EventType.PopTip, "合成成功");
        ResManager.playSound("composeSuccess.mp3");
        UserData.instance.finishTask(0, 2, 1);
        let monsterData = GameDataManager.Instance.getMonsterData([this.composeResultData.monsterId]);
        let closeHandler = Laya.Handler.create(this, () => {
            if (this.composeResultData.star >= 3 && UserData.instance.luckPackLeftTime > 0) {
                LayaTool.openScene("pages/luckyPack.scene", false, true, null, null, true);
            }
        });
        LayaTool.openScene("pages/catchResult.scene", true, true, closeHandler, [true, this.composeResultData.monsterId, monsterData[0], false, 2]);
    }
    clearCompose() {
        ComposeRuntime.composeSelectData[0] = null;
        ComposeRuntime.composeSelectData[1] = null;
        this.refreshComposeList();
        let isRed = ComposeRuntime.haveRed();
        this.quickSelectRed.visible = isRed;
        EventManager.event(EventType.UpdateComposeRed, isRed);
        this.mixCost = 0;
        this.composeCostLabel.text = this.mixCost + "";
        (<CardRuntime>this.composeResultCard).visible = false;
    }
    changeComposeList(dataList: CardData[], isAdd: boolean, pos: number) {
        for (let i = 0; i < dataList.length; i++) {
            if (isAdd) {
                if (pos != -1) {
                    ComposeRuntime.composeSelectData[pos] = dataList[i];
                } else {
                    ComposeRuntime.composeSelectData[i] = dataList[i];
                }
            } else {
                if (pos != -1) {
                    ComposeRuntime.composeSelectData[pos] = null;
                } else {
                    for (let j = 0; j < ComposeRuntime.composeSelectData.length; j++) {
                        if (dataList[i].cardId == ComposeRuntime.composeSelectData[j].cardId) {
                            ComposeRuntime.composeSelectData[j] = null;
                            break;
                        }
                    }
                }
            }
        }
        (<CardBagRuntime>this.cardBag).initMonsterList();
        this.refreshComposeList();
        let isRed = ComposeRuntime.haveRed();
        this.quickSelectRed.visible = isRed;
        EventManager.event(EventType.UpdateComposeRed, isRed);
        let cardNum = 0;
        for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
            if (ComposeRuntime.composeSelectData[i]) {
                cardNum++;
            }
        }
        if (cardNum == 2) {
            let mixMonsterId = 0;
            if (this.haveNewAddMonsterId()) {
                mixMonsterId = this.getNewAddMixMonsterId();
            } else {
                mixMonsterId = GameDataManager.Instance.mixConfig[ComposeRuntime.composeSelectData[0].monsterId].tab[ComposeRuntime.composeSelectData[1].monsterId];
            }
            let mixMonsterData = GameDataManager.Instance.getMonsterData([mixMonsterId])[0];
            this.composeResultData = GameDataManager.Instance.getOneCardData(mixMonsterId);
            (<CardRuntime>this.composeResultCard).visible = true;
            (<CardRuntime>this.composeResultCard).dataSource = this.composeResultData;
            this.mixCost = GameDataManager.Instance.mixCostConfig[mixMonsterData.level - 1].cost;
            this.composeCostLabel.text = this.mixCost + "";
        } else {
            (<CardRuntime>this.composeResultCard).visible = false;
            this.mixCost = 0;
            this.composeCostLabel.text = this.mixCost + "";
        }
        if (cardNum == 1) {
            GuideManager.finishInnerTask("选择第一只怪");
        } else {
            GuideManager.finishInnerTask("选择第二只怪");
        }
    }
    refreshComposeList() {
        let cardList = [this.card1, this.card2];
        for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
            if (ComposeRuntime.composeSelectData[i]) {
                cardList[i].visible = true;
                cardList[i].dataSource = ComposeRuntime.composeSelectData[i];
            } else {
                cardList[i].visible = false;
            }
        }
    }
    exceptFunc(cardData: CardData) {
        if (GameDataManager.Instance.isSuperEva(cardData)) {
            return false;
        }
        for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
            if (ComposeRuntime.composeSelectData[i] && cardData.cardId == ComposeRuntime.composeSelectData[i].cardId) {
                return false;
            }
        }
        return true;
    }
    onDestroy() {
        EventManager.off(EventType.SelectCard, this, this.onSelectPageCard);
        EventManager.off(EventType.ComposeEnd, this, this.onComposeEnd);
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
    }
    onSelectPageCard(list: Laya.List, e: Laya.Event, card: CardRuntime) {
        if (list != (<CardBagRuntime>this.cardBag).monsterList) return;
        if (card.data["isEgg"]) {
            if (e.type == Laya.Event.MOUSE_DOWN) {
                AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                    LayaTool.openScene("pages/superEggMachine.scene", false, true, null, [false, true]);
                    card.removeStoreEgg();
                });
            }
        } else {
            if (e.type == Laya.Event.MOUSE_DOWN) {
                this.selectFrom = 1;
                this.selectCard = card;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.cloneCard.dataSource = card.data;
            } else if (e.type == Laya.Event.CLICK) {
                let index = this.getFirstEmptyIndex();
                if (index == -1) return;
                if (!ComposeRuntime.isAllowCompose(card.data)) return;
                this.changeComposeList([card.data], true, index);
            }
        }
    }
    offSelect() {
        this.clearCompose();
        (<CardBagRuntime>this.cardBag).initMonsterList();
        this.visible = false;
        GameDataManager.Instance.isPause = false;
        GuideManager.finishInnerTask("点击关闭合成");
        if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
    }
    onSelect() {
        this.visible = true;
        GameDataManager.Instance.isPause = true;
        EventManager.event(EventType.PlayerStopMove);
        GuideManager.finishInnerTask("点击怪物合成");
    }
    haveNewAddMonsterId() {
        for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
            let selectData = ComposeRuntime.composeSelectData[i];
            if (selectData.monsterId >= 129 && selectData.monsterId <= 1008) {
                return true;
            }
        }
        return false;
    }
    getNewAddMixMonsterId() {
        let mixStar = 1;
        if (ComposeRuntime.composeSelectData[0].star == ComposeRuntime.composeSelectData[1].star) {
            mixStar = ComposeRuntime.composeSelectData[0].star + 1;
        } else {
            mixStar = Math.floor((ComposeRuntime.composeSelectData[0].star + ComposeRuntime.composeSelectData[1].star) / 2);
        }
        mixStar = mixStar > 10 ? 10 : mixStar;
        let monsterIdList = [2, 12, 22, 34, 46, 57, 71, 81, 90, 101];
        return monsterIdList[mixStar - 1];
    }
    public static haveRed() {
        let normalLength = 0;
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            let card = UserData.instance.cardList[i];
            if (this.isAllowCompose(card)) {
                normalLength++;
            }
        }
        return normalLength >= 2 && ComposeRuntime.composeSelectData.indexOf(null) != -1;
    }
    getFirstEmptyIndex() {
        for (let i = 0; i < ComposeRuntime.composeSelectData.length; i++) {
            if (ComposeRuntime.composeSelectData[i] == null) {
                return i;
            }
        }
        return -1;
    }
    static isAllowCompose(cardData: CardData) {
        if (GameDataManager.Instance.isTryCard(cardData) ||
            GameDataManager.Instance.isSuperEva(cardData)
        ) {
            return false;
        }
        return true;
    }
}