import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class SucceedDialogRuntime extends ui.pages.succeedUI {
    onAwake() {
        this.levelLabel.text = `${UserData.instance.highestChallengeLevel[0]}-${UserData.instance.highestChallengeLevel[1]}`;
        this.levelLabel.filters = [CatchRuntime.blackColorFilter];
        this.yesBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.yesBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-eb988867c0754d81", () => {
                this.close("yes");
            });
        } else if (e.target == this.closeBtn) {
            this.close("no");
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}