import { GameDataManager } from "../gamelogic/GameDataManager";
import { ui } from "../ui/layaMaxUI";

export default class ShopCellRuntime extends ui.pages.shopCellUI {
    data: any = null;
    set dataSource(data: any) {
        this.data = data;
        if (!data) return;
        this.setItemState();
        this.setItemInfo(this.data.goods[0]);
        this.setItemMoney(this.data);
        this.setItemExtra(this.data);
        this.setItemName(this.data);
    }
    setItemState() {
        let buyTime = GameDataManager.Instance.getShopItemBuyTime(this.data.productId);
        if (this.data.limit > 0 && buyTime >= this.data.limit) {
            this.itemMask.visible = true;
        } else {
            this.itemMask.visible = false;
        }
        if (this.data.limit > 0) {
            this.limitLabel.visible = true;
            this.limitLabel.text = `每日限购：${buyTime}/${this.data.limit}`;
        } else {
            this.limitLabel.visible = false;
        }
    }
    setItemInfo(goods: any) {
        if (goods.type == "gold") {
            this.itemImage.skin = "count/Ui_TwoGold.png";
        } else if (goods.type == "chip") {
            this.itemImage.skin = "newSystem/Ui_MonsterHeadChip.png";
        } else if (goods.type == "fightTicket") {
            this.itemImage.skin = "spriteShop/Ui_TwoQuan.png";
        } else if (goods.type == "monster") {
            this.itemImage.skin = `monster/Ui_Monster${goods.num}.png`;
        } else if (goods.type == "diamond") {
            this.itemImage.skin = `pay/Ui_Diamonds.png`;
        }
        if (goods.type == "monster") {
            this.starBg.visible = true;
            this.starLabel.text = GameDataManager.Instance.monsterData[goods.num].level;
            this.itemImage.scaleX = this.itemImage.scaleY = 0.5;
        } else {
            this.starBg.visible = false;
            this.itemImage.scaleX = this.itemImage.scaleY = 1;
        }
    }
    setItemName(goods: any) {
        if (goods.goods[0].type == "monster") {
            this.itemNameLabel.text = GameDataManager.Instance.monsterData[goods.goods[0].num].name;
        } else {
            let usNameList = ["gold", "diamond", "chip", "badge", "fightTicket"];
            let cnNameList = ["金币", "钻石", "碎片", "徽章", "战斗券"];
            let num = GameDataManager.Instance.haveExtra(goods) ? (goods.goods[0].num + goods.extra) : goods.goods[0].num;
            this.itemNameLabel.text = cnNameList[usNameList.indexOf(goods.goods[0].type)] + "X" + num;
        }
    }
    setItemMoney(goods: any) {
        if (goods.costType == "rmb") {
            this.itemMoneyIcon.visible = false;
            if (goods.costNum == 0) {
                this.itemMoneyNum.visible = false;
                this.freeTag.visible = true;
            } else {
                this.itemMoneyNum.visible = true;
                this.freeTag.visible = false;
                this.itemMoneyNum.value = "￥" + goods.costNum;
            }
        } else {
            this.itemMoneyNum.visible = true;
            this.freeTag.visible = false;
            this.itemMoneyIcon.visible = true;
            this.itemMoneyNum.value = goods.costNum;
            if (goods.costType == "diamond") {
                this.itemMoneyIcon.skin = "pay/Ui_Diamond.png";
            } else if (goods.costType == "badge") {
                this.itemMoneyIcon.skin = "newSystem/Ui_Badge.png";
            }
        }
    }
    setItemExtra(goods: any) {
        if (GameDataManager.Instance.haveExtra(goods)) {
            this.extraTag.visible = true;
            this.extraValue.value = Math.floor(goods.extra / goods.goods[0].num * 100) + "%";
        } else {
            this.extraTag.visible = false;
        }
    }
}