import { GameDataManager } from "../gamelogic/GameDataManager";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class MapCellRuntime extends ui.pages.mapCellUI {
    data: any;
    set dataSource(data: any) {
        this.data = data;
        if (!data) return;
        this.bg.skin = `newSystem/Ui_Map${data.bigLevel}.png`;
        let cellPosConfig = [{ x: 398, y: 245 }, { x: 251, y: 184 }, { x: 267, y: 304 }, { x: 375, y: 378 }, { x: 212, y: 327 },
        { x: 256, y: 339 }, { x: 221, y: 309 }, { x: 316, y: 238 }, { x: 349, y: 252 }, { x: 305, y: 247 }
        ];
        let pos = cellPosConfig[data.bigLevel - 1];
        this.mapNameImage.x = pos.x;
        this.mapNameImage.y = pos.y;
        this.updateCellState();
    }
    setCellState(state: number) {
        this.curLevelBg.visible = (state == 0 || state == 1);
        this.mapIcon.visible = (state == 0 || state == 1);
        if (state == 0) {
            //已经通关
            this.mapNameImage.skin = 'newSystem/Ui_FrameDaoGuan.png';
            this.mapIcon.skin = `newSystem/Ui_DaoGuan${this.data.bigLevel}.png`;
            this.levelLabel.text = `${this.data.bigLevel}-${GameDataManager.Instance.challengeMapData[this.data.bigLevel - 1].length}`;
        } else if (state == 1) {
            //正在通关
            this.mapNameImage.skin = 'newSystem/Ui_FrameDaoGuan.png';
            this.mapIcon.skin = `newSystem/Ui_DaoGuan${this.data.bigLevel}.png`;
            this.levelLabel.text = `${this.data.bigLevel}-${UserData.instance.challengeLevel[1]}`;
        } else if (state == 2) {
            //未开始通关
            this.mapNameImage.skin = 'newSystem/Ui_DaoGuan.png';
        }
    }
    updateCellState() {
        if (!this.data) return;
        if (this.data.bigLevel < UserData.instance.challengeLevel[0]) {
            this.setCellState(0);
        } else if (this.data.bigLevel == UserData.instance.challengeLevel[0]) {
            this.setCellState(1);
        } else {
            this.setCellState(2);
        }
    }
}