import { ui } from "../ui/layaMaxUI";

export default class DivisionIconRuntime extends ui.pages.divisionIconUI {
    positionList1 = [[51, 127], [85, 135], [118, 127]];
    positionList2 = [[31, 120], [65, 134], [107, 134], [140, 120]];
    onAwake(): void {

    }
    setStarNum(num: number) {
        let starList = [this.star1, this.star2, this.star3, this.star4];
        let posIndexList: number[] = [];
        let positionList = this.positionList1;
        if (num == 1) {
            posIndexList = [1];
            positionList = this.positionList1;
        } else if (num == 2) {
            posIndexList = [1, 2];
            positionList = this.positionList2;
        } else if (num == 3) {
            posIndexList = [0, 1, 2];
            positionList = this.positionList1;
        } else if (num == 4) {
            posIndexList = [0, 1, 2, 3];
            positionList = this.positionList2;
        }
        for (let i = 0; i < num; i++) {
            starList[i].visible = true;
            starList[i].x = positionList[posIndexList[i]][0];
            starList[i].y = positionList[posIndexList[i]][1];
        }
        for (let i = num; i < starList.length; i++) {
            starList[i].visible = false;
        }
    }
    setDivision(division: number) {
        this.divisionIcon.skin = `pvp/Ui_Rank${division}.png`;
        this.divisionName.skin = `pvp/Ui_RankWord${division}.png`;
    }
    setShowType(isDivisionNameShow: boolean, isStarShow: boolean) {
        this.divisionName.visible = isDivisionNameShow;
        this.starBox.visible = isStarShow;
    }
    setScore(score: number) {
        
    }
}