export default class DataBase {
    private static _db: any;
    public static init(env: string) {
        if (typeof wx == "undefined") return Promise.reject("database:fail");
        this._db = wx["cloud"].database({ env: env });
        return Promise.resolve("database:ok");
    }
    public static add(collection: string, data: any): Promise<any> {
        if (typeof wx == "undefined") return Promise.reject("collection.add:fail");
        return this._db.collection(collection).add({ data: data });
    }
    public static get(collection: string, rule: any): Promise<any> {
        if (typeof wx == "undefined") return Promise.reject("collection.get:fail");
        return this._db.collection(collection).where(rule).get();
    }
    public static update(collection: string, id: string, data: any): Promise<any> {
        if (typeof wx == "undefined") return Promise.reject("collection.update:fail");
        return this._db.collection(collection).doc(id).update({ data: data });
    }
}