import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CardRuntime from "./CardRuntime";

export default class HandBookRuntime extends ui.pages.handBookUI {
    curPage: number = 0;
    maxPage: number = 0;
    panelDownX: number = 0;
    isUpTrigger: boolean = false;
    require: number = 0;
    reward: number = 0;
    cardPageDataList: CardData[][] = [];
    cardDataList: CardData[] = [];
    redFilter: Laya.ColorFilter = new Laya.ColorFilter([
        1, 0, 0, 0, 0, //R
        0, 0, 0, 0, 0, //G
        0, 0, 0, 0, 0, //B
        0, 0, 0, 1, 0, //A
    ]);
    onAwake() {
        this.rewardTargetLabel.filters = [this.redFilter];
        this.monsterList.hScrollBarSkin = null;
        this.monsterList.scrollBar.rollRatio = 0;
        this.monsterList.elasticEnabled = true;
        this.pagePoint.x = 24 * this.curPage;
        this.leftBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.monsterList.on(Laya.Event.MOUSE_UP, this, this.onPanelEvent);
        this.monsterList.on(Laya.Event.MOUSE_OUT, this, this.onPanelEvent);
        this.monsterList.on(Laya.Event.MOUSE_DOWN, this, this.onPanelEvent);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.initCardDataList();
        this.initMonsterList();
        this.pageLabel.text = `${this.curPage + 1}/${this.maxPage}`;
        this.updateCollect();
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.on(EventType.SelectCard, this, this.onSelectPageCard);
    }
    onSelectPageCard(list: Laya.List, e: Laya.Event, card: CardRuntime) {
        if (list != this.monsterList) return;
        if (e.type == Laya.Event.CLICK) {
            LayaTool.openScene("pages/handBookDetail.scene", false, true, null, [card.data.monsterId], false);
        }
    }
    onPanelEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.panelDownX = e.stageX;
        } else {
            if (e.type == Laya.Event.MOUSE_UP) this.isUpTrigger = true;
            if (e.type == Laya.Event.MOUSE_OUT && this.isUpTrigger) {
                this.isUpTrigger = false;
                return;
            }
            let dx = this.panelDownX - e.stageX;
            if (Math.abs(dx) < 100) {
                if (
                    (this.curPage == 0 && Math.sign(dx) == -1) ||
                    (this.curPage == this.maxPage - 1 && Math.sign(dx) == 1)
                ) return;
                this.tweenList(this.curPage);
                return;
            }
            if (this.curPage + Math.sign(dx) >= 0 && this.curPage + Math.sign(dx) < this.maxPage) {
                this.tweenList(this.curPage + Math.sign(dx));
            }
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.leftBtn) {
            if (this.curPage > 0) {
                this.curPage--;
                this.tweenList(this.curPage);
            }
        } else if (e.target == this.rightBtn) {
            if (this.curPage < this.maxPage - 1) {
                this.curPage++;
                this.tweenList(this.curPage);
            }
        } else if (e.target == this.receiveBtn) {
            if (UserData.instance.collectReceived) {
                return;
            }
            if (UserData.instance.collectMonsterIdList.length < this.require) {
                EventManager.event(EventType.PopTip, "收集足够怪");
                return;
            }
            TongYiEventManager.sendEvent(10008, { mission_type: "3", mission_id: UserData.instance.collectStage + 101 });
            EventManager.event(EventType.PopTip, "领取成功");
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, this.reward, 3]);
            UserData.instance.collectReceived = 1;
            UserData.instance.onDataChange("collectReceived");
            if (UserData.instance.collectStage < 25) {
                UserData.instance.collectReceived = 0;
                UserData.instance.onDataChange("collectReceived");
                UserData.instance.collectStage++;
                UserData.instance.onDataChange("collectStage");
            }
        } else if (e.target == this.closeBtn) {
            EventManager.event(EventType.ShowHandBook, false);
        }
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.off(EventType.SelectCard, this, this.onSelectPageCard);
    }
    tweenList(index: number) {
        if (index == -1) return;
        this.curPage = index;
        let targetX = Laya.stage.width * index;
        this.pageLabel.text = `${this.curPage + 1}/${this.maxPage}`;
        if (this.curPage == 0) {
            Laya.Tween.to(this.leftBtn, { alpha: 0.5 }, 100, Laya.Ease.sineOut);
        } else {
            Laya.Tween.to(this.leftBtn, { alpha: 1 }, 100, Laya.Ease.sineOut);
        }
        if (this.curPage == this.maxPage - 1) {
            Laya.Tween.to(this.rightBtn, { alpha: 0.5 }, 100, Laya.Ease.sineOut);
        } else {
            Laya.Tween.to(this.rightBtn, { alpha: 1 }, 100, Laya.Ease.sineOut);
        }
        Laya.Tween.to(this.monsterList.scrollBar, { value: targetX }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            this.monsterList.scrollBar.stopScroll();
        }), 0, true);
        Laya.Tween.to(this.pagePoint, { x: 24 * this.curPage }, 200, Laya.Ease.sineOut);
    }
    updateCollect() {
        this.receiveLabel.text = `${UserData.instance.collectMonsterIdList.length}/${this.cardDataList.length}`;
        let collectData = GameDataManager.Instance.collectConfig[UserData.instance.collectStage];
        this.require = collectData.require;
        this.reward = collectData.reward;
        this.rewardTargetLabel.text = collectData.require;
        this.rewardMoneyLabel.text = collectData.reward;
        this.receiveRed.visible = HandBookRuntime.haveRed();
    }
    initMonsterList() {
        this.cardPageDataList.length = 0;
        for (let i = 0; i < this.cardDataList.length; i++) {
            let pageIndex = Math.floor(i / 12);
            if (!this.cardPageDataList[pageIndex]) this.cardPageDataList[pageIndex] = [];
            this.cardPageDataList[pageIndex].push(this.cardDataList[i]);
        }
        this.maxPage = this.cardPageDataList.length;
        this.monsterList.array = this.cardPageDataList;
        this.monsterList.refresh();
        this.updateArrow();
    }
    updateArrow() {
        if (this.maxPage <= 1) {
            this.leftBtn.alpha = 0.5;
            this.rightBtn.alpha = 0.5;
        } else {
            if (this.curPage == 0) {
                this.leftBtn.alpha = 0.5;
                this.rightBtn.alpha = 1;
            } else if (this.curPage == this.maxPage - 1) {
                this.rightBtn.alpha = 0.5;
                this.leftBtn.alpha = 1;
            } else {
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;
            }
        }
    }
    onUserDataChange(key: string) {
        if (key == "collectStage") {
            this.updateCollect();
        } else if (key == "collectMonsterIdList") {
            this.initMonsterList();
            this.updateCollect();
        }
    }
    initCardDataList() {
        for (const key in GameDataManager.Instance.monsterData) {
            let cardData = GameDataManager.Instance.getOneCardData(Number(key));
            this.cardDataList.push(cardData);
        }
    }
    onSelect() {
        GuideManager.finishInnerTask("点击图鉴");
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    offSelect() {
        if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
        GuideManager.finishInnerTask("点击关闭图鉴");
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    static haveRed() {
        let collectData = GameDataManager.Instance.collectConfig[UserData.instance.collectStage];
        let require = collectData.require;
        return UserData.instance.collectMonsterIdList.length >= require && UserData.instance.collectReceived == 0;
    }
}