import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class CureCellRuntime extends ui.pages.cureCellUI {
    cureCostList: number[] = [0, 0, 0];
    fightMonsterList: CardData[] = [];
    fightGroupIndexList: number[] = [];
    public static mask = new Laya.Image();
    isBigOpen: boolean = true;
    onAwake() {
        CureCellRuntime.mask.skin = "main/Ui_MiniHead1.png";
        this.cureList.hScrollBarSkin = null;
        this.cureList.array = this.cureCostList;
        this.cureList.refresh();
        this.cureList.mouseHandler = Laya.Handler.create(this, this.onMouseHandler, null, false);
        EventManager.on(EventType.OpenBig, this, this.onOpenBig);
        EventManager.on(EventType.OneCure, this, this.onOneCure);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        EventManager.on("UserDataChange", this, this.onUserDataChange);
    }
    onUserDataChange(key: string) {
        if (key == "gold") {
            this.upadteCureBtnState();
        }
    }
    onOpenBig(isOpen: boolean) {
        this.isBigOpen = isOpen;
        if (isOpen) {
            Laya.Tween.to(this.bigBg, { x: 0 }, 200, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.closeBtn.visible = true;
                this.mouseEnabled = true;
            }), 0, true);
        } else {
            this.closeBtn.visible = false;
            this.mouseEnabled = false;
            Laya.Tween.to(this.bigBg, { x: -535 }, 200, Laya.Ease.sineOut, null, 0, true);
            EventManager.event(EventType.OpenSmall, true);
        }
    }
    onOneCure(fromNeverGiveUp: boolean) {
        for (let i = 0; i < this.cureCostList.length; i++) {
            this.cureMonster(i);
        }
        EventManager.event(EventType.PopTip, "体力恢复");
        EventManager.event(EventType.CureMonster, -1);
        if (fromNeverGiveUp) {
            EventManager.event(EventType.FullHpRevive);
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.onOpenBig(false);
        }
    }
    onDestroy() {
        EventManager.off(EventType.OpenBig, this, this.onOpenBig);
        EventManager.off(EventType.OneCure, this, this.onOneCure);
        EventManager.off("UserDataChange", this, this.onUserDataChange);
    }
    onMouseHandler(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            let cost = this.cureCostList[index];
            if (cost > 0) {
                if (UserData.instance.gold >= cost || UserData.instance.freeCure) {
                    if (!UserData.instance.freeCure) {
                        UserData.instance.gold -= cost;
                        UserData.instance.onDataChange("gold");
                        EventManager.event(EventType.UseMoney, "gold");
                    }
                    TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: cost * -1, get_type: 12, last_num: UserData.instance.gold });
                    this.cureMonster(index);
                    EventManager.event(EventType.PopTip, "体力恢复");
                    EventManager.event(EventType.CureMonster, index);
                } else {
                    AllPlatformAdMgr.Instance.sendVideoData = { page_id: 5, button_id: 5, ad_id: 2, ad_type: 1 };
                    AllPlatformAdMgr.Instance.showVideoAd("adunit-344c07eccef75220", () => {
                        this.cureMonster(index);
                        EventManager.event(EventType.PopTip, "体力恢复");
                        EventManager.event(EventType.CureMonster, index);
                    });
                }
            }
        }
    }
    cureMonster(index: number) {
        let cardData = UserData.instance.fightCardList[index];
        if (cardData) {
            UserData.instance.finishTask(1, 2, 1);
            this.cureCostList[index] = 0;
            cardData.curHp = cardData.hp;
            UserData.instance.onDataChange("fightCardList");
            this.syncHp(cardData);
        }
    }
    syncHp(fightCardData: CardData) {
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            if (UserData.instance.cardList[i].cardId == fightCardData.cardId) {
                UserData.instance.cardList[i].curHp = fightCardData.curHp;
                UserData.instance.onDataChange("cardList");
                break;
            }
        }
    }
    initBigCellData() {
        this.fightMonsterList.length = 0;
        this.fightGroupIndexList.length = 0;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let card = UserData.instance.fightCardList[i];
            if (card) {
                this.fightMonsterList.push(card);
                this.fightGroupIndexList.push(i);
            }
        }
        this.cardList.array = this.fightMonsterList;
        this.cardList.refresh();
        for (let i = 0; i < 3; i++) {
            if (i < this.fightMonsterList.length) {
                let cardData = this.fightMonsterList[i];
                let fullHp = GameDataManager.Instance.monsterData[cardData.monsterId].hp;
                if (cardData.special) {
                    fullHp += Math.round(Math.max(5, 0.1 * fullHp));
                }
                this.cureCostList[i] = (fullHp - cardData.curHp) * 2;
            } else {
                this.cureCostList[i] = 0;
            }
        }
        for (let i = 0; i < 3; i++) {
            let cell = this.cureList.getCell(i);
            let costLabel = cell.getChildByName("costLabel") as Laya.Label;
            costLabel.text = this.cureCostList[i] + "";
            if (this.cureCostList[i] == 0) {
                Laya.Tween.to(cell, { y: 75 }, 100, Laya.Ease.sineOut);
            } else {
                Laya.Tween.to(cell, { y: 0 }, 100, Laya.Ease.sineOut);
            }
        }
        this.setBtnVideoTag();
        this.upadteCureBtnState();
    }
    upadteCureBtnState() {
        for (let i = 0; i < 3; i++) {
            let cell = this.cureList.getCell(i);
            let videoCure = cell.getChildByName("videoCure") as Laya.Sprite;
            if (UserData.instance.gold >= this.cureCostList[i]) {
                videoCure.visible = false;
            } else {
                videoCure.visible = true;
            }
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        for (let i = 0; i < 3; i++) {
            let cell = this.cureList.getCell(i);
            let videoTag = cell.getChildByName("videoTag") as Laya.Sprite;
            videoTag.visible = isShowVideoTag;
        }
    }
}