import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { ui } from "../ui/layaMaxUI";

export default class PauseDialogRuntime extends ui.pages.pauseUI {
    onAwake() {

    }
    onOpened() {
        this.continueBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.backBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.continueBtn) {
            this.close();
        } else if (e.target == this.backBtn) {
            EventManager.event(EventType.ClickMainBtn);
            this.close();
        }
    }
}