export class Geometry {
    public faces: Array<Face>;
    public vertices: Array<Vector3>;
    constructor(geometry: { vertices: number[], faces: number[] }) {
        this.faces = []
        this.vertices = []
        this.addFace(geometry.faces)
        this.addVertex(geometry.vertices)
    }

    /*
     * Checks for duplicate vertices with hashmap.
     * Duplicated vertices are removed
     * and faces' vertices are updated.
     */

    mergeVertices() {

        let verticesMap: Object = {}; // Hashmap for looking up vertice by position coordinates (and making sure they are unique)
        let unique: Array<Vector3> = [], changes: Array<number> = [];

        let v: Vector3, key: string;
        let precisionPoints: number = 4; // number of decimal points, eg. 4 for epsilon of 0.0001
        let precision: number = Math.pow(10, precisionPoints);
        let face: Face
        let indices: number[]

        for (let i = 0; i < this.vertices.length; i++) {
            v = this.vertices[i];
            key = Math.round(v.x * precision) + '_' + Math.round(v.y * precision) + '_' + Math.round(v.z * precision);

            if (verticesMap[key] == null) {

                verticesMap[key] = i;
                unique.push(v);
                changes[i] = unique.length - 1;

            } else {

                //console.log('Duplicate vertex found. ', i, ' could be using ', verticesMap[key]);
                changes[i] = changes[verticesMap[key]];

            }

        }

        // if faces are completely degenerate after merging vertices, we
        // have to remove them from the geometry.
        let faceIndicesToRemove: Array<number> = [];

        for (let i = 0; i < this.faces.length; i++) {

            face = this.faces[i];

            face.a = changes[face.a];
            face.b = changes[face.b];
            face.c = changes[face.c];

            indices = [face.a, face.b, face.c];

            let dupIndex: number = - 1;

            // if any duplicate vertices are found in a Face3
            // we have to remove the face as nothing can be saved
            for (let n: number = 0; n < 3; n++) {
                if (indices[n] == indices[(n + 1) % 3]) {

                    dupIndex = n;
                    faceIndicesToRemove.push(i);
                    break;

                }
            }

        }

        for (let i = faceIndicesToRemove.length - 1; i >= 0; i--) {
            let idx: number = faceIndicesToRemove[i];

            this.faces.splice(idx, 1);

            /*for ( j = 0, jl = this.faceVertexUvs.length; j < jl; j ++ ) {
    
                this.faceVertexUvs[ j ].splice( idx, 1 );
    
            }*/

        }

        // Use unique set of vertices

        let diff: number = this.vertices.length - unique.length;
        this.vertices = unique;
        return diff;

    }

    addVertex(vertices: number[]) {
        for (let i = 0; i < vertices.length; i += 3) {
            this.vertices.push(new Vector3(vertices[i], vertices[i + 1], vertices[i + 2]));
        }
    }

    addFace(faces: number[]) {
        for (let i = 0; i < faces.length / 5; i++) {
            this.faces.push(new Face(faces[i * 5 + 1], faces[i * 5 + 2], faces[i * 5 + 3]));
        }
    }


}
export class Vector3 {
    x = 0;
    y = 0;
    z = 0;
    constructor(x = 0, y = 0, z = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    add(v: Vector3, w?: Vector3) {
        if (w !== undefined) {
            return this.addVectors(v, w);
        }
        this.x += v.x;
        this.y += v.y;
        this.z += v.z;
        return this;
    }
    addVectors(a: Vector3, b: Vector3) {
        this.x = a.x + b.x;
        this.y = a.y + b.y;
        this.z = a.z + b.z;
        return this;
    }
    divideScalar(n: number) {
        return this.multiplyScalar(1 / n);
    }
    multiplyScalar(scalar: number) {
        if (isFinite(scalar)) {
            this.x *= scalar;
            this.y *= scalar;
            this.z *= scalar;
        } else {
            this.x = 0;
            this.y = 0;
            this.z = 0;
        }
        return this;
    }
    subVectors(a: Vector3, b: Vector3) {
        this.x = a.x - b.x;
        this.y = a.y - b.y;
        this.z = a.z - b.z;
        return this;
    }
    cross(v: Vector3, w?: Vector3) {
        if (w !== undefined) {
            return this.crossVectors(v, w);
        }
        var x = this.x, y = this.y, z = this.z;
        this.x = y * v.z - z * v.y;
        this.y = z * v.x - x * v.z;
        this.z = x * v.y - y * v.x;
        return this;
    }
    crossVectors(a: Vector3, b: Vector3) {
        var ax = a.x, ay = a.y, az = a.z;
        var bx = b.x, by = b.y, bz = b.z;
        this.x = ay * bz - az * by;
        this.y = az * bx - ax * bz;
        this.z = ax * by - ay * bx;
        return this;
    }
    normalize() {
        return this.divideScalar(this.length());
    }
    length() {
        return Math.sqrt(this.x * this.x + this.y * this.y + this.z * this.z);
    }
    copy(v: Vector3) {
        this.x = v.x;
        this.y = v.y;
        this.z = v.z;
        return this;
    }
    dot(v: Vector3) {
        return this.x * v.x + this.y * v.y + this.z * v.z;
    }
    set(x: number, y: number, z: number) {
        this.x = x;
        this.y = y;
        this.z = z;
        return this;
    }
    sub(v: Vector3, w?: Vector3) {
        if (w !== undefined) {
            return this.subVectors(v, w);
        }
        this.x -= v.x;
        this.y -= v.y;
        this.z -= v.z;
        return this;
    }
    negate() {
        this.x = - this.x;
        this.y = - this.y;
        this.z = - this.z;
        return this;
    }

    distanceToSquared(v: Vector3) {
        var dx = this.x - v.x, dy = this.y - v.y, dz = this.z - v.z;
        return dx * dx + dy * dy + dz * dz;
    }
    addScaledVector(v: Vector3, s: number) {

        this.x += v.x * s;
        this.y += v.y * s;
        this.z += v.z * s;

        return this;

    }
}
