export class Triangle {
    a: Vector3;
    b: Vector3;
    c: Vector3;
    constructor(a?: Vector3, b?: Vector3, c?: Vector3) {
        this.a = (a !== undefined) ? a : new Vector3();
        this.b = (b !== undefined) ? b : new Vector3();
        this.c = (c !== undefined) ? c : new Vector3();
    }
    set(a: Vector3, b: Vector3, c: Vector3) {
        this.a.copy(a);
        this.b.copy(b);
        this.c.copy(c);
        return this;
    }
    closestPointToPoint(point: Vector3, optionalTarget: Vector3) {

        var plane: Plane, edgeList: Line3[], projectedPoint: Vector3, closestPoint: Vector3;
        if (plane === undefined) {

            plane = new Plane();
            edgeList = [new Line3(), new Line3(), new Line3()];
            projectedPoint = new Vector3();
            closestPoint = new Vector3();

        }

        var result = optionalTarget || new Vector3();
        var minDistance = Infinity;

        // project the point onto the plane of the triangle

        plane.setFromCoplanarPoints(this.a, this.b, this.c);
        plane.projectPoint(point, projectedPoint);

        // check if the projection lies within the triangle

        if (this.containsPoint(projectedPoint) === true) {

            // if so, this is the closest point

            result.copy(projectedPoint);
        } else {
            // if not, the point falls outside the triangle. the result is the closest point to the triangle's edges or vertices
            edgeList[0].set(this.a, this.b);
            edgeList[1].set(this.b, this.c);
            edgeList[2].set(this.c, this.a);

            for (var i = 0; i < edgeList.length; i++) {

                edgeList[i].closestPointToPoint(projectedPoint, true, closestPoint);

                var distance = projectedPoint.distanceToSquared(closestPoint);

                if (distance < minDistance) {

                    minDistance = distance;

                    result.copy(closestPoint);

                }

            }

        }
        return result;
    }
    containsPoint(point: Vector3) {
        return Triangle.containsPoint(point, this.a, this.b, this.c);
    }
    static containsPoint(point: Vector3, a: Vector3, b: Vector3, c: Vector3) {
        var v1 = new Vector3();
        var result = Triangle.barycoordFromPoint(point, a, b, c, v1);
        return (result.x >= 0) && (result.y >= 0) && ((result.x + result.y) <= 1);
    }
    static barycoordFromPoint(point: Vector3, a: Vector3, b: Vector3, c: Vector3, optionalTarget: Vector3) {
        var v0 = new Vector3();
        var v1 = new Vector3();
        var v2 = new Vector3();
        v0.subVectors(c, a);
        v1.subVectors(b, a);
        v2.subVectors(point, a);

        var dot00 = v0.dot(v0);
        var dot01 = v0.dot(v1);
        var dot02 = v0.dot(v2);
        var dot11 = v1.dot(v1);
        var dot12 = v1.dot(v2);

        var denom = (dot00 * dot11 - dot01 * dot01);

        var result = optionalTarget || new Vector3();

        // collinear or singular triangle
        if (denom === 0) {

            // arbitrary location outside of triangle?
            // not sure if this is the best idea, maybe should be returning undefined
            return result.set(- 2, - 1, - 1);

        }

        var invDenom = 1 / denom;
        var u = (dot11 * dot02 - dot01 * dot12) * invDenom;
        var v = (dot00 * dot12 - dot01 * dot02) * invDenom;

        // barycentric coordinates must always sum to 1
        return result.set(1 - u - v, v, u);
    }
}
export class Line3 {
    start: Vector3;
    end: Vector3;
    constructor(start?: Vector3, end?: Vector3) {
        this.start = (start !== undefined) ? start : new Vector3();
        this.end = (end !== undefined) ? end : new Vector3();
    }
    set(start: Vector3, end: Vector3) {
        this.start.copy(start);
        this.end.copy(end);
        return this;
    }
    closestPointToPoint(point: Vector3, clampToLine: boolean, optionalTarget: Vector3) {
        var t = this.closestPointToPointParameter(point, clampToLine);
        var result = optionalTarget || new Vector3();
        return this.delta(result).multiplyScalar(t).add(this.start);
    }
    closestPointToPointParameter(point: Vector3, clampToLine: boolean) {
        var startP = new Vector3();
        var startEnd = new Vector3();
        startP.subVectors(point, this.start);
        startEnd.subVectors(this.end, this.start);

        var startEnd2 = startEnd.dot(startEnd);
        var startEnd_startP = startEnd.dot(startP);

        var t = startEnd_startP / startEnd2;

        if (clampToLine) {
            t = Math.max(0, Math.min(1, t));
        }
        return t;
    }
    delta(optionalTarget: Vector3) {
        var result = optionalTarget || new Vector3();
        return result.subVectors(this.end, this.start);
    }
}
