import { GameDataManager } from "../gamelogic/GameDataManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import DivisionIconRuntime from "./DivisionIconRuntime";

export default class PvpRankCellRuntime extends ui.pages.pvpRankCellUI {
    data: any;
    onAwake(): void {
    }
    public set dataSource(data: any) {
        this.data = data;
        if (!data) return;
        this.setRankBg(data.index, data.isMe);
        let fixName = LayaTool.getSpecifyCharLengthStr(data.UserName, 10, "...");
        this.setRankData(data.index, data.isMe, data.RankValue, fixName);
        (<DivisionIconRuntime>this.divisionIcon).setShowType(false, true);
    }
    setRankBg(index: number, isMe: boolean) {
        if (index <= 3) {
            this.bg.skin = `pvp/Ui_FrameRank${index}.png`;
        } else {
            this.bg.skin = isMe ? `pvp/Ui_FrameRankMe.png` : `pvp/Ui_FrameRank.png`;
        }
        this.meTag.visible = isMe;
    }
    setRankData(index: number, isMe: boolean, score: number, name: string) {
        if (index <= 3) {
            this.rankNumLabel.visible = false;
        } else {
            this.rankNumLabel.text = index + "";
            this.rankNumLabel.visible = true;
        }
        this.selfNameLabel.visible = isMe;
        this.otherNameLabel.visible = !isMe;
        if (!isMe) this.otherNameLabel.text = name;
        this.winTimeLabel.text = score + "";
        let divisionData = GameDataManager.Instance.getPvpDivisionDataByScore(score);
        (<DivisionIconRuntime>this.divisionIcon).setDivision(divisionData[0]);
        (<DivisionIconRuntime>this.divisionIcon).setStarNum(divisionData[1]);
    }
}