import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class TaskRuntime extends ui.pages.taskUI {
    public static taskConfig1 = [
        { id: 1, desc: "收集*只妖怪", num: 10, reward: 150 },
        { id: 2, desc: "通过*个世界关卡", num: 2, reward: 150 },
        { id: 3, desc: "合成*次", num: 5, reward: 150 },
    ];
    public static taskConfig2 = [
        { id: 4, desc: "集齐*只怪物", num: [20, 40, 60, 100, 126], reward: [100, 200, 300, 400, 500] },
        { id: 5, desc: "通过*局战斗", num: [5, 10, 20, 50, 100], reward: [200, 240, 340, 460, 500] },
        { id: 6, desc: "治疗*次", num: [10, 50, 100, 150, 200], reward: [200, 240, 340, 460, 500] },
        { id: 7, desc: "消灭*只怪物", num: [10, 50, 100, 150, 200], reward: [200, 240, 340, 460, 500] },
        { id: 8, desc: "获得*拼图", num: [100, 300, 500, 1000, 2000], reward: [200, 240, 340, 460, 500] },
    ];
    taskType: number = 0;
    onAwake() {
        this.taskList.vScrollBarSkin = null;
        this.showTask(0);
        this.dayRed.visible = TaskRuntime.dayHaveRed();
        this.achieveRed.visible = TaskRuntime.achieveHaveRed();
        EventManager.on(EventType.UpdateTask, this, this.onUpdateTask);
    }
    onDestroy() {
        EventManager.off(EventType.UpdateTask, this, this.onUpdateTask);
    }
    onUpdateTask() {
        this.showTask(this.taskType);
        this.dayRed.visible = TaskRuntime.dayHaveRed();
        this.achieveRed.visible = TaskRuntime.achieveHaveRed();
    }
    onOpened() {
        this.achieveTaskBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.dayTaskBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (this.achieveTaskBtn == e.target) {
            this.showTask(1);
        } else if (this.dayTaskBtn == e.target) {
            this.showTask(0);
        }
    }
    showTask(task: number) {
        this.taskType = task;
        this.dayIcon.skin = task == 0 ? "sign/Btn_Task4.png" : "sign/Btn_Task3.png";
        this.acheiveIcon.skin = task == 1 ? "sign/Btn_Task6.png" : "sign/Btn_Task5.png";
        let dataList = [];
        if (task == 0) {
            for (let i = 0; i < TaskRuntime.taskConfig1.length; i++) {
                let config = TaskRuntime.taskConfig1[i];
                let data = {
                    rewardNum: config.reward,
                    taskDesc: config.desc.replace("*", config.num + ""),
                    taskType: 0,
                    index: i,
                    num: config.num
                };
                dataList.push(data);
            }
        } else if (task == 1) {
            for (let i = 0; i < TaskRuntime.taskConfig2.length; i++) {
                let config = TaskRuntime.taskConfig2[i];
                let stage = UserData.instance.achieveTaskStage[i];
                let reward = config.reward[stage];
                let num = config.num[stage];
                let data = {
                    rewardNum: reward,
                    taskDesc: config.desc.replace("*", num + ""),
                    taskType: 1,
                    index: i,
                    num: num
                };
                dataList.push(data);
            }
        }
        this.taskList.array = dataList;
        this.taskList.refresh();
    }
    public static dayHaveRed() {
        for (let i = 0; i < TaskRuntime.taskConfig1.length; i++) {
            let config = TaskRuntime.taskConfig1[i];
            let dayCount = UserData.instance.dayTaskCountRec[i];
            if (dayCount >= config.num) {
                //达到目标
                let receive = UserData.instance.dayTaskReceiveRec[i];
                if (receive) {
                    //领取

                } else {
                    //未领取
                    return true;
                }
            } else {
                //未达到目标

            }

        }
        return false;
    }
    public static achieveHaveRed() {
        for (let i = 0; i < TaskRuntime.taskConfig2.length; i++) {
            let config = TaskRuntime.taskConfig2[i];
            let stage = UserData.instance.achieveTaskStage[i];
            let reward = config.reward[stage];
            let num = config.num[stage];
            let stageCount = UserData.instance.achieveTaskCountRec[i];
            if (stageCount >= num) {
                //达到目标
                let receive = UserData.instance.achieveTaskReceiveRec[i];
                if (receive) {
                    //领取

                } else {
                    //未领取
                    return true;
                }
            } else {
                //未达到目标

            }
        }
        return false;
    }
    public static haveRed() {
        return this.dayHaveRed() || this.achieveHaveRed();
    }
}