export default class MathTool {
    /**弧度转角度 */
    public static rad2Deg: number = 180 / Math.PI;
    /**角度转弧度 */
    public static deg2Rad: number = Math.PI / 180;
    static getRandom(min: number, max: number) {
        return Math.floor(min + Math.random() * (max - min + 1));
    }
    /**获取两个向量之间的夹角(弧度) */
    static getRotateRad(left: Laya.Vector3, right: Laya.Vector3) {
        /**点积算出向量夹角 */
        Laya.Vector3.normalize(left, left);
        Laya.Vector3.normalize(right, right);
        let rad = Math.acos(MathTool.fixedDot(left, right));
        // let rad = Math.acos(Laya.Vector3.dot(left, right));
        /**用叉积判断旋转角的正负 */
        let cross = new Laya.Vector3;
        Laya.Vector3.cross(left, right, cross);
        if (cross.y < 0) {
            return -rad;
        } else {
            return rad;
        }
    }
    public static eulerToQuaternion(e: Laya.Vector3, out: Laya.Quaternion) {
        Laya.Quaternion.createFromYawPitchRoll(e.y * Math.PI / 180, e.x * Math.PI / 180, e.z * Math.PI / 180, out)
    }
    public static quaternionToEuler(q: Laya.Quaternion, out: Laya.Vector3) {
        q.getYawPitchRoll(out)
        out.setValue(out.y * 180 / Math.PI, out.x * 180 / Math.PI, out.z * 180 / Math.PI)
    }
    /**获取两个向量之间的夹角(弧度) 点乘居然有误差!*/
    static getPitchRotateRad(left: Laya.Vector3, right: Laya.Vector3) {
        /**点积算出向量夹角 */
        Laya.Vector3.normalize(left, left);
        Laya.Vector3.normalize(right, right);
        let rad = Math.acos(MathTool.fixedDot(left, right));
        let dir = new Laya.Vector3(left.x, 0, left.z);
        Laya.Vector3.normalize(dir, dir);
        let leftSign = left.y;
        let rightSign = right.y;
        let leftRad = Math.acos(MathTool.fixedDot(left, dir));
        let rightRad = Math.acos(MathTool.fixedDot(right, dir));
        if (leftSign == rightSign) {
            if (leftSign >= 0) {
                if (leftRad >= rightRad) {
                    return rad;
                } else {
                    return -rad;
                }
            } else {
                if (leftRad >= rightRad) {
                    return -rad;
                } else {
                    return rad;
                }
            }
        } else {
            if (leftSign > rightSign) {
                return rad;
            } else {
                return -rad;
            }
        }
    }
    /**获取点乘的值 */
    public static fixedDot(left: Laya.Vector3, right: Laya.Vector3) {
        let dot = Laya.Vector3.dot(left, right);
        dot = dot > -1 ? dot : -1;
        dot = dot < 1 ? dot : 1;
        return dot;
    }
    /**根据平面上的两个向量 求出法向量*/
    getNormalByVecInFace(vec1: Laya.Vector3, vec2: Laya.Vector3) {
        //假设z为1
        let n = new Laya.Vector3(0, 0, 1);
        let x1 = vec1.x, y1 = vec1.y, z1 = vec1.z;
        let x2 = vec2.x, y2 = vec2.y, z2 = vec2.z;
        n.x = (z2 * y1 - z1 * y2) / (x1 * y2 - x2 * y1);
        n.y = (z2 * x1 - z1 * x2) / (y1 * x2 - y2 * x1);
        Laya.Vector3.normalize(n, n);
    }
    /**求向量在平面上的投影向量 */
    getProjectionVecInFace(vec: Laya.Vector3, normal: Laya.Vector3) {
        Laya.Vector3.normalize(vec, vec);
        Laya.Vector3.normalize(normal, normal);
        let projectVec = new Laya.Vector3;
        Laya.Vector3.scale(normal, Laya.Vector3.dot(vec, normal), normal)
        Laya.Vector3.subtract(vec, normal, projectVec);
        return projectVec;
    }
    /**求两个三维向量之间的夹角*/
    public static getRadBetweenVec3(vec1: Laya.Vector3, vec2: Laya.Vector3) {
        Laya.Vector3.normalize(vec1, vec1);
        Laya.Vector3.normalize(vec2, vec2);
        let rad = Math.acos(MathTool.fixedDot(vec1, vec2));
        return rad;
    }
    /**求两个二维向量之间的夹角*/
    public static getRadBetweenVec2(vec1: Laya.Vector2, vec2: Laya.Vector2) {
        Laya.Vector2.normalize(vec1, vec1);
        Laya.Vector2.normalize(vec2, vec2);
        let x1 = vec1.x, y1 = vec1.y, x2 = vec2.x, y2 = vec2.y;
        let rad = Math.acos(x1 * x2 + y1 * y2);
        let cross = x1 * y2 - x2 * y1;
        return rad * Math.sign(cross);
    }
    /**二维向量旋转 */
    public static vector2Rotate(vec2: Laya.Vector2, rotateRad: number) {
        let x0 = vec2.x, y0 = vec2.y;
        vec2.x = x0 * Math.cos(rotateRad) + y0 * Math.sin(rotateRad);
        vec2.y = -x0 * Math.sin(rotateRad) + y0 * Math.cos(rotateRad);
    }
    public static projectionDir1: Laya.Vector3 = new Laya.Vector3;
    public static projectionDir2: Laya.Vector3 = new Laya.Vector3;
    public static rotateVec2: Laya.Vector2 = new Laya.Vector2;
    public static rotateQua: Laya.Quaternion = new Laya.Quaternion;
    public static rotateVec: Laya.Vector3 = new Laya.Vector3;
    /**获取仰角差和偏航角差 */
    public static getYawAndPitch(dir1: Laya.Vector3, dir2: Laya.Vector3) {
        let pitchDiff = 0;
        let yawDiff = 0;
        this.projectionDir1.x = dir1.x;
        this.projectionDir1.z = dir1.z;
        this.projectionDir1.y = 0;
        this.projectionDir2.x = dir2.x;
        this.projectionDir2.z = dir2.z;
        this.projectionDir2.y = 0;
        yawDiff = MathTool.getRotateRad(this.projectionDir1, this.projectionDir2);
        this.projectionDir1.x = dir1.x;
        this.projectionDir1.z = dir1.z;
        this.projectionDir1.y = dir1.y;
        this.projectionDir2.x = dir2.x;
        this.projectionDir2.z = dir2.z;
        this.projectionDir2.y = dir2.y;
        Laya.Quaternion.createFromYawPitchRoll(yawDiff, 0, 0, this.rotateQua);
        Laya.Vector3.transformQuat(this.projectionDir1, this.rotateQua, this.rotateVec);
        // console.log(this.projectionDir1);

        // this.rotateVec2.x = dir1.x;
        // this.rotateVec2.y = dir1.z;
        // MathTool.vector2Rotate(this.rotateVec2, yawDiff);
        // this.projectionDir1.x = this.rotateVec2.x;
        // this.projectionDir1.z = this.rotateVec2.y;
        // this.projectionDir1.y = dir1.y;
        // console.log(this.projectionDir1);

        pitchDiff = MathTool.getPitchRotateRad(this.rotateVec, this.projectionDir2);
        return { yawDiff: yawDiff, pitchDiff: pitchDiff };
    }
    public static floatAdd(arg1, arg2) {
        var r1, r2, m;
        try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
        try { r2 = arg2.toString().split(".")[1].length } catch (e) { r2 = 0 }
        m = Math.pow(10, Math.max(r1, r2));
        return (arg1 * m + arg2 * m) / m;
    }

    //减    
    public static floatSub(arg1, arg2) {
        var r1, r2, m, n;
        try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
        try { r2 = arg2.toString().split(".")[1].length } catch (e) { r2 = 0 }
        m = Math.pow(10, Math.max(r1, r2));
        //动态控制精度长度    
        n = (r1 >= r2) ? r1 : r2;
        return ((arg1 * m - arg2 * m) / m).toFixed(n);
    }

    //乘    
    public static floatMul(arg1, arg2) {
        var m = 0, s1 = arg1.toString(), s2 = arg2.toString();
        try { m += s1.split(".")[1].length } catch (e) { }
        try { m += s2.split(".")[1].length } catch (e) { }
        return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m);
    }


    //除   
    public static floatDiv(arg1, arg2) {
        var t1 = 0, t2 = 0, r1, r2;
        try { t1 = arg1.toString().split(".")[1].length } catch (e) { }
        try { t2 = arg2.toString().split(".")[1].length } catch (e) { }

        r1 = Number(arg1.toString().replace(".", ""));

        r2 = Number(arg2.toString().replace(".", ""));
        return (r1 / r2) * Math.pow(10, t2 - t1);
    }
}