import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import { ui } from "../ui/layaMaxUI";

export default class FightCellRuntime extends ui.pages.fightCellUI {
    onAwake() {
        this.monsterList.mouseHandler = Laya.Handler.create(this, this.onMonsterListEvent, null, false);
        this.atkTimeList.mouseHandler = Laya.Handler.create(this, this.onAtkTimeListEvent, null, false);
    }
    onMonsterListEvent(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            EventManager.event(EventType.AttackOnce, [index, false, false]);
        }
    }
    onAtkTimeListEvent(e: Laya.Event, index: number) {
        if (e.type == Laya.Event.CLICK) {
            let time = GameDataManager.Instance.monsterAtkTimeList[index];
            if (time >= 3) {
                let btn = e.target as Laya.Image;
                if (btn.disabled == false)
                    EventManager.event(EventType.AttackOnce, [index, true, false]);
            }
        }
    }
}