import { NetManager } from "../gframe-work/manager/NetManager";
import UserData from "../gframe-work/manager/UserData";

export class TongYiEventManager {
    private static _instance: TongYiEventManager = null;
    public static init() {
        if (TongYiEventManager._instance == null) {
            TongYiEventManager._instance = new TongYiEventManager();
        }
        window["TongYiEventManager"] = TongYiEventManager._instance;
    }
    private constructor() { }
    public static sendEvent(event: 10001 | 10002 | 10003 | 10004 | 10005 | 10006 | 10007 | 10008, subData: any) {
        let sendData = {
            event: event,
            game_id: "ZYXLS",
            game_name: "ZYXLS",
            play_id: UserData.instance.uuid,
            event_date: Math.floor(Date.now() / 1000 * 60),
        };
        for (const key in subData) {
            sendData[key] = subData[key];
        }
        NetManager.send("https://ebk.17u.cn/noveladmin/api/game/event/log", "post", { msg: JSON.stringify(sendData) }, (res: any) => {
            console.log(res);
        });
    }
}