import ResManager from "../manager/ResManager";

export default class ButtonScale extends Laya.Sprite {
    constructor() { super(); }
    isReverse: boolean = false;
    sign: number = 1;
    onAwake() {
        this.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
        this.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
        this.on(Laya.Event.MOUSE_OUT, this, this.onMouseOut);
        this.sign = Math.sign(this.scaleX);
    }
    onMouseDown(e: Laya.Event) {
        Laya.Tween.to(this, { scaleX: 0.9 * this.sign, scaleY: 0.9 }, 50, Laya.Ease.sineOut);
    }
    onMouseUp(e: Laya.Event) {
        ResManager.playSound("click.mp3");
        Laya.Tween.to(this, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    onMouseOut(e: Laya.Event) {
        Laya.Tween.to(this, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
}