import CardData from "../gamelogic/CardData";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import { ui } from "../ui/layaMaxUI";

export default class CardPageRuntime extends ui.pages.cardPageUI {
    onAwake() {
        this.cardList.vScrollBarSkin = null;
        this.cardList.mouseHandler = Laya.Handler.create(this, this.onMouseHandler, null, false);
    }
    public set dataSource(data: CardData[]) {
        if (!data) return;
        this.cardList.array = data;
        this.cardList.refresh();
    }
    onMouseHandler(e: Laya.Event, index: number) {
        let card = this.cardList.getCell(index);
        EventManager.event(EventType.SelectCard, [this.parent.parent, e, card]);
    }
}