import LayaTool from "../tool/LayaTool";
import ActorBaseData from "./ActorBaseData";
import ActorBaseState from "./ActorBaseState";
import AnimatorStateScript from "./AnimatorStateScript";
import AnimatorTriggerScript from "./AnimatorTriggerScript";

export class ActorBase extends Laya.Script3D {
    private _states: Laya.AnimatorState[];
    public animator: Laya.Animator = null;
    protected physicsCollider: Laya.PhysicsCollider = null;
    protected rigidbody3D: Laya.Rigidbody3D = null;
    protected _curState: string = ActorBaseState.none;
    protected _animationId: string = null;
    protected sign: string = null;
    protected actorData: ActorBaseData = null;
    public gameObject: Laya.Sprite3D = null;
    onAwake() {
        this.sign = ActorBase.name;
        this.animator = this.owner.getComponent(Laya.Animator);
        this.gameObject = this.owner as Laya.Sprite3D;
        if (!this.animator) {
            this.animator = LayaTool.getComponentInChildren(this.owner, Laya.Animator);
        }
        if (this.animator) {
            let triggerScript: AnimatorTriggerScript = this.animator.owner.addComponent(AnimatorTriggerScript);
            triggerScript.stateTriggerHandler = Laya.Handler.create(this, this.onStateTrigger, null, false);
            this._states = this.animator.getControllerLayer(0)["_states"];
            for (let i = 0; i < this._states.length; i++) {
                let state = this._states[i];
                let script = state.addScript(AnimatorStateScript) as AnimatorStateScript;
                script.name = state.name;
                script.stateEnterHandler = Laya.Handler.create(this, this.onStateEnter, null, false);
                script.stateUpdateHandler = Laya.Handler.create(this, this.onStateUpdate, null, false);
                script.stateExitHandler = Laya.Handler.create(this, this.onStateExit, null, false);
            }
        }
        this.physicsCollider = this.owner.getComponent(Laya.PhysicsCollider);
        this.rigidbody3D = this.owner.getComponent(Laya.Rigidbody3D);
    }
    onEnable() {
        this.onRegisterCustomEvent();
    }
    onDisable() {
        this.onCancelCustomEvent();
    }
    onCollisionEnter(collision: Laya.Collision): void { }
    onCollisionStay(collision: Laya.Collision): void { }
    onCollisionExit(collision: Laya.Collision): void { }
    onTriggerEnter(other: Laya.PhysicsComponent): void { }
    onTriggerStay(other: Laya.PhysicsComponent): void { }
    onTriggerExit(other: Laya.PhysicsComponent): void { }
    onStateEnter(name: string): void { }
    onStateUpdate(name: string): void { }
    onStateExit(name: string): void { }
    onStateTrigger(param1: number, param2: number, param3: string): void { }
    /**注册用户事件 */
    onRegisterCustomEvent(): void { }
    /**注销用户事件 */
    onCancelCustomEvent(): void { }
    /**改变角色状态 */
    changeActorState(newState: string, id: string, isCrossFade = false) {
        if (newState == this._curState && this._animationId == id) return;
        this.onAnimationChange(newState + id, isCrossFade);
        this._curState = newState;
        this._animationId = id;
    }
    /**清楚角色状态 */
    clearActorState() {
        this._curState = ActorBaseState.none;
        this._animationId = null;
    }
    onAnimationChange(name: string, isCrossFade = false) {
        if (this.animator) {
            if (isCrossFade) {
                this.animator.crossFade(name, 0.2, 0, 0);
            } else {
                this.animator.play(name, 0, 0);
            }
        }
    }
    /**回收 */
    onRecovery() {
        this.owner.removeSelf();
        Laya.Pool.recover(this.sign, this);
    }
    /**用数据初始化角色 */
    onInit(data: ActorBaseData) {
        this.actorData = data;
    }
    /**受伤 */
    onDamage(value: number) {
        if (this.actorData.hp == 0) return;
        this.actorData.hp -= value;
        if (this.actorData.hp <= 0) {
            this.actorData.hp = 0;
            this.changeActorState(ActorBaseState.death, '');
        }
    }
    /**获取当前的状态 */
    public get curState() {
        return this._curState;
    }
    /**获取当前的状态id */
    public get animationId() {
        return this._animationId;
    }
}