  private initVideoAd() {
    if (!this.adUnitId_Video) return;
    switch (this.platform) {
      case Platform.wechat:
        this.rewardedVideoAd = wx.createRewardedVideoAd({
          adUnitId: this.adUnitId_Video,
        });
        let rewardedVideoAd_wx = this.rewardedVideoAd as wx.RewardedVideoAd;
        rewardedVideoAd_wx.onError((res) => {
          console.log(res);
        });
        rewardedVideoAd_wx.onClose((res) => {
          if (this.sendVideoData) {
            this.sendVideoData.ad_id = 3;
            TongYiEventManager.sendEvent(10002, this.sendVideoData);
          }
          if (res.isEnded) {
            if (this.sendVideoData) {
              this.sendVideoData.ad_id = 4;
              TongYiEventManager.sendEvent(10002, this.sendVideoData);
            }
            this.video_OnClose_Callback && this.video_OnClose_Callback();
            if (this.showVideoEventId) {
              // mpsdk.Report.reportEvent(this.showVideoEventId, "0");
              this.showVideoEventId = 0;
            }
          } else {
            if (this.showVideoEventId) {
              // mpsdk.Report.reportEvent(this.showVideoEventId, "1");
              this.showVideoEventId = 0;
            }
          }
          // mpsdk.Report.reportVideoTimeEvent();
          this.video_OnClose_Callback = null;
          this.sendVideoData = null;
          EventManager.event(EventType.VideoAdClosed, res.isEnded);
        });
        break;
      case Platform.qq:
        this.rewardedVideoAd = qq.createRewardedVideoAd({
          adUnitId: this.adUnitId_Video,
        });
        let rewardedVideoAd_qq = this.rewardedVideoAd as qq.RewardedVideoAd;
        rewardedVideoAd_qq.onError((res) => {
          console.log(res);
        });
        rewardedVideoAd_qq.onClose((res) => {
          if (res.isEnded) {
            this.video_OnClose_Callback && this.video_OnClose_Callback();
            if ((<any>qq).aly) {
              if (window["videoPosition"]) {
                (<any>qq).aly.videoDot(window["videoPosition"], "success");
                window["videoPosition"] = undefined;
              }
            }
          } else {
            if ((<any>qq).aly) {
              if (window["videoPosition"]) {
                (<any>qq).aly.videoDot(window["videoPosition"], "fail");
                window["videoPosition"] = undefined;
              }
            }
          }
          this.video_OnClose_Callback = null;
        });
        break;
      case Platform.tt:
        this.rewardedVideoAd = tt.createRewardedVideoAd({
          adUnitId: this.adUnitId_Video,
        });
        let rewardedVideoAd_tt = this.rewardedVideoAd as tt.RewardedVideoAd;
        rewardedVideoAd_tt.onError((res) => {
          console.log(res);
        });
        break;
      case Platform.oppo:
        if (this.rewardedVideoAd) {
          (<qg.RewardedVideoAd>this.rewardedVideoAd).destroy();
          this.rewardedVideoAd = null;
        }
        this.rewardedVideoAd = qg.createRewardedVideoAd({
          adUnitId: this.adUnitId_Video,
        });
        let rewardedVideoAd_qg = this.rewardedVideoAd as qg.RewardedVideoAd;
        rewardedVideoAd_qg.onError((res) => {
          console.error(res);
          qg.showToast({ title: `视频广告加载失败`, icon: "none" } as any);
        });
        rewardedVideoAd_qg.onClose((res) => {
          if (res.isEnded) {
            this.video_OnClose_Callback && this.video_OnClose_Callback();
          } else {
          }
        });
        rewardedVideoAd_qg.onLoad(() => {
          rewardedVideoAd_qg.show();
        });
        break;
      case Platform.vivo:
        this.rewardedVideoAd = qg.createRewardedVideoAd({
          posId: this.adUnitId_Video,
        });
        let rewardedVideoAd_vivo = this.rewardedVideoAd as qg.RewardedVideoAd;
        rewardedVideoAd_vivo.onError((res) => {
          console.error(res);
          qg.showToast({ title: `视频广告加载失败`, icon: "none" } as any);
        });
        rewardedVideoAd_vivo.onClose((res) => {
          rewardedVideoAd_vivo.load();
          if (res.isEnded) {
            this.video_OnClose_Callback && this.video_OnClose_Callback();
          } else {
          }
          Laya.SoundManager.musicMuted = false;
        });
        rewardedVideoAd_vivo.onLoad(() => { });
        break;
      default:
        break;
    }
  }
