  public showInterstitialAd(callback: Function) {
    if (callback && this.platform === Platform.editor) callback();
    if (!this.interstitialAd && this.platform != Platform.vivo) return;
    switch (this.platform) {
      case Platform.wechat:
        let interAd_wx = this.interstitialAd as wx.InterstitialAd;
        interAd_wx.show();
        break;
      case Platform.qq:
        let interAd_qq = this.interstitialAd as qq.InterstitialAd;
        interAd_qq.show();
        break;
      case Platform.oppo:
        let interAd_qg = this.interstitialAd as qg.InsertAd;
        interAd_qg.onClose(() => {
          interAd_qg.load();
          if (callback) callback();
        });
        interAd_qg.show();
        break;
      case Platform.vivo:
        this.interstitialAd = (qg as any).createInterstitialAd({
          posId: this.adUnitId_Interstitial,
        });
        let interAd_vivo = this.interstitialAd as qg.InsertAd;
        interAd_vivo.onError((res) => {
          console.error("vivo插屏广告加载失败", res);
          qg.showToast({ title: `插屏广告加载失败`, icon: "none" } as any);
        });
        interAd_vivo.onLoad(() => {
          console.log("vivo插屏广告加载成功");
        });
        interAd_vivo.show();
        break;
      default:
        break;
    }
  }

