import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import ResManager from "../gframe-work/manager/ResManager";
import { ui } from "../ui/layaMaxUI";

export default class SettingDialogRuntime extends ui.pages.settingUI {
    onAwake() {
        this.changeBtnState(this.musicBtn, false);
        this.changeBtnState(this.soundBtn, false);
        this.changeBtnState(this.vibrateBtn, false);
    }
    onOpened() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.musicBtnBg.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.soundBtnBg.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.vibrateBtnBg.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else {
            if (e.target == this.musicBtnBg) {
                this.changeBtnState(this.musicBtn, true);
            } else if (e.target == this.soundBtnBg) {
                this.changeBtnState(this.soundBtn, true);
            } else if (e.target == this.vibrateBtnBg) {
                this.changeBtnState(this.vibrateBtn, true);
            }
        }
    }
    changeBtnState(btn: Laya.Image, isChange: boolean) {
        let isOpen = false;
        if (btn == this.musicBtn) {
            if (isChange) {
                ResManager.musicOpen = !ResManager.musicOpen;
                if (ResManager.musicOpen) {
                    if (ResManager.musicName) ResManager.playMusic(ResManager.musicName);
                } else {
                    ResManager.stopMusic();
                }
            }
            isOpen = ResManager.musicOpen;
        } else if (btn == this.soundBtn) {
            if (isChange) ResManager.soundOpen = !ResManager.soundOpen;
            isOpen = ResManager.soundOpen;
        } else if (btn == this.vibrateBtn) {
            if (isChange) ResManager.vibrateOpen = !ResManager.vibrateOpen;
            isOpen = ResManager.vibrateOpen;
        }
        btn.skin = isOpen ? "main/Ui_FrameSet3.png" : "main/Ui_FrameSet4.png";
        if (isOpen) {
            Laya.Tween.to(btn, { x: 243 }, 100, Laya.Ease.sineOut);
        } else {
            Laya.Tween.to(btn, { x: 202 }, 100, Laya.Ease.sineOut);
        }
    }
}