import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { StoreEggData } from "../gamelogic/StoreEggData";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class TreasureBoxRuntime extends ui.pages.treasureBoxUI {
    goldNum: number = 200;
    levelAdd: number = 3;
    rewardType: number = 0;
    onAwake() {
        this.eggLabel.filters = [CatchRuntime.blackColorFilter];
        this.goldLabel.filters = [CatchRuntime.blackColorFilter];
        this.setBtnVideoTag();
    }
    onOpened() {
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.vidoeReceiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.openBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.storeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    setParam(type: number) {
        if (type == 0 || type == 3 || type == 4) {
            let levelNum = GameDataManager.Instance.enterMapIndex + 1;
            if (GameDataManager.Instance.isCream) levelNum += 1;
            if (type == 0) {
                this.rewardType = 1;
                this.goldNum = 150 * levelNum;
                this.goldBg.skin = "count/Ui_TwoGold.png";
            } else if (type == 3) {
                this.rewardType = 5;
                this.goldNum = 1 * levelNum;
                this.goldBg.skin = "spriteShop/Ui_TwoQuan.png";
            } else if (type == 4) {
                this.rewardType = 4;
                this.goldNum = 10 * levelNum;
                this.goldBg.skin = "newSystem/Ui_MonsterHeadChip.png";
            }
            this.goldLabel.text = "X" + this.goldNum;
            this.goldBg.visible = true;
            this.egg.visible = false;
            this.receiveBtn.visible = true;
            this.vidoeReceiveBtn.visible = true;
            this.openBtn.visible = false;
            this.storeBtn.visible = false;
            this.eggTip.visible = false;
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 8, button_id: 8, ad_id: 1, ad_type: 1 };
            TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        } else {
            this.levelAdd = 3;
            if (UserData.instance.niuDanSuperBag.length >= 1) {
                if (Math.random() * 100 < 80) {
                    this.levelAdd = 2;
                }
            }
            this.eggTip.visible = true;
            this.goldBg.visible = false;
            this.egg.visible = true;
            this.receiveBtn.visible = false;
            this.vidoeReceiveBtn.visible = false;
            this.openBtn.visible = true;
            this.storeBtn.visible = true;
            this.storeBtn.visible = false;
            Laya.timer.once(AllPlatformAdMgr.Instance.delayTime, this, () => {
                this.storeBtn.visible = true;
            });
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 8, button_id: 9, ad_id: 1, ad_type: 1 };
            TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        }
    }
    onBtnClick(e: Laya.Event) {
        let closeHandler = Laya.Handler.create(this, () => {
            EventManager.event(EventType.OpenTreasureBoxFinish);
        });
        if (e.target == this.receiveBtn) {
            LayaTool.openScene("pages/getCard.scene", false, true, closeHandler, [this.rewardType, this.goldNum, 7]);
            this.close();
        } else if (e.target == this.vidoeReceiveBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 8, button_id: 8, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-92987caa2fa84b4e", () => {
                LayaTool.openScene("pages/getCard.scene", false, true, closeHandler, [this.rewardType, this.goldNum * 2, 7]);
                this.close();
            });
        } else if (e.target == this.openBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 8, button_id: 9, ad_id: 2, ad_type: 1 };
            TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
            AllPlatformAdMgr.Instance.showVideoAd("adunit-ad045e709bd7ac27", () => {
                LayaTool.openScene("pages/superEggMachine.scene", false, true, closeHandler, [false, false]);
                this.close();
            })
        } else if (e.target == this.storeBtn) {
            if (UserData.instance.niuDanSuperBag.length < 2) {
                let storeEggData = new StoreEggData();
                storeEggData.level = this.levelAdd;
                storeEggData.saveTime = Date.now();
                UserData.instance.niuDanSuperBag.push(storeEggData);
                UserData.instance.onDataChange("niuDanSuperBag");
            }
            closeHandler.run();
            this.close();
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}