import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import StartRuntime from "./StartRuntime";

export default class MapRuntime extends ui.pages.mapUI {
    data: any = null;
    public set dataSource(data: any) {
        if (!data) return;
        this.data = data;
        this.mapImg.skin = `mapImage/Ui_Guan${data.index + 1}.png`;
        let rank = StartRuntime.isElite ? UserData.instance.eliteGameRank : UserData.instance.gameRank;
        let maxRankIndex = StartRuntime.isElite ? GameDataManager.Instance.creamStageMaxIndex : GameDataManager.Instance.stageMaxIndex;
        let allPass = StartRuntime.isElite ? UserData.instance.eliteGameAllPass : UserData.instance.gameAllPass;
        if (data.listIndex <= rank) {
            this.lockImage.visible = false;
            this.mapImg.filters = null;
        } else {
            this.lockImage.visible = true;
            this.mapImg.filters = [StartRuntime.mapColorFiter];
        }
        if (data.listIndex < maxRankIndex) {
            if (data.listIndex < rank) {
                this.passImage.visible = true;
            } else {
                this.passImage.visible = false;
            }
        } else {
            this.passImage.visible = allPass;
        }
        this.setElite(StartRuntime.isElite);
    }
    setElite(isElite: boolean) {
        this.eliteFrog.visible = isElite;
        if (isElite) {
            this.mapImg.skin = `mapImage/Ui_GuanElite${this.data.index + 1}.png`;
        } else {
            this.mapImg.skin = `mapImage/Ui_Guan${this.data.index + 1}.png`;
        }
    }
}