import DataBase from "../gamelogic/DataBase";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { WXCloud } from "../gamelogic/WXCloud";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import ShaderManager from "../gframe-work/manager/ShaderManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class LoadViewRuntime extends ui.pages.loadUI {
    finish1: boolean = false;
    finish2: boolean = false;
    imageList: Laya.Image[] = [];
    gap: number = 88;
    onAwake() {
        this.setShareMessageToFriendCallback();
        WXCloud.init().then(() => {
            DataBase.init("cloud1-8gvhoxyo84834c09");
        });
        this.sendInitInfo();
        this.openWxShare();
        this.progressBar.width = 0;
        this.progressLabel.text = "0%";
        for (let i = 0; i < 18; i++) {
            let image = new Laya.Image("load/Ui_LoadingBg2.png");
            image.sizeGrid = "0,0,0,0,1";
            image.height = 80;
            image.y = 37 + i * 88;
            if ((i + 1) % 2 == 0) {
                image.width = 900;
                image.x = -135;
            } else {
                image.width = 810;
                image.x = 0;
            }
            this.imageList.push(image);
            this.bg.addChild(image);
        }
        Laya.timer.frameLoop(1, this, this.FrameLoop);
        Laya.Dialog.manager.closeEffectHandler = null;
        ShaderManager.init();
        this.loadBox.visible = false;
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.progressLabel.text = "0%";
        Laya.Shader3D.debugMode = true;
        this.loadSk.visible = false;
        this.loadSk.x = Laya.stage.width / 2;
        this.loadSk.y = Laya.stage.height / 2;
        this.loadSk["_complete"] = Laya.Handler.create(this, () => {
            this.loadSk.play(0, false);
            this.loadSk.on(Laya.Event.STOPPED, this, () => {
                Laya.timer.once(500, this, () => {
                    Laya.Tween.to(this.loadSk, { alpha: 0 }, 500, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                        this.loadBox.visible = true;
                        this.finish1 = true;
                        let mustResList = [
                            "stage.json",
                            "creamStage.json",
                            "passReward.json",
                            "standardModel.json",
                            "eliteStage.json",
                            "enemy.json",
                            "collect.json",
                            "mix.json",
                            "mixCost.json",
                            "specialMix.json",
                            "gold.json",
                            "upgrade.json",
                            "tower.json",
                            "shop.json",
                            "pvp.json",
                            "pvpScore.json",
                            "goods.json",
                            "ShowScene.ls"];
                        ResManager.create(mustResList).then(() => {
                            GameDataManager.Instance.init();
                            let mapDataResList = [
                                "SceneNav1.json",
                                "ScenePoint1.json",
                                "SceneNav2.json",
                                "ScenePoint2.json",
                                "SceneNav3.json",
                                "ScenePoint3.json",
                                "SceneNav4.json",
                                "ScenePoint4.json",
                                "SceneNav5.json",
                                "ScenePoint5.json",
                                "SceneNav6.json",
                                "ScenePoint6.json",
                                "SceneNav7.json",
                                "ScenePoint7.json",
                                "SceneNav8.json",
                                "ScenePoint8.json"
                            ];
                            ResManager.create(mapDataResList, this.onProgress.bind(this)).then(() => {
                                ResManager.download(["main.atlas"]).then(() => {
                                    this.initOpenDataContext();
                                    DataBase.get("usersinfo", { _openid: "{openid}" }).then((res) => {
                                        if (res.data.length > 0) {
                                            let netData = res.data[0];
                                            UserData._id = netData._id;
                                            UserData.instance.init(netData);
                                        } else {
                                            console.log('用户记录数据0条');
                                            let userData = JSON.stringify(UserData.instance);
                                            userData = JSON.parse(userData);
                                            delete userData["$_GID"];
                                            DataBase.add("usersinfo", userData).then((res) => {
                                                console.log(res);
                                            }).catch(res => {
                                                console.error(res);
                                            });
                                        }
                                        AllPlatformAdMgr.Instance.noAd = UserData.instance.noAd;
                                        GuideManager.init("guideconfig.json", ui.pages.guideUI).then(() => {
                                            LayaTool.openScene("pages/main.scene", true, false, null, null);
                                        });    
                                    }).catch(res => {
                                        console.error(res);
                                        AllPlatformAdMgr.Instance.noAd = UserData.instance.noAd;
                                        GuideManager.init("guideconfig.json", ui.pages.guideUI).then(() => {
                                            LayaTool.openScene("pages/main.scene", true, false, null, null);
                                        });    
                                    });
                                });
                            });
                        });
                    }));
                });
            });
        });
    }
    onProgress(res: {
        type: string;
        progress: number;
        location?: string;
        name?: string;
        totalBytesWritten?: number;
        totalBytesExpectedToWrite?: number;
    }) {
        this.progressBar.width = 620 * res.progress;
        this.progressLabel.text = Math.floor(100 * res.progress) + "%";
    }
    FrameLoop() {
        for (let i = 0; i < this.imageList.length; i++) {
            let image = this.imageList[i];
            if ((i + 1) % 2 == 0) {
                image.x += 0.45;
                if (image.x >= -45) {
                    image.x = -135;
                }
            } else {
                image.x -= 0.45;
                if (image.x <= -90) {
                    image.x = 0;
                }
            }
        }
    }
    onDestroy() {
        Laya.timer.clear(this, this.FrameLoop);
    }
    openWxShare() {
        if (window.wx) {
            (wx as any).showShareMenu({
                withShareTicket: true,
                menus: ["shareAppMessage", "shareTimeline"],
            });
            // // 1. 设置全局变量
            // LoadViewRuntime.shareSuccess = false;
            // // 3.1. 当点击右上角微信功能菜单触发被动分享
            // (wx as any).onShareAppMessage(function () {
            //     const shareInfo = {
            //         serial: 0,//分享点序号
            //         title: '僵尸入侵！帮助队友逃出生天！',//当随机分享内容不可用时使用的默认分享标题
            //         image: "https://cdn.xxtch.com/ZombieEscape/share.png",
            //         scoreValue: 0, //当前玩家等级或分数或游戏时长（服务端的每组分享图可以设置下限阀值，当玩家分值过低时不可用）
            //     };
            //     LoadViewRuntime.shareSuccess = true;// 拉起分享前预设分享成功
            //     return mpsdk.Share.commonShare(shareInfo, null, () => LoadViewRuntime.shareSuccess = false, this);
            // });
        }
    }
    sendInitInfo() {
        if (typeof wx != "undefined") {
            let data = { wifi: 0, origin: "", extra1: "", extra2: "" };
            wx.getNetworkType({
                success: (res) => {
                    if (res.networkType == "wifi") {
                        data.wifi = 1;
                    } else {
                        data.wifi = 0;
                    }
                    wx["cloud"].callFunction({
                        // 云函数名称
                        name: 'getOpenid',
                        success: function (res: any) {
                            let enterOption = (wx as any).getEnterOptionsSync();
                            data.origin = enterOption.scene + "";
                            data.extra1 = UserData.instance.registerTime + "";
                            if (res.result) {
                                data.extra2 = res.result.openid;
                            }
                            TongYiEventManager.sendEvent(10001, data);
                        },
                        fail: console.error
                    })

                },
                fail: () => { },
                complete: () => { }
            });
        }
    }
    initOpenDataContext() {
        if (Laya.Browser.onMiniGame) {
            //加载一个json和图集
            Laya.loader.load(["res/atlas/challengeRank.atlas", "res/atlas/pvp.atlas", "res/config/pvpScore.json", "res/atlas/AiImage.atlas", "res/atlas/share.atlas"], Laya.Handler.create(null, () => {
                //加载完成
                //使用接口将配置文件透传到子域
                Laya.MiniAdpter.sendJsonDataToDataContext("res/config/pvpScore.json");
                //使用接口将图集透传到子域
                Laya.MiniAdpter.sendAtlasToOpenDataContext("res/atlas/challengeRank.atlas");
                //使用接口将图集透传到子域
                Laya.MiniAdpter.sendAtlasToOpenDataContext("res/atlas/pvp.atlas");
                //使用接口将图集透传到子域
                Laya.MiniAdpter.sendAtlasToOpenDataContext("res/atlas/AiImage.atlas");
                //使用接口将图集透传到子域
                Laya.MiniAdpter.sendAtlasToOpenDataContext("res/atlas/share.atlas");
            }));
        }
    }
    setShareMessageToFriendCallback() {
        if (typeof wx != "undefined") {
            (wx as any).onShareMessageToFriend && (wx as any).onShareMessageToFriend(this.shareMessageToFriendCallBack.bind(this));
        }
    }
    shareMessageToFriendCallBack(res: any) {
        console.log('wx.shareMessageToFriend', res);
        let tips = res.success ? '分享成功' : '分享失败';
        wx.showToast({
            title: tips,
            icon: 'none',
            duration: 2000
        });
        // 这里可触发游戏逻辑
    }
}