import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";
import TaskRuntime from "./TaskRuntime";

export default class TaskCellRuntime extends ui.pages.taskCellUI {
    taskType: number = 0;
    taskState: number = 0;
    _dataSource: any = null;
    static brownFilter = new Laya.ColorFilter(
        [
            164 / 255, 0, 0, 0, 0, //R
            0, 100 / 255, 0, 0, 0, //G
            0, 0, 38 / 255, 0, 0, //B
            0, 0, 0, 1, 0, //A
        ]);
    onAwake() {
        this.descLabel.filters = [TaskCellRuntime.brownFilter];
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goldLabel.filters = [CatchRuntime.blackColorFilter];
    }
    public set dataSource(data: any) {
        if (!data) return;
        if (data.nativeAd) {

        } else {
            this._dataSource = data;
            this.goldLabel.text = "X" + data.rewardNum;
            this.descLabel.text = data.taskDesc;
            this.taskType = data.taskType;
            if (this.taskType == 0) {
                let dayCount = UserData.instance.dayTaskCountRec[data.index];
                if (dayCount >= data.num) {
                    //达到目标
                    let receive = UserData.instance.dayTaskReceiveRec[data.index];
                    if (receive) {
                        //领取
                        this.setState(1);
                    } else {
                        //未领取
                        this.setState(2);
                    }
                } else {
                    //未达到目标
                    this.setState(0);
                }
                this.progressLabel.text = `(${dayCount}/${data.num})`;
            } else {
                let stageCount = UserData.instance.achieveTaskCountRec[data.index];
                if (stageCount >= data.num) {
                    //达到目标
                    let receive = UserData.instance.achieveTaskReceiveRec[data.index];
                    if (receive) {
                        //领取
                        this.setState(1);
                    } else {
                        //未领取
                        this.setState(2);
                    }
                } else {
                    //未达到目标
                    this.setState(0);
                }
                this.progressLabel.text = `(${stageCount}/${data.num})`;
            }
        }
    }
    setState(state: number) {
        this.taskState = state;
        if (state == 0) {
            this.running.visible = true;
            // this.finishMask.visible = false;
            this.completeTag.visible = false;
            this.receiveBtn.visible = false;
            this.taskBg.skin = "sign/Ui_FrameTask1.png";
        } else if (state == 2) {
            this.running.visible = false;
            // this.finishMask.visible = false;
            this.completeTag.visible = false;
            this.receiveBtn.visible = true;
            this.taskBg.skin = "sign/Ui_FrameTask1.png";
        } else if (state == 1) {
            this.running.visible = false;
            // this.finishMask.visible = true;
            this.receiveBtn.visible = false;
            this.completeTag.visible = true;
            this.taskBg.skin = "sign/Ui_FrameTask3.png";
        }
    }
    onBtnClick() {
        if (this._dataSource.taskType == 0) {
            //每日任务
            UserData.instance.dayTaskReceiveRec[this._dataSource.index] = 1;
            UserData.instance.onDataChange("dayTaskReceiveRec");
            let rewardConfig = TaskRuntime.taskConfig1[this._dataSource.index];
            let reward = rewardConfig.reward;
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, reward, 6]);
            TongYiEventManager.sendEvent(10008, { mission_type: "1", mission_id: rewardConfig.id });
        } else {
            //成就任务
            let stage = UserData.instance.achieveTaskStage[this._dataSource.index];
            let rewardConfig = TaskRuntime.taskConfig2[this._dataSource.index];
            let num = rewardConfig.num;
            let reward = rewardConfig.reward[stage];
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, reward, 6]);
            TongYiEventManager.sendEvent(10008, { mission_type: "2", mission_id: rewardConfig.id });
            if (stage < num.length - 1) {
                UserData.instance.achieveTaskStage[this._dataSource.index]++;
                UserData.instance.onDataChange("achieveTaskStage");
                UserData.instance.achieveTaskReceiveRec[this._dataSource.index] = 0;
            } else {
                UserData.instance.achieveTaskReceiveRec[this._dataSource.index] = 1;
            }
            UserData.instance.onDataChange("achieveTaskReceiveRec");
        }
        EventManager.event(EventType.UpdateTask);
        EventManager.event(EventType.UpdateRed);
    }
}