import EventManager from "../gframe-work/manager/EventManager";
import FightSceneLogic from "./FightSceneLogic";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";

export class ChallengeFightScene extends FightSceneLogic {
    waveIndex = 0;
    onAwake() {
        super.onAwake();
    }
    onOpened() {
        this.setMonsterIdList();
        super.onOpened();
        this.fightCameraData = { "position": [2.7, 7.37, 18.5], "rotation": [0.1435388, -0.2619153, -0.03942669, -0.9535421] };
        this.setCameraFightPosAndRot();
    }
    setMonsterIdList() {
        let monsterIdList = GameDataManager.Instance.challengeMonsterIdList[this.waveIndex];
        GameDataManager.Instance.setMonsterIdList(monsterIdList, false);
    }
    haveNextWave() {
        return ++this.waveIndex < GameDataManager.Instance.challengeMonsterIdList.length;
    }
    switchNextWaveEnemy() {
        return new Promise<void>(resolve => {
            this.clearEnemyGroup();
            this.setMonsterIdList();
            this.enemyFightIndex = 0;
            this.createEnemyGroup();
            EventManager.event(EventType.InitFightCell);
            resolve();
        });
    }
}