import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class AskForCardRuntime extends ui.pages.askForCardUI {
    monsterIdIndex: number;
    onAwake(): void {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.askBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.askBtn) {
            LayaTool.openScene("pages/selectShareFriend.scene", false, true, null, [this.monsterIdIndex], false);
        } else if (e.target == this.closeBtn) {
            this.close();
        }
    }
    setParam(cardData: CardData) {
        let list = GameDataManager.Instance.askForCardList;
        let monsterIdIndex = list.indexOf(cardData.monsterId);
        if (monsterIdIndex == -1) monsterIdIndex = list.length - 1;
        this.monsterIdIndex = monsterIdIndex;
        let monsterId = list[monsterIdIndex];
        let monsterData = GameDataManager.Instance.getOneCardData(monsterId);
        this.card.dataSource = monsterData;
    }
}