import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import MathTool from "../gframe-work/tool/MathTool";
import { ui } from "../ui/layaMaxUI";

export default class ChallengeRankRuntime extends ui.pages.challengeRankUI {
    challengeRankRewardDataList = [];
    onAwake() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.wxOpenDataViewer.visible = false;
        this._list.vScrollBarSkin = null;
        this.showRankList();
        this.showFakeRankList();
        this.initChallengeRankRewardDataList();
        this.rankRewardList.vScrollBarSkin = null;
        this.rankRewardList.array = this.challengeRankRewardDataList;
        this.rankRewardList.refresh();
        EventManager.on(EventType.ReceiveRankReward, this, this.onReceiveRankReward);
        EventManager.on(EventType.GoChallenge, this, this.onGoChallenge);
    }
    onDestroy(): void {
        EventManager.off(EventType.ReceiveRankReward, this, this.onReceiveRankReward);
        EventManager.off(EventType.GoChallenge, this, this.onGoChallenge);
        this.hideRankList();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        }
    }
    showRankList() {
        // if (typeof wx != "undefined") {
        //     this.wxOpenDataViewer.postMsg({ type: "showchallenge" });
        // }
    }
    hideRankList() {
        // if (typeof wx != "undefined") {
        //     this.wxOpenDataViewer.postMsg({ type: "hidechallenge" });
        // }
    }
    showFakeRankList() {
        let listData = [];
        let challengeLevel = UserData.instance.challengeLevel;
        let selfData = { index: 0, avatarIP: "", UserName: "我", RankValue: (challengeLevel[0] - 1) * 10 + challengeLevel[1], isMe: true };
        listData.push(selfData);
        this.addAiData(listData);
        //根据RankValue排序
        listData.sort((a, b) => {
            return b.RankValue - a.RankValue;
        });
        //增加一个用于查看的index排名
        for (let i = 0; i < listData.length; i++) {
            listData[i].index = i + 1;
        }
        this._list.array = listData;
        this._list.refresh();
    }
    initChallengeRankRewardDataList() {
        for (let i = 0; i < GameDataManager.Instance.towerConfig.length; i++) {
            let configData = GameDataManager.Instance.towerConfig[i];
            if (configData.rankReward.length > 0 || configData.rankExtraReward.length > 0) {
                let level = configData.level - (Math.ceil(configData.level / 10) - 1) * 10;
                let rewardData = { index: this.challengeRankRewardDataList.length, level: [configData.tower, level] };
                if (configData.rankReward.length > 0) {
                    let rewardList = GameDataManager.Instance.parseRewardData(configData.rankReward);
                    rewardData["rankReward"] = GameDataManager.Instance.countRewardList(rewardList, FightType.Tower);
                }
                if (configData.rankExtraReward.length > 0) {
                    let rewardList = GameDataManager.Instance.parseRewardData(configData.rankExtraReward);
                    rewardData["rankExtraReward"] = GameDataManager.Instance.countRewardList(rewardList, FightType.Tower);
                }
                this.challengeRankRewardDataList.push(rewardData);
            }
        }
    }
    onReceiveRankReward(index: number, type: string) {
        let rewardData = this.challengeRankRewardDataList[index];
        if (type == "normal") {
            let level = `${rewardData.level[0]}-${rewardData.level[1]}`;
            UserData.instance.challengeRewardReceiveList.push(level);
            UserData.instance.onDataChange("challengeRewardReceiveList");
            GameDataManager.Instance.receiveReward(rewardData.rankExtraReward);
            this.rankRewardList.refresh();
        } else {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-670d87306dc1c259", () => {
                let level = `${rewardData.level[0]}-${rewardData.level[1]}`;
                UserData.instance.challengeExtraRewardReceiveList.push(level);
                UserData.instance.onDataChange("challengeExtraRewardReceiveList");
                GameDataManager.Instance.receiveReward(rewardData.rankReward);
                this.rankRewardList.refresh();
            });
        }
    }
    onGoChallenge() {
        this.close();
    }
    addAiData(listData: any[]) {
        for (let i = 0; i < GameDataManager.Instance.aiConfig.length; i++) {
            let ai = GameDataManager.Instance.aiConfig[i];
            let otherData = { index: 0, avatarIP: ai.icon, UserName: ai.name, RankValue: MathTool.getRandom(0, 50), isMe: false };
            listData.push(otherData);
        }
        console.log(listData);
    }
}