import DataBase from "./DataBase";

export class WXPay {
    public static iosOrder(itemData: any) {
        return new Promise<void>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("iosOrder:fail");
            } else {
                this.createOrder(itemData, "IOS").then((orderData: any) => {
                    (wx as any).navigateToMiniProgram({
                        appId: 'wx9a9c65769d117255',
                        path: `pages/pay/test?name=${itemData.desc}&amount=${itemData.costNum}&remark=${orderData._id}`,
                        envVersion: 'trial',
                        success: () => {
                            let onShowCallback = (res: any) => {
                                if (res.referrerInfo.extraData.code == 0) {
                                    this.changeOrderStatus(orderData, "待发货");
                                    this.changeOrderStatus(orderData, "已发货");
                                    resolve();
                                }
                                (wx as any).offShow(onShowCallback);
                            }
                            (wx as any).onShow(onShowCallback);
                        },
                        fail: () => {

                        }
                    })
                });
            }
        });
    }
    /**
     * 发起米大师充值
     * @returns 
     */
    public static requestMidasPayment(data: { buyQuantity: number }) {
        return new Promise((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("requestMidasPayment:fail");
            } else {
                (wx as any).requestMidasPayment({
                    mode: "game",
                    env: 0,
                    offerId: "1450032621",
                    platform: "android",
                    currencyType: "CNY",
                    buyQuantity: data.buyQuantity,
                    zoneId: "1",
                    success: (res: any) => {
                        console.log(res);
                        resolve(res);
                    },
                    fail: (err: any) => {
                        console.error(err);
                        reject("requestMidasPayment:fail");
                    },
                    complete: (res: any) => {
                        console.log(res);
                    }
                });
            }
        });
    }
    /**
     * 查询游戏币账户余额
     */
    public static getBalance() {
        return new Promise<{ balance: number }>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("getBalance:fail");
            } {
                wx["cloud"].callFunction({
                    // 云函数名称
                    name: 'getBalance',
                    success: (res: any) => {
                        console.log(res);
                        if (res.result && res.result.hasOwnProperty("balance")) {
                            let balance: number = res.result.balance;
                            resolve({ balance: balance });
                        } else {
                            reject("getBalance:fail");
                        }
                    },
                    fail: (err: any) => {
                        console.error(err);
                        reject("getBalance:fail");
                    }
                });
            }
        });
    }
    /**
     * 购买道具
     * @param itemData 
     */
    public static buyItem(itemData: any) {
        return new Promise<void>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("buyItem:fail");
            } {
                let isIos = false;
                let systemInfo = wx.getSystemInfoSync() as any;
                if (systemInfo.model.indexOf("iPhone") != -1) {
                    isIos = true;
                }
                if (isIos) {
                    this.iosOrder(itemData).then(() => {
                        resolve();
                    })
                } else {
                    let amt = itemData.costNum * 100;
                    this.createOrder(itemData, "Android").then((orderData: any) => {
                        this.requestMidasPayment({ buyQuantity: amt }).then(() => {
                            this.changeOrderStatus(orderData, "待发货");
                            this.midasPay(orderData).then(() => {
                                this.changeOrderStatus(orderData, "已发货");
                                resolve();
                            }).catch(() => {

                            })
                        }).catch(() => {

                        })
                    });
                }
            }
        });
    }
    /**
     * 创建订单
     * @param itemData 
     */
    public static createOrder(itemData: any, pt: string) {
        return new Promise<any>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("createOrder:fail");
            } {
                let data = {
                    pt: pt,
                    itemId: itemData.productId, // 下单的商品
                    price: itemData.costNum, // 成交价格
                    amount: 1, // 成交数量
                    desc: itemData.desc,
                    status: "待支付", // 状态         
                    createTime: new Date(), // 创建时间
                    updateTime: new Date(), // 更新时间
                }
                DataBase.add("orderinfo", data).then((res) => {
                    console.log(res);
                    data["_id"] = res._id;
                    data["amt"] = data.price * 100;
                    resolve(data);
                }).catch(err => {
                    reject("createOrder:fail");
                });
            }
        });
    }
    /**
     * 发起米大师消费
     */
    public static midasPay(orderData: any) {
        return new Promise<void>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("midasPay:fail");
            } {
                wx["cloud"].callFunction({
                    // 云函数名称
                    name: 'midasPay',
                    data: { amt: orderData.amt, billNo: orderData._id },
                    success: (res: any) => {
                        console.log(res);
                        if (res.result && res.result.errCode == 0) {
                            resolve();
                        } else {
                            reject("midasPay:fail");
                        }
                    },
                    fail: (err: any) => {
                        this.midasCancelPay(orderData);
                        console.error(err);
                        reject("midasPay:fail");
                    }
                });
            }
        });
    }/**
     * 取消米大师消费
     */
    public static midasCancelPay(orderData: any) {
        return new Promise<void>((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("midasCancelPay:fail");
            } {
                wx["cloud"].callFunction({
                    // 云函数名称
                    name: 'midasCancelPay',
                    data: { billNo: orderData._id },
                    success: (res: any) => {
                        console.log(res);
                        if (res.result && res.result.errCode == 0) {
                            resolve();
                        } else {
                            reject("midasCancelPay:fail");
                        }
                    },
                    fail: (err: any) => {
                        console.error(err);
                        reject("midasCancelPay:fail");
                    }
                });
            }
        });
    }
    public static changeOrderStatus(orderData: any, status: string) {
        return DataBase.update("orderinfo", orderData._id, { status: status });
    }
}