import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class AddMoneyRuntime extends ui.pages.addMoneyUI {
    money: number = 200;
    onAwake() {
        this.goldLabel.filters = [CatchRuntime.blackColorFilter];
        this.setBtnVideoTag();
    }
    onOpened() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.getBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 1, button_id: 1, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    setParam(type: number, gold: number) {
        let goldList = [200, 300, 400, 450, 500, 550, 600, 700];
        this.money = goldList[UserData.instance.gameRank];
        this.goldLabel.text = "X" + this.money;
        if (type == 1) {
            this.title1.visible = true;
            this.title2.visible = false;
            this.moneyBg.y = -261;
        } else {
            this.title1.visible = false;
            this.title2.visible = true;
            this.moneyBg.y = -243;
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (e.target == this.getBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 1, button_id: 1, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                UserData.instance.gold += this.money;
                UserData.instance.onDataChange("gold");
                EventManager.event(EventType.AddMoney, ["gold", this.money]);
            });
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}