import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import TimeTool from "../gframe-work/tool/TimeTool";
import { ui } from "../ui/layaMaxUI";
import GiftPackRuntime from "./GiftPackRuntime";

export default class GiftPackPageRuntime extends ui.pages.giftPackPageUI {
    curTabIndex: number = -1;
    giftPackList: GiftPackRuntime[] = [];
    giftPackDataList: any[];
    spaceY = 10;
    onAwake(): void {
        this.tabList.mouseHandler = Laya.Handler.create(this, this.onTabListMouseHandler, null, false);
        this.initTabList(0);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.packPanel.vScrollBarSkin = null;
        EventManager.on(EventType.RefreshGiftPackList, this, this.onRefreshGiftPackList);
    }
    onDestroy(): void {
        EventManager.off(EventType.RefreshGiftPackList, this, this.onRefreshGiftPackList);
    }
    initTabList(tabIndex: number) {
        if (tabIndex == this.curTabIndex) return;
        let tabDataList = [];
        for (let i = 0; i < 3; i++) {
            if (tabIndex != i) {
                tabDataList.push({ tabBg: { skin: `spriteShop/Btn_Shop1.png` }, tabLabel: { skin: `pay/Btn_Giftword${(i + 1) * 2 - 1}.png` } });
            } else {
                tabDataList.push({ tabBg: { skin: `spriteShop/Btn_Shop2.png` }, tabLabel: { skin: `pay/Btn_Giftword${(i + 1) * 2}.png` } });
            }
        }
        this.tabList.array = tabDataList;
        this.tabList.refresh();
        this.curTabIndex = tabIndex;
        this.initItemList(this.curTabIndex);
    }
    initItemList(itemIndex: number) {
        let keyList = ["newGiftPack", "middleGiftPack", "superGiftPack"];
        this.giftPackDataList = this.getGiftPackDataList(keyList[itemIndex]);
        let diff = this.giftPackDataList.length - this.giftPackList.length;
        let biggerlength = diff > 0 ? this.giftPackDataList.length : this.giftPackList.length;
        let smallerlength = diff > 0 ? this.giftPackList.length : this.giftPackDataList.length;
        let offsetY = 0;
        for (let i = 0; i < biggerlength;) {
            if (i < smallerlength) {
                //正常赋值操作
                let cell = this.giftPackList[i];
                let data = this.giftPackDataList[i];
                cell.dataSource = data;
                cell.y = offsetY;
                offsetY += (cell.height + this.spaceY);
                i++;
            } else {
                if (diff > 0) {
                    //创建新格子操作
                    let cell = this.getOneCell();
                    let data = this.giftPackDataList[i];
                    cell.dataSource = data;
                    cell.y = offsetY;
                    offsetY += (cell.height + this.spaceY);
                    this.packPanel.addChild(cell);
                    this.giftPackList.push(cell);
                    i++;
                } else {
                    //回收格子操作
                    let cell = this.giftPackList[i];
                    cell.removeSelf();
                    Laya.Pool.recover("GiftPack", cell);
                    this.giftPackList.splice(i, 1);
                    biggerlength = this.giftPackList.length;
                }
            }
        }
    }
    onTabListMouseHandler(e: Laya.Event, index: number) {
        if (e.type != Laya.Event.CLICK) return;
        this.initTabList(index);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        }
    }
    getOneCell() {
        let cell: GiftPackRuntime = Laya.Pool.getItem("GiftPack");
        if (!cell) cell = new GiftPackRuntime();
        return cell;
    }
    getGiftPackDataList(key: string) {
        let giftPackDataList = [...GameDataManager.Instance.goodsItem[key]];
        if (key == "newGiftPack") {
            let dayDiff = TimeTool.DateDiff(UserData.instance.registerTimestamp, Date.now());
            if (dayDiff == 0) {
                //首日
                let newGiftPackDataList = [];
                for (let i = 0; i < 3; i++) {
                    let giftPackData = giftPackDataList[i];
                    let giftPackBuyTime = GameDataManager.Instance.getGiftPackBuyTime(giftPackData.productId);
                    if (giftPackBuyTime == 0) {
                        newGiftPackDataList.push(giftPackData);
                    }
                }
                giftPackDataList = newGiftPackDataList;
            } else {
                //次日及后续
                let isBuyNewGiftPack = false;
                for (let i = 0; i < giftPackDataList.length; i++) {
                    let giftPackData = giftPackDataList[i];
                    let giftPackBuyTime = GameDataManager.Instance.getGiftPackBuyTime(giftPackData.productId);
                    if (giftPackBuyTime > 0) {
                        isBuyNewGiftPack = true;
                        break;
                    }
                }
                if (isBuyNewGiftPack) {
                    giftPackDataList.length = 0;
                } else {
                    giftPackDataList = [giftPackDataList[3]];
                }
            }
        } else if (key == "superGiftPack") {
            for (let i = 0; i < giftPackDataList.length;) {
                let giftPackData = giftPackDataList[i];
                let giftPackBuyTime = GameDataManager.Instance.getGiftPackBuyTime(giftPackData.productId);
                if (giftPackBuyTime >= giftPackData.limit) {
                    giftPackDataList.splice(i, 1);
                } else {
                    i++;
                }
            }
        }
        return giftPackDataList;
    }
    onRefreshGiftPackList() {
        this.initItemList(this.curTabIndex);
    }
}