import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class NeverGiveUpRuntime extends ui.pages.neverGiveUpUI {
    onAwake() {
        this.continueFightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.continueFightBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-670d87306dc1c259", () => {
                LayaTool.openScene("pages/superEggMachine.scene", false, true, null, [false, true]);
                EventManager.once(EventType.CatchResultClose, this, this.onCatchResultClose);
            });
        } else if (e.target == this.closeBtn) {
            this.close("no");
        }
    }
    onCatchResultClose(cardData: CardData) {
        this.changeFightMonsterList(cardData);
        this.close("yes");
    }
    changeFightMonsterList(newCardData: CardData) {
        //先找到已解锁的空位放置
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let unlock = UserData.instance.fightPosUnlock[i];
            let cardData = UserData.instance.fightCardList[i];
            if (unlock && !cardData) {
                UserData.instance.fightCardList[i] = newCardData;
                UserData.instance.onDataChange("fightCardList");
                GameDataManager.Instance.monsterAtkTimeList.push(0);
                return;
            }
        }
        //没有找到已解锁的空位就替换战斗力最低的
        let lowestFightValue = newCardData.atk + newCardData.hp;
        let lowestFightIndex = -1;
        let haveIndex = -1;
        let atkTimeIndex = -1;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let unlock = UserData.instance.fightPosUnlock[i];
            let cardData = UserData.instance.fightCardList[i];
            if (unlock && cardData) {
                haveIndex++;
                let fightValue = cardData.atk + cardData.hp;
                if (fightValue < lowestFightValue) {
                    atkTimeIndex = haveIndex;
                    lowestFightIndex = i;
                    lowestFightValue = fightValue;
                }
            }
        }
        if (lowestFightIndex != -1) {
            UserData.instance.fightCardList[lowestFightIndex] = newCardData;
            UserData.instance.onDataChange("fightCardList");
        }
        if (atkTimeIndex >= 0 && atkTimeIndex < GameDataManager.Instance.monsterAtkTimeList.length) {
            GameDataManager.Instance.monsterAtkTimeList[atkTimeIndex] = 0;
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}