export default class StageManager {
    private static _StageManager: StageManager;
    private static _UI3D: Laya.Sprite;
    private static _GameSceneRoot: Laya.Sprite;
    private static _GameUIRoot: Laya.Sprite;
    private static _UpRoot: Laya.Sprite;
    private static _UpDialogRoot: Laya.Sprite;
    private static _AdRoot: Laya.Sprite;
    private static _GuideRoot: Laya.Sprite;
    private static _EffectRoot: Laya.Sprite;
    public static init(): void {
        if (!StageManager._StageManager) {
            StageManager._StageManager = new StageManager();
        }
    }
    private constructor() {
        window["StageManager"] = this;
        UIConfig.closeDialogOnSide = false;
        Laya.Scene.root;
        Laya.Dialog.manager.name = 'DialogRoot';
        StageManager._GameSceneRoot = new Laya.Sprite;
        StageManager._GameSceneRoot.name = 'GameSceneRoot'
        StageManager._GameUIRoot = new Laya.Sprite;
        StageManager._GameUIRoot.name = 'GameUIRoot'
        StageManager._UpRoot = new Laya.Sprite;
        StageManager._UpRoot.name = 'UpRoot';
        StageManager._UpDialogRoot = new Laya.Sprite;
        StageManager._UpDialogRoot.name = 'UpDialogRoot';
        StageManager._UI3D = new Laya.Sprite;
        StageManager._AdRoot = new Laya.Sprite;
        StageManager._AdRoot.name = 'AdRoot';
        StageManager._GuideRoot = new Laya.Sprite;
        StageManager._GuideRoot.name = 'GuideRoot';
        StageManager._EffectRoot = new Laya.Sprite;
        StageManager._EffectRoot.name = 'EffectRoot';
        let layers = [StageManager._GameSceneRoot, StageManager._GameUIRoot, Laya.Scene.root, StageManager._UpRoot, Laya.Dialog.manager, StageManager._UpDialogRoot, StageManager._AdRoot, StageManager._GuideRoot, StageManager._EffectRoot];
        for (let i = 0; i < layers.length; i++) {
            let layer = layers[i];
            layer.zOrder = i + 1;
            layer.width = Laya.stage.width;
            layer.height = Laya.stage.height;
            layer.mouseThrough = true;
            if (layer != Laya.Scene.root && layer != Laya.Dialog.manager) {
                Laya.stage.addChild(layer);
            }
        }
        Laya.stage.updateZOrder();
    }
    static get UI3D() {
        return StageManager._UI3D;
    }
    static get GameSceneRoot() {
        return StageManager._GameSceneRoot;
    }
    static get GameUIRoot() {
        return StageManager._GameUIRoot;
    }
    static get UpRoot() {
        return StageManager._UpRoot;
    }
    static get UpDialogRoot() {
        return StageManager._UpDialogRoot;
    }
    static get GuideRoot() {
        return StageManager._GuideRoot;
    }
    static get AdRoot() {
        return StageManager._AdRoot;
    }
    static get EffectRoot() {
        return StageManager._EffectRoot;
    }
}