import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import { Node, Vector3, Zone } from "../gframe-work/pathfinding/Geometry";
import { Pathfinding } from "../gframe-work/pathfinding/Pathfinding";
import Scene3DBase from "../gframe-work/scene3D/Scene3DBase";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import Adventurer from "./Adventurer";
import { CameraFollowScript } from "./CameraFollowScript";
import Chest from "./Chest";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";
import Npc from "./Npc";
import Picture from "./Picture";
import { TongYiEventManager } from "./TongYiEventManager";


export default class GameSceneLogic extends Scene3DBase {
    pathfinding: Pathfinding = null;
    player: Adventurer = null;
    /**相机跟随脚本 */
    cameraFollowScript: CameraFollowScript = null;
    npcMap: Map<string, Npc> = new Map();
    chestMap: Map<string, Chest> = new Map();
    pictureMap: Map<string, Picture> = new Map();
    zone: Zone;
    enterLastNode: Node = null;
    openChest: Chest = null;
    onAwake() {
        this.cameraUrl = "MainCamera/MainCamera";
        super.onAwake();
        this.initNavMesh();
        this.initPlayer();
        this.initNpc();
        this.initPicture();
        this.initChest();
        if (this.pictureMap.size == 0) {
            this.initBoss();
        }
        EventManager.on(EventType.ControlChange, this, this.onControlChange);
        EventManager.on(EventType.OpenTreasureBoxFinish, this, this.onOpenTreasureBoxFinish);
        this.cameraFollowScript = this.camera.parent.getComponent(CameraFollowScript);
        if (!this.cameraFollowScript) {
            this.cameraFollowScript = this.camera.parent.addComponent(CameraFollowScript);
        }
        this.cameraFollowScript.setCameraTargetTransform(this.player.gameObject.transform);
        // let vertices = this.pathfinding.zones.get(`level${GameDataManager.Instance.enterMapIndex + 1}`).vertices
        // for (let i = 0; i < vertices.length; i++) {
        //     let vertice = new Laya.Vector3(vertices[i].x, vertices[i].y, vertices[i].z);
        //     let ball = new Laya.MeshSprite3D(Laya.PrimitiveMesh.createSphere(0.1));
        //     this.clearNode.addChild(ball);
        //     ball.transform.position = vertice;
        // }
        // console.log(this.pathfinding.zones.get(`level${GameDataManager.Instance.enterMapIndex + 1}`));
        window["GameSceneLogic"] = this;
    }
    onOpened(refreshChest: boolean) {
        GameDataManager.Instance.isPause = false;
        GameDataManager.Instance.isGotoFightScene = false;
        this.player.isMoving = false;
        this.player.initPosAndRot();
        this.player.clearActorState();
        this.player.changeActorState("Stand", "");
        this.player.initPathfinding(this.pathfinding, GameDataManager.Instance.zoneId);
        if (!this.player.lastNode) this.player.lastNode = this.enterLastNode;
        if (GameDataManager.Instance.lastPos) {
            GameDataManager.Instance.lastPos.y = 0;
            this.player.gameObject.transform.position = GameDataManager.Instance.lastPos.clone();
            GameDataManager.Instance.lastPos = null;
        }
        this.cameraFollowScript.cameraNode.transform.position = this.player.gameObject.transform.position.clone();
        for (const iterator of this.npcMap) {
            iterator[1].initPosAndRot();
        }
        if (refreshChest) {
            for (const iterator of this.chestMap) {
                if (!iterator[1].gameObject.active)
                    iterator[1].gameObject.active = true;
            }
        }
        EventManager.event(EventType.UpdateSmallMap, this.player.gameObject.transform.position);
    }
    onClosed() {
        for (const iterator of this.npcMap) {
            iterator[1].setFollowTarget(null);
        }
    }
    initNavMesh() {
        GameDataManager.Instance.zoneId = `level${GameDataManager.Instance.enterMapIndex + 1}`;
        this.pathfinding = new Pathfinding();
        let naveMeshData = GameDataManager.Instance.navMeshData;
        this.zone = Pathfinding.createZone(naveMeshData);
        this.pathfinding.setZoneData(GameDataManager.Instance.zoneId, this.zone);
        window["pathfinding"] = this.pathfinding;
    }
    initPlayer() {
        let playerModel = ResManager.getRes<Laya.Sprite3D>("Player1.lh");
        this.clearNode.addChild(playerModel);
        this.player = playerModel.getComponent(Adventurer);
        if (!this.player) {
            this.player = playerModel.addComponent(Adventurer);
        }
        this.player.bornPoint = GameDataManager.Instance.pointData["Born1"];
    }
    initNpc() {
        let pointData = GameDataManager.Instance.pointData;
        for (const key in pointData) {
            if (key.indexOf("Npc") != -1) {
                let index = Number(key.replace("Npc", ""));
                let list = GameDataManager.Instance.mapMonsterIdList[index - 1];
                let npcName = `Enemy${list[list.length - 1]}`;
                let point = pointData[key];
                let npc = LayaTool.createSprite3DWithScript(npcName, Npc, this.clearNode);
                npc.bornPoint = point;
                this.npcMap.set(key, npc);
                npc.initPosAndRot();
                npc.initPathfinding(this.pathfinding, GameDataManager.Instance.zoneId);
                npc.changeActorState("Stand", "", true);
            }
        }
    }
    initBoss() {
        let pointData = GameDataManager.Instance.pointData;
        let bossName = `Enemy${GameDataManager.Instance.curMapData.bossStat.id}`;
        let point = pointData["Boss1"];
        let boss = LayaTool.createSprite3DWithScript(bossName, Npc, this.clearNode);
        boss.bornPoint = point;
        this.npcMap.set("Boss1", boss);
        boss.initPosAndRot();
        EventManager.event(EventType.BossOut);
    }
    initPicture() {
        let collect = UserData.instance.pictureCollectList[GameDataManager.Instance.enterMapIndex];
        if (GameDataManager.Instance.isCream) {
            collect = UserData.instance.creamPictureCollectList[GameDataManager.Instance.enterMapIndex];
        }
        for (let i = 0; i < GameDataManager.Instance.curMapData.piece; i++) {
            let pictureId = i + 1;
            if (collect.indexOf(pictureId) == -1) {
                let pictureName = "Picture" + pictureId;
                let point = GameDataManager.Instance.pointData[pictureName];
                if (point) {
                    let picture = LayaTool.createSprite3DWithScript("Picture", Picture, this.clearNode);
                    picture.pictureId = pictureId;
                    picture.gameObject.name = pictureName;
                    picture.bornPoint = point;
                    picture.initPosAndRot();
                    this.pictureMap.set(pictureName, picture);
                }
            }
        }
    }
    initChest() {
        let pointData = GameDataManager.Instance.pointData;
        for (const key in pointData) {
            if (key.indexOf("Chest") != -1) {
                let point = pointData[key];
                if (point) {
                    let chest = LayaTool.createSprite3DWithScript("Box", Chest, this.clearNode);
                    chest.gameObject.name = key;
                    chest.bornPoint = point;
                    chest.initPosAndRot();
                    this.chestMap.set(key, chest);
                }
            }
        }
    }
    getRandomPosInZone(zone: Zone, groupId: number) {
        let group = zone.groups[groupId];
        let node = group[Math.floor(Math.random() * group.length)];
        let a = this.getCloneVector3(zone.vertices[node.vertexIds[0]]);
        let b = this.getCloneVector3(zone.vertices[node.vertexIds[1]]);
        let c = this.getCloneVector3(zone.vertices[node.vertexIds[2]]);
        return this.getRandomPointInTriangle(a, b, c);
    }
    getCloneVector3(vec: Vector3) {
        let o = new Vector3();
        o.x = vec.x;
        o.y = vec.y;
        o.z = vec.z;
        return o;
    }
    getRandomPointInTriangle(a: Vector3, b: Vector3, c: Vector3) {
        let c1 = new Vector3();
        c1.copy(c);
        let ac = c.sub(a);
        let cb = b.sub(c1);
        cb.multiplyScalar(Math.random());
        ac.add(cb);
        ac.multiplyScalar(Math.random());
        a.add(ac);
        return a;
    }
    updateVisibleByCamera() {
        for (const iterator of this.npcMap) {
            let visible = LayaTool.getVisibleByCamera(this.camera, iterator[1].mesh);
            iterator[1].setVisibleByCamera(visible);
        }
    }
    updateFollowTarget() {
        for (const iterator of this.npcMap) {
            if (iterator[1].gameObject.active && iterator[0].indexOf("Npc") != -1) {
                let distance = Laya.Vector3.distance(iterator[1].gameObject.transform.position, this.player.gameObject.transform.position);
                if (distance <= iterator[1].warnRadius) {
                    iterator[1].setFollowTarget(this.player);
                }
            }
        }
    }
    updateReachTarget() {
        for (const iterator of this.npcMap) {
            if (iterator[1].gameObject.active) {
                let distance = Laya.Vector3.distance(iterator[1].gameObject.transform.position, this.player.gameObject.transform.position);
                if (distance <= 1) {
                    ResManager.playSound("hitEnemy.mp3");
                    if (iterator[0].indexOf("Npc") != -1) {
                        GameDataManager.Instance.isBoss = false;
                        // console.log("碰到npc了！");
                        let index = Number(iterator[0].replace("Npc", ""));
                        GameDataManager.Instance.setMonsterIdList(GameDataManager.Instance.mapMonsterIdList[index - 1], false);
                    } else {
                        GameDataManager.Instance.isBoss = true;
                        let monsterIdList = GameDataManager.Instance.getMapEnemyMonsterIdList(GameDataManager.Instance.enterMapIndex, 1);
                        GameDataManager.Instance.setMonsterIdList(monsterIdList, true);
                        // console.log("碰到Boss了！");
                    }
                    iterator[1].enterFight();
                    this.gotoFight();
                }
            }
        }
    }
    gotoFight() {
        GameDataManager.Instance.isPause = true;
        this.player.isMoving = false;
        this.player.walkDir.x = 0;
        this.player.walkDir.y = 0;
        this.player.walkDir.z = 0;
        GameDataManager.Instance.isGotoFightScene = true;
        Laya.MouseManager.enabled = false;
        GameDataManager.Instance.lastPos = this.player.gameObject.transform.position.clone();
        this.enterLastNode = this.player.lastNode;
        EventManager.event(EventType.GotoScene, "fight");
        let enemyMonsterCardList = GameDataManager.Instance.enemyMonsterCardList;
        let monsterId = enemyMonsterCardList[enemyMonsterCardList.length - 1].monsterId;
        window["fightSendData"] = { level_id: GameDataManager.Instance.enterMapIndex + 1, scene_id: monsterId, category: 1, extra1: 0 };
        if (GameDataManager.Instance.isCream) {
            window["fightSendData"].level_id += 8;
        }
        if (GameDataManager.Instance.isBoss) {
            window["fightSendData"].extra1 = 1;
        }
        TongYiEventManager.sendEvent(10003, window["fightSendData"]);
    }
    onUpdate() {
        if (GameDataManager.Instance.isGotoFightScene ||
            !GameDataManager.Instance.isInSearchMap ||
            GameDataManager.Instance.isPause
        ) return;
        this.updateVisibleByCamera();
        this.updateFollowTarget();
        this.updateReachTarget();
        this.detectCollectPicture();
        this.detectCollectChest();
        this.updatePicturePointer();
        this.updateBossPointer();
        this.updateChestPointer();
    }
    onDestroy() {
        this.player.gameObject.removeSelf();
        this.player.destroy();
        this.cameraFollowScript.destroy();
        super.onDestroy();
        EventManager.off(EventType.ControlChange, this, this.onControlChange);
        EventManager.off(EventType.OpenTreasureBoxFinish, this, this.onOpenTreasureBoxFinish);
    }
    onControlChange(isMoving: boolean, dirX: number, dirY: number) {
        if (GameDataManager.Instance.isPause) return;
        if (!this.player.isMoving && isMoving) {
            this.player.changeActorState("Run", "");
        } else if (this.player.isMoving && !isMoving) {
            this.player.changeActorState("Stand", "");
        }
        this.player.isMoving = isMoving;
        if (isMoving) {
            this.player.moveDir.x = dirX;
            this.player.moveDir.y = dirY;
        }
    }
    detectCollectPicture() {
        for (const iterator of this.pictureMap) {
            let picture = iterator[1];
            if (Laya.Vector3.distance(picture.gameObject.transform.position, this.player.gameObject.transform.position) < 1) {
                this.pictureMap.delete(iterator[0]);
                picture.gameObject.removeSelf();
                Laya.Pool.recover(Picture.name, picture);
                UserData.instance.finishTask(1, 4, 1);
                ResManager.playSound("treasure.mp3");
                if (GameDataManager.Instance.isCream) {
                    UserData.instance.creamPictureCollectList[GameDataManager.Instance.enterMapIndex].push(picture.pictureId);
                    UserData.instance.onDataChange("creamPictureCollectList");
                } else {
                    UserData.instance.pictureCollectList[GameDataManager.Instance.enterMapIndex].push(picture.pictureId);
                    UserData.instance.onDataChange("pictureCollectList");
                }
                if (this.pictureMap.size == 0) {
                    this.initBoss();
                    this.showBossOutCamera();
                    EventManager.event(EventType.ShowPicturePointer, false);
                }
                return;
            }
        }
    }
    detectCollectChest() {
        for (const iterator of this.chestMap) {
            let chest = iterator[1];
            if (chest.gameObject.active && Laya.Vector3.distance(chest.gameObject.transform.position, this.player.gameObject.transform.position) < 1) {
                this.player.isMoving = false;
                this.player.changeActorState("Stand", "");
                GameDataManager.Instance.isPause = true;
                chest.animator.play("Open", 0, 0);
                ResManager.playSound("treasure.mp3");
                Laya.timer.once(1000, this, () => {
                    this.showChestReward(chest);
                });
                return;
            }
        }
    }
    showChestReward(chest: Chest) {
        let rewardType = 2;
        let rate = Math.random();
        if (rate < 0.05) {
            //宝箱怪
            rewardType = 2;
        } else if (rate < 0.05) {
            //金币
            rewardType = 0;
        } else if (rate < 0.3) {
            //战斗券
            rewardType = 3;
        } else if (rate < 0.6) {
            //碎片
            rewardType = 4;
        } else if (rate < 1) {
            //扭蛋
            rewardType = 1;
        }
        if (rewardType == 0 || rewardType == 1 || rewardType == 3 || rewardType == 4) {
            this.openChest = chest;
            LayaTool.openScene("pages/treasureBox.scene", false, true, null, [rewardType]);
        } else {
            GameDataManager.Instance.isBoss = false;
            chest.openChest();
            EventManager.event(EventType.ShowTreasurePointer, false);
            let monsterIdList = GameDataManager.Instance.getMapEnemyMonsterIdList(GameDataManager.Instance.enterMapIndex, 2);
            GameDataManager.Instance.setMonsterIdList(monsterIdList, false);
            // console.log("碰到宝箱怪了");
            this.gotoFight();
        }
    }
    updatePicturePointer() {
        if (this.pictureMap.size > 0) {
            let closetDis = Infinity;
            let closetPic: Picture = null;
            for (const iterator of this.pictureMap) {
                let picture = iterator[1];
                let distance = Laya.Vector3.distance(picture.gameObject.transform.position, this.player.gameObject.transform.position);
                if (distance < closetDis) {
                    closetPic = picture;
                    closetDis = distance;
                }
            }
            if (closetPic) {
                EventManager.event(EventType.ShowPicturePointer, true);
                EventManager.event(EventType.UpdatePicturePointer, closetPic.gameObject.transform.position);
            }
        } else {
            EventManager.event(EventType.ShowPicturePointer, false);
        }
    }
    updateBossPointer() {
        let boss = this.npcMap.get("Boss1");
        if (boss) {
            EventManager.event(EventType.ShowBossPointer, true);
            EventManager.event(EventType.UpdateBossPointer, boss.gameObject.transform.position);
        }
    }
    updateChestPointer() {
        if (this.chestMap.size > 0) {
            let closetDis = Infinity;
            let closetChest: Chest = null;
            for (const iterator of this.chestMap) {
                let chest = iterator[1];
                if (chest.gameObject.active) {
                    let distance = Laya.Vector3.distance(chest.gameObject.transform.position, this.player.gameObject.transform.position);
                    if (distance < closetDis) {
                        closetChest = chest;
                        closetDis = distance;
                    }
                }
            }
            if (closetChest) {
                if (closetChest.gameObject.active) {
                    EventManager.event(EventType.ShowTreasurePointer, true);
                    EventManager.event(EventType.UpdateTreasurePointer, closetChest.gameObject.transform.position);
                }
            }
        }
    }
    /**获取指示器的方向和旋转 */
    getPointerPosAndRot(self: Laya.Vector3, other: Laya.Vector3) {
        let other2d = LayaTool.convert3DTo2DPosition(this.camera, other);
        let otherX = other2d.x, otherY = other2d.y;
        let self2d = LayaTool.convert3DTo2DPosition(this.camera, self);
        let selfX = self2d.x, selfY = self2d.y;
        let radius = 80;
        let x = otherX < 0 + radius ? 0 + radius : otherX;
        x = x > (Laya.stage.width - radius) ? (Laya.stage.width - radius) : x;
        let y = otherY < 0 + radius ? 0 + radius : otherY;
        y = y > (Laya.stage.height - radius) ? (Laya.stage.height - radius) : y;
        let rotation = (Math.atan2(otherY - selfY, otherX - selfX)) * MathTool.rad2Deg;
        return [x, y, rotation];
    }
    /**boss出场相机跟随 */
    showBossOutCamera() {
        this.player.isMoving = false;
        GameDataManager.Instance.isPause = true;
        let boss = this.npcMap.get("Boss1");
        this.cameraFollowScript.setCameraTargetTransform(boss.gameObject.transform);
        EventManager.once(CameraFollowScript.ReachDest, this, () => {
            Laya.timer.once(2000, this, () => {
                GameDataManager.Instance.isPause = false;
                this.player.isMoving = true;
                this.cameraFollowScript.setCameraTargetTransform(this.player.gameObject.transform);
            })
        });
    }
    onOpenTreasureBoxFinish() {
        if (this.openChest) {
            this.openChest.openChest();
            this.openChest = null;
        }
        EventManager.event(EventType.ShowTreasurePointer, false);
        GameDataManager.Instance.isPause = false;
    }
}