import EventManager from "../gframe-work/manager/EventManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
// import { GameDataManager } from "./GameDataManager";

export class CameraFollowScript extends Laya.Script3D {
    followTransform: Laya.Transform3D = null;
    camera: Laya.Camera = null;
    cameraNode: Laya.Sprite3D = null;
    /**x方向偏移 */
    offsetX: number = 0;
    /**y方向偏移 */
    offsetY: number = 0;
    /**z方向偏移 */
    offsetZ: number = 0;
    /**移动速度 */
    moveSpeed: number = 5;
    /**镜头转动速度 */
    rotateSpeed: number = 2;
    maxRotateSpeed: number = 2;
    minRotateSpeed: number = 0.1;
    /**相机的目标位置 */
    cameraTargetPos: Laya.Vector3 = new Laya.Vector3;
    /**移动量 */
    translation: Laya.Vector3 = new Laya.Vector3;
    /**移动量 */
    rotation: Laya.Vector3 = new Laya.Vector3;
    /**是否到达目标点 */
    isSendEvent: boolean = false;
    /**自己旋转 */
    selfEuler: Laya.Vector3 = new Laya.Vector3;
    /**目标旋转 */
    targetEuler: Laya.Vector3 = new Laya.Vector3;
    /**旋转差 */
    diff: Laya.Vector3 = new Laya.Vector3;
    /**是否与地形产生碰撞 */
    isHitMap: boolean = false;
    isHitWaterFace: boolean = false;
    /**本地移动方向 */
    verticalTranslation: Laya.Vector3 = new Laya.Vector3(0, 0, 0);
    /**本地移动速度 */
    upSpeed: number = 1
    /**最大移动距离 */
    maxDis: number = 2;
    /**当前移动距离 */
    curDis: number = 0;
    /**最近的位置 */
    closestPosition = new Laya.Vector3(0, 1.68, -3.84);
    /**最近的位置 */
    normalPosition = new Laya.Vector3(0, 5.33, -10.58);
    /**目标y */
    targetZ: number = 0;
    /**灵敏度 */
    sensitive: number = 1;
    forward: Laya.Vector3 = new Laya.Vector3;
    scale: number = 1;
    /**标准最近Z */
    standerNearZ: number = -3.84;
    /**标准最远Z */
    standerFarZ: number = -10.58;
    standerFarY: number = -10.58;
    targetY: number = 0;
    /**偏移Z */
    standerOffsetZ: number = 0;
    lockOffsetZ: number = 0;
    scaleOffsetZ: number = 0;
    /**是否在锁定攻击敌人的状态中 */
    isLockEnemy: boolean = false;
    /**相机旋转跟随节点 */
    rotateFollowTransform: Laya.Transform3D = null;
    /**相机初始的本地旋转 */
    originalLocalRotationEuler: Laya.Vector3 = new Laya.Vector3;
    /**相机本地旋转 */
    targetLocalRotationEuler: Laya.Vector3 = new Laya.Vector3;
    /**看向的节点 */
    lookNode: Laya.Sprite3D = null;
    /**上方向的向量 */
    upVec: Laya.Vector3 = new Laya.Vector3(0, 1, 0);
    /**当前的旋转x */
    curLocalRotationX: number = 0;
    cameraLocalTargetPos: Laya.Vector3 = new Laya.Vector3;

    startYawDiff: number = 0;
    centerYawDiff: number = 0;
    endYawDiff: number = 0;
    public static ReachDest = "ReachDest";
    onAwake() {
        this.cameraNode = this.owner as Laya.Sprite3D;
        this.camera = this.findNodeByType<Laya.Camera>(this.cameraNode, Laya.Camera);
        this.originalLocalRotationEuler = this.camera.transform.localRotationEuler.clone();
        this.lookNode = new Laya.Sprite3D;
        this.lookNode.transform.localRotationEuler = this.originalLocalRotationEuler.clone();
        this.cameraNode.addChild(this.lookNode);
    }
    onUpdate() {
        this.positionFollow();
        // this.rotationFollow();
        // // this.lookTargetFollow();
        // this.recoveryCameraSensitive();
        // // this.cameraMoveClose();
        // this.cameraMoveClose1();
        // this.lookNode.transform.position = this.camera.transform.position.clone();
    }
    updateFowardFrame: number = -1;
    updateForward() {
        if (Laya.timer.currFrame != this.updateFowardFrame) {
            this.updateFowardFrame = Laya.timer.currFrame;
            this.camera.transform.getForward(this.forward);
        }
    }
    /**设置相机的目标点 */
    setCameraTargetTransform(transform: Laya.Transform3D) {
        this.isSendEvent = false;
        this.followTransform = transform;
    }
    findNodeByType<T>(parent: Laya.Node, type: { new(): T; }): T {
        if (parent instanceof type) {
            return parent;
        }
        for (let i = 0; i < parent.numChildren; i++) {
            let child = parent.getChildAt(i);
            return this.findNodeByType(child, type);
        }
        return null;
    }
    scaleDistance(level: number) {
        let cameraPosList = [
            [4.6, -10], [5.5, -12], [6.6, -14.4], [7.6, -16.6], [8.7, -19],
            [9.5, -20.6], [9.6, -21], [9.9, -21.6], [10.3, -22.6], [10.4, -22.8]
        ]
        this.standerFarZ = cameraPosList[level - 1][1];
        this.standerOffsetZ = this.lockOffsetZ + this.scaleOffsetZ;
        this.targetZ = this.standerFarZ + this.standerOffsetZ;
        this.standerFarY = cameraPosList[level - 1][0];
        this.targetY = this.standerFarY;

        this.cameraLocalTargetPos.x = 0;
        this.cameraLocalTargetPos.y = this.targetY;
        this.cameraLocalTargetPos.z = this.targetZ;
    }
    onDestroy() {

    }
    positionFollow() {
        if (!this.followTransform) return;
        this.cameraTargetPos.x = this.followTransform.position.x + this.offsetX;
        this.cameraTargetPos.z = this.followTransform.position.z + this.offsetZ;
        this.cameraTargetPos.y = this.followTransform.position.y + this.offsetY;
        let diffX = this.cameraTargetPos.x - this.cameraNode.transform.position.x;
        let diffY = this.cameraTargetPos.y - this.cameraNode.transform.position.y;
        let diffZ = this.cameraTargetPos.z - this.cameraNode.transform.position.z;
        if (diffX == 0 && diffY == 0 && diffZ == 0 && !this.isSendEvent) {
            this.isSendEvent = true;
            EventManager.event(CameraFollowScript.ReachDest);
            return;
        }
        let frameSpeed = this.moveSpeed * Laya.timer.delta / 1000;
        this.translation.x = Math.abs(diffX) <= 0.01 ? diffX : diffX * frameSpeed;
        this.translation.y = Math.abs(diffY) <= 0.01 ? diffY : diffY * frameSpeed;
        this.translation.z = Math.abs(diffZ) <= 0.01 ? diffZ : diffZ * frameSpeed;
        this.cameraNode.transform.translate(this.translation, false);
    }
    rotationFollow() {
        if (!this.followTransform) return;
        if (this.isLockEnemy) {
            this.normalizeRotationEuler(this.rotateFollowTransform.localRotationEuler, this.targetEuler);
        } else {
            this.normalizeRotationEuler(this.followTransform.localRotationEuler, this.targetEuler);
        }
        // this.normalizeRotationEuler(this.followTransform.localRotationEuler, this.targetEuler);
        this.normalizeRotationEuler(this.cameraNode.transform.localRotationEuler, this.selfEuler);
        this.nearestRotationDiff(this.selfEuler, this.targetEuler, this.diff);
        let diffX = this.diff.x;
        let diffY = this.diff.y;
        let diffZ = this.diff.z;
        if (diffX == 0 && diffY == 0 && diffZ == 0 && !this.isSendEvent) {
            this.isSendEvent = true;
            return;
        }
        this.rotateSpeed = 100;
        let frameSpeed = this.rotateSpeed * Laya.timer.delta / 1000;
        let ratioX = 1 - Math.abs(Math.abs(diffX) - 90) / 90;
        let ratioY = 1 - Math.abs(Math.abs(diffY) - 90) / 90;
        let ratioZ = 1 - Math.abs(Math.abs(diffZ) - 90) / 90;
        this.rotation.x = Math.abs(diffX) <= 0.01 * this.sensitive ? ratioX * Math.sign(diffX) * this.sensitive : ratioX * Math.sign(diffX) * frameSpeed * this.sensitive;
        this.rotation.y = Math.abs(diffY) <= 0.01 * this.sensitive ? ratioY * Math.sign(diffY) * this.sensitive : ratioY * Math.sign(diffY) * frameSpeed * this.sensitive;
        this.rotation.z = Math.abs(diffZ) <= 0.01 * this.sensitive ? ratioZ * Math.sign(diffZ) * this.sensitive : ratioZ * Math.sign(diffZ) * frameSpeed * this.sensitive;
        this.rotation.x = this.fixLocalRotationX(this.rotation.x);
        this.curLocalRotationX += this.rotation.x;
        this.cameraNode.transform.rotate(this.rotation, true, false);
    }

    getRotateSpeed(diffY: number) {
        if (this.centerYawDiff == 0) {
            return this.maxRotateSpeed;
        } else {
            if (diffY < this.centerYawDiff) {
                return this.minRotateSpeed + (this.maxRotateSpeed - this.minRotateSpeed) * (this.startYawDiff - diffY) / this.centerYawDiff;
            } else if (diffY == this.centerYawDiff) {
                return this.maxRotateSpeed;
            } else {
                return this.maxRotateSpeed + (this.minRotateSpeed - this.maxRotateSpeed) * ((this.startYawDiff - diffY) / this.centerYawDiff - 1);
            }
        }
    }

    setRotateFollowTrans(rotateFollowTrans: Laya.Transform3D) {
        this.rotateFollowTransform = rotateFollowTrans;
        if (rotateFollowTrans) {
            this.isLockEnemy = true;
            this.lockOffsetZ = -3;
        } else {
            this.isLockEnemy = false;
            this.lockOffsetZ = 0;
            this.normalizeRotationEuler(this.followTransform.localRotationEuler, this.targetEuler);
            this.normalizeRotationEuler(this.cameraNode.transform.localRotationEuler, this.selfEuler);
            this.startYawDiff = Math.abs(this.targetEuler.y - this.selfEuler.y);
            this.endYawDiff = 0;
            this.centerYawDiff = (this.startYawDiff + this.endYawDiff) / 2;
        }

        this.standerOffsetZ = this.lockOffsetZ + this.scaleOffsetZ;
        this.targetZ = this.standerFarZ + this.standerOffsetZ;

        this.cameraLocalTargetPos.x = 0;
        this.cameraLocalTargetPos.y = this.targetY;
        this.cameraLocalTargetPos.z = this.targetZ;
    }

    lookTargetFollow() {
        if (this.rotateFollowTransform) {
            this.lookNode.transform.lookAt(this.rotateFollowTransform.position, this.upVec, false);
            this.normalizeRotationEuler(this.lookNode.transform.localRotationEuler, this.targetEuler);
        } else {
            this.normalizeRotationEuler(this.originalLocalRotationEuler, this.targetEuler);
        }
        this.normalizeRotationEuler(this.camera.transform.localRotationEuler, this.selfEuler);
        this.nearestRotationDiff(this.selfEuler, this.targetEuler, this.diff);
        let diffX = this.diff.x;
        let diffY = this.diff.y;
        let diffZ = this.diff.z;
        if (diffX == 0 && diffY == 0 && diffZ == 0 && !this.isSendEvent) {
            this.isSendEvent = true;
            return;
        }
        let frameSpeed = this.rotateSpeed * Laya.timer.delta / 1000;
        this.rotation.x = Math.abs(diffX) <= 0.01 * this.sensitive ? diffX * this.sensitive : diffX * frameSpeed * this.sensitive;
        this.rotation.y = Math.abs(diffY) <= 0.01 * this.sensitive ? diffY * this.sensitive : diffY * frameSpeed * this.sensitive;
        this.rotation.z = Math.abs(diffZ) <= 0.01 * this.sensitive ? diffZ * this.sensitive : diffZ * frameSpeed * this.sensitive;
        this.rotation.x = this.fixLocalRotationX(this.rotation.x);
        this.curLocalRotationX += this.rotation.x;
        this.camera.transform.rotate(this.rotation, true, false);
    }
    /**限制仰角 */
    fixLocalRotationX(planeRotate: number) {
        let degLimit = 0;
        let destLocalRotationX = this.curLocalRotationX + planeRotate;
        let fixPlaneRotate = planeRotate;
        if (planeRotate > 0) {
            if (destLocalRotationX > degLimit) {
                fixPlaneRotate = degLimit - this.curLocalRotationX;
            }
        } else if (planeRotate < 0) {
            if (destLocalRotationX < -degLimit) {
                fixPlaneRotate = -degLimit - this.curLocalRotationX;
            }
        }
        return fixPlaneRotate;
    }

    cameraMoveClose() {
        let frameSpeed = this.upSpeed * Laya.timer.delta / 1000;
        let localPosition = this.camera.transform.localPosition;
        this.standerOffsetZ = this.lockOffsetZ + this.scaleOffsetZ;
        if (localPosition.z != this.targetZ) {
            this.verticalTranslation.y = 0;
            if (this.isHitMap) {
                this.verticalTranslation.z = -(this.targetZ - localPosition.z) * frameSpeed * 5;
                // this.verticalTranslation.z = -frameSpeed * 3;
                if (localPosition.z - this.verticalTranslation.z > this.standerNearZ + this.standerOffsetZ) {
                    this.verticalTranslation.z = 0;
                    let newPos = this.camera.transform.localPosition.clone();
                    newPos.z = this.standerNearZ + this.standerOffsetZ;
                    this.camera.transform.localPosition = newPos;
                }
            } else {
                this.verticalTranslation.z = frameSpeed * 2.5;
                if (localPosition.z - this.verticalTranslation.z < this.standerFarZ + this.standerOffsetZ) {
                    this.verticalTranslation.z = 0;
                    let newPos = this.camera.transform.localPosition.clone();
                    newPos.z = this.standerFarZ + this.standerOffsetZ;
                    this.camera.transform.localPosition = newPos;
                }
            }
            this.camera.transform.translate(this.verticalTranslation, true);
        }
        if (localPosition.y != this.targetY) {
            this.verticalTranslation.z = 0;
            if (Math.abs(this.targetY - localPosition.y) > frameSpeed * 2.5) {
                this.verticalTranslation.y = frameSpeed * 2.5 * Math.sign(this.targetY - localPosition.y);
            } else {
                this.verticalTranslation.y = this.targetY - localPosition.y;
            }
            this.camera.transform.translate(this.verticalTranslation, true);
        }
    }
    cameraMoveClose1() {
        let frameSpeed = this.upSpeed * Laya.timer.delta / 1000;
        let localPosition = this.camera.transform.localPosition;
        Laya.Vector3.subtract(this.cameraLocalTargetPos, localPosition, this.verticalTranslation);
        let distance = Laya.Vector3.distance(localPosition, this.cameraLocalTargetPos);
        if (this.isHitMap) {
            frameSpeed = frameSpeed * distance * 5;
        }
        if (frameSpeed > distance) {
            frameSpeed = 1;
        } else {
            frameSpeed = frameSpeed * 3;
            Laya.Vector3.normalize(this.verticalTranslation, this.verticalTranslation);
        }
        Laya.Vector3.scale(this.verticalTranslation, frameSpeed, this.verticalTranslation);
        let newPos = new Laya.Vector3();
        Laya.Vector3.add(this.camera.transform.localPosition, this.verticalTranslation, newPos);
        this.camera.transform.localPosition = newPos;
        if (this.isHitWaterFace) {
            let newPos = this.camera.transform.position.clone();
            // newPos.y = GameDataManager.Instance.playerSafeY - 0.5;
            this.camera.transform.position = newPos;
        }
    }
    /**归一化旋转 */
    normalizeRotationEuler(src: Laya.Vector3, out: Laya.Vector3) {
        out.x = (src.x % 360 + 360) % 360;
        out.y = (src.y % 360 + 360) % 360;
        out.z = (src.z % 360 + 360) % 360;
    }
    /**旋转最近化处理 */
    nearestRotationDiff(left: Laya.Vector3, right: Laya.Vector3, out: Laya.Vector3) {
        Laya.Vector3.subtract(left, right, out);
        if (Math.abs(out.x) > 180) {
            if (out.x > 0) {
                out.x = out.x - 360;
            } else {
                out.x = out.x + 360;
            }
        }
        if (Math.abs(out.y) > 180) {
            if (out.y > 0) {
                out.y = out.y - 360;
            } else {
                out.y = out.y + 360;
            }
        }
        if (Math.abs(out.z) > 180) {
            if (out.z > 0) {
                out.z = out.z - 360;
            } else {
                out.z = out.z + 360;
            }
        }
        out.x *= -1;
        out.y *= -1;
        out.z *= -1;
        return out;
    }
    /**设置是否碰到地图 */
    setHitMap(isHitMap: boolean, isHitWaterFace: boolean) {
        this.isHitMap = isHitMap;
        this.isHitWaterFace = isHitWaterFace;
        this.standerOffsetZ = this.lockOffsetZ + this.scaleOffsetZ;
        if (this.isHitMap) {
            let standerZ = this.standerNearZ + this.standerOffsetZ;
            this.targetZ = this.camera.transform.localPosition.z + (standerZ - this.camera.transform.localPosition.z) / 5;
            Laya.Vector3.subtract(new Laya.Vector3(0, 0, standerZ), this.camera.transform.localPosition, this.verticalTranslation);
            Laya.Vector3.scale(this.verticalTranslation, 1 / 5, this.verticalTranslation);
            Laya.Vector3.add(this.camera.transform.localPosition, this.verticalTranslation, this.cameraLocalTargetPos);

            // this.cameraLocalTargetPos.x = 0;
            // this.cameraLocalTargetPos.y = 0
            // this.cameraLocalTargetPos.z = standerZ;
        } else {
            this.targetZ = this.standerFarZ + this.standerOffsetZ;
            this.targetY = this.standerFarY;

            this.cameraLocalTargetPos.x = 0;
            this.cameraLocalTargetPos.y = this.targetY;
            this.cameraLocalTargetPos.z = this.targetZ;
        }
    }
    /**是否相机的灵敏度在恢复中 */
    isRecoverySensitive: boolean = false;
    /**回复相机旋转的灵敏度 */
    setCameraRotateSensitive(sensitive: number) {
        this.isRecoverySensitive = false;
        if (sensitive == 1) {
            this.isRecoverySensitive = true;
        } else {
            this.sensitive = sensitive;
        }
    }
    /**逐步回复相机旋转灵敏度 */
    recoveryCameraSensitive() {
        if (this.isRecoverySensitive) {
            let sensitiveAdd = Laya.timer.delta / 1000;
            if (this.sensitive + sensitiveAdd > 1) {
                sensitiveAdd = 1 - this.sensitive;
                this.isRecoverySensitive = false;
            }
            this.sensitive += sensitiveAdd;
        }
    }
}
