import { Node, Vector3 } from "./Geometry";

class Utils {
  static roundNumber(value: number, decimals: number) {
    const factor = Math.pow(10, decimals);
    return Math.round(value * factor) / factor;
  }

  static sample<T>(list: T[]) {
    return list[Math.floor(Math.random() * list.length)];
  }

  static distanceToSquared(a: Vector3, b: Vector3) {
    var dx = a.x - b.x;
    var dy = a.y - b.y;
    var dz = a.z - b.z;

    return dx * dx + dy * dy + dz * dz;
  }

  //+ Jonas Raoni Soares Silva
  //@ http://jsfromhell.com/math/is-point-in-poly [rev. #0]
  static isPointInPoly(poly: Vector3[], pt: Vector3) {
    for (var c = false, i = -1, l = poly.length, j = l - 1; ++i < l; j = i)
      ((poly[i].z <= pt.z && pt.z < poly[j].z) || (poly[j].z <= pt.z && pt.z < poly[i].z)) && (pt.x < (poly[j].x - poly[i].x) * (pt.z - poly[i].z) / (poly[j].z - poly[i].z) + poly[i].x) && (c = !c);
    return c;
  }

  static isVectorInPolygon(vector: Vector3, polygon: Node, vertices: Vector3[]) {
    // reference point will be the centroid of the polygon
    // We need to rotate the vector as well as all the points which the polygon uses

    var lowestPoint = 100000;
    var highestPoint = -100000;

    var polygonVertices = [];

    polygon.vertexIds.forEach((vId: number) => {
      lowestPoint = Math.min(vertices[vId].y, lowestPoint);
      highestPoint = Math.max(vertices[vId].y, highestPoint);
      polygonVertices.push(vertices[vId]);
    });

    if (
      vector.y < highestPoint + 0.5 &&
      vector.y > lowestPoint - 0.5 &&
      this.isPointInPoly(polygonVertices, vector)
    ) {
      return true;
    }
    return false;
  }

  static triarea2(a: Vector3, b: Vector3, c: Vector3) {
    var ax = b.x - a.x;
    var az = b.z - a.z;
    var bx = c.x - a.x;
    var bz = c.z - a.z;
    return bx * az - ax * bz;
  }

  static vequal(a: Vector3, b: Vector3) {
    return this.distanceToSquared(a, b) < 0.00001;
  }
}

export { Utils };
