import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class MoneyUiRuntime extends ui.pages.moneyUiUI {
    moneyPoint: Laya.Point = new Laya.Point();
    type: string;
    enableEffect: boolean = true;
    onAwake() {
        EventManager.on(EventType.AddMoney, this, this.onAddMoney);
        EventManager.on(EventType.UseMoney, this, this.onUseMoney);
        this.addMoneyBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    onDestroy() {
        EventManager.off(EventType.AddMoney, this, this.onAddMoney);
        EventManager.off(EventType.UseMoney, this, this.onUseMoney);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.addMoneyBtn) {
            if (this.type == "gold") {
                LayaTool.openScene("pages/addMoney.scene", true, true, null, [1, 200]);
            } else if (this.type == "badge") {
                LayaTool.openScene("pages/addBadge.scene", true, true, null, null);
            } else if (this.type == "diamond") {
                EventManager.event(EventType.JumpToPage, 5);
            }
        }
    }
    setMoneyType(type: string, enableAdd: boolean) {
        this.addMoneyBtn.visible = enableAdd;
        this.type = type;
        if (type == "gold") {
            //金币
            this.updateGoldLabel(UserData.instance.gold);
            this.moneyIcon.skin = "main/Ui_Money.png";
            this.moneyIcon.x = 21;
            this.moneyIcon.y = 19;
        } else if (type == "badge") {
            //徽章
            this.updateGoldLabel(UserData.instance.badge);
            this.moneyIcon.skin = "newSystem/Ui_Badge.png";
            this.moneyIcon.x = 21;
            this.moneyIcon.y = 19;
        } else if (type == "fightTicket") {
            //战斗券
            this.updateGoldLabel(UserData.instance.fightTicket);
            this.moneyIcon.skin = "newSystem/Ui_Quan.png";
            this.moneyIcon.x = 27;
            this.moneyIcon.y = 19;
        } else if (type == "chip") {
            //碎片
            this.updateGoldLabel(UserData.instance.chip);
            this.moneyIcon.skin = "newSystem/Ui_Chip.png";
            this.moneyIcon.x = 27;
            this.moneyIcon.y = 19;
        } else if (type == "diamond") {
            //钻石
            this.updateGoldLabel(UserData.instance.diamond);
            this.moneyIcon.skin = "pay/Ui_Diamond.png";
            this.moneyIcon.x = 27;
            this.moneyIcon.y = 19;
        }
    }
    createMoney(num: number) {
        Laya.timer.once(200 * Math.random(), this, () => {
            let money = new Laya.Image();
            money.anchorX = 0.5;
            money.anchorY = 0.5;
            money.skin = this.moneyIcon.skin;
            money.zOrder = 1000;
            Laya.stage.addChild(money);
            let randomRad = Math.random() * 2 * Math.PI;
            let randomRadius = Math.random() * 100;
            money.x = Laya.stage.width / 2 + randomRadius * Math.cos(randomRad);
            money.y = Laya.stage.height / 2 + randomRadius * Math.sin(randomRad);
            let sx = money.x;
            let sy = money.y;
            let ex = this.moneyPoint.x;
            let ey = this.moneyPoint.y;
            let kx = sx - 300 * Math.random();
            if (sx >= Laya.stage.width / 2) {
                kx = sx + 300 * Math.random();
            }
            let ky = sy;
            let tweenTarget = { value: 0 };
            Laya.Tween.to(tweenTarget, {
                value: 1, update: Laya.Handler.create(this, () => {
                    let pos = LayaTool.bezier(tweenTarget.value, sx, sy, kx, ky, ex, ey);
                    money.x = pos.x;
                    money.y = pos.y;
                }, null, false)
            }, 800, Laya.Ease.sineIn, Laya.Handler.create(this, () => {
                money.destroy();
                this.moneyPop.play(0, false);
                this.updateGoldLabel(Number(this.moneyLabel.text.replace(/,/g, "")) + num);
            }));
        });
    }
    onAddMoney(type: string, num: number) {
        if (type != this.type) return;
        if (this.enableEffect) {
            this.addMoney(num);
        } else {
            this.onUseMoney("diamond");
        }
    }
    onUseMoney(type: string) {
        if (type != this.type) return;
        if (this.type == "gold") {
            this.updateGoldLabel(UserData.instance.gold);
        } else if (this.type == "badge") {
            this.updateGoldLabel(UserData.instance.badge);
        } else if (this.type == "fightTicket") {
            this.updateGoldLabel(UserData.instance.fightTicket);
        } else if (this.type == "chip") {
            this.updateGoldLabel(UserData.instance.chip);
        } else if (this.type == "diamond") {
            this.updateGoldLabel(UserData.instance.diamond);
        }
    }
    addMoney(num: number) {
        let numList = [];
        let index = 0;
        while (num > 0) {
            if (!numList[index]) {
                numList.push(0);
            }
            numList[index]++;
            num--;
            index = ++index % 20;
        }
        for (let i = 0; i < numList.length; i++) {
            this.createMoney(numList[i]);
        }
    }
    updateGoldLabel(num: number) {
        let goldNum = num + "";
        let newGoldNum = "";
        let count = 0;
        for (let i = goldNum.length - 1; i >= 0; i--) {
            newGoldNum += goldNum[i];
            count++;
            if (count == 3) {
                if (goldNum[i - 1]) newGoldNum += ",";
                count = 0;
            }
        }
        newGoldNum = newGoldNum.split("").reverse().join("");
        this.moneyLabel.text = newGoldNum;
    }
}