import { ActorBase } from "../gframe-work/actor/ActorBase";
import ResManager from "../gframe-work/manager/ResManager";

export default class Fighter extends ActorBase {
    hp: number = 0;
    atk: number = 0;
    isDeath: boolean = false;
    damageEffect: Laya.Sprite3D = null;
    onAwake() {
        super.onAwake();
        this.damageEffect = ResManager.getResClone("Effect_GetHit.lh");
        this.gameObject.addChild(this.damageEffect);
        this.damageEffect.transform.localPosition = new Laya.Vector3();
        this.damageEffect.transform.localRotationEuler = new Laya.Vector3();
        this.damageEffect.active = false;
    }
    onEnable() {
        
    }
    init(data: { hp: number, atk: number }) {
        this.hp = data.hp;
        this.atk = data.atk;
        if (data.hp == 0) {
            this.isDeath = true;
        } else {
            this.isDeath = false;
        }
        this.changeActorState("Stand", "", true);
    }
    onHurt(value: number) {
        if (this.isDeath) return 0;
        let realDamage = value;
        if (this.hp - value < 0) {
            realDamage = this.hp;
        }
        this.hp -= realDamage;
        this.damageEffect.active = true;
        Laya.timer.once(500, this, () => {
            this.damageEffect.active = false;
        });
        this.changeActorState("GetHit", "");
        Laya.timer.once(500, this, () => {
            this.changeActorState("Stand", "");
        })
        if (this.hp <= 0) {
            this.isDeath = true;
        }
        return realDamage;
    }
}