import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class LotteryRuntime extends ui.pages.lotteryUI {
    lotteryData = [
        { type: "gold", desc: "X100", value: 100, per: 0.3 },
        { type: "gold", desc: "X150", value: 150, per: 0.1 },
        { type: "superEgg", desc: "X1", value: 2, per: 0.03 },
        { type: "card", desc: "飞鸡", value: 90, per: 0.3 },
        { type: "gold", desc: "X150", value: 150, per: 0.1 },
        { type: "gold", desc: "X500", value: 500, per: 0.015 },
        { type: "gold", desc: "X800", value: 800, per: 0.015 },
        { type: "commonEgg", desc: "X1", value: 1, per: 0.14 },
    ];
    cellList: ui.pages.lotteryCellUI[];
    /**抽中的奖励 */
    lotteryIndex: number = 0;
    curIndex: number = 0;
    sign: number = 1;
    mustCardTime = 5;
    public static popGetFreeTimeWindow = false;
    onAwake() {
        this.cellList = [this.cell1, this.cell2, this.cell3, this.cell4, this.cell5, this.cell6, this.cell7, this.cell8];
        let nowTime = Date.now();
        if ((nowTime - UserData.instance.freeLotteryTimestamp) > 24 * 3600 * 1000) {
            UserData.instance.freeLotteryTimeLeft = 1;
            UserData.instance.onDataChange("freeLotteryTimeLeft");
        }
        for (let i = 0; i < this.cellList.length; i++) {
            this.setCellData(i);
        }
        this.updateFree();
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.rollBg.visible = false;
        this.rollBgCenter.visible = false;
        this.updateMustCardTimeLeftLabel();
    }
    onDestroy() {
        Laya.timer.clear(this, this.onFrameLoop);
    }
    onFrameLoop() {
        this.rollBg.rotation += 0.8;
    }
    onMouseDown(e: Laya.Event) {
        Laya.Tween.to(this.lotteryBtn, { scaleX: 0.9 * this.sign, scaleY: 0.9 }, 50, Laya.Ease.sineOut);
    }
    onMouseUp(e: Laya.Event) {
        ResManager.playSound("click.mp3");
        Laya.Tween.to(this.lotteryBtn, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    onMouseOut(e: Laya.Event) {
        Laya.Tween.to(this.lotteryBtn, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    onOpened() {
        this.lotteryBtn.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
        this.lotteryBtn.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
        this.lotteryBtn.on(Laya.Event.MOUSE_OUT, this, this.onMouseOut);
        this.sign = Math.sign(this.lotteryBtn.scaleX);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.lotteryBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goShopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);

        AllPlatformAdMgr.Instance.showBannerAd();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 6, button_id: 6, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        } else if (e.target == this.lotteryBtn) {
            if (UserData.instance.freeLotteryTimeLeft > 0) {
                if (UserData.instance.freeLotteryTimeLeft == 1) {
                    UserData.instance.freeLotteryTimestamp = Date.now();
                    UserData.instance.onDataChange("freeLotteryTimestamp");
                }
                UserData.instance.freeLotteryTimeLeft--;
                UserData.instance.onDataChange("freeLotteryTimeLeft");
                this.startLottery();
            } else {
                if (!LotteryRuntime.popGetFreeTimeWindow) {
                    LotteryRuntime.popGetFreeTimeWindow = true;
                    LayaTool.openScene("pages/getLotteryTime.scene", false, true, Laya.Handler.create(this, (reason: string) => {
                        if (reason == "yes") {
                            this.close();
                            EventManager.event(EventType.JumpToPage, 5);
                        }
                    }), null);
                } else {
                    AllPlatformAdMgr.Instance.sendVideoData = { page_id: 6, button_id: 6, ad_id: 2, ad_type: 1 };
                    AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", this.startLottery.bind(this));
                }
            }
        } else if (e.target == this.goShopBtn) {
            this.close();
            EventManager.event(EventType.JumpToPage, 5);
        }
    }
    rotate(index: number, timeCount: number) {
        return new Promise<void>(resolve => {
            let timeDelay = 200 - timeCount * 10
            if (timeCount > 16) {
                timeDelay = 200 + (timeCount - 32) * 10
            }
            Laya.timer.once(timeDelay, this, () => {
                timeCount++;
                let lastCell = this.cellList[this.curIndex];
                lastCell.lightBg.visible = false;
                lastCell.normalBg.visible = true;
                this.curIndex = ++this.curIndex % 8;
                let cell = this.cellList[this.curIndex];
                cell.lightBg.visible = true;
                cell.normalBg.visible = false;
                if (timeCount <= 24 || this.curIndex != index) {
                    this.rotate(index, timeCount).then(() => {
                        resolve();
                    })
                } else {
                    return resolve();
                }
            })
        })
    }
    startLottery() {
        let mustCardTimeLeft = this.getMustCardTimeLeft();
        UserData.instance.lotteryTime++;
        UserData.instance.onDataChange("lotteryTime");
        this.updateFree();
        this.lotteryIndex = this.calculateResult();
        if (mustCardTimeLeft == 1) {
            this.lotteryIndex = 3;
        }
        this.lotteryBtn.disabled = true;
        let lastCell = this.cellList[this.curIndex];
        lastCell.lightBg.visible = true;
        lastCell.normalBg.visible = false;
        this.rotate(this.lotteryIndex, 0).then(() => {
            this.lotteryBtn.disabled = false;
            let data = this.lotteryData[this.lotteryIndex];
            if (data.type == "gold") {
                LayaTool.openScene("pages/getCard.scene", false, true, null, [1, data.value, 4]);
            } else if (data.type == "card") {
                let monsterDataList = GameDataManager.Instance.getMonsterData([data.value]);
                let param = [true, data.value, monsterDataList[0], false, 3];
                LayaTool.openScene("pages/catchResult.scene", false, true, null, param);
            } else {
                LayaTool.openScene("pages/getCard.scene", false, true, null, [3, data.value, 4]);
            }
            this.updateMustCardTimeLeftLabel();
        })
    }
    calculateResult() {
        let totalPer = 0;
        let random = Math.random();
        for (let i = 0; i < this.lotteryData.length; i++) {
            let data = this.lotteryData[i];
            totalPer += data.per;
            if (random < totalPer) {
                console.log(data);
                return i;
            }
        }
    }
    updateFree() {
        this.freeLeftLabel.text = UserData.instance.freeLotteryTimeLeft + "";
        if (UserData.instance.freeLotteryTimeLeft > 0) {
            this.videoTag.visible = false;
        } else {
            this.setBtnVideoTag();
        }
    }
    setCellData(index: number) {
        let cell = this.cellList[index];
        let data = this.lotteryData[index];
        cell.lightBg.visible = false;
        cell.normalBg.visible = true;
        cell.goldLabel.filters = [CatchRuntime.blackColorFilter];
        if (data.type == "gold") {
            cell.rewardImage.scaleX = cell.rewardImage.scaleY = 1;
            cell.rewardImage.skin = "count/Ui_TwoGold.png";
            cell.goldLabel.text = "X" + data.value;
        } else if (data.type == "card") {
            cell.rewardImage.skin = `monster/Ui_Monster${data.value}.png`;
            cell.goldLabel.text = data.desc;
            cell.rewardImage.scaleX = cell.rewardImage.scaleY = 0.5;
        } else {
            cell.rewardImage.scaleX = cell.rewardImage.scaleY = 1;
            if (data.type == "superEgg") {
                cell.rewardImage.skin = "main/Ui_Ball.png";
            } else {
                cell.rewardImage.skin = "main/Ui_Ball2.png";
            }
            cell.goldLabel.text = "X1";
        }
    }
    updateMustCardTimeLeftLabel() {
        let mustCardTimeLeft = this.getMustCardTimeLeft();
        this.mustCardLabel.text = mustCardTimeLeft + "";
    }
    getMustCardTimeLeft() {
        return this.mustCardTime - UserData.instance.lotteryTime % this.mustCardTime;
    }
    static haveRed() {
        return UserData.instance.freeLotteryTimeLeft > 0;
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}