import LayaTool from "../gframe-work/tool/LayaTool";
import TaskRuntime from "../uilogic/TaskRuntime";

export default class Chest extends Laya.Script3D {
    bornPoint: { position: number[], rotation: number[] } = null;
    isVisible: boolean = false;
    mesh: any = null;
    gameObject: Laya.Sprite3D = null;
    animator: Laya.Animator = null;
    isOpened: boolean = false;
    onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.animator = this.gameObject.getComponent(Laya.Animator);
        this.mesh = this.gameObject.getChildByName("Box") as Laya.SkinnedMeshSprite3D;
    }
    onEnable() {
        this.animator.play("Stand", 0, 0);
    }
    initPosAndRot() {
        this.gameObject.transform.position = LayaTool.createVector3FromArray(this.bornPoint.position);
        this.gameObject.transform.rotation = LayaTool.createQuaternionFromArray(this.bornPoint.rotation);
    }
    openChest() {
        this.isOpened = true;
        this.gameObject.active = false;
    }
}