import CardData from "../gamelogic/CardData";
import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import { ForceTouchAdType } from "./QuickClickTreasureRuntime";
import SuperEvaRuntime from "./SuperEvaRuntime";

export default class CatchResultRuntime extends ui.pages.catchResultUI {
    monsterId: number = -1;
    monsterData: any = null;
    isBacktoSearch: boolean = false;
    sceneType: number = 0;
    showScene3d: Laya.Scene3D = null;
    showCamera: Laya.Camera = null;
    showMonster: Laya.Sprite3D = null;
    isCatch = false;
    static isRunNiuDanGuide = false;
    cardData: CardData;
    onAwake() {
        this.initShowScene();
        this.setBtnVideoTag();
    }
    onOpened() {
        this.sureBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.videoCatchBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.noForceBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.showScene3d.active = true;
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        if (this.sceneType == 1) {
            ResManager.playSound("catchSuccess.mp3");
        }
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        ResManager.stopSound("catchSuccess.mp3");
        AllPlatformAdMgr.Instance.hideBannerAd();
        if (this.sceneType == 1 && this.isCatch) {
            AllPlatformAdMgr.Instance.initBannerAd("adunit-9379d0955f8130cb");
        }
        EventManager.event(EventType.CatchResultClose, this.cardData);
        if (CatchResultRuntime.isRunNiuDanGuide) {
            CatchResultRuntime.isRunNiuDanGuide = false;
            GuideManager.startOutTask("介绍扭蛋");
        }
    }
    initShowScene() {
        this.showScene3d = new Laya.Scene3D();
        this.showScene3d.ambientColor.x = 0.8254717;
        this.showScene3d.ambientColor.y = 0.8793155;
        this.showScene3d.ambientColor.z = 1;
        let camera = new Laya.Camera(0, 0.3, 200);
        camera.name = "MainCamera";
        camera.enableHDR = false;
        camera.clearFlag = Laya.CameraClearFlags.DepthOnly;
        camera.transform.position = new Laya.Vector3(0, 5.94, 13.98);
        camera.transform.rotation = new Laya.Quaternion(-0.2088847, 0, 0, 0.9779403);
        this.showScene3d.addChild(camera);
        let light = new Laya.DirectionLight();
        light.name = "MainLight";
        light.transform.position = new Laya.Vector3(0, 13.81, 6.95);
        light.transform.rotation = new Laya.Quaternion(-0.2416573, 0.8379717, 0.4228851, 0.2461164);
        light.color.x = 0.9056604;
        light.color.y = 0.8615166;
        light.color.z = 0.7732289;
        light.intensity = 0.5;
        this.showScene3d.addChild(light);
        this.sceneBox.addChild(this.showScene3d);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.sureBtn || e.target == this.noForceBtn) {
            this.hideShowScene();
            this.close();
            if (this.sceneType == 1 && GameDataManager.Instance.isBoss && GameDataManager.Instance.fightType != FightType.Tower) {
                if (!this.isCatch) {
                    UserData.instance.catchBossRec.timestamp = Date.now();
                    UserData.instance.catchBossRec.bossId = this.monsterId;
                    UserData.instance.onDataChange("catchBossRec");
                }
                EventManager.event(EventType.ShowCount, true);
            } else {
                if (this.isBacktoSearch) {
                    if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
                    EventManager.event(EventType.BacktoScene, "search");
                }
            }
        } else if (e.target == this.videoCatchBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 2, button_id: 2, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                this.setParam(true, this.monsterId, this.monsterData, this.isBacktoSearch, this.sceneType, null);
            });
        }
    }
    hideShowScene() {
        if (this.showMonster) this.showMonster.destroy();
        this.showScene3d.destroy();
    }
    addMonster() {
        ResManager.create([`Enemy${this.monsterId}.lh`]).then(() => {
            if (this.destroyed) return;
            this.showMonster = LayaTool.createSprite3D(`Enemy${this.monsterId}`, this.showScene3d);
            this.showMonster.transform.position = new Laya.Vector3(0, 0, 0);
            this.showMonster.transform.rotationEuler = new Laya.Vector3();
            let animator = this.showMonster.getChildAt(0).getComponent(Laya.Animator) as Laya.Animator;
            animator.play("Stand", 0, 0);
        });
    }
    onDestroy() {
        Laya.timer.clearAll(this);
    }
    /**sceneType1:战斗胜利后的抓捕2：合成3:直接获取4:扭蛋获得5:boss抓取6：精灵商店购买7超级合成第一次试用8超级合成第二次直接领取9究极合成获取 */
    setParam(isCatch: boolean, monsterId: number, monsterData: any, isBacktoSearch: boolean, sceneType: number, isSpecial: boolean, forceData?: { hp: number, atk: number, star: number }) {
        this.isCatch = isCatch;
        this.sceneType = sceneType;
        this.isBacktoSearch = isBacktoSearch;
        this.monsterId = monsterId;
        this.monsterData = monsterData;
        this.nameLabel.text = monsterData.name;
        let cardData = GameDataManager.Instance.getOneCardData(monsterId);
        if (forceData) {
            cardData.atk = forceData.atk;
            cardData.hp = forceData.hp;
            cardData.star = forceData.star;
        }
        if (cardData.star >= 11) {
            this.starBg.skin = "count/Ui_FrameStarPurple.png";
        } else {
            this.starBg.skin = "count/Ui_FrameStar.png";
        }
        this.hpLabel.text = cardData.hp + "";
        this.atkLabel.text = cardData.atk + "";
        if (cardData.star > 10) {
            this.starLabel.text = (cardData.star - 10) + "";
        } else {
            this.starLabel.text = cardData.star + "";
        }
        if (this.sceneType == 1) {
            if (!UserData.instance.catchTimeRec[monsterId]) {
                UserData.instance.catchTimeRec[monsterId] = 0;
            }
            UserData.instance.catchTimeRec[monsterId]++;
            UserData.instance.onDataChange("catchTimeRec");
        }
        if (isCatch) {
            if (UserData.instance.collectMonsterIdList.indexOf(monsterId) == -1) {
                this.newTag.visible = true;
            } else {
                this.newTag.visible = false;
            }
            this.failTag.visible = false;
            this.lightBg.visible = true;
            this.sureBtn.visible = true;
            this.videoCatchBtn.visible = false;
            this.noForceBtn.visible = false;
            if (sceneType == 1 || sceneType == 2 || sceneType == 3 || sceneType == 5 || sceneType == 7 || sceneType == 8 || sceneType == 9) {
                let cardFrom = 14;
                if (sceneType == 1) {
                    cardFrom = 13;
                } else if (sceneType == 2 || sceneType == 3) {
                    cardFrom = 14;
                } else if (sceneType == 7 || sceneType == 8 || sceneType == 9) {
                    cardFrom = 15;
                }
                let cardData: CardData = UserData.instance.addCard(monsterId, false, cardFrom, forceData);
                if (this.sceneType == 1) {
                    let collectList = UserData.instance.stageCollectObj[GameDataManager.Instance.enterMapIndex];
                    if (GameDataManager.Instance.isCream) {
                        collectList = UserData.instance.creamStageCollectObj[GameDataManager.Instance.enterMapIndex];
                    }
                    if (collectList.indexOf(monsterId) == -1) {
                        collectList.push(monsterId);
                        if (GameDataManager.Instance.isCream) {
                            UserData.instance.onDataChange("creamStageCollectObj");
                        } else {
                            UserData.instance.onDataChange("stageCollectObj");
                        }
                    }
                } else if (this.sceneType == 2) {
                    EventManager.event(EventType.ComposeNew, cardData);
                } else if (this.sceneType == 5) {
                    UserData.instance.catchBossRec = { timestamp: 0, bossId: -1 };
                    UserData.instance.onDataChange("catchBossRec");
                } else if (this.sceneType == 7) {
                    let index = SuperEvaRuntime.superEvaIndex;
                    UserData.instance.tryCardDataList[index].cardId = cardData.cardId;
                    UserData.instance.tryCardDataList[index].monsterId = cardData.monsterId;
                    UserData.instance.tryCardDataList[index].timestamp = Date.now();
                    UserData.instance.onDataChange("tryCardData");
                } else if (this.sceneType == 8 || this.sceneType == 9) {
                    this.clearTryAndAddSuperEvaIndex(this.sceneType == 8);
                }
            } else {
                this.cardData = UserData.instance.addCard(monsterId, isSpecial, 2);
            }
            this.addMonster();
        } else {
            this.noForceBtn.visible = true;
            this.newTag.visible = false;
            this.failTag.visible = true;
            this.lightBg.visible = false;
            this.sureBtn.visible = false;
            this.videoCatchBtn.visible = true;
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 2, button_id: 2, ad_id: 1, ad_type: 1 };
            TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        }
        if (this.sceneType == 1 && !this.isCatch) {
            GuideManager.startOutTask("捕捉失败");
        }
        if (isCatch) {
            if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
        }
        if (this.sceneType == 1 && this.isCatch) {
            if (AllPlatformAdMgr.Instance.isForceTouch()) {
                LayaTool.openScene("pages/quickClickTreasure.scene", false, true, null, [ForceTouchAdType.Banner]);
            }
        }
    }
    onFrameLoop() {
        if (!this.lightBg.visible) {
            return;
        }
        this.lightBg.rotation += 1;
        if (this.lightBg.rotation >= 360) {
            this.lightBg.rotation = this.lightBg.rotation % 360;
        }
    }
    clearTryAndAddSuperEvaIndex(isTryGet: boolean) {
        let index = SuperEvaRuntime.superEvaIndex;
        if (UserData.instance.tryCardDataList[index].cardId) {
            for (let i = 0; i < UserData.instance.cardList.length; i++) {
                if (UserData.instance.cardList[i].cardId == UserData.instance.tryCardDataList[index].cardId) {
                    UserData.instance.cardList.splice(i, 1);
                    UserData.instance.onDataChange("cardList");
                    break;
                }
            }
        }
        UserData.instance.tryCardDataList[index].isLose = true;
        UserData.instance.tryCardDataList[index].isTryGet = isTryGet;
        UserData.instance.onDataChange("tryCardData");
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}