import { ui } from "../ui/layaMaxUI";

export default class SelectShareFriendRuntime extends ui.pages.selectShareFriendUI {
    monsterIdIndex: number;
    onAwake(): void {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    setParam(monsterIdIndex: number) {
        this.monsterIdIndex = monsterIdIndex;
        if (typeof wx != "undefined") {
            let fixMonsterIdIndex = this.monsterIdIndex + 1;
            monsterIdIndex = monsterIdIndex > 50 ? 50 : monsterIdIndex;
            let success = (wx as any).setMessageToFriendQuery({ shareMessageToFriendScene: fixMonsterIdIndex });
            if (success) {
                console.log('设置定向分享query成功', fixMonsterIdIndex);
            } else {
                console.log('设置定向分享query失败', fixMonsterIdIndex);
            }
            this.wxOpenDataViewer.postMsg({ type: "showshare", monsterIdIndex: fixMonsterIdIndex - 1 });
        }
    }
    onDestroy(): void {
        if (typeof wx != "undefined") {
            this.wxOpenDataViewer.postMsg({ type: "hideshare" });
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            this.close();
        }
    }
}