import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import DivisionIconRuntime from "./DivisionIconRuntime";

export default class PopCountRuntime extends ui.pages.pvpCountUI {
    onAwake(): void {
        this.backBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.againBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        if (GameDataManager.Instance.isGameWin) {
            UserData.instance.winCount++;
            UserData.instance.onDataChange("winCount");
            this.uploadPvpRankScore();
        }
        this.setCountCell(true, this.selfCell, GameDataManager.Instance.isGameWin);
        this.setCountCell(false, this.enemyCell, !GameDataManager.Instance.isGameWin);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.backBtn) {
            this.close("no");
        } else if (e.target == this.againBtn) {
            this.close("yes");
        }
    }
    setCountCell(isSelf: boolean, countCell: ui.pages.pvpCountCellUI, win: boolean) {
        countCell.resultImage.skin = win ? "pvp/Ui_RankWin.png" : "pvp/Ui_RankLose.png";
        countCell.sideImage.skin = isSelf ? "pvp/Ui_RankMe.png" : "pvp/Ui_RankEnemy.png";
        this.updateFightMonsterImage(GameDataManager.Instance.pvpMonsterCardList, this.enemyCell);
        let fightMonsterCardList: CardData[] = [];
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let cardData = UserData.instance.fightCardList[i];
            if (cardData) {
                fightMonsterCardList.push(cardData);
            }
        }
        this.updateFightMonsterImage(fightMonsterCardList, this.selfCell);
        countCell.curDivisionIcon.visible = win;
        countCell.nextDivisionIcon.visible = win;
        countCell.divisionProgressBg.visible = win;
        countCell.divisionIcon.visible = !win;
        let winValue = isSelf ? UserData.instance.winCount : GameDataManager.Instance.pvpEnemyData.RankValue + 1;
        let divisionData = GameDataManager.Instance.getPvpDivisionDataByScore(winValue);
        if (win) {
            this.updateDivision(countCell);
        } else {
            (<DivisionIconRuntime>countCell.divisionIcon).setDivision(divisionData[0]);
            (<DivisionIconRuntime>countCell.divisionIcon).setStarNum(divisionData[1]);
        }
    }
    updateFightMonsterImage(cardList: CardData[], countCell: ui.pages.pvpCountCellUI) {
        let imageList = [countCell.monsterImage1, countCell.monsterImage2, countCell.monsterImage3];
        for (let i = 0; i < 3; i++) {
            let cardData = cardList[i];
            let image = imageList[i];
            if (cardData) {
                image.visible = true;
                image.skin = `monster/Ui_Monster${cardData.monsterId}.png`;
            } else {
                image.visible = false;
            }
        }
    }
    uploadPvpRankScore() {
        if (typeof wx != "undefined") {
            let score = UserData.instance.winCount;
            let openDataContext = (wx as any).getOpenDataContext();
            openDataContext.postMessage({ type: "uploadpvp", score: score });
        }
    }
    updateDivision(countCell: ui.pages.pvpCountCellUI) {
        let dataList = GameDataManager.Instance.pvpScoreDataList;
        let divisionData = GameDataManager.Instance.getPvpDivisionDataByScore(UserData.instance.winCount);
        (<DivisionIconRuntime>countCell.curDivisionIcon).setDivision(divisionData[0]);
        (<DivisionIconRuntime>countCell.curDivisionIcon).setStarNum(divisionData[1]);
        let nextDivisionData = [...divisionData];
        if (nextDivisionData[1] + 1 >= dataList[nextDivisionData[0] - 1].length) {
            if (nextDivisionData[0] + 1 < dataList.length) {
                nextDivisionData[0]++;
                nextDivisionData[1] = 0;
            }
        } else {
            nextDivisionData[1]++;
        }
        (<DivisionIconRuntime>countCell.nextDivisionIcon).setDivision(nextDivisionData[0]);
        (<DivisionIconRuntime>countCell.nextDivisionIcon).setStarNum(nextDivisionData[1]);
        let totalUpgradeScore = dataList[nextDivisionData[0] - 1][nextDivisionData[1] - 1].totalUpgradeScore;
        countCell.winTimeLabel.text = `${UserData.instance.winCount}/${totalUpgradeScore}`;
        let progress = UserData.instance.winCount / totalUpgradeScore;
        progress = progress > 1 ? 1 : progress;
        countCell.divisionProgress.width = progress * 250;
    }
}