import EventManager from "./EventManager";
import UserData from "./UserData";

export class GuideManager {
    public static OutTaskStart = "OutTaskStart";
    public static OutTaskFinish = "OutTaskFinish";
    public static InnerTaskStart = "InnerTaskStart";
    public static InnerTaskFinish = "InnerTaskFinish";
    private static _curTask: { outTaskId: number, innerTaskId: number };
    private static _guideUICls: new () => any;
    private static _guideUIInstance: any;
    private static _handPointList: Laya.Point[];
    private static _handPointIndex = -1;
    private static _isOpen = false;
    private static _isRunTask = false;
    private static _isShowHand = false;
    private static _isShowTalk = false;
    private static _enable = false;
    private static _t = 0;
    private static _growSpeed = 0.01;
    private static _handStandAni: Laya.FrameAnimation;
    private static _handClickAni: Laya.FrameAnimation;
    private static _nodeList: Laya.Sprite[];
    private static _guideRecord: number[];
    private static _guideConfig: { taskName: string, innertask: { taskName: string, type: number, text: string }[] }[];
    private static _nodeExDataList: { parent: Laya.Node, x: number, y: number, scaleX: number, scaleY: number }[];
    static init(guideConfigUrl: string, guideUICls: new () => any) {
        return new Promise<void>((resolve, reject) => {
            Laya.loader.load(guideConfigUrl, Laya.Handler.create(this, (guideConfig: any) => {
                if (guideConfig) {
                    this._guideConfig = guideConfig;
                    this._guideUICls = guideUICls;
                    this._guideRecord = UserData.instance.guideRecord;
                    resolve();
                } else {
                    reject("引导配置加载失败");
                }
            }));
        });
    }
    private static analyzeNodeList(nodeList: { node: Laya.Sprite, isHandNode: boolean }[]) {
        let nodeListData: { addNodeList: Laya.Sprite[], handNodeList: Laya.Sprite[] } = { addNodeList: [], handNodeList: [] };
        if (nodeList) {
            for (let i = 0; i < nodeList.length; i++) {
                nodeListData.addNodeList.push(nodeList[i].node);
                if (nodeList[i].isHandNode) {
                    nodeListData.handNodeList.push(nodeList[i].node);
                }
            }
        }
        return nodeListData;
    }
    static startInnerTask(name: string, nodeList: { node: Laya.Sprite, isHandNode: boolean }[] = null) {
        if (!this._enable || this.finished || this._isRunTask) return;
        if (name != this.curInnerTask.taskName) return;
        this._isRunTask = true;
        this.open();
        this.showTalk(this.curInnerTask.text);
        let analyzeResult = this.analyzeNodeList(nodeList);
        this.showHand(analyzeResult.handNodeList);
        this.addNode(analyzeResult.addNodeList);
    }
    static hideMask() {
        Laya.Dialog.manager.maskLayer.alpha = 0;
    }
    static finishInnerTask(name: string) {
        if (!this._enable || this.finished || !this._isRunTask) return;
        if (name != this.curInnerTask.taskName) return;
        this._isRunTask = false;
        this.hideTalk();
        this.hideHand();
        this.backNode();
        this.close();
        EventManager.event(this.InnerTaskFinish, this.curInnerTask.taskName);
        if (++this._curTask.innerTaskId == this.curOutTask.innertask.length) {
            UserData.instance.guideRecord.push(this._curTask.outTaskId);
            UserData.instance.onDataChange("guideRecord");
            this.finishOutTask();
        } else {
            EventManager.event(this.InnerTaskStart, this.curInnerTask.taskName);
        }
    }
    static startOutTask(taskName: string) {
        if (!this._curTask) {
            for (let i = 0; i < this._guideConfig.length; i++) {
                if (this._guideConfig[i].taskName == taskName) {
                    if (this._guideRecord.indexOf(i) != -1) {
                        return;
                    }
                    this._curTask = { outTaskId: i, innerTaskId: 0 };
                    break;
                }
            }
            EventManager.event(this.OutTaskStart, this.curOutTask.taskName);
            EventManager.event(this.InnerTaskStart, this.curInnerTask.taskName);
        }
    }
    private static finishOutTask() {
        if (this._curTask) {
            let outTaskName = this.curOutTask.taskName;
            this._curTask = null;
            EventManager.event(this.OutTaskFinish, outTaskName);
        }
    }
    static set enable(value: boolean) {
        this._enable = value;
    }
    static get enable() {
        return this._enable;
    }
    static get finished() {
        return this._guideRecord.length == this._guideConfig.length;
    }
    static get isOpen() {
        return this._isOpen;
    }
    private static open() {
        if (!this._enable || this.finished || this._isOpen) return;
        this._isOpen = true;
        this._guideUIInstance = new this._guideUICls();
        this._guideUIInstance.isModal = true;
        this._guideUIInstance.autoDestroyAtClosed = true;
        this._guideUIInstance.width = Laya.stage.width;
        this._guideUIInstance.height = Laya.stage.height;
        this._guideUIInstance.talkBg.visible = false;
        this._guideUIInstance.hand.visible = false;
        this._handStandAni = this._guideUIInstance.handStand;
        this._handClickAni = this._guideUIInstance.handClick;
        Laya.Dialog.manager.open(this._guideUIInstance, false, false);
        this._guideUIInstance.on(Laya.Event.CLICK, this, this.onPageEvent);
    }
    private static close() {
        if (!this._enable || this.finished || !this._isOpen) return;
        Laya.Dialog.manager.close(this._guideUIInstance);
        Laya.Dialog.manager.maskLayer.alpha = UIConfig.popupBgAlpha;
        this._guideUIInstance = null;
        this._isOpen = false;
    }
    private static showHand(nodeList: Laya.Sprite[]) {
        if (!nodeList || nodeList.length == 0 || this._isShowHand) return;
        this._isShowHand = true;
        this._handPointList = [];
        this._handPointIndex = 0;
        for (let i = 0; i < nodeList.length; i++) {
            let node = nodeList[i];
            let point = new Laya.Point();
            point.x = node.width / 2 * node.globalScaleX;
            point.y = node.height / 2 * node.globalScaleY;
            node.localToGlobal(point, false);
            this._handPointList.push(point);
        }
        this._guideUIInstance.hand.visible = true;
        let curPoint = this._handPointList[this._handPointIndex];
        this._guideUIInstance.hand.x = curPoint.x;
        this._guideUIInstance.hand.y = curPoint.y;
        Laya.timer.clear(this, this.onFrameLoop);
        if (this._handPointList.length >= 2) {
            this._handStandAni.play(0, false);
            Laya.timer.frameLoop(1, this, this.onFrameLoop);
        } else {
            this._handClickAni.wrapMode = Laya.FrameAnimation.WRAP_PINGPONG;
            this._handClickAni.play(0, true);
        }
    }
    private static hideHand() {
        if (!this._handPointList || !this._isShowHand) return;
        this._isShowHand = false;
        this._handPointList.length >= 2 && Laya.timer.clear(this, this.onFrameLoop);
        this._handPointList = null;
        this._handPointIndex = -1;
        this._guideUIInstance.hand.visible = false;
        this._handStandAni.play(0, false);
    }
    private static showTalk(content: string) {
        if (!content || this._isShowTalk) return;
        this._isShowTalk = true;
        this._guideUIInstance.talkBg.visible = true;
        this._guideUIInstance.talkLabel.text = content;
    }
    private static hideTalk() {
        if (!this._isShowTalk) return;
        this._isShowTalk = false;
        this._guideUIInstance.talkBg.visible = false;
        this._guideUIInstance.talkLabel.text = "";
    }
    private static onFrameLoop() {
        let curPoint = this._handPointList[this._handPointIndex];
        let targetPoint = this._handPointList[this._handPointIndex + 1];
        this._t += this._growSpeed;
        this._t = this._t > 1 ? 1 : this._t;
        this._guideUIInstance.hand.x = curPoint.x + (targetPoint.x - curPoint.x) * this._t;
        this._guideUIInstance.hand.y = curPoint.y + (targetPoint.y - curPoint.y) * this._t;
        if (this._t == 1) {
            this._t = 0;
            this._handPointIndex = ++this._handPointIndex % this._handPointList.length;
        }
    }
    private static addNode(nodeList: Laya.Sprite[]) {
        if (!nodeList || nodeList.length == 0) return;
        this._nodeList = nodeList;
        this._nodeExDataList = [];
        for (let i = 0; i < this._nodeList.length; i++) {
            let node = this._nodeList[i];
            let exData = { parent: node.parent, x: node.x, y: node.y, scaleX: node.scaleX, scaleY: node.scaleY };
            this._nodeExDataList.push(exData);
            let globalScaleX = node.globalScaleX;
            let globalScaleY = node.globalScaleY;
            let localPoint = new Laya.Point();
            localPoint.x = node.pivotX;
            localPoint.y = node.pivotY;
            node.localToGlobal(localPoint, false);
            this._guideUIInstance.contentBox.addChild(node);
            node.x = localPoint.x;
            node.y = localPoint.y;
            node.scaleX = globalScaleX;
            node.scaleY = globalScaleY;
        }
    }
    private static backNode() {
        if (!this._nodeList) return;
        for (let i = 0; i < this._nodeList.length; i++) {
            let node = this._nodeList[i];
            let exData = this._nodeExDataList[i];
            exData.parent.addChild(node);
            node.x = exData.x;
            node.y = exData.y;
            node.scaleX = exData.scaleX;
            node.scaleY = exData.scaleY;
        }
        this._nodeList = null;
        this._nodeExDataList = null;
    }
    private static onPageEvent(e: Laya.Event) {
        if (this.curInnerTask.type != 1) return;
        this.finishInnerTask(this.curInnerTask.taskName);
    }
    private static get curOutTask() {
        return this._guideConfig[this._curTask.outTaskId];
    }
    private static get curInnerTask() {
        return this.curOutTask.innertask[this._curTask.innerTaskId];
    }
}