import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class SuperEvaRuntime extends ui.pages.superEvaUI {
    maxPage: number = 0;
    curPageIndex: number = 0;
    cardDataList: CardData[] = [];
    cardStateList: any[] = [];
    mixMonsterId: number = -1;
    haveMonsterNum: number = 0;
    cost: number = 0;
    curTabIndex = -1;
    dayUnlockStartIndex = 3;
    //解锁类型1 关卡解锁 2 天数解锁
    static superEvaIndex: number = 0;
    static superEvaDataList = [{ type: 1, value: 1, specialMixId: 0 }, { type: 1, value: 2, specialMixId: 1 }, { type: 1, value: 3, specialMixId: 2 }, { type: 2, value: 1, specialMixId: 6 }, { type: 2, value: 2, specialMixId: 3 }, { type: 2, value: 3, specialMixId: 4 }, { type: 2, value: 4, specialMixId: 5 }];
    onAwake() {
        this.updateTryState();
        this.monsterList.hScrollBarSkin = null;
        this.stateList.hScrollBarSkin = null;
        this.maxPage = SuperEvaRuntime.superEvaDataList.length - 1;
        for (let i = 0; i < 3; i++) {
            this.cardDataList.push(new CardData());
            this.cardStateList.push({ missTag: { visible: false }, getBtn: { visible: false }, haveTag: { visible: false }, videoTag: { visible: false } });
        }
        this.monsterNameLabel.filters = [CatchRuntime.blackColorFilter];
        this.starNumLabel.filters = [CatchRuntime.blackColorFilter];
        this.hpLabel.filters = [CatchRuntime.blackColorFilter];
        this.atkLabel.filters = [CatchRuntime.blackColorFilter];
        this.setSelectTab(0);
        EventManager.on(GuideManager.InnerTaskStart, this, this.onInnerTaskStart);
        // Laya.Scene.root.filters = [new Laya.BlurFilter(5)];
        Laya.Dialog.manager.maskLayer.alpha = 0.9;
        this.setBtnVideoTag(1);
    }
    onOpened() {
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.superBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.leftBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.dayUnlockTabBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.stageUnlockTabBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.receiveWelfareBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 7, button_id: 7, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        this.speedBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        // Laya.Scene.root.filters = null;
        Laya.Dialog.manager.maskLayer.alpha = UIConfig.popupBgAlpha;
    }
    onDestroy() {
        EventManager.off(GuideManager.InnerTaskStart, this, this.onInnerTaskStart);
    }
    onMouseHandler(index: number, e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            if (!this.lastMonsterHave()) {
                EventManager.event(EventType.PopTip, "究极进化上一级");
                return;
            }
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 7, button_id: 7, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-eb988867c0754d81", () => {
                let monsterData = GameDataManager.Instance.getMonsterData([this.cardDataList[index].monsterId])[0];
                LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                    this.setPageData(this.curPageIndex);
                }), [true, this.cardDataList[index].monsterId, monsterData, false, 3]);
            });
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            GuideManager.finishInnerTask("关闭究极进化");
            this.close();
        } else if (e.target == this.speedBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                UserData.instance.speedUpTimeRec[this.curPageIndex]++;
                UserData.instance.onDataChange("speedUpTimeRec");
                this.setPageData(this.curPageIndex);
            });
        } else if (e.target == this.superBtn) {
            if (this.haveMonsterNum == 3) {
                if (UserData.instance.gold >= this.cost) {
                    if (!this.lastMonsterHave()) {
                        EventManager.event(EventType.PopTip, "究极进化上一级");
                        return;
                    }
                    UserData.instance.gold -= this.cost;
                    EventManager.event(EventType.UseMoney, "gold");
                    UserData.instance.onDataChange("gold");
                    TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: this.cost * -1, get_type: 11, last_num: UserData.instance.gold });
                    for (let j = 0; j < this.cardDataList.length; j++) {
                        for (let i = 0; i < UserData.instance.cardList.length; i++) {
                            if (this.cardDataList[j].monsterId == UserData.instance.cardList[i].monsterId) {
                                let card = UserData.instance.cardList.splice(i, 1)[0];
                                UserData.instance.removeCard(card, 11);
                                break;
                            }
                        }
                    }
                    let monsterData = GameDataManager.Instance.getMonsterData([this.mixMonsterId])[0];
                    EventManager.event(EventType.PopTip, "合成成功");
                    ResManager.playSound("composeSuccess.mp3");
                    UserData.instance.finishTask(0, 2, 1);
                    LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                        this.setPageData(this.curPageIndex);
                        EventManager.event(EventType.UpdateSuperRed);
                        EventManager.event(EventType.UpdateComposeRed);
                    }), [true, this.mixMonsterId, monsterData, false, 9]);
                } else {
                    LayaTool.openScene("pages/addMoney.scene", false, true, null, [2, 200]);
                    EventManager.event(EventType.ShowGiftPack);
                }
            } else {
                EventManager.event(EventType.PopTip, "条件不满足");
            }
        } else if (e.target == this.leftBtn) {
            let smallIndex = 0;
            if (this.curTabIndex == 1) {
                smallIndex = 3;
            }
            if (this.curPageIndex > smallIndex) {
                this.setPageData(--this.curPageIndex);
            }
        } else if (e.target == this.rightBtn) {
            let maxIndex = 2;
            if (this.curTabIndex == 1) {
                maxIndex = this.maxPage;
            }
            if (this.curPageIndex < maxIndex) {
                this.setPageData(++this.curPageIndex);
            }
        } else if (e.target == this.receiveWelfareBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-eb988867c0754d81", () => {
                let tryState = this.getTryState(this.curPageIndex);
                let cardData = GameDataManager.Instance.getOneCardData(this.mixMonsterId);
                let monsterData = GameDataManager.Instance.monsterData[cardData.monsterId];
                if (tryState == 0) {
                    LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                        this.setPageData(this.curPageIndex);
                    }), [true, this.mixMonsterId, monsterData, false, 7]);
                } else if (tryState == 2) {
                    LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                        this.setPageData(this.curPageIndex);
                        EventManager.event(EventType.UpdateSuperRed);
                        EventManager.event(EventType.UpdateComposeRed);
                    }), [true, this.mixMonsterId, monsterData, false, 8]);
                }
            });
        } else if (e.target == this.dayUnlockTabBtn) {
            this.setSelectTab(1);
        } else if (e.target == this.stageUnlockTabBtn) {
            this.setSelectTab(0);
        }
    }
    setPageData(pageIndex: number) {
        SuperEvaRuntime.superEvaIndex = pageIndex;
        this.curPageIndex = pageIndex;
        let specialMixId = SuperEvaRuntime.superEvaDataList[pageIndex].specialMixId;
        let mixConfig = GameDataManager.Instance.specialMixConfig[specialMixId];
        this.mixMonsterId = mixConfig.index;
        this.monsterImage.skin = `monster/Ui_Monster${this.mixMonsterId}.png`;
        this.monsterNameLabel.text = GameDataManager.Instance.monsterData[this.mixMonsterId].name;
        this.speedBtn.visible = false;
        this.unlockInfoBg.visible = false;
        let cardData = GameDataManager.Instance.getOneCardData(this.mixMonsterId);
        this.hpLabel.text = cardData.hp + "";
        this.atkLabel.text = cardData.atk + "";
        this.starNumLabel.text = cardData.star + "";
        if (this.isTrueHaveMonster()) {
            //已拥有
            this.partBg.visible = false;
            this.superBtn.visible = false;
            this.haveTag.visible = true;
        } else {
            //没有拥有
            let unlockConfition = SuperEvaRuntime.superEvaDataList[pageIndex];
            if (unlockConfition.type == 1) {
                if (UserData.instance.gameRank < unlockConfition.value) {
                    this.partBg.visible = false;
                    this.superBtn.visible = false;
                    this.haveTag.visible = false;
                    this.unlockInfoBg.visible = true;
                    this.unlockDatLeftLabel.text = unlockConfition.value + "";
                    this.unlockInfoBg.skin = "changeAdd1/Ui_FrameComposeDay2.png";
                    this.updateArrowBtn();
                    this.updateWelfare();
                    return;
                }
            } else {
                let dayleft = this.getDayLeft(pageIndex);
                if (dayleft > 0) {
                    this.partBg.visible = false;
                    this.superBtn.visible = false;
                    this.haveTag.visible = false;
                    this.unlockInfoBg.visible = true;
                    this.unlockDatLeftLabel.text = dayleft + "";
                    this.unlockInfoBg.skin = "changeAdd1/Ui_FrameComposeDay.png";
                    this.speedBtn.visible = true;
                    this.updateArrowBtn();
                    this.updateWelfare();
                    return;
                }
            }
            this.partBg.visible = true;
            this.superBtn.visible = true;
            this.haveTag.visible = false;
            this.cost = (this.curPageIndex + 1) * 2000;
            this.moneyLabel.text = this.cost + "";
            for (let i = 0; i < 3; i++) {
                this.cardDataList[i] = GameDataManager.Instance.getOneCardData(mixConfig.mixIndex[i]);
            }
            this.haveMonsterNum = 0;
            let mixNeedList = [...mixConfig.mixIndex];
            let haveList = [0, 0, 0];
            for (let i = 0; i < UserData.instance.cardList.length; i++) {
                let card = UserData.instance.cardList[i];
                let listIndex = mixNeedList.indexOf(card.monsterId);
                if (listIndex != -1) {
                    mixNeedList[listIndex] = -1;
                    haveList[listIndex] = 1;
                    this.haveMonsterNum++;
                    if (this.haveMonsterNum == mixNeedList.length) break;
                }
            }
            for (let i = 0; i < haveList.length; i++) {
                let stateData = this.cardStateList[i];
                if (haveList[i]) {
                    stateData.missTag.visible = false;
                    stateData.getBtn.visible = false;
                    stateData.haveTag.visible = true;
                    stateData.videoTag.visible = false;
                } else {
                    stateData.missTag.visible = true;
                    stateData.getBtn.visible = true;
                    stateData.haveTag.visible = false;
                    stateData.videoTag.visible = true;
                }
            }
            this.monsterList.array = this.cardDataList;
            this.monsterList.refresh();
            this.stateList.array = this.cardStateList;
            this.stateList.refresh();
            for (let i = 0; i < this.stateList.length; i++) {
                let stateCell = this.stateList.getCell(i);
                let getBtn = stateCell.getChildByName("getBtn") as Laya.Image;
                if (!getBtn.hasListener(Laya.Event.CLICK)) {
                    getBtn.on(Laya.Event.CLICK, this, this.onMouseHandler, [i]);
                }
            }
            this.setBtnVideoTag(2);
        }
        this.updateArrowBtn();
        this.updateWelfare();
    }
    updateArrowBtn() {
        let maxPage = 0;
        let curPageIndex = 0;
        if (this.curTabIndex == 0) {
            maxPage = 2;
            curPageIndex = this.curPageIndex;
        } else if (this.curTabIndex == 1) {
            maxPage = 3;
            curPageIndex = this.curPageIndex - 3;
        }
        if (maxPage == 0) {
            this.leftBtn.alpha = 0.5;
            this.rightBtn.alpha = 0.5;
        } else {
            if (curPageIndex == 0) {
                this.leftBtn.alpha = 0.5;
                this.rightBtn.alpha = 1;
            } else if (curPageIndex == maxPage) {
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 0.5;
            } else {
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;
            }
        }
    }
    public static haveRed() {
        if (UserData.instance.gameRank >= GameDataManager.Instance.openSuperEvaRank) {
            for (let i = 0; i < this.superEvaDataList.length; i++) {
                let specialMixId = SuperEvaRuntime.superEvaDataList[i].specialMixId;
                let mixConfig = GameDataManager.Instance.specialMixConfig[specialMixId];
                let mixMonsterId = mixConfig.index;
                if (!GameDataManager.Instance.haveMonster(mixMonsterId)) {
                    let allHave = this.haveMixList([...mixConfig.mixIndex]);
                    if (allHave) {
                        return true;
                    }
                }
            }
            return false;
        } else {
            return false;
        }
    }
    public static haveMixList(list: number[]) {
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            let card = UserData.instance.cardList[i];
            let listIndex = list.indexOf(card.monsterId);
            if (listIndex != -1) {
                list.splice(listIndex, 1);
                if (list.length == 0) return true;
            }
        }
        return false;
    }
    // updateMaxPage() {
    //     if (UserData.instance.unlockDayTimestamp > 0) {
    //         //有天数解锁了
    //         let startIndex = 3;
    //         this.maxPage = 3;
    //         for (let i = startIndex; i < this.unlockConditionList.length; i++) {
    //             let dayLeft = this.getDayLeft(i);
    //             if (dayLeft <= 0) {
    //                 if (this.maxPage < this.unlockConditionList.length - 1) {
    //                     this.maxPage++;
    //                 }
    //             }
    //         }
    //     } else {
    //         //没有天数解锁
    //         this.maxPage = UserData.instance.gameRank - GameDataManager.Instance.openSuperEvaRank + 1;
    //     }
    // }
    getDayLeft(pageIndex: number) {
        let unlockCondition = SuperEvaRuntime.superEvaDataList[pageIndex];
        let nowTime = Date.now();
        let dayPass = Math.floor((nowTime - UserData.instance.registerTimestamp) / (24 * 3600 * 1000));
        let dayLeft = unlockCondition.value - dayPass - UserData.instance.speedUpTimeRec[pageIndex];
        return dayLeft;
    }
    onInnerTaskStart(taskName: string) {
        if (taskName == "关闭究极进化") {
            GuideManager.startInnerTask(taskName, [{ node: this.closeBtn, isHandNode: true }]);
        }
    }
    updateWelfare() {
        let unlockState = this.getSuperEvaUnlockState(this.curPageIndex);
        if (unlockState.unlock) {
            let tryState = this.getTryState(this.curPageIndex);
            //解锁了
            if (GameDataManager.Instance.haveMonster(this.mixMonsterId)) {
                //拥有
                if (tryState > 0 && UserData.instance.tryCardDataList[this.curPageIndex].isLose == false) {
                    //试用的拥有
                    this.welfareBg.visible = false;
                } else {
                    //真正的拥有
                    if (UserData.instance.tryCardDataList[this.curPageIndex].monsterId != -1 &&
                        UserData.instance.tryCardDataList[this.curPageIndex].isTryGet) {
                        this.welfareBg.visible = true;
                        this.welfareBg.skin = "ad/Ui_FrameFuLi3.png";
                        this.receiveWelfareBtn.visible = false;
                    } else {
                        this.welfareBg.visible = false;
                    }
                }
            } else {
                //没有拥有
                if (tryState == 0) {
                    //没有试用过
                    this.welfareBg.visible = true;
                    this.welfareBg.skin = "ad/Ui_FrameFuLi1.png";
                    this.receiveWelfareBtn.visible = true;
                } else {
                    this.welfareBg.visible = true;
                    this.welfareBg.skin = "ad/Ui_FrameFuLi2.png";
                    this.receiveWelfareBtn.visible = true;
                }
            }
        } else {
            //没有解锁
            this.welfareBg.visible = false;
        }
    }
    getSuperEvaUnlockState(pageIndex: number) {
        let unlockConfition = SuperEvaRuntime.superEvaDataList[pageIndex];
        let unlockState = { type: unlockConfition.type, unlock: true };
        if (unlockConfition.type == 1) {
            if (UserData.instance.gameRank < unlockConfition.value) {
                unlockState.unlock = false;
            }
        } else {
            let dayleft = this.getDayLeft(pageIndex);
            if (dayleft > 0) {
                unlockState.unlock = false;
            }
        }
        return unlockState;
    }
    getTryState(index: number) {
        if (UserData.instance.tryCardDataList[index].monsterId != -1) {
            //有试用
            let tryTime = Date.now() - UserData.instance.tryCardDataList[index].timestamp;
            if (tryTime <= 24 * 3600 * 1000) {
                //试用时间不超过1天
                return 1;
            } else if (tryTime <= 48 * 3600 * 1000) {
                //试用时间不超过2天
                return 2;
            } else {
                //试用时间超过2天
                return 3;
            }
        } else {
            //没有试用
            return 0;
        }
    }
    updateTryState() {
        for (let i = 0; i < UserData.instance.tryCardDataList.length; i++) {
            let tryState = this.getTryState(i);
            if (tryState == 2) {
                UserData.instance.tryCardDataList[i].isLose = true;
                for (let j = 0; j < UserData.instance.cardList.length; j++) {
                    if (UserData.instance.cardList[j].cardId == UserData.instance.tryCardDataList[i].cardId) {
                        UserData.instance.cardList.splice(j, 1);
                        break;
                    }
                }
            }
        }
        UserData.instance.onDataChange("tryCardData");
        UserData.instance.onDataChange("cardList");
    }
    setSelectTab(newTabIndex: number) {
        if (newTabIndex == this.curTabIndex) return;
        this.curTabIndex = newTabIndex;
        if (newTabIndex == 0) {
            this.stageUnlockTabBtn.alpha = 1;
            this.stageUnlockTabBtn.scaleX = 1;
            this.stageUnlockTabBtn.scaleY = 1;

            this.dayUnlockTabBtn.alpha = 0.8;
            this.dayUnlockTabBtn.scaleX = 0.8;
            this.dayUnlockTabBtn.scaleY = 0.8;
            this.setPageData(0);
        } else {
            this.dayUnlockRed.visible = false;
            this.dayUnlockTabBtn.alpha = 1;
            this.dayUnlockTabBtn.scaleX = 1;
            this.dayUnlockTabBtn.scaleY = 1;

            this.stageUnlockTabBtn.alpha = 0.8;
            this.stageUnlockTabBtn.scaleX = 0.8;
            this.stageUnlockTabBtn.scaleY = 0.8;
            this.setPageData(this.dayUnlockStartIndex);
        }
    }
    //上一个究极进化的怪是否拥有
    lastMonsterHave() {
        if ((this.curPageIndex <= 2 && this.curPageIndex == 0) ||
            (this.curPageIndex >= 3 && this.curPageIndex == 3)) {
            return true;
        } else {
            let specialMixId = SuperEvaRuntime.superEvaDataList[this.curPageIndex - 1].specialMixId;
            let mixConfig = GameDataManager.Instance.specialMixConfig[specialMixId];
            return GameDataManager.Instance.haveMonster(mixConfig.index);
        }
    }
    //是否真正拥有怪物
    isTrueHaveMonster() {
        if (GameDataManager.Instance.haveMonster(this.mixMonsterId)) {
            if (UserData.instance.tryCardDataList[this.curPageIndex].monsterId == -1 ||
                UserData.instance.tryCardDataList[this.curPageIndex].isLose) {
                return true;
            } else {
                return false;
            }
        } else {
            return false;
        }
    }
    setBtnVideoTag(type: number) {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        if (type == 1) {
            this.videoTag1.visible = isShowVideoTag;
            this.videoTag2.visible = isShowVideoTag;
        } else {
            for (let i = 0; i < this.stateList.length; i++) {
                let stateCell = this.stateList.getCell(i);
                let videoTag = stateCell.getChildByName("videoTag") as Laya.Image;
                videoTag.visible = isShowVideoTag;
            }
        }
    }
}