import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class PvpMatchRuntime extends ui.pages.pvpMatchUI {
    onAwake(): void {
        let divisionData = GameDataManager.Instance.getPvpDivisionDataByScore(UserData.instance.winCount);
        GameDataManager.Instance.initPvpMonsterCardList(divisionData);
        let fightMonsterCardList: CardData[] = [];
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let cardData = UserData.instance.fightCardList[i];
            if (cardData) {
                fightMonsterCardList.push(cardData);
            }
        }
        this.selfCardList.width = fightMonsterCardList.length * 139;
        this.selfCardList.array = fightMonsterCardList;
        this.selfCardList.refresh();
        this.enemyCardList.width = GameDataManager.Instance.pvpMonsterCardList.length * 139;
        this.enemyCardList.array = GameDataManager.Instance.pvpMonsterCardList;
        this.enemyCardList.refresh();
        this.ani1.on(Laya.Event.COMPLETE, this, () => {
            this.close();
            Laya.MouseManager.enabled = false;
            EventManager.event(EventType.GotoScene, "pvp");
        });
    }
}