import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { WXPay } from "../gamelogic/WXPay";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import MoneyUiRuntime from "./MoneyUiRuntime";
import ShopCellRuntime from "./ShopCellRuntime";

export default class SpriteShopRuntime extends ui.pages.spriteShopUI {
    curTabIndex: number = -1;
    itemDataList: any[];
    onAwake() {
        this.itemList.vScrollBarSkin = null;
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.tabList.mouseHandler = Laya.Handler.create(this, this.onTabListMouseHandler, null, false);
        this.itemList.mouseHandler = Laya.Handler.create(this, this.onItemListMouseHandler, null, false);
        this.initTabList(0);
        this.initMoneyPoint();
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
    }
    initTabList(tabIndex: number) {
        if (tabIndex == this.curTabIndex) return;
        let tabDataList = [];
        for (let i = 0; i < 4; i++) {
            if (tabIndex != i) {
                tabDataList.push({ tabBg: { skin: `spriteShop/Btn_Shop1.png` }, tabLabel: { skin: `pay/Btn_Shopword${(i + 1) * 2 - 1}.png` } });
            } else {
                tabDataList.push({ tabBg: { skin: `spriteShop/Btn_Shop2.png` }, tabLabel: { skin: `pay/Btn_Shopword${(i + 1) * 2}.png` } });
            }
        }
        this.tabList.array = tabDataList;
        this.tabList.refresh();
        this.curTabIndex = tabIndex;
        this.initItemList(this.curTabIndex);
    }
    initItemList(itemIndex: number) {
        this.diamondByTips.visible = itemIndex == 0;
        let keyList = ["diamondShop", "superExchange", "ticketExchange", "monsterExchange"];
        this.itemDataList = GameDataManager.Instance.goodsItem[keyList[itemIndex]];
        this.itemList.array = this.itemDataList;
        this.itemList.refresh();
    }
    onTabListMouseHandler(e: Laya.Event, index: number) {
        if (e.type != Laya.Event.CLICK) return;
        this.initTabList(index);
    }
    onItemListMouseHandler(e: Laya.Event, index: number) {
        if (e.type != Laya.Event.CLICK) return;
        let cell: ShopCellRuntime = this.itemList.getCell(index) as any;
        if (cell.itemMask.visible) return;
        if (cell.data.costType == "rmb") {
            if (cell.data.costNum == 0 || typeof wx == "undefined") {
                this.receiveShopItem(cell.data);
                this.addLotteryTime(cell.data.costNum);
            } else {
                WXPay.buyItem(cell.data).then(() => {
                    this.receiveShopItem(cell.data);
                    this.addLotteryTime(cell.data.costNum);
                });
            }
        } else if (cell.data.costType == "diamond" || cell.data.costType == "badge") {
            if (cell.data.costType == "badge") {
                if (UserData.instance.badge >= cell.data.costNum) {
                    UserData.instance.badge -= cell.data.costNum;
                    UserData.instance.onDataChange("badge");
                    EventManager.event(EventType.UseMoney, "badge");
                } else {
                    return;
                }
            } else if (cell.data.costType == "diamond") {
                if (UserData.instance.diamond >= cell.data.costNum) {
                    UserData.instance.diamond -= cell.data.costNum;
                    UserData.instance.onDataChange("diamond");
                    EventManager.event(EventType.UseMoney, "diamond");
                } else {
                    return;
                }
            }
            this.receiveShopItem(cell.data);
        }
    }
    receiveShopItem(data: any) {
        if (data.goods[0].type == "monster") {
            //直接购买怪物
            let cardData = GameDataManager.Instance.getOneCardData(data.goods[0].num);
            let monsterConfig = GameDataManager.Instance.monsterData;
            LayaTool.openScene("pages/catchResult.scene", false, true, null, [true, cardData.monsterId, monsterConfig[cardData.monsterId], false, 6, false]);
        } else {
            //购买1金币，碎片4，远征券5，钻石6
            let type = 1;
            let num = data.goods[0].num;
            if (data.goods[0].type == "chip") {
                type = 4;
            } else if (data.goods[0].type == "fightTicket") {
                type = 5;
            } else if (data.goods[0].type == "diamond") {
                type = 6;
                if (GameDataManager.Instance.haveExtra(data)) {
                    num += data.extra;
                }
            }
            LayaTool.openScene("pages/getCard.scene", false, true, null, [type, num]);
        }
        if (!UserData.instance.shopBuyRecord[data.productId]) {
            UserData.instance.shopBuyRecord[data.productId] = 0;
        }
        UserData.instance.shopBuyRecord[data.productId]++;
        UserData.instance.onDataChange("shopBuyRecord");
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.closeBtn) {
            EventManager.event(EventType.ShowSpriteShop, false);
        }
    }
    onUserDataChange(key: string) {
        if (key == "shopBuyRecord") {
            this.itemList.refresh();
        }
    }
    initMoneyPoint() {
        let badgeMoneyUi: MoneyUiRuntime = this.badgeMoneyUi;
        badgeMoneyUi.setMoneyType("badge", true);
        badgeMoneyUi.enableEffect = false;
        badgeMoneyUi.moneyIcon.localToGlobal(badgeMoneyUi.moneyPoint, false, this);

        let diamondMoneyUi: MoneyUiRuntime = this.diamondMoneyUi;
        diamondMoneyUi.setMoneyType("diamond", false);
        diamondMoneyUi.enableEffect = false;
        diamondMoneyUi.moneyIcon.localToGlobal(diamondMoneyUi.moneyPoint, false, this);
    }
    onSelect() {
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    offSelect() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    addLotteryTime(rmb: number) {
        let addTime = rmb * 3;
        UserData.instance.freeLotteryTimeLeft += addTime;
        UserData.instance.onDataChange("freeLotteryTimeLeft");
    }
    static haveRed() {
        let buyTime = GameDataManager.Instance.getShopItemBuyTime(1);
        return buyTime == 0;
    }
}