import { AllPlatformAdMgr } from "./advert/AllPlatformAdMgr";
import EventManager from "./manager/EventManager";
import PlatFormManager from "./manager/PlatFormManager";
import ResManager from "./manager/ResManager";
import StageManager from "./manager/StageManager";
import UIManager from "./manager/UIManager";

export default class GFrameWork {
    private constructor() { }
    public static init() {
        return new Promise<void>(resolve => {
            UIConfig.closeDialogOnSide = false;
            Laya.MouseManager.multiTouchEnabled = true;
            UIConfig.popupBgAlpha = 0.8;
            EventManager.init();
            StageManager.init();
            UIManager.init();
            PlatFormManager.init();
            AllPlatformAdMgr.Instance.init();
            ResManager.init().then(() => {
                resolve();
            });
        });
    }
}