import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import Scene3DBase from "../gframe-work/scene3D/Scene3DBase";
import LayaTool from "../gframe-work/tool/LayaTool";
import CatchBallScript from "./CatchBallScript";
import Fighter from "./Fighter";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";

export default class CatchSceneLogic extends Scene3DBase {
    ball: Laya.Sprite3D = null;
    catchEffect: Laya.Sprite3D = null;
    catchLight: Laya.Sprite3D = null;
    boundSphere: Laya.BoundSphere = null;
    isClickBall: boolean = false;
    catchCameraData = { "position": [0, 4.46, 14.93], "rotation": [0.192584, 0, 0, -0.9812805] };
    ballScript: CatchBallScript = null;
    /**射线 */
    ray: Laya.Ray = new Laya.Ray(new Laya.Vector3, new Laya.Vector3);
    /**射线碰撞结果 */
    hitResult: Laya.HitResult = new Laya.HitResult();
    progress: number = 0;
    lastProgress: number = 0;
    progressAdd: number = 0.01;
    progressBack: number = 0.005;
    onAwake() {
        ResManager.playSound("success.mp3");
        this.cameraUrl = "MainCamera/FightCamera";
        super.onAwake();
        this.ball = this.scene3d.getChildByName("Ball") as Laya.Sprite3D;
        this.catchEffect = this.scene3d.getChildByName("Effect_Catch") as Laya.Sprite3D;
        this.catchLight = this.scene3d.getChildByName("Light_Catch") as Laya.Sprite3D;
        this.boundSphere = new Laya.BoundSphere(new Laya.Vector3(0, 1, 11), 0.5);
        this.ballScript = this.ball.getComponent(CatchBallScript);
        if (!this.ballScript) this.ballScript = this.ball.addComponent(CatchBallScript);
        this.ballScript.catchSuccessHandler = Laya.Handler.create(this, this.onCatchSuccess);
        this.ballScript.catchEndHandler = Laya.Handler.create(this, this.onCatchEnd);
        this.ball.active = false;
        this.catchEffect.active = false;
        this.catchLight.active = false;
        this.camera.transform.localPosition = LayaTool.createVector3FromArray(this.catchCameraData.position);
        this.camera.transform.localRotation = LayaTool.createQuaternionFromArray(this.catchCameraData.rotation);
        let catchMonsterId = GameDataManager.Instance.catchMonsterId;
        let fighter = LayaTool.createSprite3DWithScript(`Enemy${catchMonsterId}`, Fighter, this.clearNode);
        fighter.gameObject.transform.position = new Laya.Vector3();
        fighter.gameObject.transform.localRotationEuler = new Laya.Vector3();
        this.progress = 0;
    }
    onOpened(isVideoCatchBoss: boolean) {
        Laya.MouseManager.enabled = false;
        EventManager.on(EventType.BallStartCatch, this, this.onBallStartCatch);
        if (isVideoCatchBoss) {
            EventManager.event(EventType.GotoCatch, [isVideoCatchBoss]);
        } else {
            let catchMonsterId = GameDataManager.Instance.catchMonsterId;
            let catchRate = GameDataManager.Instance.getMonsterCatchRate(catchMonsterId, GameDataManager.Instance.isBoss);
            let isShowBall = catchRate < 100;
            this.isClickBall = false;
            if (isShowBall) {
                this.ball.active = true;
                Laya.stage.on(Laya.Event.MOUSE_DOWN, this, this.onClickScreen);
                Laya.stage.on(Laya.Event.MOUSE_UP, this, this.onClickScreen);
                Laya.timer.frameLoop(1, this, this.onFrameLoop);
            }
            let pos = LayaTool.convert3DTo2DPosition(this.camera, new Laya.Vector3(0, 1, 11));
            EventManager.event(EventType.GotoCatch, [isVideoCatchBoss, pos.y]);
        }
    }
    onCatchEnd() {
        EventManager.event(EventType.CatchEnd);
    }
    onCatchSuccess() {
        this.catchEffect.active = true;
        this.catchLight.active = true;
    }
    onClickScreen(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            let newPoint = new Laya.Vector2(e.stageX * Laya.stage.clientScaleX, e.stageY * Laya.stage.clientScaleY);
            this.camera.viewportPointToRay(newPoint, this.ray);
            let dis = Laya.CollisionUtils.intersectsRayAndSphereRD(this.ray, this.boundSphere);
            if (dis >= 0) {
                this.isClickBall = true;
                this.ballScript.rollEffect.active = true;
                this.ballScript.animator.crossFade("Around", 0.5, 0, 0);
                ResManager.playSound("catchRoll.mp3", 0);
            }
        } else if (e.type == Laya.Event.MOUSE_UP) {
            if (this.isClickBall) {
                ResManager.stopSound("catchRoll.mp3");
                this.isClickBall = false;
                this.ballScript.rollEffect.active = false;
                this.ballScript.animator.crossFade("Stand", 0.5, 0, 0);
                if (this.progress >= 0.8) {
                    this.ballFly();
                }
            }
        }
    }
    ballFly() {
        ResManager.playSound("catchFly.mp3");
        this.ballScript.animator.crossFade("Attack", 0.2, 0, 0);
        EventManager.event(EventType.StartCatch);
        Laya.stage.off(Laya.Event.MOUSE_DOWN, this, this.onClickScreen);
        Laya.stage.off(Laya.Event.MOUSE_UP, this, this.onClickScreen);
        Laya.timer.clear(this, this.onFrameLoop);
    }
    onBallStartCatch() {
        this.ball.active = true;
        this.ballFly();
    }
    onFrameLoop() {
        if (this.isClickBall) {
            this.progress += this.progressAdd;
            this.progress = this.progress < 1 ? this.progress : 1;
        } else {
            this.progress -= this.progressBack;
            this.progress = this.progress > 0 ? this.progress : 0;
        }
        EventManager.event(EventType.UpdateProgress, this.progress);
    }
    onDestroy() {
        super.onDestroy();
        EventManager.off(EventType.BallStartCatch, this, this.onBallStartCatch);
    }
}