import { ActorBase } from "../gframe-work/actor/ActorBase";
import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import { Node, Vector3 } from "../gframe-work/pathfinding/Geometry";
import { Pathfinding } from "../gframe-work/pathfinding/Pathfinding";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import Adventurer from "./Adventurer";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";

export default class Npc extends ActorBase {
    followTarget: Adventurer = null;
    isVisible: boolean = false;
    mesh: any = null;
    bornPoint: { position: number[], rotation: number[] } = null;
    warnRadius: number = 5;
    endTarget: Vector3 = new Vector3();
    startPos: Vector3 = new Vector3();
    endPos: Vector3 = new Vector3();
    tempVec3: Vector3 = new Vector3();
    lastNode: Node = null;
    pathNode: Vector3[] = null;
    walkDir: Laya.Vector3 = new Laya.Vector3();
    curTargetPoint: Laya.Vector3 = null;
    translation: Laya.Vector3 = new Laya.Vector3();
    moveSpeed: number = 3;
    frameMoveSpeed: number = 0;
    frameTime: number = 0;
    pathfinding: Pathfinding = null;
    zoneId: string = "";
    rotateSpeedSection: number[] = [10, 1000];
    isTurning: boolean = false;
    forwardDir: Laya.Vector3 = new Laya.Vector3();
    rotation: Laya.Vector3 = new Laya.Vector3();
    targetDir: Laya.Vector3 = new Laya.Vector3();
    pathBallList: Laya.MeshSprite3D[] = [];
    isAteOriginal: boolean = false;
    onAwake() {
        super.onAwake();
        let childNode = this.gameObject.getChildAt(0);
        for (let i = 0; i < childNode.numChildren; i++) {
            let child = childNode.getChildAt(i);
            if (child instanceof Laya.SkinnedMeshSprite3D) {
                this.mesh = child;
                break;
            }
        }
    }
    // drawPathBall() {
    //     if (this.pathNode && this.pathNode.length > 0) {
    //         for (let i = 0; i < this.pathNode.length; i++) {
    //             let pathNode = this.pathNode[i];
    //             let ball = Laya.Pool.getItem("pathBall");
    //             if (!ball) {
    //                 ball = ResManager.getResClone("Sphere.lh");
    //             }
    //             let vertice = new Laya.Vector3(pathNode.x, pathNode.y, pathNode.z);
    //             this.gameObject.parent.addChild(ball);
    //             ball.transform.position = vertice;
    //             this.pathBallList.push(ball);
    //         }
    //     } else {
    //         for (let i = 0; i < this.pathBallList.length; i++) {
    //             let pathball = this.pathBallList[i];
    //             pathball.removeSelf();
    //             Laya.Pool.recover("pathBall", pathball);
    //         }
    //         this.pathBallList.length = 0;
    //     }
    // }
    onEnable() {
        this.changeActorState("Stand", "", true);
    }
    onDestroy() {
        EventManager.off(EventType.TargetNodeChange, this, this.onTargetNodeChange);
    }
    initPathfinding(pathfinding: Pathfinding, zoneId: string) {
        this.pathfinding = pathfinding;
        this.zoneId = zoneId;
        LayaTool.cloneToVec3(this.gameObject.transform.position, this.tempVec3);
        let groupId = this.pathfinding.getGroup(this.zoneId, this.tempVec3, true);
        this.lastNode = this.pathfinding.getClosestNode(this.tempVec3, this.zoneId, groupId, true);
    }
    onUpdate() {
        if (GameDataManager.Instance.isGotoFightScene ||
            GameDataManager.Instance.isPause
        ) return;
        this.frameTime = Laya.timer.delta / 1000;
        this.frameMoveSpeed = this.moveSpeed * this.frameTime;
        if (this.followTarget) {
            if (this.curTargetPoint == null) {
                if (this.pathNode && this.pathNode.length > 0) {
                    let vec3 = this.pathNode.shift();
                    this.curTargetPoint = new Laya.Vector3();
                    LayaTool.cloneToLayaVec3(vec3, this.curTargetPoint);
                } else {
                    this.curTargetPoint = this.followTarget.gameObject.transform.position;
                }
                this.updateWalkDir();
                this.changeActorState("Run", "", true);
            }
            if (this.curTargetPoint) {
                let dx = 0, dz = 0;
                dx = this.curTargetPoint.x - this.gameObject.transform.position.x;
                dz = this.curTargetPoint.z - this.gameObject.transform.position.z;
                this.targetDir.x = dx;
                this.targetDir.z = dz;
                if (this.curTargetPoint == this.followTarget.gameObject.transform.position) {
                    this.updateWalkDir();
                }
                this.turn();
                if (dx * dx + dz * dz > 0.05 * 0.05) {
                    Laya.Vector3.scale(this.walkDir, this.frameMoveSpeed, this.translation);
                    this.gameObject.transform.translate(this.translation, false);
                    this.isAteOriginal = false;
                } else {
                    this.curTargetPoint = null;
                    this.changeActorState("Stand", "", true);
                }
            }
        }
    }
    turn() {
        this.gameObject.transform.getForward(this.forwardDir);
        this.forwardDir.y = 0;
        this.forwardDir.x *= -1;
        this.forwardDir.z *= -1;
        let diff = MathTool.getRotateRad(this.forwardDir, this.targetDir) * MathTool.rad2Deg;
        if (diff != 0) {
            this.isTurning = true;
            let rotateSpeed = this.rotateSpeedSection[0] + (this.rotateSpeedSection[1] - this.rotateSpeedSection[0]) * Math.abs(diff) / 180;
            let frameRotateSpeed = rotateSpeed * this.frameTime * Math.sign(diff);
            if ((frameRotateSpeed > 0 && frameRotateSpeed > diff) ||
                (frameRotateSpeed < 0 && frameRotateSpeed < diff)
            ) {
                frameRotateSpeed = diff;
            }
            this.rotation.y = frameRotateSpeed;
            this.gameObject.transform.rotate(this.rotation, false, false);
        } else {
            this.isTurning = false;
        }
    }
    updateWalkDir() {
        this.walkDir.x = this.curTargetPoint.x - this.gameObject.transform.position.x;
        this.walkDir.y = 0;
        this.walkDir.z = this.curTargetPoint.z - this.gameObject.transform.position.z;
        Laya.Vector3.normalize(this.walkDir, this.walkDir);
    }
    setVisibleByCamera(visible: boolean) {
        if (visible != this.isVisible) {
            this.isVisible = visible;
            if (visible) {
                this.gameObject.active = true;
            } else {
                this.gameObject.active = false;
                this.initPosAndRot();
            }
        }
    }
    enterFight() {
        this.gameObject.active = false;
        this.initPosAndRot();
        this.isVisible = true;
    }
    initPosAndRot() {
        if (!this.isAteOriginal) {
            this.isAteOriginal = true;
            this.gameObject.transform.position = LayaTool.createVector3FromArray(this.bornPoint.position);
            this.gameObject.transform.rotation = LayaTool.createQuaternionFromArray(this.bornPoint.rotation);
        }
        this.setFollowTarget(null);
    }
    setFollowTarget(target: Adventurer) {
        if (target) {
            if (target != this.followTarget) {
                this.followTarget = target;
                EventManager.on(EventType.TargetNodeChange, this, this.onTargetNodeChange);
                this.findPath();
            }
        } else {
            this.followTarget = null;
            EventManager.off(EventType.TargetNodeChange, this, this.onTargetNodeChange);
        }
    }
    onTargetNodeChange(target: Adventurer) {
        if (target == this.followTarget) {
            if (this.pathfinding) this.findPath();
        }
    }
    findPath() {
        this.curTargetPoint = null;
        LayaTool.cloneToVec3(this.gameObject.transform.position, this.startPos);
        let groupId = this.pathfinding.getGroup(this.zoneId, this.startPos, true);
        LayaTool.cloneToVec3(this.followTarget.gameObject.transform.position, this.endPos);
        this.pathNode = this.pathfinding.findPath(this.startPos, this.endPos, this.zoneId, groupId);
    }
}