import Scene3DBase from "../scene3D/Scene3DBase";
import ResManager from "./ResManager";
import StageManager from "./StageManager";

export default class SceneManager {
    private static _instance: SceneManager;
    public static get Instance(): SceneManager {
        if (!this._instance) this._instance = new SceneManager();
        return this._instance;
    }
    activeSceneMap: Map<string, Scene3DBase> = new Map();
    getScene3DLogic<T>(name: string): T {
        return this.activeSceneMap.get(name) as any;
    }
    closeScene3D(name: string, destroyAtClose = true, arg: any[] = []) {
        let sceneLogic = this.activeSceneMap.get(name);
        if (sceneLogic) {
            if (sceneLogic["onClosed"]) sceneLogic["onClosed"](...arg);
            sceneLogic.owner.removeSelf();
            if (destroyAtClose) {
                sceneLogic.destroy();
            }
            this.activeSceneMap.delete(name);
        }
    }
    openScene3D(name: string, cls: new () => Scene3DBase, arg: any[] = []) {
        let scene3d = ResManager.getRes<Laya.Scene3D>(name + ".ls");
        StageManager.GameSceneRoot.addChild(scene3d);
        let sceneLogic = scene3d.getComponent(cls);
        if (!sceneLogic) {
            sceneLogic = scene3d.addComponent(cls);
        }
        if (sceneLogic["onOpened"]) sceneLogic["onOpened"](...arg);
        this.activeSceneMap.set(name, sceneLogic);
    }
}