import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CardRuntime from "./CardRuntime";

export default class GetCardRuntime extends ui.pages.getCardUI {
    cardList: CardRuntime[] = [];
    dataList: CardData[] = [];
    rewardNum: number = 0;
    rewardType: number = 0;
    // 1:�״�ѡ��2��Ť���̵�3��ͼ��4���齱5��ǩ��6������7������8����ܵ���9������10:�ϳ�����11��������������12����������,13:ץ��,14:�ϳ�
    rewardFrom: number = 1;
    onAwake() {
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.setBtnVideoTag();
    }
    onOpened() {
        this.sureBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.doubleBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        ResManager.playSound("reward.mp3");
        AllPlatformAdMgr.Instance.showBannerAd();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 9, button_id: 10, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.sureBtn || e.target == this.doubleBtn) {
            if (this.rewardType == 1 || this.rewardType == 4 || this.rewardType == 5 || this.rewardType == 6) {
                let rewardNum = this.rewardNum;
                if (e.target == this.doubleBtn) {
                    rewardNum = this.rewardNum * 2;
                    AllPlatformAdMgr.Instance.sendVideoData = { page_id: 9, button_id: 10, ad_id: 2, ad_type: 1 };
                    TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
                    AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                        this.sendReward(rewardNum);
                        TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: rewardNum, get_type: this.rewardFrom, last_num: UserData.instance.gold });
                        this.close();
                    });
                } else {
                    this.sendReward(rewardNum);
                    TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: rewardNum, get_type: this.rewardFrom, last_num: UserData.instance.gold });
                    this.close();
                }
            } else {
                this.close();
            }
        }
    }
    updateBtnType(type: number) {
        if (type == 0) {
            //ֻ��ʾ������ȡ��ť
            this.doubleBtn.visible = false;
            this.sureBtn.visible = true;
            this.sureBtn.y = 112;
        } else if (type == 1) {
            //������ȡ��ť����˫����ȡ��ť����ʾ
            this.doubleBtn.visible = true;
            this.sureBtn.visible = true;
            this.sureBtn.y = 204;
            this.sureBtn.visible = false;
            Laya.timer.once(AllPlatformAdMgr.Instance.delayTime, this, () => {
                this.sureBtn.visible = true;
            });
        }
    }
    onFrameLoop() {
        if (!this.lightBg.visible) {
            return;
        }
        this.lightBg.rotation += 1;
        if (this.lightBg.rotation >= 360) {
            this.lightBg.rotation = this.lightBg.rotation % 360;
        }
    }
    onDestroy() {

    }
    setParam(type: number, reward: any, rewardFrom: any) {
        this.rewardType = type;
        this.rewardNum = reward;
        this.rewardFrom = rewardFrom;
        if (type == 1 || type == 3 || type == 4 || type == 5 || type == 6) {
            this.goldBg.visible = true;
            this.cardBox.visible = false;
            if (type == 1 || type == 4 || type == 5 || type == 6) {
                this.updateBtnType(1);
                if (type == 1) {
                    this.rewardImage.skin = "count/Ui_TwoGold.png";
                } else if (type == 4) {
                    this.rewardImage.skin = "newSystem/Ui_MonsterHeadChip.png";
                } else if (type == 5) {
                    this.rewardImage.skin = "spriteShop/Ui_TwoQuan.png";
                } else if (type == 6) {
                    this.doubleBtn.visible = false;
                    this.rewardImage.skin = "pay/Ui_Diamonds.png";
                }
                this.goldLabel.text = "X" + reward;
            } else {
                this.updateBtnType(0);
                this.goldLabel.text = "X1";
                if (reward == 1) {
                    this.rewardImage.skin = "main/Ui_Ball2.png";
                    if (UserData.instance.niuDanLeftTime < 4) {
                        UserData.instance.niuDanLeftTime++;
                        UserData.instance.onDataChange("niuDanLeftTime");
                    }
                } else {
                    this.rewardImage.skin = "main/Ui_Ball.png";
                    if (UserData.instance.niuDanSuperLeftTime < 10) {
                        UserData.instance.niuDanSuperLeftTime++;
                        UserData.instance.onDataChange("niuDanSuperLeftTime");
                    }
                }
            }
        } else {
            this.updateBtnType(0);
            this.goldBg.visible = false;
            this.cardBox.visible = true;
            for (let i = 0; i < reward.length; i++) {
                this.dataList.push(UserData.instance.addCard(reward[i], false, 1));
            }
            for (let i = 0; i < this.dataList.length; i++) {
                let row = Math.floor(i / 3);
                let col = i % 3;
                let data = this.dataList[i];
                let card = new CardRuntime();
                card.dataSource = data;
                this.cardBox.addChild(card)
                this.cardList.push(card);
                card.x = (card.width + 10) * col;
                card.y = (card.height + 10) * row;
                if (i == this.dataList.length - 1 || col == 2) {
                    let totalWidth = card.x + card.width;
                    let moveX = (this.cardBox.width - totalWidth) / 2;
                    for (let j = 0; j < col + 1; j++) {
                        this.cardList[row * 3 + j].x += moveX;
                    }
                }
            }
            let totalHeight = this.cardList[this.cardList.length - 1].y + this.cardList[this.cardList.length - 1].height;
            let moveY = (this.cardBox.height - totalHeight) / 2;
            for (let i = 0; i < this.cardList.length; i++) {
                this.cardList[i].y += moveY;
            }
            UserData.instance.isFirstSelect = 1;
            UserData.instance.onDataChange("isFirstSelect");
            EventManager.event(EventType.ChangeFightList, [[this.dataList[0]], true, 0]);
        }
    }
    sendReward(rewardNum: number) {
        let rewardType = "";
        if (this.rewardType == 1) {
            rewardType = "gold";
        } else if (this.rewardType == 4) {
            rewardType = "chip";
        } else if (this.rewardType == 5) {
            rewardType = "fightTicket";
        } else if (this.rewardType == 6) {
            rewardType = "diamond";
        }
        GameDataManager.Instance.receiveReward([{ type: rewardType, num: rewardNum }]);
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}