    public static getGameSceneResList() {
        let curStageData = GameDataManager.Instance.curMapData;
        let resList = ["Player1.lh", "Picture.lh", "Box.lh"];
        if (curStageData.mapIndex == 2) {
            resList.push("Foot.lh");
        }
        if (GameDataManager.Instance.isCream) {
            resList.push(`Scene${curStageData.mapIndex + 1}_1.ls`);
        } else {
            resList.push(`Scene${curStageData.mapIndex + 1}.ls`);
        }
        resList.push(`ScenePoint${curStageData.mapIndex + 1}.json`);
        resList.push(`SceneNav${curStageData.mapIndex + 1}.json`);
        for (let i = 0; i < GameDataManager.Instance.mapMonsterIdList.length; i++) {
            let list = GameDataManager.Instance.mapMonsterIdList[i];
            let catchId = list[list.length - 1];
            let monsterName = `Enemy${catchId}.lh`;
            if (resList.indexOf(monsterName) == -1) {
                resList.push(monsterName);
            }
        }
        let bossName = `Enemy${curStageData.bossStat.id}.lh`;
        if (resList.indexOf(bossName)) resList.push(bossName);
        let treasureMonster = `Enemy10.lh`;
        if (resList.indexOf(treasureMonster)) resList.push(treasureMonster);
        return resList;
    }

