    pickLowestCardCell() {
        let cardBag = <CardBagRuntime>(<DownPanelRuntime>this.downPanel).getPage<ComposeRuntime>(0).cardBag;
        let minFightValue = 100000000;
        let pageIndex = -1;
        let cellIndex = -1;
        for (let i = 0; i < cardBag.monsterList.array.length; i++) {
            for (let j = 0; j < cardBag.monsterList.array[i].length; j++) {
                let data = cardBag.monsterList.array[i][j];
                let fightValue = data.hp + data.atk;
                if (fightValue < minFightValue) {
                    pageIndex = i;
                    cellIndex = j;
                    minFightValue = fightValue;
                }
            }
        }
        if (pageIndex == -1) pageIndex = 0;
        if (cellIndex == -1) cellIndex = 0;
        return ((<any>cardBag.monsterList.getCell(pageIndex)) as CardPageRuntime).cardList.getCell(cellIndex);
    }
