import { GameDataManager } from "../gamelogic/GameDataManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class HandBookDetailRuntime extends ui.pages.handBookDetailUI {
    monsterIdMap: Map<number, number[]> = new Map();
    pageData: any[] = [];
    curLevel = 0;
    curPageIndex = 0;
    maxPageIndex = 0;
    isClose = false;
    monsterData: any;
    monsterId: number;
    isPullShare = false;
    isShareOut = false;
    onAwake() {
        this.nameLabel.filters = [CatchRuntime.blackColorFilter];
        this.atkLabel.filters = [CatchRuntime.blackColorFilter];
        this.hpLabel.filters = [CatchRuntime.blackColorFilter];
        this.appearLabel.filters = [CatchRuntime.blackColorFilter];
        this.descLabel.filters = [CatchRuntime.blackColorFilter];
        this.leftBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.rightBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.shareBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        Laya.Tween.to(this.middleBox, { x: 0 }, 400, Laya.Ease.sineOut);
        if (typeof wx != "undefined") {
            (wx as any).onShow(this.onShow.bind(this));
            (wx as any).onHide(this.onHide.bind(this));
        }
        this.shareTimeLabel.text = `(${UserData.instance.shareHandBookTime}/3)`;
    }
    onOpened() {
        AllPlatformAdMgr.Instance.showBannerAd();
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
    }
    setParam(monsterId: number) {
        let page = 1;
        for (const key in GameDataManager.Instance.monsterData) {
            if (!this.monsterIdMap.has(page)) {
                this.monsterIdMap.set(page, []);
            }
            this.monsterIdMap.get(page).push(Number(key));
            if (this.monsterIdMap.get(page).length >= 12) page++;
        }
        for (const iterator of this.monsterIdMap) {
            iterator[1].sort((a, b) => {
                return a - b;
            });
        }
        this.initTabList(this.monsterIdMap.keys());
        let level = this.getLevel(monsterId);
        let pageIndex = this.monsterIdMap.get(level).indexOf(monsterId);
        this.initPage(level, pageIndex);
    }
    initTabList(iterator: IterableIterator<number>) {
        let levelList = [];
        for (const iter of iterator) {
            levelList.push(iter);
        }
        levelList.sort((a, b) => {
            return a - b;
        });
        let listData = [];
        for (let i = 0; i < levelList.length; i++) {
            listData.push({ levelLabel: { text: levelList[i] + "" } });
        }
        this.tabList.repeatY = listData.length;
        this.tabList.array = listData;
        this.tabList.refresh();
        for (let i = 0; i < this.tabList.length; i++) {
            let cell = this.tabList.getCell(i);
            let bg = cell.getChildByName("tabBg");
            bg.on(Laya.Event.CLICK, this, this.onTabClick, [i]);
        }
    }
    getLevel(monsterId: number) {
        for (const iterator of this.monsterIdMap) {
            if (iterator[1].indexOf(monsterId) != -1) {
                return iterator[0];
            }
        }
    }
    initPage(level: number, pageIndex: number) {
        if (this.curLevel != level) {
            if (this.curLevel != 0) {
                this.openCell(this.curLevel - 1, false)
            }
            this.curLevel = level;
            this.openCell(this.curLevel - 1, true)
            this.maxPageIndex = this.monsterIdMap.get(this.curLevel).length;
            this.setPageData(pageIndex);
        }
    }
    openCell(index: number, isSelect: boolean) {
        let cell = this.tabList.getCell(index);
        let tabBg = cell.getChildByName("tabBg") as Laya.Image;
        tabBg.skin = isSelect ? "handBook/Btn_LabelSlect.png" : "handBook/Btn_Label.png";
        if (isSelect) {
            Laya.Tween.to(tabBg, { x: 18 }, 100, Laya.Ease.sineOut);
        } else {
            Laya.Tween.to(tabBg, { x: 0 }, 100, Laya.Ease.sineOut);
        }
    }
    updateArrow() {
        if (this.maxPageIndex <= 1) {
            this.leftBtn.alpha = 0.5;
            this.rightBtn.alpha = 0.5;
        } else {
            if (this.curPageIndex == 0) {
                this.leftBtn.alpha = 0.5;
                this.rightBtn.alpha = 1;
            } else if (this.curPageIndex == this.maxPageIndex - 1) {
                this.rightBtn.alpha = 0.5;
                this.leftBtn.alpha = 1;
            } else {
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;
            }
        }
    }
    setPageData(pageIndex: number) {
        this.curPageIndex = pageIndex;
        this.monsterId = this.monsterIdMap.get(this.curLevel)[pageIndex];
        this.monsterImage.skin = `monster/Ui_Monster${this.monsterId}.png`;
        this.monsterData = GameDataManager.Instance.monsterData[this.monsterId];
        this.nameLabel.text = this.monsterData.name;
        this.hpLabel.text = this.monsterData.hp + "";
        this.atkLabel.text = this.monsterData.atk + "";
        this.descLabel.text = this.monsterData.describe || "";
        this.appearLabel.text = this.getAppear(this.monsterId).join(",");
        this.updateArrow();
    }
    getAppear(monsterId: number) {
        let signMonsterIdList = [126, 128];
        let cardData = GameDataManager.Instance.getOneCardData(monsterId);
        if (GameDataManager.Instance.isSuperEva(cardData)) {
            return ["究极进化"];
        } else if (signMonsterIdList.indexOf(monsterId) != -1) {
            return ["签到"];
        } else {
            //其他途径
            let appear = [];
            for (const key in GameDataManager.Instance.stageConfig) {
                let mapData = GameDataManager.Instance.stageConfig[key];
                let list = [...mapData.list, mapData.bossStat.id];
                if (list.indexOf(monsterId) != -1) {
                    appear.push(mapData.name);
                    break;
                }
            }
            for (const key in GameDataManager.Instance.creamStageConfig) {
                let mapData = GameDataManager.Instance.creamStageConfig[key];
                let list = [...mapData.list, mapData.bossStat.id];
                if (list.indexOf(monsterId) != -1) {
                    appear.push(mapData.name);
                    break;
                }
            }
            appear.push("合成");
            appear.push("扭蛋商店");
            return appear;
        }
    }
    onTabClick(index: number) {
        this.initPage(index + 1, 0);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.leftBtn) {
            if (this.curPageIndex > 0) {
                this.curPageIndex--;
                this.setPageData(this.curPageIndex);
            }
        } else if (e.target == this.rightBtn) {
            if (this.curPageIndex < this.maxPageIndex - 1) {
                this.curPageIndex++;
                this.setPageData(this.curPageIndex);
            }
        } else if (e.target == this.closeBtn) {
            if (this.isClose) return;
            this.isClose = true;
            Laya.Tween.to(this.middleBox, { x: -720 }, 400, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
                this.close();
            }));
        } else if (e.target == this.shareBtn) {
            if (UserData.instance.shareHandBookTime >= 3) {
                return;
            }
            if (typeof wx != "undefined") {
                let shareImageUrl = `https://demo.xxtch.com/Pokemon/res/wx/ShareImages/Ui_MonsterHead${this.monsterId}.png`;
                (wx as any).shareAppMessage({ title: this.monsterData.describe, imageUrl: shareImageUrl });
                this.isPullShare = true;
            }
        }
    }
    onDestroy() {
        if (typeof wx != "undefined") {
            (wx as any).offShow(this.onShow.bind(this));
            (wx as any).offHide(this.onHide.bind(this));
        }
    }
    onShow() {
        console.log("HandBookOnShow");
        if (this.isShareOut) {
            this.isShareOut = false;
            this.onShareSuccess();
        }
    }
    onHide() {
        console.log("HandBookOnHide");
        if (this.isPullShare) this.isShareOut = true;
        this.isPullShare = false;
    }
    onShareSuccess() {
        UserData.instance.shareHandBookTime++;
        UserData.instance.onDataChange("shareHandBookTime");
        this.shareTimeLabel.text = `(${UserData.instance.shareHandBookTime}/3)`;
        GameDataManager.Instance.receiveReward([{ type: "diamond", num: 5 }]);
    }
}