import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";
import TryGamePageRuntime from "./TryGamePageRuntime";

export default class TryGameCellRuntime extends ui.pages.tryGameCellUI {
    data: any;
    public set dataSource(data: any) {
        if (!data) return;
        this.data = data;
        this.gameIcon.skin = data.appIcon;
        this.gameName.text = data.appName;
        this.operateBtn.skin = "pay/Btn_PlayGet2.png";
        let tryOutTime = TryGamePageRuntime.tryOutTime(this.data.appId);
        if (tryOutTime > 0) {
            let playTime = Date.now() - tryOutTime;
            console.log(Date.now(), tryOutTime, playTime);
            Laya.timer.frameOnce(2, this, () => {
                console.log("延迟时间", new Date().toString());
            })
            if (playTime >= 60000) {
                this.operateBtn.skin = "pay/Btn_PlayGet.png";
            }
        }
    }
    onAwake(): void {
        this.operateBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.moneyLabel.filters = [CatchRuntime.blackColorFilter];
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.operateBtn) {
            let type = this.operateBtn.skin == "pay/Btn_PlayGet2.png" ? "try" : "receive";
            EventManager.event(EventType.ClickGameCell, [type, this.data]);
        }
    }
}