import AnimatorStateScript from "../gframe-work/actor/AnimatorStateScript";
import LayaTool from "../gframe-work/tool/LayaTool";
import { GameDataManager } from "./GameDataManager";

export default class CatchBallScript extends Laya.Script3D {
    animator: Laya.Animator = null;
    gameObject: Laya.Sprite3D = null;
    catchSuccessHandler: Laya.Handler = null;
    catchEndHandler: Laya.Handler = null;
    rollEffect: Laya.ShuriKenParticle3D = null;
    onAwake() {
        this.gameObject = this.owner as Laya.Sprite3D;
        this.rollEffect = LayaTool.findNodeByUrl(this.gameObject, "Bone001/Effect_Around");
        this.rollEffect.active = false;
        this.animator = this.gameObject.getComponent(Laya.Animator);
        let state = this.animator.getControllerLayer(0).getAnimatorState("Attack");
        let script = state.addScript(AnimatorStateScript) as AnimatorStateScript;
        script.name = "Attack";
        script.stateExitHandler = Laya.Handler.create(this, this.onStateExit, null, false);
    }
    onStateExit() {
        if (this.catchEndHandler) {
            this.catchEndHandler.run();
        }
    }
    onEnable() {
        this.animator.play("Stand", 0, 0);
    }
    Attack_Ani() {
        if (this.catchSuccessHandler) {
            if (GameDataManager.Instance.isCatch)
                this.catchSuccessHandler.run();
        }
    }
}