import CardData from "../gamelogic/CardData";
import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CardRuntime from "./CardRuntime";
import FightCellRuntime from "./FightCellRuntime";
import FightDotRuntime from "./FightDotRuntime";
import QteRuntime from "./QteRuntime";

export default class FightRuntime extends ui.pages.fightUI {
    fightMonsterCardList: CardData[] = [];
    onAwake() {
        EventManager.on(EventType.UpdateFightData, this, this.onUpdateFightData);
        EventManager.on(EventType.InitFightCell, this, this.onInitFightCell);
        EventManager.on(EventType.ChangeQteState, this, this.onChangeQteState);
        EventManager.on(EventType.QteEnd, this, this.onQteEnd);
        EventManager.on(EventType.EnableMustKill, this, this.onEnableMustKill);
        EventManager.on(EventType.FighterDeath, this, this.onFighterDeath);
        EventManager.on(EventType.CloseAutoFight, this, this.onCloseAutoFight);
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.escapeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.autoFightBg.on(Laya.Event.CLICK, this, this.onBtnClick);
        (<QteRuntime>this.qtePage).visible = false;
        window["FightScene2"] = this;
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.escapeBtn) {
            if (!GameDataManager.Instance.isGameEnd && !GameDataManager.Instance.allEmptyBlood) {
                LayaTool.openScene("pages/escape.scene", false, true, Laya.Handler.create(this, (reason: string) => {
                    if (reason == "yes") {
                        GameDataManager.Instance.escapeTimeLeft--;
                        if (GameDataManager.Instance.fightType == FightType.Normal || GameDataManager.Instance.fightType == FightType.Cream) {
                            EventManager.event(EventType.BacktoScene, "search");
                        } else if (GameDataManager.Instance.fightType == FightType.Tower) {
                            EventManager.event(EventType.BacktoScene, "challenge");
                        } else if (GameDataManager.Instance.fightType == FightType.Pvp) {
                            EventManager.event(EventType.BacktoScene, "pvp");
                        }
                    }
                }), null);
            }
        } else if (e.target == this.autoFightBg) {
            if (UserData.instance.fightTicket <= 0 && !UserData.instance.freeAutoFight) return;
            this.gou.visible = !this.gou.visible;
            GameDataManager.Instance.autoFight = this.gou.visible;
            EventManager.event(EventType.ChangeAutoFight);
        }
    }
    onChangeQteState(state: number) {
        if (state == 0) {
            //开始
            (<QteRuntime>this.qtePage).visible = true;
            (<QteRuntime>this.qtePage).qteStart();
        } else if (state == 1) {
            //观赏开始
            (<QteRuntime>this.qtePage).visible = false;
        } else if (state == 2) {
            //观赏结束
            (<QteRuntime>this.qtePage).visible = true;
        } else if (state == 3) {
            //结束
            (<QteRuntime>this.qtePage).visible = false;
        }
    }
    onQteEnd() {
        (<QteRuntime>this.qtePage).qteEnd();
    }
    onUpdateFightData(side: string, index: number) {
        if (side == "self") {
            let dot: FightDotRuntime = (<FightCellRuntime>this.fightCell).atkTimeList.getCell(index) as any;
            dot.dataSource = GameDataManager.Instance.monsterAtkTimeList[index];
            if (GameDataManager.Instance.monsterAtkTimeList[index] == 3) {
                GuideManager.startOutTask("介绍必杀技");
            }
            let card: CardRuntime = (<FightCellRuntime>this.fightCell).monsterList.getCell(index) as any;
            card.dataSource = card.data;
        } else if (side == "enemy") {
            let card: CardRuntime = this.enemyCardList.getCell(index) as any;
            card.dataSource = card.data;
        }
    }
    onInitFightCell() {
        this.fightMonsterCardList.length = 0;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let card = UserData.instance.fightCardList[i];
            if (card) {
                this.fightMonsterCardList.push(card);
            }
        }
        if (this.fightMonsterCardList.length == 0) {
            (<FightCellRuntime>this.fightCell).visible = false;
        } else {
            (<FightCellRuntime>this.fightCell).visible = true;
            (<FightCellRuntime>this.fightCell).width = 85 + this.fightMonsterCardList.length * 150;
            (<FightCellRuntime>this.fightCell).x = (Laya.stage.width - (<FightCellRuntime>this.fightCell).width) / 2;
            (<FightCellRuntime>this.fightCell).monsterList.array = this.fightMonsterCardList;
            (<FightCellRuntime>this.fightCell).monsterList.refresh();
            (<FightCellRuntime>this.fightCell).atkTimeList.array = GameDataManager.Instance.monsterAtkTimeList;
            (<FightCellRuntime>this.fightCell).atkTimeList.refresh();
        }
        if (GameDataManager.Instance.enemyMonsterCardList.length == 0) {
            this.enemyCardBg.visible = false;
        } else {
            this.enemyCardBg.visible = true;
            this.enemyCardBg.width = 83 + GameDataManager.Instance.enemyMonsterCardList.length * 150;
            if (GameDataManager.Instance.isBoss) {
                GameDataManager.Instance.enemyMonsterCardList[0].hp = GameDataManager.Instance.curMapData.bossStat.hp;
                GameDataManager.Instance.enemyMonsterCardList[0].atk = GameDataManager.Instance.curMapData.bossStat.atk;
            }
            this.enemyCardList.array = GameDataManager.Instance.enemyMonsterCardList;
            this.enemyCardList.refresh();
        }
    }
    onEnableMustKill(enabled: boolean) {
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let card = UserData.instance.fightCardList[i];
            if (card) {
                let dotCell: FightDotRuntime = (<FightCellRuntime>this.fightCell).atkTimeList.getCell(i) as any;
                if (card.curHp > 0 && dotCell.mustKillBtn.visible) {
                    dotCell.mustKillBtn.disabled = enabled;
                }
            }
        }
    }
    onFighterDeath(fighterSide: number, fighterIndex: number) {
        if (fighterSide == 1) {
            let dotCell: FightDotRuntime = (<FightCellRuntime>this.fightCell).atkTimeList.getCell(fighterIndex) as any;
            if (dotCell.mustKillBtn.visible) {
                dotCell.mustKillBtn.disabled = true;
            }
        }
    }
    setVisible(visible: boolean) {
        this.visible = visible;
        if (this.visible) {
            AllPlatformAdMgr.Instance.showBannerAd();
            if (typeof wx != "undefined") {
                let bannerHeight = AllPlatformAdMgr.Instance.banner_Height * wx.getSystemInfoSync()["pixelRatio"];
                this.fightCell.y = -232 - bannerHeight;
                this.autoFightBg.y = -262 - bannerHeight;
            }
            this.setAutoFight();
        } else {
            AllPlatformAdMgr.Instance.hideBannerAd();
            this.fightCell.y = -400;
            this.autoFightBg.y = -430;
        }
    }
    onDestroy() {
        EventManager.off(EventType.UpdateFightData, this, this.onUpdateFightData);
        EventManager.off(EventType.InitFightCell, this, this.onInitFightCell);
        EventManager.off(EventType.ChangeQteState, this, this.onChangeQteState);
        EventManager.off(EventType.QteEnd, this, this.onQteEnd);
        EventManager.off(EventType.EnableMustKill, this, this.onEnableMustKill);
        EventManager.off(EventType.FighterDeath, this, this.onFighterDeath);
        EventManager.off(EventType.CloseAutoFight, this, this.onCloseAutoFight);
    }
    setAutoFight() {
        if (UserData.instance.gameRank >= 2) {
            this.autoFightBg.visible = true;
            if (GameDataManager.Instance.autoFight && (UserData.instance.fightTicket > 0 || UserData.instance.freeAutoFight)) {
                this.gou.visible = true;
            } else {
                this.gou.visible = false;
            }
        } else {
            this.autoFightBg.visible = false;
        }
    }
    onCloseAutoFight() {
        this.gou.visible = false;
    }
}