import CardData from "../gamelogic/CardData";
import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class CountRuntime extends ui.pages.countUI {
    public static colorFilter: Laya.ColorFilter = new Laya.ColorFilter([
        226 / 256, 0, 0, 0, 0, //R
        0, 162 / 256, 0, 0, 0, //G
        0, 0, 0, 0, 0, //B
        0, 0, 0, 1, 0, //A
    ]);
    rewardDataList: { type: string, num: number }[] = [];
    rewardCellList: Laya.Sprite[];
    isWin = false;
    onAwake() {
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.stageLabel.filters = [CountRuntime.colorFilter];
        this.videoReceiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.backBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goComposeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.goEggShopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.setWin(GameDataManager.Instance.isGameWin);
        this.setBtnVideoTag();
    }
    setWin(isWin: boolean) {
        this.isWin = isWin;
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 4, button_id: 4, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        AllPlatformAdMgr.Instance.showBannerAd();
        this.setBtn(isWin);
        if (isWin) {
            ResManager.playSound("success.mp3");
        } else {
            ResManager.playSound("fail.mp3");
        }
        if (isWin) {
            if (GameDataManager.Instance.fightType == FightType.Normal) {
                this.updateGameRank();
            } else if (GameDataManager.Instance.fightType == FightType.Cream) {
                this.updateEliteGameRank();
            } else if (GameDataManager.Instance.fightType == FightType.Tower) {
                this.updateChallengeLevel();
                GameDataManager.Instance.uploadChallengeRankScore();
            }
            UserData.instance.finishTask(1, 1, 1);
        }
        this.setPlayStage();
        this.setReward(isWin);
        if (GameDataManager.Instance.fightType == FightType.Normal) {
            this.setStageCollect(false);
        } else if (GameDataManager.Instance.fightType == FightType.Cream) {
            this.setStageCollect(true);
        } else if (GameDataManager.Instance.fightType == FightType.Tower) {
            this.hideCollectMonsterList();
        }
    }
    setBtn(isWin: boolean) {
        this.bg1.skin = isWin ? "count/Ui_JieSuanBg2.png" : "count/Ui_JieSuanBg5.png";
        this.bg2.skin = isWin ? "count/Ui_JieSuanBg3.png" : "count/Ui_JieSuanBg6.png";
        this.strongTip.visible = !isWin;
        this.goComposeBtn.visible = !isWin;
        this.goEggShopBtn.visible = !isWin;
        this.videoReceiveBtn.visible = isWin;
        this.winTitleIcon.visible = isWin;
        this.lostTitleIcon.visible = !isWin;
        if (isWin) {
            this.backBtn.x = 513;
            this.backBtn.y = 870;
        } else {
            this.backBtn.x = 360;
            this.backBtn.y = 991;
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.backBtn) {
            this.onBack(false);
        } else if (e.target == this.videoReceiveBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 4, button_id: 4, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-e3e88638c0c5f30f", () => {
                this.onBack(true);
            });
        } else if (e.target == this.goComposeBtn) {
            this.onBack(false);
            EventManager.event(EventType.GotoScene, "compose");
        } else if (e.target == this.goEggShopBtn) {
            this.onBack(false);
            EventManager.event(EventType.GotoScene, "eggshop");
        }
    }
    setStageCollect(isCream: boolean) {
        this.cardList.hScrollBarSkin = null;
        let collect = UserData.instance.stageCollectObj[GameDataManager.Instance.enterMapIndex];
        if (isCream) {
            collect = UserData.instance.creamStageCollectObj[GameDataManager.Instance.enterMapIndex];
        }
        let cardDataList: CardData[] = [];
        for (let i = 0; i < collect.length; i++) {
            let monsterId = collect[i];
            let cardData = GameDataManager.Instance.getOneCardData(monsterId);
            cardDataList.push(cardData);
        }
        this.cardList.array = cardDataList;
        this.cardList.refresh();
        if (isCream) {
            UserData.instance.creamStageCollectObj[GameDataManager.Instance.enterMapIndex] = [];
            UserData.instance.onDataChange("creamStageCollectObj");
        } else {
            UserData.instance.stageCollectObj[GameDataManager.Instance.enterMapIndex] = [];
            UserData.instance.onDataChange("stageCollectObj");
        }
        if (cardDataList.length == 0) this.hideCollectMonsterList();
    }
    updateGameRank() {
        if (UserData.instance.gameRank == GameDataManager.Instance.enterMapIndex) {
            if (UserData.instance.gameRank < GameDataManager.Instance.stageMaxIndex) {
                UserData.instance.gameRank++;
                UserData.instance.onDataChange("gameRank");
            } else {
                UserData.instance.gameAllPass = true;
                UserData.instance.onDataChange("gameAllPass");
            }
            UserData.instance.finishTask(0, 1, 1);
        }
    }
    updateEliteGameRank() {
        if (UserData.instance.eliteGameRank == GameDataManager.Instance.enterMapIndex) {
            if (UserData.instance.eliteGameRank < GameDataManager.Instance.creamStageMaxIndex) {
                UserData.instance.eliteGameRank++;
                UserData.instance.onDataChange("eliteGameRank");
            } else {
                UserData.instance.eliteGameAllPass = true;
                UserData.instance.onDataChange("eliteGameAllPass");
            }
            UserData.instance.finishTask(0, 1, 1);
        }
    }
    updateChallengeLevel() {
        let bigLevel = UserData.instance.challengeLevel[0];
        let smallLevel = UserData.instance.challengeLevel[1];
        if (smallLevel + 1 > GameDataManager.Instance.challengeMapData[bigLevel - 1].length) {
            if (bigLevel + 1 > GameDataManager.Instance.challengeMapData.length) {
                UserData.instance.allPassChallenge = true;
                UserData.instance.onDataChange("allPassChallenge");
            } else {
                UserData.instance.challengeLevel[0] = bigLevel + 1;
                UserData.instance.challengeLevel[1] = 1;
            }
        } else {
            UserData.instance.challengeLevel[1] = smallLevel + 1;
        }
        UserData.instance.onDataChange("challengeLevel");
    }
    setPlayStage() {
        if (GameDataManager.Instance.fightType == FightType.Normal) {
            this.stageLabel.text = (GameDataManager.Instance.enterMapIndex + 1) + "";
        } else if (GameDataManager.Instance.fightType == FightType.Cream) {
            this.stageLabel.text = (GameDataManager.Instance.enterMapIndex + 1) + "";
        } else if (GameDataManager.Instance.fightType == FightType.Tower) {
            this.stageLabel.text = `${UserData.instance.challengeLevel[0]}-${UserData.instance.challengeLevel[1]}`;
        }
    }
    setReward(isWin: boolean) {
        if (isWin) {
            if (GameDataManager.Instance.fightType == FightType.Normal) {
                let passReward = GameDataManager.Instance.passRewardConfig[GameDataManager.Instance.enterMapIndex].passReward;
                let rewardList = GameDataManager.Instance.parseRewardData(passReward);
                this.rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Normal);
            } else if (GameDataManager.Instance.fightType == FightType.Cream) {
                let passReward = GameDataManager.Instance.eliteStageConfig[GameDataManager.Instance.enterMapIndex].passReward;
                let rewardList = GameDataManager.Instance.parseRewardData(passReward);
                this.rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Cream);
            } else if (GameDataManager.Instance.fightType == FightType.Tower) {
                let data = GameDataManager.Instance.challengeMapData[UserData.instance.challengeLevel[0] - 1][UserData.instance.challengeLevel[1] - 1];
                let passReward = GameDataManager.Instance.parseRewardData(data.passReward);
                let firstPassReward = GameDataManager.Instance.parseRewardData(data.firstPassReward);
                let rewardList = [...passReward, ...firstPassReward];
                this.rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Tower);
            }
        } else {
            this.rewardDataList.push({ type: "gold", num: 10 });
        }
        let rewardUiDataList = GameDataManager.Instance.getRewardUiDataList(this.rewardDataList);
        this.rewardList.array = rewardUiDataList;
        this.rewardList.refresh();
        this.rewardList.repeatX = rewardUiDataList.length;
        for (let i = 0; i < this.rewardList.length; i++) {
            let cell = this.rewardList.getCell(i) as any;
            let label: Laya.Label = cell.getChildByName("label");
            label.filters = [CatchRuntime.blackColorFilter];
        }
    }
    onBack(isDouble: boolean) {
        if (isDouble) {
            for (let i = 0; i < this.rewardDataList.length; i++) {
                if (this.rewardDataList[i].type == "gold" ||
                    this.rewardDataList[i].type == "badge" ||
                    this.rewardDataList[i].type == "chip" ||
                    this.rewardDataList[i].type == "fightTicket") {
                    this.rewardDataList[i].num *= 2;
                }
            }
        }
        GameDataManager.Instance.receiveReward(this.rewardDataList);
        TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: this.rewardDataList[0].num, get_type: 9, last_num: UserData.instance.gold });
        AllPlatformAdMgr.Instance.hideBannerAd();
        if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
        this.close();
        if (this.isWin) this.countPopEggMachine();
    }
    countPopEggMachine() {
        if (GameDataManager.Instance.fightType == FightType.Normal ||
            GameDataManager.Instance.fightType == FightType.Cream
        ) {
            if (UserData.instance.countPopEggLeftTime > 0) {
                UserData.instance.countPopEggLeftTime--;
                UserData.instance.onDataChange("countPopEggLeftTime");
                LayaTool.openScene("pages/superEggMachine.scene", false, true, null, [true, false]);
            }
        }
    }
    hideCollectMonsterList() {
        this.catchMonsterTitle.visible = false;
        this.cardList.visible = false;
        this.rewardTitle.y = 517;
        this.rewardList.y = 569;
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}