export default class TimeTool {
    public static dateFormat(fmt: string, date: Date) {
        let ret: RegExpExecArray;
        const opt = {
            "y+": date.getFullYear().toString(),        // 年
            "m+": (date.getMonth() + 1).toString(),     // 月
            "d+": date.getDate().toString(),            // 日
            "H+": date.getHours().toString(),           // 时
            "M+": date.getMinutes().toString(),         // 分
            "S+": date.getSeconds().toString()          // 秒
            // 有其他格式化字符需求可以继续添加，必须转化成字符串
        };
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
            };
        };
        return fmt;
    }
    public static isSameDay(first: Date, second: Date) {
        return first.toDateString() == second.toDateString();
    }
    public static convertTimeFormat(timems: number | string) {
        let str = '';
        const time = typeof timems == "number" ? timems : parseInt(timems);
        const hour = Math.floor(time / (3600 * 1000));
        const hourFormat = hour > 9 ? hour : ('0' + hour);
        const minute = Math.floor((time - hour * 3600 * 1000) / (60 * 1000));
        const minuteFormat = minute > 9 ? minute : ('0' + minute);
        const second = Math.floor(((time - hour * 3600 * 1000) - minute * 60 * 1000) / 1000);
        const secondFormat = second > 9 ? second : ('0' + second);
        str += hourFormat + ":" + minuteFormat + ":" + secondFormat;
        return str;
    }
    public static uuid(len: number, radix: number) {
        let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        let uuid: string[] = [], i: number;
        radix = radix || chars.length;
        if (len) {
            // Compact form
            for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
        } else {
            // rfc4122, version 4 form
            let r: number;

            // rfc4122 requires these characters
            uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
            uuid[14] = '4';
            // Fill in random data. At i==19 set the high bits of clock sequence as
            // per rfc4122, sec. 4.1.5
            for (i = 0; i < 36; i++) {
                if (!uuid[i]) {
                    r = 0 | Math.random() * 16;
                    uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
                }
            }
        }
        return uuid.join('');
    }
    public static generateUUID() {
        let d = new Date().getTime();
        let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
            let r = (d + Math.random() * 16) % 16 | 0;
            d = Math.floor(d / 16);
            return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
        });
        return uuid;
    }
    public static dayDiff(time1: number, time2: number) {
        let day1 = Math.floor(time1 / (24 * 3600 * 1000));
        let day2 = Math.floor(time2 / (24 * 3600 * 1000));
        return day2 - day1;
    }
    public static getYearMonthDay(year: number, month: number) {
        let dayList = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
        if (this.isLeapYear(year)) {
            dayList[1] = 29;
        }
        return dayList[month - 1];
    }
    public static isLeapYear(year: number) {
        return (year % 4 == 0) && (year % 100 != 0 || year % 400 == 0);
    }
    public static DateDiff(start: number, end: number) {
        let fmtStart = this.dateFormat("yyyy-mm-dd", new Date(start));
        let fmtEnd = this.dateFormat("yyyy-mm-dd", new Date(end));
        let newStart: any = new Date(fmtStart);
        let newEnd: any = new Date(fmtEnd);
        let days = 0;
        if (newEnd > newStart) {
            days = parseInt((Math.abs(newEnd - newStart) / 1000 / 60 / 60 / 24) + "");
        }
        return days;
    }
    public static isDuringDate(beginDateStr: string, endDateStr: string) {
        let curDate = new Date();
        let beginDate = new Date(beginDateStr);
        let endDate = new Date(endDateStr);
        if (curDate >= beginDate && curDate <= endDate) {
            return true;
        } else {
            return false;
        }
    }
}