import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";
import CardBagRuntime from "./CardBagRuntime";
import CardRuntime from "./CardRuntime";
import MoneyUiRuntime from "./MoneyUiRuntime";

export default class StarRuntime extends ui.pages.starUI {
    selectCard: CardRuntime = null;
    cloneCard: CardRuntime = null;
    /**0:空白位置1：上面2：升星卡位置3：第一张卡位置4：第二张卡位置5：第三张卡位置 */
    posType: number = 0;
    selectFrom: number = 0;
    stopClick = false;
    isMove: boolean = false;
    downPoint: Laya.Point = new Laya.Point();
    selectCardDataList: CardData[] = [null, null, null, null];
    goldCost: number = 0;
    fragmentCost: number = 0;
    upgradeParam: number[] = [];
    expNeed: number = 0;
    //指定id怪物进行升级的话一个怪物算1exp
    expAdd: number = 0;
    cardStateList: any[] = [];
    upgrageConfigData: any;
    upgradeCondition: number = 0;
    canSelectList: number[];
    expOverFlow = 0;
    nextData: { hp: number, atk: number };
    onAwake() {
        EventManager.on(EventType.SelectCard, this, this.onSelectPageCard);
        (<CardBagRuntime>this.cardBag).isShowStoreEgg = false;
        (<CardBagRuntime>this.cardBag).exceptFunc = this.exceptFunc.bind(this);
        (<CardBagRuntime>this.cardBag).canSelectFunc = this.canSelectFunc.bind(this);
        (<CardBagRuntime>this.cardBag).initMonsterList();

        this.on(Laya.Event.MOUSE_MOVE, this, this.onStarPageEvent);
        this.on(Laya.Event.MOUSE_UP, this, this.onStarPageEvent);

        this.quickSelectBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.upBtn.on(Laya.Event.CLICK, this, this.onBtnClick);

        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OUT, this, this.onCardBagEvent);
        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OVER, this, this.onCardBagEvent);

        let cardBoxList = [this.cardMainBox, this.cardBox1, this.cardBox2, this.cardBox3];
        for (let i = 0; i < cardBoxList.length; i++) {
            let cardBox = cardBoxList[i];
            cardBox.on(Laya.Event.MOUSE_OUT, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.MOUSE_OVER, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.CLICK, this, this.onCardBoxEvent);
            cardBox.on(Laya.Event.MOUSE_DOWN, this, this.onCardBoxEvent);
        }

        this.cloneCard = new CardRuntime();
        this.addChild(this.cloneCard);
        this.cloneCard.visible = false;

        this.refreshSelectCardList();
        this.initCardStateList();
        this.setBtnVideoTag();
        this.setUpStarCard(this.selectCardDataList[0]);
        (<CardBagRuntime>this.cardBag).monsterList.name = "starMonsterList";
        window["starRuntime"] = this;
        this.initChipPoint();
    }
    /**
     *  获取第一个空格子的索引
     */
    getFirstEmptyIndex() {
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            if (this.selectCardDataList[i] == null) {
                return i;
            }
        }
        return -1;
    }
    /**
     *设置升星卡牌
     * 
     */
    setUpStarCard(card: CardData) {
        if (card && card.star < 13) {
            //有升星卡牌
            this.updateUpConfigData();
            this.showUpStarBtn(true);
            this.clearUpCondition();
            this.updateUpCondition();
            this.updateExpProgressData();
            this.updateUpgradeMaterialList();
            this.showStarTip(true);
            this.showCardInfo(true);
            this.updateUpgradeCost();
            this.updateCardHaveStateList();
            this.enableMaterialSelectBox(true);
        } else {
            //没有升星卡牌
            this.showUpStarBtn(false);
            this.clearUpCondition();
            this.clearUpgradeMaterialList();
            this.showCardInfo(false);
            this.clearUpConfigData();
            this.clearExpProgressData();
            this.updateUpgradeCost();
            this.enableMaterialSelectBox(false);
            this.showStarTip(false);
        }
    }
    /** */
    enableMaterialSelectBox(enabled: boolean) {
        this.cardBox1.visible = enabled;
        this.cardBox2.visible = enabled;
        this.cardBox3.visible = enabled;
    }
    /**更新升级配置数据 */
    updateUpConfigData() {
        let star = this.selectCardDataList[0].star;
        this.upgrageConfigData = GameDataManager.Instance.upgradeConfig[star];
    }
    /**清除升级配置数据 */
    clearUpConfigData() {
        this.upgrageConfigData = null;
    }
    /**清空升星条件 */
    clearUpCondition() {
        this.upgradeCondition = 0;
        this.upgradeParam.length = 0;
    }
    /**更新升星条件 */
    updateUpCondition() {
        this.upgradeCondition = this.upgrageConfigData.upgradeCondition;
        let valueSplit = this.upgrageConfigData.upgradeParam.split(",");
        for (let i = 0; i < valueSplit.length; i++) {
            this.upgradeParam.push(Number(valueSplit[i]));
        }
    }
    /**更新材料选择区 */
    updateUpgradeMaterialList() {
        if (this.selectCardDataList[0].exp >= this.expNeed) {
            //用指定卡牌进行升星
            if (this.upgradeCondition == 1) {
                this.stateList.visible = false;
                this.quickSelectBtn.visible = true;
            } else {
                this.stateList.visible = true;
                this.quickSelectBtn.visible = false;
            }
        } else {
            //吃同星或者低星涨经验
            this.stateList.visible = false;
            this.quickSelectBtn.visible = true;
        }
        if (this.selectCardDataList[0].exp >= this.expNeed && this.upgradeCondition == 2) {
            this.selectAppointMonsterIdCard();
        }
        this.updateCanSelectLevelList();
    }
    /**清空材料选择区 */
    clearUpgradeMaterialList() {
        this.clearSelectList();
        if (this.selectCardDataList[0] && this.selectCardDataList[0].star == 13) {
            this.canSelectList = [];
        } else {
            this.canSelectList = null;
        }
        this.stateList.visible = false;
        this.quickSelectBtn.visible = false;
    }
    /**清空选中列表 */
    clearSelectList() {
        for (let i = 0; i < 3; i++) {
            this.selectCardDataList[i + 1] = null;
        }
    }
    /**更新卡牌升级所需经验值 */
    updateExpProgressData() {
        this.expNeed = this.upgrageConfigData.expNeed;
        this.expAdd = 0;
        this.setProgress(this.progressBar1, 297, this.selectCardDataList[0].exp / this.expNeed, false);
        this.setProgress(this.progressBar2, 297, (this.selectCardDataList[0].exp + this.expAdd) / this.expNeed, false);
    }
    /**清除卡牌升级所需经验值 */
    clearExpProgressData() {
        this.expNeed = 0;
        this.expAdd = 0;
        this.expOverFlow = 0;
        this.setProgress(this.progressBar1, 297, 0, false);
        this.setProgress(this.progressBar2, 297, 0, false);
    }
    /**选择指定id的怪物，如果没有用空卡代替 */
    selectAppointMonsterIdCard() {
        let upgradeParam = [...this.upgradeParam];
        let cardIndex = 0;
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            let cardData = UserData.instance.cardList[i];
            let paramIndex = upgradeParam.indexOf(cardData.monsterId);
            if (paramIndex != -1) {
                upgradeParam[paramIndex] = -1;
                this.selectCardDataList[paramIndex + 1] = cardData;
                cardIndex++;
                if (cardIndex == this.upgradeParam.length) break;
            }
        }
        for (let i = 0; i < this.upgradeParam.length; i++) {
            if (!this.selectCardDataList[i + 1]) {
                let monsterId = this.upgradeParam[i];
                let cardData = GameDataManager.Instance.getOneCardData(monsterId);
                this.selectCardDataList[i + 1] = cardData;
            }
        }
    }
    /**显示隐藏卡牌信息 */
    showCardInfo(isShow: boolean) {
        this.infoBox.visible = isShow;
        if (isShow) {
            this.nextData = GameDataManager.Instance.getNextMonsterData(this.selectCardDataList[0]);
            this.updateBeforeAfterInfo();
        } else {
            this.nextData = null;
        }
    }
    /**
     * 显示隐藏升级按钮
     */
    showUpStarBtn(isShow: boolean) {
        this.upBtn.visible = isShow;
    }
    /**更新升星消耗 */
    updateUpgradeCost() {
        this.goldCost = 0;
        this.fragmentCost = 0;
        for (let i = 0; i < 3; i++) {
            let card = this.selectCardDataList[i + 1];
            if (card) {
                this.goldCost += GameDataManager.Instance.upgradeConfig[card.star - 1].goldCost;
                this.fragmentCost += GameDataManager.Instance.upgradeConfig[card.star - 1].fragmentCost;
            }
        }
        this.moneyCostLabel.text = this.goldCost + "";
        this.chipCostLabel.text = this.fragmentCost + "";
    }
    /**
     * 扣除升星消耗的钱
     */
    takeOffUpStarCostMoney() {
        if (this.goldCost > 0) {
            UserData.instance.gold -= this.goldCost;
            UserData.instance.onDataChange("gold");
            EventManager.event(EventType.UseMoney, "gold");
        }
        if (this.fragmentCost > 0) {
            UserData.instance.chip -= this.fragmentCost;
            UserData.instance.onDataChange("chip");
            EventManager.event(EventType.UseMoney, "chip");
        }
    }
    /**
     * 移除升星消耗的卡牌
     */
    removeUpStarCostCard() {
        for (let j = 0; j < 3; j++) {
            let selectData = this.selectCardDataList[j + 1];
            if ((this.upgradeCondition == 1 && selectData && selectData.cardId > 0) ||
                (this.upgradeCondition == 2 && selectData)
            ) {
                let index = UserData.instance.cardList.indexOf(selectData);
                if (index != -1) UserData.instance.cardList.splice(index, 1);
            }
        }
    }
    /**
     * 初始化卡牌状态列表
     */
    initCardStateList() {
        for (let i = 0; i < 3; i++) {
            this.cardStateList.push({ missTag: { visible: false }, getBtn: { visible: false } });
        }
        this.stateList.array = this.cardStateList;
        this.stateList.refresh();
        for (let i = 0; i < this.stateList.length; i++) {
            let stateCell = this.stateList.getCell(i);
            let getBtn = stateCell.getChildByName("getBtn") as Laya.Image;
            getBtn.on(Laya.Event.CLICK, this, this.onStateListMouseHandler, [i]);
        }
    }
    /**获取指定卡牌事件 */
    onStateListMouseHandler(index: number, e: Laya.Event) {
        if (e.type == Laya.Event.CLICK) {
            let monsterData = GameDataManager.Instance.getMonsterData([this.selectCardDataList[index + 1].monsterId])[0];
            LayaTool.openScene("pages/catchResult.scene", false, true, Laya.Handler.create(this, () => {
                this.selectAppointMonsterIdCard();
                this.updateCardHaveStateList();
                this.refreshSelectCardList();
                this.updateCanSelectLevelList();
                (<CardBagRuntime>this.cardBag).initMonsterList(this.compareFn.bind(this));
            }), [true, this.selectCardDataList[index + 1].monsterId, monsterData, false, 3]);
        }
    }
    /**
     * 更新卡牌拥有状态列表
     */
    updateCardHaveStateList() {
        //0空位1没有2有
        let stateList = [0, 0, 0];
        for (let i = 0; i < 3; i++) {
            let cardData = this.selectCardDataList[i + 1];
            if (cardData) {
                if (cardData.cardId == 0) {
                    stateList[i] = 1;
                } else {
                    stateList[i] = 2;
                }
            }
        }
        for (let i = 0; i < stateList.length; i++) {
            let stateData = this.cardStateList[i];
            if (stateList[i] == 2 || stateList[i] == 0) {
                stateData.missTag.visible = false;
                stateData.getBtn.visible = false;
            } else {
                stateData.missTag.visible = true;
                stateData.getBtn.visible = true;
            }
        }
        this.stateList.array = this.cardStateList;
        this.stateList.refresh();
    }
    /**检查升星所需卡牌的条件 */
    checkCardCondition() {
        let result = { result: true, reason: "" };
        let cardNum = 0;
        for (let i = 0; i < 3; i++) {
            let cardData = this.selectCardDataList[i + 1];
            if (this.selectCardDataList[0].exp >= this.expNeed && this.upgradeCondition == 2) {
                if (cardData && cardData.cardId > 0) {
                    cardNum++;
                }
            } else {
                if (cardData) {
                    cardNum++;
                }
            }
        }
        if (this.selectCardDataList[0].exp >= this.expNeed && cardNum < this.upgradeParam.length) {
            result.result = false;
            result.reason = "card not enough";
        } else {
            if (cardNum == 0) {
                result.result = false;
                result.reason = "card not enough";
            }
        }
        return result;
    }
    /**检查升星所需金币条件 */
    checkMoneyCondition() {
        let result = { result: true, reason: "" };
        if (UserData.instance.gold < this.goldCost) {
            result.result = false;
            result.reason = "gold not enough";
        } else if (UserData.instance.chip < this.fragmentCost) {
            result.result = false;
            result.reason = "chip not enough";
        }
        return result;
    }

    onBtnClick(e: Laya.Event) {
        if (e.target == this.quickSelectBtn) {
            this.quickSelectCard();
        } else if (e.target == this.upBtn) {
            let cardResult = this.checkCardCondition();
            let moneyResult = this.checkMoneyCondition();
            if (!cardResult.result) {

            } else if (!moneyResult.result) {
                if (moneyResult.reason == "gold not enough") {
                    EventManager.event(EventType.PopTip, "金币不足");
                    EventManager.event(EventType.ShowGiftPack);
                } else if (moneyResult.reason == "chip not enough") {
                    EventManager.event(EventType.PopTip, "碎片不足");
                }
            } else {
                this.upCardStar();
            }
        }
    }
    onStarPageEvent(e: Laya.Event) {
        if (!this.selectCard) return;
        if (e.type == Laya.Event.MOUSE_MOVE) {
            if (this.selectCard.visible) {
                this.cloneCard.visible = true;
                this.selectCard.visible = false;
                (<CardBagRuntime>this.cardBag).monsterList.scrollBar.stopScroll();
            }
            if (this.cloneCard.visible) {
                this.cloneCard.x = e.stageX - this.cloneCard.width / 2;
                this.cloneCard.y = e.stageY - this.cloneCard.height / 2;
            }
            this.isMove = true;
        } else if (e.type == Laya.Event.MOUSE_UP) {
            if (this.posType == 0 || this.posType == this.selectFrom || !this.allowOperate() || !this.allowSelect(this.posType - 2) || (this.selectFull() && !this.isMaterialPosOperate()) || (this.posType - 2 > 0 && !this.isAllowAsUpStarCostCard(this.selectCard.data))) {
                if (this.isMove && this.posType == this.selectFrom && this.posType >= 2 && this.posType <= 5) {
                    this.stopClick = true;
                    Laya.timer.frameOnce(2, this, () => {
                        this.stopClick = false;
                    });
                }
                //空白位置或者位置相同
                this.clearSelect(true);
            } else {
                let parseSelectFrom = this.selectFrom - 2;
                let parsePosType = this.posType - 2;
                if (this.posType == 1) {
                    //卡库位置
                    this.changeSelectList([this.selectCard.data], false, parseSelectFrom);
                    this.onChangeSelectCardList(parseSelectFrom == 0, true);
                } else {
                    if (this.selectFrom == 1) {
                        this.changeSelectList([this.selectCard.data], true, parsePosType);
                        this.onChangeSelectCardList(parsePosType == 0, true);
                    } else {
                        let temp = this.selectCardDataList[parsePosType];
                        this.selectCardDataList[parsePosType] = this.selectCardDataList[parseSelectFrom];
                        this.selectCardDataList[parseSelectFrom] = temp;
                        this.changeSelectList([...this.selectCardDataList], true, -1);
                        this.onChangeSelectCardList(parsePosType == 0 || parseSelectFrom == 0, true);
                    }
                }
            }
            this.isMove = false;
        }
    }
    isMaterialPosOperate() {
        let parseSelectFrom = this.selectFrom - 2;
        let parsePosType = this.posType - 2;
        if ((parseSelectFrom >= 1 && parseSelectFrom <= 3) && (parsePosType >= 1 && parsePosType <= 3)) {
            return true;
        }
        return false;
    }
    onCardBagEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = 1;
        }
    }
    onCardBoxEvent(e: Laya.Event) {
        if (this.upgradeCondition == 2 && (e.target == this.cardBox1 || e.target == this.cardBox2 || e.target == this.cardBox3)) {
            return;
        }
        let cardBoxList = [this.cardMainBox, this.cardBox1, this.cardBox2, this.cardBox3];
        let cardList = [this.cardMain, this.card1, this.card2, this.card3];
        let posTypeList = [2, 3, 4, 5];
        let cardBoxIndex = cardBoxList.indexOf(e.target as Laya.Box);
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = posTypeList[cardBoxIndex];
        } else if (e.type == Laya.Event.MOUSE_DOWN) {
            let pickCard = cardList[cardBoxIndex] as CardRuntime;
            if (!pickCard.visible) return;
            this.selectCard = pickCard;
            this.selectFrom = posTypeList[cardBoxIndex];
            this.downPoint.x = e.stageX;
            this.downPoint.y = e.stageY;
            this.cloneCard.dataSource = this.selectCard.data;
        } else if (e.type == Laya.Event.CLICK) {
            let pickCard = cardList[cardBoxIndex] as CardRuntime;
            if (this.stopClick || !pickCard.visible) return;
            this.changeSelectList([pickCard.data], false, cardBoxIndex);
            this.onChangeSelectCardList(cardBoxIndex == 0, false);
        }
    }
    static haveRed() {
        return false;
    }
    onSelectPageCard(list: Laya.List, e: Laya.Event, card: CardRuntime) {
        if (list != (<CardBagRuntime>this.cardBag).monsterList) return;
        if (card.cardBg.disabled) {
            EventManager.event(EventType.PopTip, "请选择正确的怪");
            return;
        }
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.selectFrom = 1;
            this.selectCard = card;
            this.downPoint.x = e.stageX;
            this.downPoint.y = e.stageY;
            this.cloneCard.dataSource = card.data;
        } else if (e.type == Laya.Event.CLICK) {
            let firstEmptyIndex = this.getFirstEmptyIndex();
            if (firstEmptyIndex == -1) return;
            if (!this.allowSelect(firstEmptyIndex) || this.selectFull()) return;
            if (firstEmptyIndex > 0 && !this.isAllowAsUpStarCostCard(card.data)) return;
            this.changeSelectList([card.data], true, firstEmptyIndex);
            this.onChangeSelectCardList(firstEmptyIndex == 0, false);
        }
    }
    exceptFunc(cardData: CardData) {
        let exceptCardList = [...this.selectCardDataList];
        for (let i = 0; i < exceptCardList.length; i++) {
            let expectCard = exceptCardList[i];
            if (expectCard && cardData.cardId == expectCard.cardId) {
                return false;
            }
        }
        return true;
    }
    changeSelectList(dataList: CardData[], isAdd: boolean, pos: number) {
        for (let i = 0; i < dataList.length; i++) {
            if (isAdd) {
                if (pos != -1) {
                    this.selectCardDataList[pos] = dataList[i];
                } else {
                    this.selectCardDataList[i] = dataList[i];
                }
            } else {
                if (pos != -1) {
                    this.selectCardDataList[pos] = null;
                } else {
                    for (let j = 0; j < this.selectCardDataList.length; j++) {
                        if (dataList[i].cardId == this.selectCardDataList[j].cardId) {
                            this.selectCardDataList[j] = null;
                            break;
                        }
                    }
                }
            }
        }
    }

    upCardStar() {
        this.takeOffUpStarCostMoney();
        this.removeUpStarCostCard();
        this.clearSelectList();
        if (this.selectCardDataList[0].exp >= this.expNeed) {
            this.selectCardDataList[0].hp = this.nextData.hp;
            this.selectCardDataList[0].curHp = this.nextData.hp;
            this.selectCardDataList[0].atk = this.nextData.atk;
            this.selectCardDataList[0].star += 1;
            this.selectCardDataList[0].exp = 0;
            this.setUpStarCard(this.selectCardDataList[0]);
            if (this.selectCardDataList[0].star < 13) {
                (<CardBagRuntime>this.cardBag).initMonsterList(this.compareFn.bind(this));
            } else {
                (<CardBagRuntime>this.cardBag).initMonsterList();
            }
            UserData.instance.onDataChange("cardList");
            this.refreshSelectCardList();
        } else {
            EventManager.event(EventType.PopTip, "获得经验");
            this.setProgress(this.progressBar2, 297, 0, false);
            let expAdd = this.getExpAdd();
            Laya.MouseManager.enabled = false;
            this.updateProgressBar(this.progressBar1, expAdd, Laya.Handler.create(this, () => {
                Laya.MouseManager.enabled = true;
                this.selectCardDataList[0].exp += expAdd;
                if (this.selectCardDataList[0].exp >= this.expNeed) {
                    this.selectCardDataList[0].hp = this.nextData.hp;
                    this.selectCardDataList[0].curHp = this.nextData.hp;
                    this.selectCardDataList[0].atk = this.nextData.atk;
                    this.selectCardDataList[0].star += 1;
                    this.selectCardDataList[0].exp = 0;
                    EventManager.event(EventType.PopTip, ["升星成功", this.selectCardDataList[0].star]);
                }
                this.setUpStarCard(this.selectCardDataList[0]);
                if (this.selectCardDataList[0].star < 13) {
                    (<CardBagRuntime>this.cardBag).initMonsterList(this.compareFn.bind(this));
                } else {
                    (<CardBagRuntime>this.cardBag).initMonsterList();
                }
                UserData.instance.onDataChange("cardList");
                this.refreshSelectCardList();
            }));
        }
    }
    quickSelectCard() {
        if ((<CardBagRuntime>this.cardBag).monsterList.array.length > 0) {
            this.clearSelectList();
            this.refreshSelectCardList();
            this.updateCanSelectLevelList();
            (<CardBagRuntime>this.cardBag).initMonsterList(this.compareFn.bind(this));
            let posId = 0;
            for (let i = 0; i < (<CardBagRuntime>this.cardBag).monsterList.array.length; i++) {
                for (let j = 0; j < (<CardBagRuntime>this.cardBag).monsterList.array[i].length; j++) {
                    let selectCard = (<CardBagRuntime>this.cardBag).monsterList.array[i][j];
                    if (selectCard["canSelect"]) {
                        if (this.canSelectFunc(selectCard)) {
                            this.changeSelectList([selectCard], selectCard, posId + 1);
                            this.updateCanSelectLevelList();
                            posId += 1;
                            if (posId == 3) {
                                this.onChangeSelectCardList(false, false);
                                return;
                            }
                        }
                    } else {
                        this.onChangeSelectCardList(false, false);
                        return;
                    }
                }
            }
            this.onChangeSelectCardList(false, false);
        }
    }
    refreshSelectCardList() {
        let cardList = [this.cardMain, this.card1, this.card2, this.card3];
        for (let i = 0; i < this.selectCardDataList.length; i++) {
            let selectCardData = this.selectCardDataList[i];
            if (selectCardData) {
                cardList[i].visible = true;
                cardList[i].dataSource = selectCardData;
            } else {
                cardList[i].visible = false;
            }
        }
    }
    clearSelect(isRecovery: boolean) {
        this.cloneCard.visible = false;
        if (isRecovery) this.selectCard.visible = true;
        this.selectCard = null;
        this.selectFrom = 0;
        this.posType = 0;
        this.downPoint.x = 0;
        this.downPoint.y = 0;
    }
    updateBeforeAfterInfo() {
        let updagradeCard = this.selectCardDataList[0];
        let beforeData = { hp: updagradeCard.hp, atk: updagradeCard.atk };
        this.beforeHpLabel.text = beforeData.hp + "";
        this.beforeAtkLabel.text = beforeData.atk + "";
        this.afterHpLabel.text = this.nextData.hp + "";
        this.afterAtkLabel.text = this.nextData.atk + "";
        this.setStarInfo(updagradeCard.star, this.beforeStarImage, this.beforeStarLabel);
        this.setStarInfo(updagradeCard.star + 1, this.afterStarImage, this.afterStarLabel);
    }
    setStarInfo(starNum: number, starImage: Laya.Image, starLabel: Laya.Label) {
        if (starNum <= 10) {
            starLabel.text = starNum + "";
            starImage.skin = "newSystem/Ui_StarYellow.png";
        } else {
            starLabel.text = (starNum - 10) + "";
            starImage.skin = "newSystem/Ui_StarPurple.png";
        }
    }
    setProgress(target: Laya.Sprite, fullWidth: number, targetProgress: number, isTween = false, completeHandler: Laya.Handler = null) {
        let progressMask = this.progressMask1;
        if (target == this.progressBar2) progressMask = this.progressMask2;
        if (isTween) {
            Laya.Tween.to(progressMask, { x: (targetProgress - 1) * fullWidth }, 200, Laya.Ease.sineOut, completeHandler, 0, true);
        } else {
            progressMask.x = (targetProgress - 1) * fullWidth;
        }
    }
    allowOperate() {
        if (this.selectFrom == 1) {
            if (this.selectCardDataList[0] && this.posType == 2) {
                return false;
            } else {
                return true;
            }
        } else {
            let parseSelectFrom = this.selectFrom - 2;
            let parsePosType = this.posType - 2;
            if ((parsePosType == 0 && (parseSelectFrom >= 1 && parseSelectFrom <= 3)) ||
                (parseSelectFrom == 0 && (parsePosType >= 1 && parsePosType <= 3))
            ) {
                return false;
            }
            return true;
        }
    }
    /**排序方程 */
    compareFn(a: CardData, b: CardData) {
        let conditiona = a.star;
        let conditionb = b.star;
        if (this.selectCardDataList[0].exp >= this.expNeed && this.upgradeCondition == 2) {
            conditiona = a.monsterId;
            conditionb = b.monsterId;
        }
        if (this.canSelectList.indexOf(conditiona) != -1 && this.canSelectList.indexOf(conditionb) == -1) {
            return -1;
        } else if (this.canSelectList.indexOf(conditiona) == -1 && this.canSelectList.indexOf(conditionb) != -1) {
            return 1;
        } else {
            return 0;
        }
    }
    /**是否可以被选择 */
    canSelectFunc(card: CardData) {
        if (!this.canSelectList) {
            return true;
        } else {
            let condition = card.star;
            if (this.selectCardDataList[0].exp >= this.expNeed && this.upgradeCondition == 2) {
                condition = card.monsterId;
            }
            if (this.isAllowAsUpStarCostCard(card) && this.canSelectList.indexOf(condition) != -1) {
                return true;
            } else {
                return false;
            }
        }
    }
    updateCanSelectLevelList() {
        this.initCanSelectLevelList();
        if (this.selectCardDataList[0].exp >= this.expNeed) {
            for (let i = 0; i < this.selectCardDataList.length; i++) {
                let cardData = this.selectCardDataList[i + 1];
                if (this.upgradeCondition == 1) {
                    if (cardData) {
                        let index = this.canSelectList.indexOf(cardData.star);
                        if (index != -1) this.canSelectList.splice(index, 1);
                    }
                } else {
                    if (cardData && cardData.cardId > 0) {
                        let index = this.canSelectList.indexOf(cardData.monsterId);
                        if (index != -1) this.canSelectList.splice(index, 1);
                    }
                }
            }
        }
    }
    initCanSelectLevelList() {
        if (this.selectCardDataList[0].exp >= this.expNeed) {
            this.canSelectList = [...this.upgradeParam];
        } else {
            this.canSelectList = [];
            let minStar = 1;
            minStar = minStar > 1 ? minStar : 1;
            let maxStar = this.selectCardDataList[0].star;
            for (let i = minStar; i <= maxStar; i++) {
                this.canSelectList.push(i);
            }
        }
    }
    selectFull() {
        if (this.canSelectList && this.canSelectList.length == 0) return true;
        return false;
    }
    allowSelect(index: number) {
        if (index > 0 && this.selectCardDataList[0].exp >= this.expNeed && this.upgradeCondition == 2) return false;
        return true;
    }
    updateProgressBar(progressMask: Laya.Image, expAdd: number, completeHandler: Laya.Handler = null) {
        if (!this.selectCardDataList[0]) return;
        let afterProgress = (this.selectCardDataList[0].exp + expAdd) / this.expNeed;
        this.setProgress(progressMask, 297, afterProgress, true, completeHandler);
        return afterProgress;
    }
    updateExpAdd() {
        this.expAdd = 0;
        if (this.selectCardDataList[0]) {
            if (this.selectCardDataList[0].exp < this.expNeed) {
                for (let i = 0; i < 3; i++) {
                    let selectData = this.selectCardDataList[i + 1];
                    if (selectData) {
                        let add = GameDataManager.Instance.upgradeConfig[selectData.star - 1].expGain;
                        this.expAdd += add;
                    }
                }
            }
        }
    }
    getExpAdd() {
        let afterExp = this.selectCardDataList[0].exp + this.expAdd;
        afterExp = afterExp > this.expNeed ? this.expNeed : afterExp;
        return afterExp - this.selectCardDataList[0].exp;
    }
    onChangeSelectCardList(isChangeUpCard: boolean, isClearSelect: boolean) {
        if (isChangeUpCard) this.setUpStarCard(this.selectCardDataList[0]);
        if (isClearSelect) this.clearSelect(false);
        this.refreshSelectCardList();
        if (this.selectCardDataList[0]) {
            this.updateCanSelectLevelList();
            (<CardBagRuntime>this.cardBag).initMonsterList(this.compareFn.bind(this));
            if (this.selectCardDataList[0].exp < this.expNeed) {
                this.updateExpAdd();
                let expAdd = this.getExpAdd();
                this.expOverFlow = this.selectCardDataList[0].exp + this.expAdd - this.expNeed;
                this.updateProgressBar(this.progressBar2, expAdd);
            }
        } else {
            (<CardBagRuntime>this.cardBag).initMonsterList();
        }
        this.updateUpgradeCost();

    }
    addStarCard(star: number, num: number) {
        for (const key in GameDataManager.Instance.monsterData) {
            if (!GameDataManager.Instance.isMaterial(key)) {
                if (GameDataManager.Instance.monsterData[key].level == star) {
                    for (let i = 0; i < num; i++) {
                        UserData.instance.addCard(Number(key), false, 1);
                    }
                    return;
                }

            }
        }
    }
    onSelect() {
        this.visible = true;
        GameDataManager.Instance.isPause = true;
        EventManager.event(EventType.PlayerStopMove);
    }
    offSelect() {
        if (this.selectCardDataList[0]) {
            this.changeSelectList([this.selectCardDataList[0]], false, 0);
            this.onChangeSelectCardList(true, false);
        }
        this.visible = false;
        GameDataManager.Instance.isPause = false;
    }
    showStarTip(isShow: boolean) {
        if (isShow) {
            if (this.selectCardDataList[0].exp >= this.expNeed) {
                this.starTip.visible = false;
            } else {
                this.starTip.visible = true;
            }
        } else {
            this.starTip.visible = false;
        }
    }
    /**是否允许作为升星消耗的卡牌 */
    isAllowAsUpStarCostCard(card: CardData) {
        if (GameDataManager.Instance.isSuperEva(card) ||
            GameDataManager.Instance.isTryCard(card)) {
            return false;
        }
        return true;
    }
    initChipPoint() {
        this.chipUi.visible = true;
        let goldMoneyUi: MoneyUiRuntime = this.chipUi;
        goldMoneyUi.setMoneyType("chip", false);
        goldMoneyUi.moneyIcon.localToGlobal(goldMoneyUi.moneyPoint, false, this);
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        for (let i = 0; i < this.stateList.length; i++) {
            let stateCell = this.stateList.getCell(i);
            let videoTag = stateCell.getChildByName("videoTag") as Laya.Sprite;
            videoTag.visible = isShowVideoTag;
        }
    }
}