export class WXCloud {
    private static _cloud: any;
    public static get cloud() {
        return this._cloud;
    }
    public static init() {
        return new Promise((resolve, reject) => {
            if (typeof wx == "undefined") {
                reject("wx.cloud.init:fail");
            } else {
                this._cloud = wx["cloud"];
                this._cloud.init();
                resolve("wx.cloud.init:ok");
            }
        });
    }
}