import { GameDataManager } from "../gamelogic/GameDataManager";
import { ui } from "../ui/layaMaxUI";

export default class EscapeRuntime extends ui.pages.escapeUI {
    onAwake() {
        this.sureBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.sureDisableBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.continueBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.initBtn();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.sureBtn) {
            this.close("yes");
        } else if (e.target == this.sureDisableBtn) {

        } else if (e.target == this.continueBtn) {
            this.close("no");
        }
    }
    initBtn() {
        if (GameDataManager.Instance.escapeTimeLeft > 0) {
            this.sureBtn.visible = true;
            this.sureDisableBtn.visible = false;
        } else {
            this.sureBtn.visible = false;
            this.sureDisableBtn.visible = true;
        }
    }
}