import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class CatchRuntime extends ui.pages.catchUI {
    public static blackColorFilter = new Laya.ColorFilter([
        0, 0, 0, 0, 0, //R
        0, 0, 0, 0, 0, //G
        0, 0, 0, 0, 0, //B
        0, 0, 0, 1, 0, //A
    ]);
    monsterId: number = -1;
    monsterData: any = null;
    progressMask: Laya.Image = new Laya.Image();
    catchRate: number = 0;
    isVideoCatchBoss = false;
    closeHandler: Laya.Handler = null;
    onAwake() {
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.perLabel.filters = [CatchRuntime.blackColorFilter];
        this.progressMask.skin = "count/Ui_CatchTiao3.png";
        this.progressMask.sizeGrid = "0,5,0,5";
        this.progressMask.width = 0;
        this.progressBar.mask = this.progressMask;
        this.catchBossBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.quitCatchBossBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        EventManager.on(EventType.StartCatch, this, this.onStartCatch);
        EventManager.on(EventType.UpdateProgress, this, this.onUpdateProgress);
        EventManager.on(EventType.CatchEnd, this, this.onCatchEnd);
        this.setBtnVideoTag();
    }
    onDestroy() {
        EventManager.off(EventType.StartCatch, this, this.onStartCatch);
        EventManager.off(EventType.UpdateProgress, this, this.onUpdateProgress);
        EventManager.off(EventType.CatchEnd, this, this.onCatchEnd);
    }
    onStartCatch() {
        if (UserData.instance.gameRank <= 1) {
            if (this.catchRate < 100) {
                this.catchRate = Math.floor(this.catchRate / 2);
            }
        }
        if (UserData.instance.isFirstCatch || this.isVideoCatchBoss || Math.random() * 100 < this.catchRate) {
            GameDataManager.Instance.isCatch = true;
        }
        if (UserData.instance.isFirstCatch) {
            UserData.instance.isFirstCatch = false;
            UserData.instance.onDataChange("isFirstCatch");
        }
        this.visible = false;
    }
    initCatchData(isVideoCatchBoss: boolean) {
        ResManager.stopMusic();
        this.isVideoCatchBoss = isVideoCatchBoss;
        this.monsterId = GameDataManager.Instance.catchMonsterId;
        this.monsterData = GameDataManager.Instance.catchMonsterData;
        this.catchBossBtn.visible = isVideoCatchBoss;
        this.quitCatchBossBtn.visible = isVideoCatchBoss;
        this.catchProgressBg.visible = !isVideoCatchBoss;
        this.catchInfoBg.visible = !isVideoCatchBoss;
        if (!this.isVideoCatchBoss) {
            this.starLabel.text = this.monsterData.level + "";
            this.catchRate = GameDataManager.Instance.getMonsterCatchRate(this.monsterId, GameDataManager.Instance.isBoss);
            this.setCatchPer(this.catchRate);
            UserData.instance.finishTask(1, 1, 1);
            if (this.catchRate == 100) {
                EventManager.event(EventType.BallStartCatch);
            }
        }
    }
    setCatchPer(per: number) {
        let updateHandler = Laya.Handler.create(this, () => {
            this.perLabel.text = Math.floor(this.progressMask.width / 456 * 100) + "%";
        }, null, false);
        Laya.Tween.to(this.progressMask, { width: per / 100 * 456, update: updateHandler }, 200, Laya.Ease.sineOut);
    }
    setProgress(progress: number) {
        this.progressMask2.graphics.clear();
        this.progressMask2.graphics.drawPie(161, 161, 170, 135, 135 + 271 * progress, "#ff0000", "#ff0000", 0);
    }
    onUpdateProgress(progress: number) {
        this.setProgress(progress);
    }
    onCatchEnd() {
        this.popCatchResult();
    }
    popCatchResult() {
        Laya.timer.once(500, this, () => {
            if (this.isVideoCatchBoss) {
                let closeHandler = Laya.Handler.create(this, () => {
                    this.closeHandler && this.closeHandler.run();
                    this.closeHandler = null;
                });
                let param = [GameDataManager.Instance.isCatch, GameDataManager.Instance.catchMonsterId, GameDataManager.Instance.catchMonsterData, false, 5];
                LayaTool.openScene("pages/catchResult.scene", true, true, closeHandler, param);

            } else {
                let param = [GameDataManager.Instance.isCatch, GameDataManager.Instance.catchMonsterId, GameDataManager.Instance.catchMonsterData, true, 1];
                LayaTool.openScene("pages/catchResult.scene", true, true, null, param);
            }
        });
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.catchBossBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                EventManager.event(EventType.BallStartCatch);
            });
        } else if (e.target == this.quitCatchBossBtn) {
            this.visible = false;
            this.closeHandler && this.closeHandler.run();
            this.closeHandler = null;
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}