import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import SceneManager from "../gframe-work/manager/SceneManager";
import StageManager from "../gframe-work/manager/StageManager";
import UserData from "../gframe-work/manager/UserData";
import Scene3DBase from "../gframe-work/scene3D/Scene3DBase";
import LayaTool from "../gframe-work/tool/LayaTool";
import MathTool from "../gframe-work/tool/MathTool";
import CardData from "./CardData";
import CatchSceneLogic from "./CatchSceneLogic";
import Fighter from "./Fighter";
import { FightType } from "./FightType";
import { GameDataManager } from "./GameDataManager";
import { EventType } from "./GEnum";
import { TongYiEventManager } from "./TongYiEventManager";

export default class FightSceneLogic extends Scene3DBase {
    enemyGroup: Fighter[] = [];
    selfGroup: Fighter[] = [];
    selfFightGroupIndexList: number[] = [];
    selfStandPoint: Laya.Sprite3D = null;
    enemyStandPoint: Laya.Sprite3D = null;
    enemyOutPoint: Laya.Sprite3D = null;
    selfOutPoint: Laya.Sprite3D = null;
    /**1:自己回合2：对方回合 */
    roundSide: number = 1;
    isRoundFinish: boolean = true;
    selfFightIndex: number = 0;
    enemyFightIndex: number = 0;
    isQte: boolean = false;
    isClickBall: boolean = false;
    fightCameraData = { "position": [0.85, 7.37, 15.55], "rotation": [0.257019, -0.2552845, -0.07059698, -0.9294016] };
    onAwake() {
        if (GameDataManager.Instance.fightType == FightType.Pvp || GameDataManager.Instance.fightType == FightType.Tower) {
            ResManager.playMusic("fight1.mp3", 0);
        } else {
            ResManager.playMusic("fight.mp3", 0);
        }
        this.cameraUrl = "MainCamera/FightCamera";
        super.onAwake();
        this.getStandPoint();
        this.createSelfGroup();
        this.selfFightIndex = this.getFirstLiveMonsterIndex();
        this.initSelfGroupPosition();
        if (GameDataManager.Instance.fightType == FightType.Normal || GameDataManager.Instance.fightType == FightType.Cream) this.hideCatchModel();
        EventManager.on(EventType.AttackOnce, this, this.onAttackOnce);
        EventManager.on(EventType.QteSuccess, this, this.onQteSuccess);
        EventManager.on(EventType.ChangeQteState, this, this.onChangeQteState);
        EventManager.on(EventType.FullHpRevive, this, this.onFullHpRevive);
        EventManager.on(EventType.ChangeAutoFight, this, this.onChangeAutoFight);
    }
    onChangeQteState(state: number) {
        if (state == 3) {
            //结束
            this.isQte = false;
        }
    }
    onOpened() {
        this.initAutoFight();
        this.createEnemyGroup();
        EventManager.event(EventType.InitFightCell);
        GameDataManager.Instance.isGameEnd = false;
        GameDataManager.Instance.newMonsterIdList.length = 0;
        this.setCameraFightPosAndRot();
        if (GameDataManager.Instance.allEmptyBlood) {
            Laya.timer.once(500, this, () => {
                if (GameDataManager.Instance.fightType == FightType.Tower) {
                    EventManager.event(EventType.BacktoScene, "challenge");
                } else if (GameDataManager.Instance.fightType == FightType.Pvp) {
                    EventManager.event(EventType.BacktoScene, "pvp");
                } else if (GameDataManager.Instance.fightType == FightType.Normal ||
                    GameDataManager.Instance.fightType == FightType.Cream
                ) {
                    EventManager.event(EventType.BacktoScene, "search");
                }
                if (window["fightSendData"]) {
                    window["fightSendData"].category = 3;
                    TongYiEventManager.sendEvent(10003, window["fightSendData"]);
                    window["fightSendData"] = null;
                }
                Laya.timer.once(500, null, () => {
                    EventManager.event(EventType.PopTip, "请去治疗");
                });
            });
            return;
        }
        GuideManager.startOutTask("介绍战斗");
        Laya.timer.once(1000, this, () => {
            this.autoFight();
        });
    }
    onQteSuccess() {
        EventManager.event(EventType.ChangeQteState, 1);
        this.isRoundFinish = false;
        this.selfAttack(true).then(() => {
            return this.roundCount();
        });
    }
    /**我攻击 */
    selfAttack(isMustKill: boolean) {
        return new Promise<void>(resolve => {
            this.selfGroup[this.selfFightIndex].changeActorState("Attack", "", true);
            EventManager.event(EventType.EnableMustKill, true);
            Laya.timer.once(250, this, () => {
                let atk = this.selfGroup[this.selfFightIndex].atk;
                this.playHitSound(isMustKill);
                this.enemyGroup[this.enemyFightIndex].onHurt(atk);
                let pos = this.enemyGroup[this.enemyFightIndex].gameObject.transform.position.clone();
                this.piaoHp(atk, pos, isMustKill);
                if (!GameDataManager.Instance.enemyMonsterCardList[this.enemyFightIndex]) {
                    debugger;
                }
                GameDataManager.Instance.enemyMonsterCardList[this.enemyFightIndex].curHp = this.enemyGroup[this.enemyFightIndex].hp;
                EventManager.event(EventType.UpdateFightData, ["enemy", this.enemyFightIndex]);
            });
            Laya.timer.once(500, this, () => {
                this.selfGroup[this.selfFightIndex].changeActorState("Stand", "", true);
                Laya.timer.once(500, this, () => {
                    resolve();
                });
            });
        });
    }
    /**我换人 */
    selfChange(newIndex: number) {
        return new Promise<void>(resolve => {
            if (newIndex == this.selfFightIndex) {
                resolve();
            } else {
                LayaTool.tweenTo(this.selfGroup[this.selfFightIndex].gameObject,
                    { position: this.selfOutPoint.transform.position }, 500, Laya.Ease.sineOut,
                    Laya.Handler.create(this, () => {
                        this.selfGroup[this.selfFightIndex].gameObject.active = false;
                    }));
                this.selfGroup[newIndex].gameObject.active = true;
                LayaTool.tweenTo(this.selfGroup[newIndex].gameObject,
                    { position: this.selfStandPoint.transform.position }, 600, Laya.Ease.sineOut,
                    Laya.Handler.create(this, () => {
                        this.selfFightIndex = newIndex;
                        // console.log(newIndex, this.selfFightIndex);
                        resolve();
                    }));
            }
        });
    }
    /**敌人攻击 */
    enemyAttack() {
        return new Promise<void>(resolve => {
            this.isRoundFinish = false;
            this.enemyGroup[this.enemyFightIndex].changeActorState("Attack", "", true);
            Laya.timer.once(250, this, () => {
                let atk = this.enemyGroup[this.enemyFightIndex].atk;
                this.playHitSound();
                this.selfGroup[this.selfFightIndex].onHurt(atk);
                let pos = this.selfGroup[this.selfFightIndex].gameObject.transform.position.clone();
                this.piaoHp(atk, pos, false);
                let fightCard = UserData.instance.fightCardList[this.selfFightGroupIndexList[this.selfFightIndex]];
                fightCard.curHp = this.selfGroup[this.selfFightIndex].hp;
                UserData.instance.onDataChange("fightCardList");
                this.syncHp(fightCard);
                EventManager.event(EventType.UpdateFightData, ["self", this.selfFightIndex]);
            });
            Laya.timer.once(500, this, () => {
                this.enemyGroup[this.enemyFightIndex].changeActorState("Stand", "", true);
                Laya.timer.once(500, this, () => {
                    resolve();
                });
            });
        });

    }
    playHitSound(isMustKill = false) {
        if (isMustKill) {
            ResManager.playSound(`mustKill.mp3`);
        } else {
            let hitIndex = MathTool.getRandom(1, 3);
            ResManager.playSound(`hit${hitIndex}.mp3`);
        }
    }
    /**敌人换人 */
    enemyChange(newIndex: number) {
        return new Promise<void>(resolve => {
            if (newIndex == this.enemyFightIndex) {
                resolve();
            } else {
                LayaTool.tweenTo(this.enemyGroup[this.enemyFightIndex].gameObject,
                    { position: this.enemyOutPoint.transform.position }, 500, Laya.Ease.sineOut,
                    Laya.Handler.create(this, () => {
                        this.enemyGroup[this.enemyFightIndex].gameObject.active = false;
                    }));
                this.enemyGroup[newIndex].gameObject.active = true;
                LayaTool.tweenTo(this.enemyGroup[newIndex].gameObject,
                    { position: this.enemyStandPoint.transform.position }, 600, Laya.Ease.sineOut,
                    Laya.Handler.create(this, () => {
                        this.enemyFightIndex = newIndex;
                        // console.log(newIndex, this.enemyFightIndex);
                        resolve();
                    }));
            }
        });
    }
    onAttackOnce(monsterIndex: number, isQte: boolean, isAuto: boolean) {
        if (GameDataManager.Instance.isAutoFighting) {
            if (isAuto == false) {
                GameDataManager.Instance.autoFight = false;
                GameDataManager.Instance.isAutoFighting = false;
                EventManager.event(EventType.CloseAutoFight);
            }
        }
        if (GameDataManager.Instance.isGameEnd) return;
        if (GameDataManager.Instance.allEmptyBlood) return;
        if (this.roundSide != 1 || !this.isRoundFinish) return;
        // console.log(this.roundSide, this.isRoundFinish);

        let fightCard = UserData.instance.fightCardList[this.selfFightGroupIndexList[monsterIndex]];
        if (fightCard.curHp == 0) return;
        if (this.isQte) return;
        if (isQte) {
            this.isQte = true;
            GameDataManager.Instance.monsterAtkTimeList[monsterIndex] = 0;
            this.selfChange(monsterIndex).then(() => {
                EventManager.event(EventType.ChangeQteState, 0);
                EventManager.event(EventType.UpdateFightData, ["self", this.selfFightIndex]);
            });
        } else {
            GameDataManager.Instance.monsterAtkTimeList[monsterIndex]++;
            EventManager.event(EventType.UpdateFightData, ["self", this.selfFightIndex]);
            this.isRoundFinish = false;
            this.selfChange(monsterIndex)
                .then(() => {
                    return this.selfAttack(false);
                }).then(() => {
                    return this.roundCount();
                }).then(() => {
                    return this.enemyAttack();
                }).then(() => {
                    return this.roundCount();
                });
        }
    }
    popCount() {
        if (GameDataManager.Instance.isUseAutoFight && !UserData.instance.freeAutoFight) {
            UserData.instance.fightTicket -= 1;
            UserData.instance.onDataChange("fightTicket");
        }
        if (GameDataManager.Instance.isBoss) {
            if (GameDataManager.Instance.isGameWin) {
                EventManager.event(EventType.ShowBlackMask);
                Laya.timer.once(500, this, () => {
                    SceneManager.Instance.closeScene3D("FightScene2");
                    this.sendSuccessEvent();
                    this.gotoCatchScene();
                });
            } else {
                this.sendFailEvent();
                if (GameDataManager.Instance.reviveLeftTime > 0) {
                    GameDataManager.Instance.reviveLeftTime--;
                    LayaTool.openScene("pages/neverGiveUp.scene", false, true, Laya.Handler.create(this, (reason: string) => {
                        if (reason == "yes") {
                            EventManager.event(EventType.OneCure, true);
                        } else {
                            EventManager.event(EventType.ShowCount, true);
                        }
                    }), null);
                } else {
                    EventManager.event(EventType.ShowCount, true);
                }
            }
        } else {
            EventManager.event(EventType.ShowBlackMask);
            Laya.timer.once(500, this, () => {
                if (GameDataManager.Instance.fightType == FightType.Normal || GameDataManager.Instance.fightType == FightType.Cream) {
                    if (GameDataManager.Instance.isGameWin) {
                        this.sendSuccessEvent();
                        ResManager.playSound("success.mp3");
                        SceneManager.Instance.closeScene3D("FightScene2");
                        this.gotoCatchScene();
                    } else {
                        this.sendFailEvent();
                        ResManager.playSound("fail.mp3");
                        EventManager.event(EventType.BacktoScene, "search");
                        EventManager.event(EventType.ShowGiftPack);
                    }
                } else if (GameDataManager.Instance.fightType == FightType.Tower) {
                    EventManager.event(EventType.ShowCount, true);
                } else if (GameDataManager.Instance.fightType == FightType.Pvp) {
                    EventManager.event(EventType.ShowPvpCount, true);
                }
            });
        }
    }
    onDestroy() {
        super.onDestroy();
        Laya.timer.clearAll(this);
        EventManager.off(EventType.AttackOnce, this, this.onAttackOnce);
        EventManager.off(EventType.QteSuccess, this, this.onQteSuccess);
        EventManager.off(EventType.ChangeQteState, this, this.onChangeQteState);
        EventManager.off(EventType.FullHpRevive, this, this.onFullHpRevive);
        EventManager.off(EventType.ChangeAutoFight, this, this.onChangeAutoFight);
    }
    createSelfGroup() {
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let card = UserData.instance.fightCardList[i];
            if (card) {
                this.selfFightGroupIndexList.push(i);
                let fighter = LayaTool.createSprite3DWithScript(`Enemy${card.monsterId}`, Fighter, this.clearNode);
                fighter.gameObject.transform.position = this.selfStandPoint.transform.position.clone();
                fighter.gameObject.transform.localRotation = this.selfStandPoint.transform.localRotation.clone();
                fighter.init({ hp: card.curHp, atk: card.atk });
                this.selfGroup.push(fighter);
            }
        }
    }
    initSelfGroupPosition() {
        for (let i = 0; i < this.selfGroup.length; i++) {
            let fighter = this.selfGroup[i];
            if (this.selfFightIndex == i) {
                fighter.gameObject.transform.position = this.selfStandPoint.transform.position.clone();
                fighter.gameObject.active = true;
            } else {
                fighter.gameObject.transform.position = this.selfOutPoint.transform.position.clone();
                fighter.gameObject.active = false;
            }
        }
    }
    addSkin(skinNode: Laya.Sprite3D, callback: Function) {
        if (skinNode && !skinNode.destroyed) {
            callback && callback();
        }
    }
    createEnemyGroup() {
        for (let i = 0; i < GameDataManager.Instance.enemyMonsterCardList.length; i++) {
            let card = GameDataManager.Instance.enemyMonsterCardList[i];
            let fighter = LayaTool.createSprite3DWithScript(`Enemy${card.monsterId}`, Fighter, this.clearNode);
            if (i == 0) {
                fighter.gameObject.transform.position = this.enemyStandPoint.transform.position.clone();
            } else {
                fighter.gameObject.transform.position = this.enemyOutPoint.transform.position.clone();
            }
            fighter.gameObject.transform.localRotation = this.enemyStandPoint.transform.localRotation.clone();
            if (GameDataManager.Instance.isBoss) {
                fighter.init({ hp: GameDataManager.Instance.curMapData.bossStat.hp, atk: GameDataManager.Instance.curMapData.bossStat.atk });
            } else {
                fighter.init({ hp: card.hp, atk: card.atk });
            }
            this.enemyGroup.push(fighter);
            if (this.enemyGroup.length == 1) {
                fighter.gameObject.active = true;
            } else {
                fighter.gameObject.active = false;
            }
        }
    }
    /**回合结算 */
    roundCount() {
        return new Promise<void>((resolve, reject) => {
            if (this.roundSide == 1) {
                if (this.enemyGroup[this.enemyFightIndex].isDeath) {
                    //当前的敌人死了
                    UserData.instance.finishTask(1, 3, 1);
                    let monsterId = GameDataManager.Instance.enemyMonsterCardList[this.enemyFightIndex].monsterId;
                    let monsterData = GameDataManager.Instance.monsterData[monsterId];
                    UserData.instance.gold += monsterData.drop;
                    UserData.instance.onDataChange("gold");
                    EventManager.event(EventType.AddMoney, ["gold", monsterData.drop]);
                    TongYiEventManager.sendEvent(10005, { item_type: 1, item_id: 1, get_num: monsterData.drop, get_type: 8, last_num: UserData.instance.gold });
                    if (this.isQte) EventManager.event(EventType.QteEnd);
                    if (this.enemyFightIndex < this.enemyGroup.length - 1) {
                        //还有敌人换上新敌人
                        this.enemyChange(this.enemyFightIndex + 1).then(() => {
                            this.isRoundFinish = true;
                            this.autoFight();
                            EventManager.event(EventType.EnableMustKill, false);
                            reject();
                        });
                    } else {
                        //敌人全死了
                        if (this.haveNextWave()) {
                            //还有下一波游戏继续
                            this.switchNextWaveEnemy().then(() => {
                                this.isRoundFinish = true;
                                this.autoFight();
                                EventManager.event(EventType.EnableMustKill, false);
                                reject();
                            });
                        } else {
                            //游戏胜利
                            GameDataManager.Instance.isGameEnd = true;
                            GameDataManager.Instance.isGameWin = true;
                            this.popCount();
                            this.isRoundFinish = true;
                            reject();
                        }
                    }
                } else {
                    if (this.isQte) {
                        EventManager.event(EventType.QteNext);
                        this.isRoundFinish = true;
                        reject();
                    } else {
                        this.roundSide = 2;
                        this.isRoundFinish = true;
                        resolve();
                    }
                }
            } else {
                if (this.selfGroup[this.selfFightIndex].isDeath) {
                    EventManager.event(EventType.FighterDeath, [1, this.selfFightIndex]);
                    //自己的人死了
                    let liveIndex = this.getFirstLiveMonsterIndex();
                    if (liveIndex != -1) {
                        //自己还有人
                        this.selfChange(liveIndex).then(() => {
                            this.roundSide = 1;
                            EventManager.event(EventType.EnableMustKill, false);
                            this.isRoundFinish = true;
                            this.autoFight();
                            reject();
                        });
                    } else {
                        //自己人全死了
                        //游戏失败
                        GameDataManager.Instance.isGameEnd = true;
                        GameDataManager.Instance.isGameWin = false;
                        this.popCount();
                        this.isRoundFinish = true;
                        reject();
                    }
                } else {
                    this.roundSide = 1;
                    EventManager.event(EventType.EnableMustKill, false);
                    this.isRoundFinish = true;
                    this.autoFight();
                    reject();
                }
            }
        });
    }
    syncHp(fightCardData: CardData) {
        for (let i = 0; i < UserData.instance.cardList.length; i++) {
            if (UserData.instance.cardList[i].cardId == fightCardData.cardId) {
                UserData.instance.cardList[i].curHp = fightCardData.curHp;
                UserData.instance.onDataChange("cardList");
                break;
            }
        }
    }
    piaoHp(num: number, pos: Laya.Vector3, isMustKill: boolean) {
        pos.y += 1;
        let vec2 = LayaTool.convert3DTo2DPosition(this.camera, pos);
        let label = new Laya.Label("-" + num);
        if (isMustKill) {
            label.font = "Kill";
        } else {
            label.font = "HP";
        }
        label.fontSize = 80;
        label.anchorX = 0.5;
        label.anchorY = 0.5;
        label.x = vec2.x;
        label.y = vec2.y;
        label.scaleX = 1;
        label.scaleY = 1;
        StageManager.EffectRoot.addChild(label);
        Laya.Tween.to(label, { scaleX: 1.6, scaleY: 1.6, y: vec2.y - 100 }, 300, Laya.Ease.sineOut, Laya.Handler.create(this, () => {
            Laya.Tween.to(label, { scaleX: 0.6, scaleY: 0.6, y: vec2.y - 50, alpha: 0 }, 200, Laya.Ease.sineIn, Laya.Handler.create(this, () => {
                label.destroy();
            }));
        }));
    }
    onFullHpRevive() {
        GameDataManager.Instance.isGameEnd = false;
        GameDataManager.Instance.isGameWin = false;
        this.roundSide = 1;
        this.clearSelfFightGroup();
        this.createSelfGroup();
        this.selfFightIndex = this.getFirstLiveMonsterIndex();
        this.initSelfGroupPosition();
        EventManager.event(EventType.InitFightCell);
        EventManager.event(EventType.EnableMustKill, false);
    }
    gotoCatchScene() {
        let enemyMonsterCardList = GameDataManager.Instance.enemyMonsterCardList;
        GameDataManager.Instance.catchMonsterId = enemyMonsterCardList[enemyMonsterCardList.length - 1].monsterId;
        GameDataManager.Instance.catchMonsterData = GameDataManager.Instance.monsterData[GameDataManager.Instance.catchMonsterId];
        GameDataManager.Instance.isCatch = false;
        SceneManager.Instance.openScene3D("FightScene2", CatchSceneLogic, [false]);
    }
    getFirstLiveMonsterIndex() {
        let liveIndex = -1;
        for (let i = 0; i < this.selfGroup.length; i++) {
            if (!this.selfGroup[i].isDeath) {
                liveIndex = i;
                break;
            }
        }
        return liveIndex;
    }
    hideCatchModel() {
        this.scene3d.getChildByName("Ball").active = false;
        this.scene3d.getChildByName("Effect_Catch").active = false;
        this.scene3d.getChildByName("Light_Catch").active = false;
    }
    getStandPoint() {
        this.selfStandPoint = this.scene3d.getChildByName("PlayerPoint") as Laya.Sprite3D;
        this.enemyStandPoint = this.scene3d.getChildByName("EnemyPoint") as Laya.Sprite3D;
        this.selfOutPoint = this.scene3d.getChildByName("PlayerPoint1") as Laya.Sprite3D;
        this.enemyOutPoint = this.scene3d.getChildByName("EnemyPoint1") as Laya.Sprite3D;
    }
    haveNextWave() {
        return false;
    }
    switchNextWaveEnemy() {
        return new Promise<void>(resolve => {
            resolve();
        });
    }
    clearEnemyGroup() {
        for (let i = 0; i < this.enemyGroup.length; i++) {
            this.enemyGroup[i].gameObject.destroy();
        }
        this.enemyGroup.length = 0;
    }
    setCameraFightPosAndRot() {
        this.camera.transform.localPosition = LayaTool.createVector3FromArray(this.fightCameraData.position);
        this.camera.transform.localRotation = LayaTool.createQuaternionFromArray(this.fightCameraData.rotation);
    }
    onChangeAutoFight() {
        GameDataManager.Instance.isAutoFighting = GameDataManager.Instance.autoFight;
        this.autoFight();
    }
    initAutoFight() {
        GameDataManager.Instance.isUseAutoFight = false;
        GameDataManager.Instance.isAutoFighting = false;
        if (UserData.instance.gameRank >= 2 &&
            GameDataManager.Instance.autoFight &&
            UserData.instance.fightTicket > 0) {
            GameDataManager.Instance.isUseAutoFight = true;
            GameDataManager.Instance.isAutoFighting = true;
        }
    }
    autoFight() {
        if (GameDataManager.Instance.isAutoFighting) {
            let liveIndex = this.getFirstLiveMonsterIndex();
            if (liveIndex != -1) {
                EventManager.event(EventType.AttackOnce, [liveIndex, false, true]);
            }
        }
    }
    sendSuccessEvent() {
        if (window["fightSendData"]) {
            window["fightSendData"].category = 2;
            TongYiEventManager.sendEvent(10003, window["fightSendData"]);
            window["fightSendData"] = null;
        }
    }
    sendFailEvent() {
        if (window["fightSendData"]) {
            window["fightSendData"].category = 3;
            TongYiEventManager.sendEvent(10003, window["fightSendData"]);
            window["fightSendData"] = null;
        }
    }
    clearSelfFightGroup() {
        for (let i = 0; i < this.selfGroup.length; i++) {
            if (this.selfGroup[i]) {
                this.selfGroup[i].gameObject.destroy();
            }
        }
        this.selfGroup.length = 0;
        this.selfFightGroupIndexList.length = 0;
    }
}