import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";

export default class ChallengeRankRewardCellRuntime extends ui.pages.ChallengeRankRewardCellUI {
    data: any;
    onAwake() {
        this.goBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.extraReceiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.goBtn) {
            EventManager.event(EventType.GoChallenge);
        } else if (e.target == this.receiveBtn) {
            EventManager.event(EventType.ReceiveRankReward, [this.data.index, "normal"]);
        } else if (e.target == this.extraReceiveBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                EventManager.event(EventType.ReceiveRankReward, [this.data.index, "extra"]);
            });
        }
    }
    public set dataSource(data: any) {
        if (!data) return;
        this.data = data;
        let levelStr = `${this.data.level[0]}-${this.data.level[1]}`;
        this.levelLabel.text = "通过" + levelStr;
        let parseLevel1 = (this.data.level[0] - 1) * 10 + this.data.level[1];
        let parseLevel2 = (UserData.instance.challengeLevel[0] - 1) * 10 + UserData.instance.challengeLevel[1];
        if (parseLevel2 > parseLevel1) {
            //达到关卡要求
            this.goBtn.visible = false;
            let normalReceive = UserData.instance.challengeRewardReceiveList.indexOf(levelStr) != -1;
            this.receiveBtn.visible = !normalReceive;
            this.normalReceiveTag.visible = normalReceive;
            let extraReceive = UserData.instance.challengeExtraRewardReceiveList.indexOf(levelStr) != -1;
            this.extraReceiveBtn.visible = !extraReceive;
            this.extraReceiveTag.visible = extraReceive;
        } else {
            //未达到关卡要求
            this.goBtn.visible = true;
            this.receiveBtn.visible = false;
            this.extraReceiveBtn.visible = false;
            this.normalReceiveTag.visible = false;
            this.extraReceiveTag.visible = false;
        }
        let rewardUiDataList = GameDataManager.Instance.getRewardUiDataList(this.data.rankReward);
        this.extraRewardList.array = rewardUiDataList;
        this.extraRewardList.refresh();

        let extraRewardUiDataList = GameDataManager.Instance.getRewardUiDataList(this.data.rankExtraReward);
        this.rewardList.array = extraRewardUiDataList;
        this.rewardList.refresh();
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}