import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import ResManager from "../gframe-work/manager/ResManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import TimeTool from "../gframe-work/tool/TimeTool";
import { ui } from "../ui/layaMaxUI";

export default class EggShopRuntime extends ui.pages.eggShopUI {
    isPlaying: boolean = false;
    niuDanTimeLimit: number = 0;
    isFirstOpenEggShop = false;
    onAwake() {
        this.commonSpine.x = 195;
        this.commonSpine.y = Laya.stage.height / 2 - 130;
        this.superSpine.x = 530;
        this.superSpine.y = Laya.stage.height / 2 - 140;
        this.commonSpine["_complete"] = Laya.Handler.create(this, () => {
            this.commonSpine.stop();
            this.commonSpine.on(Laya.Event.STOPPED, this, this.onCommonFinish);
        });
        this.superSpine["_complete"] = Laya.Handler.create(this, () => {
            this.superSpine.stop();
            this.superSpine.on(Laya.Event.STOPPED, this, this.onSuperFinish);
        });
        this.leftLabel.text = UserData.instance.niuDanLeftTime + "";
        this.superLeftLabel.text = UserData.instance.niuDanSuperLeftTime + "";
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
        this.updateNiuDanTimeStamp();
        this.updateSuperEggTip();
        // Laya.Scene.root.filters = [new Laya.BlurFilter(5)];
        Laya.Dialog.manager.maskLayer.alpha = 0.9;
        this.setBtnVideoTag();
    }
    updateNiuDanTimeStamp() {
        if (UserData.instance.niuDanLeftTime > 0) {
            let timeLimit = 600;
            if (UserData.instance.niuDanLeftTime == 2) {
                timeLimit = 300;
            } else if (UserData.instance.niuDanLeftTime == 3) {
                timeLimit = 180;
            } else if (UserData.instance.niuDanLeftTime == 4) {
                timeLimit = 0;
            }
            this.niuDanTimeLimit = timeLimit * 1000 - (Date.now() - UserData.instance.niuDanTimestamp);
            this.niuDanTimeLimit = this.niuDanTimeLimit > 0 ? this.niuDanTimeLimit : 0;
            if (this.niuDanTimeLimit > 0) {
                this.freeRed.visible = false;
                this.timeLabelBg.visible = true;
                this.timeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.niuDanTimeLimit));
                Laya.timer.loop(1000, this, this.onLoop);
            } else {
                this.timeLabelBg.visible = false;
                this.freeRed.visible = true;
            }
        } else {
            this.timeLabelBg.visible = false;
            this.freeRed.visible = false;
        }
    }
    onLoop() {
        this.niuDanTimeLimit -= 1000;
        if (this.niuDanTimeLimit <= 0) {
            this.niuDanTimeLimit = 0;
            this.timeLabelBg.visible = false;
            if (UserData.instance.niuDanLeftTime > 0) {
                this.freeRed.visible = true;
            } else {
                this.freeRed.visible = false;
            }
            Laya.timer.clear(this, this.onLoop);
        }
        this.timeLabel.text = TimeTool.dateFormat("MM:SS", new Date(this.niuDanTimeLimit));
    }
    onUserDataChange(key: string) {
        if (key == "niuDanLeftTime") {
            this.leftLabel.text = UserData.instance.niuDanLeftTime + "";
        } else if (key == "niuDanTimestamp") {
            this.updateNiuDanTimeStamp();
        } else if (key == "niuDanSuperLeftTime") {
            this.superLeftLabel.text = UserData.instance.niuDanSuperLeftTime + "";
        }
    }
    onCommonFinish() {
        this.isPlaying = false;
        this.commonEggReward();
    }
    onSuperFinish() {
        this.isPlaying = false;
        UserData.instance.superEggPlayTime++;
        UserData.instance.onDataChange("superEggPlayTime");
        let mustHighStar = false;
        if (this.isFirstOpenEggShop || UserData.instance.superEggPlayTime % 5 == 0) {
            mustHighStar = true;
        }
        EggShopRuntime.superEggReward(true, false, mustHighStar);
        this.updateSuperEggTip();
        if (Math.random() < 0.75 && UserData.instance.luckPackLeftTime > 0) {
            LayaTool.openScene("pages/luckyPack.scene", false, true, null, null, true);
        }
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.off(GuideManager.InnerTaskStart, this, this.onInnerTaskStart);
        Laya.timer.clear(this, this.onLoop);
    }
    onOpened() {
        this.superBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.freeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.closeBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        AllPlatformAdMgr.Instance.showBannerAd();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 6, button_id: 6, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        EventManager.on(GuideManager.InnerTaskStart, this, this.onInnerTaskStart);
    }
    onClosed() {
        AllPlatformAdMgr.Instance.hideBannerAd();
        // Laya.Scene.root.filters = null;
        Laya.Dialog.manager.maskLayer.alpha = UIConfig.popupBgAlpha;
    }
    commonEggReward() {
        UserData.instance.niuDanLeftTime--;
        UserData.instance.onDataChange("niuDanLeftTime");
        UserData.instance.niuDanTimestamp = Date.now();
        UserData.instance.onDataChange("niuDanTimestamp");
        let random = Math.random() * 100;
        if (random < 50) {
            //1-3星怪
            EggShopRuntime.getCard([1, 3], null);
        } else if (random < 55) {
            //4星素材怪
            EggShopRuntime.getCard([4, 4], [1003, 1008]);
        } else if (random < 85) {
            //金币100
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 100, 2]);
        } else if (random < 95) {
            //金币200
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 200, 2]);
        } else if (random < 100) {
            //金币300
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 300, 2]);
        }
    }
    static getCard(starArea: number[], idArea: number[] = null) {
        let randomCard = GameDataManager.Instance.getSpecifyStarMonsterCard(starArea, false, idArea);
        let monsterConfig = GameDataManager.Instance.monsterData;
        LayaTool.openScene("pages/catchResult.scene", false, true, null, [true, randomCard.monsterId, monsterConfig[randomCard.monsterId], false, 4, randomCard.special]);
    }
    static superEggReward(useSuperEggTime: boolean, mustMonster: boolean, mustHighStar = false) {
        if (useSuperEggTime) {
            UserData.instance.niuDanSuperLeftTime--;
            UserData.instance.onDataChange("niuDanSuperLeftTime");
        }
        let random = 0;
        if (!mustMonster) {
            if (mustHighStar) {
                random = 50 * Math.random();
            } else {
                random = Math.random() * 100;
            }
        } else {
            if (mustHighStar) {
                random = 50 * Math.random();
            } else {
                random = Math.random() * 55;
            }
            if (UserData.instance.isFirstDie) {
                UserData.instance.isFirstDie = false;
                UserData.instance.onDataChange("isFirstDie");
                let card = GameDataManager.Instance.getOneCardData(39);
                let monsterConfig = GameDataManager.Instance.monsterData;
                LayaTool.openScene("pages/catchResult.scene", false, true, null, [true, card.monsterId, monsterConfig[card.monsterId], false, 4, card.special]);
                return;
            }
        }
        if (random < 50) {
            //4-7星怪
            if (mustHighStar) {
                EggShopRuntime.getCard([7, 7], [0, 158]);
            } else {
                EggShopRuntime.getCard([4, 7], [0, 158]);
            }
        } else if (random < 55) {
            //7星素材怪
            EggShopRuntime.getCard([7, 7], [1003, 1008]);
        } else if (random < 85) {
            //金币300
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 300, 2]);
        } else if (random < 95) {
            //金币400
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 400, 2]);
        } else if (random < 100) {
            //金币600
            LayaTool.openScene("pages/getCard.scene", false, true, null, [1, 600, 2]);
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.superBtn) {
            if (this.isPlaying || UserData.instance.niuDanSuperLeftTime == 0) return;
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 6, button_id: 6, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-b7e3aa4e04c41392", () => {
                this.isPlaying = true;
                this.superSpine.play(0, false, true, 0);
                this.playNiuDanSound();
            });
        } else if (e.target == this.freeBtn) {
            if (this.isPlaying || UserData.instance.niuDanLeftTime == 0 || this.niuDanTimeLimit > 0) {
                if (this.niuDanTimeLimit > 0) {
                    EventManager.event(EventType.PopTip, "扭蛋冷却中");
                }
                return;
            }
            this.isPlaying = true;
            this.commonSpine.play(0, false, true, 0);
            this.playNiuDanSound();
        } else if (e.target == this.closeBtn) {
            this.close();
        }
    }
    playNiuDanSound() {
        ResManager.playSound("niuDanBtn.mp3");
        Laya.timer.once(1300, this, () => {
            ResManager.playSound("niuDaning.mp3");
        })
    }
    onInnerTaskStart(taskName: string) {
        if (taskName == "点击免费扭蛋") {
            GuideManager.startInnerTask(taskName, [{ node: this.freeBtn, isHandNode: true }]);
        }
    }
    updateSuperEggTip() {
        this.isFirstOpenEggShop = UserData.instance.isFirstOpenEggShop;
        if (UserData.instance.isFirstOpenEggShop) {
            this.superTip.skin = "ad/Ui_NiuWord1.png";
            UserData.instance.isFirstOpenEggShop = false;
            UserData.instance.onDataChange("isFirstOpenEggShop");
            this.superPlayTimeBg.visible = false;
        } else {
            this.superPlayTimeBg.visible = true;
            this.superTip.skin = "ad/Ui_NiuWord2.png";
            this.superPlayTimeLabel.text = (UserData.instance.superEggPlayTime % 5 + 1) + "";
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}