import ResManager from "../gframe-work/manager/ResManager";
import { ui } from "../ui/layaMaxUI";

export default class FightDotRuntime extends ui.pages.fightDotUI {
    dotList: Laya.Sprite[];
    sign: number = 1;
    onAwake() {
        this.dotList = [this.dot1, this.dot2, this.dot3];
        this.mustKillBtn.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
        this.mustKillBtn.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
        this.mustKillBtn.on(Laya.Event.MOUSE_OUT, this, this.onMouseOut);
        this.sign = Math.sign(this.scaleX);
    }
    public set dataSource(data: number) {
        if (typeof data != "number") return;
        if (data >= 3) {
            this.mustKillBtn.visible = true;
        } else {
            this.mustKillBtn.visible = false;
        }
        for (let i = 0; i < 3; i++) {
            if (i < data) {
                this.dotList[i].visible = true;
            } else {
                this.dotList[i].visible = false;
            }
        }
    }
    onMouseDown(e: Laya.Event) {
        if (this.mustKillBtn.disabled) return;
        Laya.Tween.to(this.mustKillBtn, { scaleX: 0.9 * this.sign, scaleY: 0.9 }, 50, Laya.Ease.sineOut);
    }
    onMouseUp(e: Laya.Event) {
        if (this.mustKillBtn.disabled) return;
        ResManager.playSound("click.mp3");
        Laya.Tween.to(this.mustKillBtn, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    onMouseOut(e: Laya.Event) {
        if (this.mustKillBtn.disabled) return;
        Laya.Tween.to(this.mustKillBtn, { scaleX: 1 * this.sign, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
}