import { EventType } from "../gamelogic/GEnum";
import EventManager from "../gframe-work/manager/EventManager";
import MathTool from "../gframe-work/tool/MathTool";
import { ui } from "../ui/layaMaxUI";
import ChooseOneRuntime from "./ChooseOneRuntime";

export default class QteRuntime extends ui.pages.qteUI {
    timeLeft: number = 0;
    numList: number[] = [1, 2, 3, 4];
    targetNum: number = 0;
    isClickWrong: boolean = false;
    numImageList: Laya.Image[];
    qteTime: number = 2;
    qteTypeList: number[];
    wrongIndex: number = -1;
    rollTimeLeft: number = 3;
    totalAngle = 0;
    //1:正方向-1：反方向
    rollDir: number = 1;
    startPoint: Laya.Vector2 = new Laya.Vector2;
    nowPoint: Laya.Vector2 = new Laya.Vector2;
    centerPoint: Laya.Point = new Laya.Point;
    isSuccess: boolean = false;
    onAwake() {
        let mask = new Laya.Sprite();
        mask.width = Laya.stage.width;
        mask.height = Laya.stage.height;
        mask.graphics.clear(true);
        mask.graphics.drawRect(0, 0, Laya.stage.width, Laya.stage.height, UIConfig.popupBgColor);
        mask.alpha = UIConfig.popupBgAlpha;
        this.maskBox.addChild(mask);
        this.num1.on(Laya.Event.MOUSE_DOWN, this, this.onNumClick);
        this.num2.on(Laya.Event.MOUSE_DOWN, this, this.onNumClick);
        this.num3.on(Laya.Event.MOUSE_DOWN, this, this.onNumClick);
        this.num4.on(Laya.Event.MOUSE_DOWN, this, this.onNumClick);
        this.num4.on(Laya.Event.MOUSE_DOWN, this, this.onNumClick);
        this.rollBg.on(Laya.Event.MOUSE_MOVE, this, this.onRollBgMove);
        this.rollBg.on(Laya.Event.MOUSE_DOWN, this, this.onRollBgMove);
        this.numImageList = [this.num1, this.num2, this.num3, this.num4];
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
    }
    onQteNext() {
        this.qteNext();
    }
    onRollBgMove(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_DOWN) {
            this.centerPoint.x = 178;
            this.centerPoint.y = 178;
            this.rollBg.localToGlobal(this.centerPoint, false);
            this.startPoint.x = e.stageX - this.centerPoint.x;
            this.startPoint.y = e.stageY - this.centerPoint.y;
        } else if (e.type == Laya.Event.MOUSE_MOVE) {
            this.nowPoint.x = e.stageX - this.centerPoint.x;
            this.nowPoint.y = e.stageY - this.centerPoint.y;
            let rad = MathTool.getRadBetweenVec2(this.startPoint, this.nowPoint);
            let angle = rad * MathTool.rad2Deg;
            this.rollBg.rotation += angle;
            if (Math.sign(rad) == this.rollDir) {
                if (this.rollBg.skin != "qte/Ui_QteZhuan1.png")
                    this.rollBg.skin = "qte/Ui_QteZhuan1.png";
                this.totalAngle += Math.abs(angle);
                if (this.totalAngle >= 360) {
                    this.totalAngle = 0;
                    if (this.rollTimeLeft > 0) {
                        this.rollTimeLeft--;
                        this.rollLeftLabel.text = this.rollTimeLeft + "";
                        if (this.rollTimeLeft == 0) {
                            this.isSuccess = true;
                            EventManager.event(EventType.QteSuccess);
                        }
                    }
                }
            } else {
                if (this.rollBg.skin != "qte/Ui_QteZhuan4.png")
                    this.rollBg.skin = "qte/Ui_QteZhuan4.png";
                this.totalAngle -= Math.abs(angle);
                if (this.totalAngle < 0) this.totalAngle = 0;
            }
            this.startPoint.x = e.stageX - this.centerPoint.x;
            this.startPoint.y = e.stageY - this.centerPoint.y;
        }
    }
    onNumClick(e: Laya.Event) {
        let index = this.numImageList.indexOf(e.target as any);
        if (index == this.numList.indexOf(this.targetNum)) {
            //点对了
            if (this.wrongIndex >= 0) {
                this.numImageList[this.wrongIndex].skin = `qte/Ui_White${this.numList[this.wrongIndex]}.png`;
                this.isClickWrong = false;
                this.wrongIndex = -1;
            }
            (e.target as any).skin = `qte/Ui_Yellow${this.targetNum}.png`;
            if (this.targetNum < 4) {
                this.targetNum++;
            } else {
                this.isSuccess = true;
                EventManager.event(EventType.QteSuccess);
            }
        } else {
            //点错了
            if (this.isClickWrong) return;
            this.isClickWrong = true;
            let num = this.numList[index];
            this.wrongIndex = index;
            (e.target as any).skin = `qte/Ui_Red${num}.png`;
        }
    }
    onDestroy() {
        this.qteEnd();
    }
    onFrameLoop() {
        if (this.isSuccess) return;
        if (this.timeLeft > 0) {
            this.timeLeft -= Laya.timer.delta;
            if (this.timeLeft <= 0) {
                this.timeLeft = 0;
                this.qteNext();
            }
            this.setTimeLeft(this.timeLeft);
        }
    }
    setQteType(type: number) {
        if (type == 1) {
            this.numBox.visible = true;
            this.rollBox.visible = false;
            this.initNumBox();
        } else if (type == 2) {
            this.numBox.visible = false;
            this.rollBox.visible = true;
            this.initRollBox();
        }
        this.isSuccess = false;
        this.setTimeLeft(3000);
    }
    setTimeLeft(left: number) {
        this.timeLeft = left;
        this.progressBar.width = this.timeLeft / 3000 * 500;
    }
    initNumBox() {
        let list = [1, 2, 3, 4];
        for (let i = 0; i < 4; i++) {
            this.numList[i] = list.splice(MathTool.getRandom(0, list.length - 1), 1)[0];
        }
        for (let i = 0; i < this.numImageList.length; i++) {
            this.numImageList[i].skin = `qte/Ui_White${this.numList[i]}.png`;
        }
        this.targetNum = 1;
        this.isClickWrong = false;
    }
    initRollBox() {
        this.rollBg.skin = "qte/Ui_QteZhuan1.png";
        this.rollTimeLeft = 3;
        this.rollLeftLabel.text = this.rollTimeLeft + "";
        this.totalAngle = 0;
        if (Math.random() < 0.5) {
            this.rollDir = 1;
            this.rollBg.scaleX = 1;
        } else {
            this.rollDir = -1;
            this.rollBg.scaleX = -1;
        }
    }
    qteStart() {
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        EventManager.on(EventType.QteNext, this, this.onQteNext);
        this.qteTime = 2;
        let list = [1, 2];
        this.qteTypeList = [1, 2];
        for (let i = 0; i < 2; i++) {
            this.qteTypeList[i] = list[MathTool.getRandom(0, list.length - 1)];
        }
        this.qteNext();
    }
    qteNext() {
        console.error("netxt");
        if (this.qteTime > 0) {
            EventManager.event(EventType.ChangeQteState, 2);
            this.qteTime--;
            this.setQteType(this.qteTypeList.pop());
        } else {
            this.qteEnd();
        }
    }
    qteEnd() {
        console.error("qteend");
        Laya.timer.clear(this, this.onFrameLoop);
        EventManager.off(EventType.QteNext, this, this.onQteNext);
        EventManager.event(EventType.ChangeQteState, 3);
    }
}