import EventManager from "../gframe-work/manager/EventManager";
import ResManager from "../gframe-work/manager/ResManager";
import { ui } from "../ui/layaMaxUI";

export default class DownPanelRuntime extends ui.pages.downPanelUI {
    btnList: ui.pages.downBtnUI[] = [];
    pageList: any[] = [];
    curIndex: number = 0;
    lastIndex: number = 0;
    tweenTarget = { progress: 0 };
    isTweening: boolean = false;
    canChangePage: boolean = true;
    mainPage: any = null;
    mainBtn: any = null;
    btnDataArr: any[] = null;
    pageBox: any = null;
    isShowMain: boolean = true;
    unlockBtnList: ui.pages.downBtnUI[];
    public static OnSelectPage: string = "OnSelectPage";
    onAwake() {

    }
    initBtnArr(btnDataArr: { iconSkin: string, btn: ui.pages.downBtnUI }[]) {
        this.curIndex = -1;
        this.lastIndex = -1;
        this.btnDataArr = btnDataArr;
        for (let i = 0; i < btnDataArr.length; i++) {
            let btnData = btnDataArr[i];
            this.btnList.push(btnData.btn);
            this.pageList.push(null);
            if (btnData.iconSkin) btnData.btn.btnIcon.skin = btnData.iconSkin;
            btnData.btn.btnMask.visible = false;
            btnData.btn.on(Laya.Event.CLICK, this, this.onBtnClick, [i]);
        }
        this.tweenTarget.progress = 1;
        this.onTweenUpdate();
    }
    setBtnScale() {
        for (let i = 0; i < this.btnList.length; i++) {
            let btn = this.btnList[i];
            btn.on(Laya.Event.MOUSE_DOWN, this, this.onMouseDown);
            btn.on(Laya.Event.MOUSE_UP, this, this.onMouseUp);
            btn.on(Laya.Event.MOUSE_OUT, this, this.onMouseOut);
        }
    }
    onMouseDown(e: Laya.Event) {
        Laya.Tween.to(e.target, { scaleX: 0.9, scaleY: 0.9 }, 50, Laya.Ease.sineOut);
    }
    onMouseUp(e: Laya.Event) {
        Laya.Tween.to(e.target, { scaleX: 1, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    onMouseOut(e: Laya.Event) {
        Laya.Tween.to(e.target, { scaleX: 1, scaleY: 1 }, 50, Laya.Ease.sineIn);
    }
    setMainPage(btn: any, mainPage: any) {
        this.mainPage = mainPage;
        this.mainBtn = btn;
        this.mainBtn && this.mainBtn.on(Laya.Event.CLICK, this, this.onBtnClick, [-1]);
    }
    setPage(index: number, page: any) {
        if (!page) return;
        this.pageList[index] = page;
        page.x = Laya.stage.width + Laya.stage.width * index;
    }
    getPage<T>(index: number): T {
        if (index == -1) return this.mainPage;
        return this.pageList[index];
    }
    getBtn<T>(index: number): T {
        if (index == -1) return this.mainBtn;
        return this.btnList[index] as any;
    }
    onBtnClick(btnIndex: number, e: Laya.Event = null) {
        e && e.stopPropagation();
        let btn = this.getBtn<ui.pages.downBtnUI>(btnIndex);
        let unlockIndex = this.unlockBtnList.indexOf(btn);
        ResManager.playSound("click.mp3");
        if (this.isTweening || !this.canChangePage || btn.btnMask.visible) return;
        if (btnIndex != -1) {
            if (btn.btnIcon.skin == "handBook/Btn_Close2.png") {
                btn.btnIcon.skin = this.btnDataArr[btnIndex].iconSkin;
                btnIndex = -1;
            } else {
                btn.btnIcon.skin = "handBook/Btn_Close2.png";
            }
        }
        this.mainPage.visible = this.isShowMain;
        this.isTweening = true;
        this.tweenTarget.progress = 0;
        this.curIndex = btnIndex;
        // Laya.Tween.to(
        //     this.tweenTarget,
        //     {
        //         progress: 1,
        //         update: Laya.Handler.create(this, this.onTweenUpdate, null, false),
        //     },
        //     380,
        //     Laya.Ease.backOut,
        //     Laya.Handler.create(this, this.onTweenComplete)
        // );
        let targetX = 0;
        if (btnIndex != -1) targetX = -Laya.stage.width * (unlockIndex + 1);
        console.log("pageBox", targetX);
        Laya.Tween.to(this.pageBox, { x: targetX }, 380, Laya.Ease.backOut, Laya.Handler.create(this, this.onTweenComplete), 0, true);
        if (this.lastIndex != -1) {
            let lastBtn = this.getBtn<any>(this.lastIndex);
            if (lastBtn.btnIcon.skin == "handBook/Btn_Close2.png") {
                lastBtn.btnIcon.skin = this.btnDataArr[this.lastIndex].iconSkin;
            }
        }
        let lastPage = this.getPage<any>(this.lastIndex);
        if (lastPage.offSelect) lastPage.offSelect();
        let curPage = this.getPage<any>(this.curIndex);
        if (curPage.onSelect) curPage.onSelect();
        EventManager.event(DownPanelRuntime.OnSelectPage, curPage);
        // if (this.curIndex == -1) {
        //     this.mainBtn.visible = false;
        // } else {
        //     this.mainBtn.visible = true;
        // }
    }
    onTweenUpdate() {
        let lastPage = this.getPage<any>(this.lastIndex);
        let curPage = this.getPage<any>(this.curIndex);
        if (this.curIndex == -1) {
            curPage.x = (1 - this.tweenTarget.progress) * -Laya.stage.width;
            if (curPage != lastPage) lastPage.x = this.tweenTarget.progress * Laya.stage.width;
        } else {
            curPage.x = (1 - this.tweenTarget.progress) * Laya.stage.width;
            if (curPage != lastPage) lastPage.x = this.tweenTarget.progress * -Laya.stage.width;
        }
    }
    onTweenComplete() {
        this.isTweening = false;
        this.lastIndex = this.curIndex;
    }
    showBtnRedPoint(index: number, isShow: boolean) {
        let btn = this.btnList[index];
        btn.redPoint.visible = isShow;
    }
    setBtnLockState(btnIndex: number, lock: boolean) {
        let btn = this.getBtn<ui.pages.downBtnUI>(btnIndex);
        btn.btnMask.visible = lock;
    }
}