import CardData from "../gamelogic/CardData";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import UserData from "../gframe-work/manager/UserData";
import LayaTool from "../gframe-work/tool/LayaTool";
import TimeTool from "../gframe-work/tool/TimeTool";
import { ui } from "../ui/layaMaxUI";
import CardBagRuntime from "./CardBagRuntime";
import CardRuntime from "./CardRuntime";

export default class GroupRuntime extends ui.pages.groupUI {
    selectCard: CardRuntime = null;
    cloneCard: CardRuntime = null;
    /**0:空白位置1：上面2：第一张卡位置3：第二张卡位置4：第三张卡位置 */
    posType: number = 0;
    selectFrom: number = 0;
    stopClick = false;
    isMove: boolean = false;
    downPoint: Laya.Point = new Laya.Point();
    onAwake() {
        EventManager.on(EventType.SelectCard, this, this.onSelectPageCard);
        this.refreshFightGroupList();
        (<CardBagRuntime>this.cardBag).isShowStoreEgg = true;
        (<CardBagRuntime>this.cardBag).exceptFunc = this.exceptFunc;
        (<CardBagRuntime>this.cardBag).initMonsterList();

        this.quickUpBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.on(Laya.Event.MOUSE_MOVE, this, this.onGroupPageEvent);
        this.on(Laya.Event.MOUSE_UP, this, this.onGroupPageEvent);
        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OUT, this, this.onCardBagEvent);
        (<CardBagRuntime>this.cardBag).monsterList.on(Laya.Event.MOUSE_OVER, this, this.onCardBagEvent);

        this.cardBox1.on(Laya.Event.MOUSE_OUT, this, this.onUpListEvent);
        this.cardBox1.on(Laya.Event.MOUSE_OVER, this, this.onUpListEvent);
        this.cardBox1.on(Laya.Event.CLICK, this, this.onUpListEvent);
        this.cardBox1.on(Laya.Event.MOUSE_DOWN, this, this.onUpListEvent);

        this.cardBox2.on(Laya.Event.MOUSE_OUT, this, this.onUpListEvent);
        this.cardBox2.on(Laya.Event.MOUSE_OVER, this, this.onUpListEvent);
        this.cardBox2.on(Laya.Event.CLICK, this, this.onUpListEvent);
        this.cardBox2.on(Laya.Event.MOUSE_DOWN, this, this.onUpListEvent);

        this.cardBox3.on(Laya.Event.MOUSE_OUT, this, this.onUpListEvent);
        this.cardBox3.on(Laya.Event.MOUSE_OVER, this, this.onUpListEvent);
        this.cardBox3.on(Laya.Event.CLICK, this, this.onUpListEvent);
        this.cardBox3.on(Laya.Event.MOUSE_DOWN, this, this.onUpListEvent);

        this.cloneCard = new CardRuntime();
        this.addChild(this.cloneCard);
        this.cloneCard.visible = false;
        EventManager.on(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.on(EventType.ComposeNew, this, this.onComposeNew);
        EventManager.on(EventType.ChangeFightList, this, this.onChangeFightList);
        this.quickUpRed.visible = GroupRuntime.haveRed();
        this.judgeUnlockFightPos();
        this.updateFightPosState();
        this.registerUnlockPosBtnEvent();
        this.setBtnVideoTag();
    }
    onChangeFightList(dataList: CardData[], isAdd: boolean, pos: number) {
        this.changeFightList(dataList, isAdd, pos);
    }
    onUpListEvent(e: Laya.Event) {
        if (e.target == this.cardBox1) {
            if (e.type == Laya.Event.MOUSE_OUT) {
                this.posType = 0;
            } else if (e.type == Laya.Event.MOUSE_OVER) {
                this.posType = 2;
            } else if (e.type == Laya.Event.MOUSE_DOWN) {
                if (!(<CardRuntime>this.card1).visible) return;
                this.selectCard = (<CardRuntime>this.card1);
                this.selectFrom = 2;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.cloneCard.dataSource = this.selectCard.data;
            } else if (e.type == Laya.Event.CLICK) {
                if (this.stopClick || !(<CardRuntime>this.card1).visible) return;
                if (this.otherAllEmpty(0)) {
                    EventManager.event(EventType.PopTip, "至少上阵一只怪物");
                    return;
                }
                if (!this.fightPosUnlock(0)) {
                    return;
                }
                this.changeFightList([(<CardRuntime>this.card1).data], false, 0);
            }
        } else if (e.target == this.cardBox2) {
            if (e.type == Laya.Event.MOUSE_OUT) {
                this.posType = 0;
            } else if (e.type == Laya.Event.MOUSE_OVER) {
                this.posType = 3;
            } else if (e.type == Laya.Event.MOUSE_DOWN) {
                if (!(<CardRuntime>this.card2).visible) return;
                this.selectCard = (<CardRuntime>this.card2);
                this.selectFrom = 3;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.cloneCard.dataSource = this.selectCard.data;
            } else if (e.type == Laya.Event.CLICK) {
                if (this.stopClick || !(<CardRuntime>this.card2).visible) return;
                if (this.otherAllEmpty(1)) {
                    EventManager.event(EventType.PopTip, "至少上阵一只怪物");
                    return;
                }
                if (!this.fightPosUnlock(1)) {
                    return;
                }
                this.changeFightList([(<CardRuntime>this.card1).data], false, 1);
            }
        } else if (e.target == this.cardBox3) {
            if (e.type == Laya.Event.MOUSE_OUT) {
                this.posType = 0;
            } else if (e.type == Laya.Event.MOUSE_OVER) {
                this.posType = 4;
            } else if (e.type == Laya.Event.MOUSE_DOWN) {
                if (!(<CardRuntime>this.card3).visible) return;
                this.selectCard = (<CardRuntime>this.card3);
                this.selectFrom = 4;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.cloneCard.dataSource = this.selectCard.data;
            } else if (e.type == Laya.Event.CLICK) {
                if (this.stopClick || !(<CardRuntime>this.card3).visible) return;
                if (this.otherAllEmpty(2)) {
                    EventManager.event(EventType.PopTip, "至少上阵一只怪物");
                    return;
                }
                if (!this.fightPosUnlock(2)) {
                    return;
                }
                this.changeFightList([(<CardRuntime>this.card1).data], false, 2);
            }
        }
    }
    getFirstEmptyUnlockIndex() {
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightCardList[i] == null && this.fightPosUnlock(i)) {
                return i;
            }
        }
        return -1;
    }
    onCardBagEvent(e: Laya.Event) {
        if (e.type == Laya.Event.MOUSE_OUT) {
            this.posType = 0;
        } else if (e.type == Laya.Event.MOUSE_OVER) {
            this.posType = 1;
        }
    }
    onGroupPageEvent(e: Laya.Event) {
        if (!this.selectCard) return;
        if (e.type == Laya.Event.MOUSE_MOVE) {
            if (this.selectCard.visible) {
                this.cloneCard.visible = true;
                this.selectCard.visible = false;
                (<CardBagRuntime>this.cardBag).monsterList.scrollBar.stopScroll();
            }
            if (this.cloneCard.visible) {
                this.cloneCard.x = e.stageX - this.cloneCard.width / 2;
                this.cloneCard.y = e.stageY - this.cloneCard.height / 2;
            }
            this.isMove = true;
        } else if (e.type == Laya.Event.MOUSE_UP) {
            if (this.posType == 0 || this.posType == this.selectFrom ||
                (this.posType >= 2 && this.posType <= 4 && !this.fightPosUnlock(this.posType - 2)) ||
                (this.posType == 1 && this.otherAllEmpty(this.selectFrom - 2))) {
                if (this.isMove && this.posType == this.selectFrom && this.posType >= 2 && this.posType <= 4) {
                    this.stopClick = true;
                    Laya.timer.frameOnce(2, this, () => {
                        this.stopClick = false;
                    });
                }
                if (this.posType == 1 && this.otherAllEmpty(this.selectFrom - 2)) {
                    EventManager.event(EventType.PopTip, "至少上阵一只怪物");
                }
                //空白位置或者位置相同或者落点位置没有解锁
                this.clearSelect(true);
            } else {
                if (this.posType == 1) {
                    //卡库位置
                    if (this.selectFrom == 2) {
                        this.changeFightList([this.selectCard.data], false, 0);
                        this.clearSelect(false);
                    } else if (this.selectFrom == 3) {
                        this.changeFightList([this.selectCard.data], false, 1);
                        this.clearSelect(false);
                    } else if (this.selectFrom == 4) {
                        this.changeFightList([this.selectCard.data], false, 2);
                        this.clearSelect(false);
                    }
                } else if (this.posType == 2) {
                    //第一张卡位置
                    if (this.selectFrom == 1) {
                        this.changeFightList([this.selectCard.data], true, 0);
                        this.clearSelect(false);
                        EventManager.event(EventType.PopTip, "上阵成功");
                    } else if (this.selectFrom == 3) {
                        let temp = UserData.instance.fightCardList[0];
                        UserData.instance.fightCardList[0] = UserData.instance.fightCardList[1];
                        UserData.instance.fightCardList[1] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    } else if (this.selectFrom == 4) {
                        let temp = UserData.instance.fightCardList[0];
                        UserData.instance.fightCardList[0] = UserData.instance.fightCardList[2];
                        UserData.instance.fightCardList[2] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    }
                } else if (this.posType == 3) {
                    //第二张卡位置
                    if (this.selectFrom == 1) {
                        this.changeFightList([this.selectCard.data], true, 1);
                        this.clearSelect(false);
                        EventManager.event(EventType.PopTip, "上阵成功");
                    } else if (this.selectFrom == 2) {
                        let temp = UserData.instance.fightCardList[1];
                        UserData.instance.fightCardList[1] = UserData.instance.fightCardList[0];
                        UserData.instance.fightCardList[0] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    } else if (this.selectFrom == 4) {
                        let temp = UserData.instance.fightCardList[1];
                        UserData.instance.fightCardList[1] = UserData.instance.fightCardList[2];
                        UserData.instance.fightCardList[2] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    }
                } else if (this.posType == 4) {
                    //第三张卡位置
                    if (this.selectFrom == 1) {
                        this.changeFightList([this.selectCard.data], true, 2);
                        this.clearSelect(false);
                        EventManager.event(EventType.PopTip, "上阵成功");
                    } else if (this.selectFrom == 2) {
                        let temp = UserData.instance.fightCardList[2];
                        UserData.instance.fightCardList[2] = UserData.instance.fightCardList[0];
                        UserData.instance.fightCardList[0] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    } else if (this.selectFrom == 3) {
                        let temp = UserData.instance.fightCardList[2];
                        UserData.instance.fightCardList[2] = UserData.instance.fightCardList[1];
                        UserData.instance.fightCardList[1] = temp;
                        this.changeFightList([...UserData.instance.fightCardList], true, -1);
                        this.clearSelect(false);
                    }
                }
            }
            this.isMove = false;
        }
    }
    clearSelect(isRecovery: boolean) {
        this.cloneCard.visible = false;
        if (isRecovery) this.selectCard.visible = true;
        this.selectCard = null;
        this.selectFrom = 0;
        this.posType = 0;
        this.downPoint.x = 0;
        this.downPoint.y = 0;
    }
    exceptFunc(cardData: CardData) {
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightCardList[i] && cardData.cardId == UserData.instance.fightCardList[i].cardId) {
                return false;
            }
        }
        return true;
    }
    onDestroy() {
        EventManager.off(UserData.UserDataChange, this, this.onUserDataChange);
        EventManager.off(EventType.SelectCard, this, this.onSelectPageCard);
        EventManager.off(EventType.ComposeNew, this, this.onComposeNew);
        EventManager.off(EventType.ChangeFightList, this, this.onChangeFightList);
    }
    onComposeNew(cardData: CardData) {
        let emptyCellIndex = -1;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightCardList[i] == null && this.fightPosUnlock(i)) {
                emptyCellIndex = i;
                break;
            }
        }
        if (emptyCellIndex != -1) {
            this.changeFightList([cardData], true, emptyCellIndex);
        }
    }
    onUpBoxEvent() {

    }
    onUserDataChange(key: string) {
        if (key == "cardList") {
            this.removeComposeCardFromFightList();
        } else if (key == "fightCardList" || key == "fightPosUnlock") {
            this.quickUpRed.visible = GroupRuntime.haveRed();
        }
    }
    onSelectPageCard(list: Laya.List, e: Laya.Event, card: CardRuntime) {
        if (list != (<CardBagRuntime>this.cardBag).monsterList) return;
        if (card.data["isEgg"]) {
            if (e.type == Laya.Event.MOUSE_DOWN) {
                AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                    LayaTool.openScene("pages/superEggMachine.scene", false, true, null, [false, true]);
                    card.removeStoreEgg();
                });
            }
        } else {
            if (e.type == Laya.Event.MOUSE_DOWN) {
                this.selectFrom = 1;
                this.selectCard = card;
                this.downPoint.x = e.stageX;
                this.downPoint.y = e.stageY;
                this.cloneCard.dataSource = card.data;
            } else if (e.type == Laya.Event.CLICK) {
                let firstEmptyIndex = this.getFirstEmptyUnlockIndex();
                if (firstEmptyIndex != -1) {
                    this.changeFightList([card.data], true, firstEmptyIndex);
                }
            }
        }
    }
    refreshFightGroupList() {
        let cardList = [this.card1, this.card2, this.card3];
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightCardList[i]) {
                cardList[i].visible = true;
                cardList[i].dataSource = UserData.instance.fightCardList[i];
            } else {
                cardList[i].visible = false;
            }
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.quickUpBtn) {
            let cardDataList = [...UserData.instance.cardList];
            cardDataList.sort((a, b) => {
                return (b.atk + b.hp) - (a.atk + a.hp);
            });
            let dataList: CardData[] = [];
            let monsterIndex = 0;
            for (let i = 0; i < 3; i++) {
                if (monsterIndex < cardDataList.length && this.fightPosUnlock(i)) {
                    dataList.push(cardDataList[monsterIndex]);
                    monsterIndex++;
                } else {
                    dataList.push(null);
                }
            }
            for (let i = 0; i < dataList.length; i++) {
                if (dataList[i]) {
                    let have = false;
                    for (let j = 0; j < UserData.instance.fightCardList.length; j++) {
                        if (UserData.instance.fightCardList[j]) {
                            if (dataList[i].cardId == UserData.instance.fightCardList[j].cardId) {
                                have = true;
                                break;
                            }
                        }
                    }
                    if (!have) {
                        EventManager.event(EventType.PopTip, "上阵成功");
                        break;
                    }
                }
            }
            this.changeFightList(dataList, true, -1);
            GuideManager.finishInnerTask("点击一键上阵");
        }
    }
    changeFightList(dataList: CardData[], isAdd: boolean, pos: number) {
        for (let i = 0; i < dataList.length; i++) {
            if (isAdd) {
                if (pos != -1) {
                    UserData.instance.fightCardList[pos] = dataList[i];
                } else {
                    UserData.instance.fightCardList[i] = dataList[i];
                }
            } else {
                if (pos != -1) {
                    UserData.instance.fightCardList[pos] = null;
                } else {
                    for (let j = 0; j < UserData.instance.fightCardList.length; j++) {
                        if (dataList[i].cardId == UserData.instance.fightCardList[j].cardId) {
                            UserData.instance.fightCardList[j] = null;
                            break;
                        }
                    }
                }
            }
        }
        UserData.instance.onDataChange("fightCardList");
        this.refreshFightGroupList();
        (<CardBagRuntime>this.cardBag).initMonsterList();
    }
    removeComposeCardFromFightList() {
        if (UserData.instance.fightCardList.length > 0) {
            let cardIdList = [];
            for (let j = 0; j < UserData.instance.cardList.length; j++) {
                cardIdList.push(UserData.instance.cardList[j].cardId);
            }
            for (let i = 0; i < UserData.instance.fightCardList.length;) {
                let fightCard = UserData.instance.fightCardList[i];
                if (fightCard && cardIdList.indexOf(fightCard.cardId) == -1) {
                    UserData.instance.fightCardList[i] = null;
                } else {
                    i++;
                }
            }
            UserData.instance.onDataChange("fightCardList");
            this.refreshFightGroupList();
        }
    }
    onSelect() {
        this.visible = true;
        GameDataManager.Instance.isPause = true;
        EventManager.event(EventType.PlayerStopMove);
        GuideManager.finishInnerTask("点击队伍组建");
        // let globalPoint = new Laya.Point();
        // this.groupBg.localToGlobal(globalPoint, false);
        // let top = (globalPoint.y + 200) / Laya.stage.height * wx.getSystemInfoSync().screenHeight;
        // AllPlatformAdMgr.Instance.initBannerAd(top);
        // AllPlatformAdMgr.Instance.showBannerAd();
    }
    offSelect() {
        this.visible = false;
        GameDataManager.Instance.isPause = false;
        GuideManager.finishInnerTask("点击关闭队伍组建");
        if (Math.random() < 0.5) AllPlatformAdMgr.Instance.showInterstitialAd(null);
        // AllPlatformAdMgr.Instance.hideBannerAd();
        // AllPlatformAdMgr.Instance.initBannerAd();
    }
    updateFightPosState() {
        let stateList = [];
        for (let i = 0; i < UserData.instance.fightPosUnlock.length; i++) {
            let unlock = UserData.instance.fightPosUnlock[i];
            let data = { unlockBtn: { visible: false }, cover: { skin: "ad/Ui_MaskShangZhen1.png", visible: false } };
            if (unlock) {
                data.unlockBtn.visible = false;
                data.cover.visible = false;
            } else {
                data.unlockBtn.visible = true;
                data.cover.visible = true;
                if (i == 1) {
                    data.cover.skin = "ad/Ui_MaskShangZhen1.png";
                } else if (i == 2) {
                    data.cover.skin = "ad/Ui_MaskShangZhen2.png";
                }
            }
            stateList.push(data);
        }
        let lockBoxList = [this.lockBox1, this.lockBox2, this.lockBox3];
        for (let i = 0; i < stateList.length; i++) {
            if (stateList[i].unlockBtn.visible == false) {
                lockBoxList[i].visible = false;
            } else {
                lockBoxList[i].visible = true;
                (<Laya.Image>lockBoxList[i].getChildByName("cover")).skin = stateList[i].cover.skin;
            }
        }
    }
    registerUnlockPosBtnEvent() {
        let lockBoxList = [this.lockBox1, this.lockBox2, this.lockBox3];
        for (let i = 0; i < lockBoxList.length; i++) {
            let box = lockBoxList[i];
            box.getChildByName("unlockBtn").on(Laya.Event.CLICK, this, this.onUnlockFightPos, [i]);
        }
    }
    onStateListHandler(e: Event, index: number) {
        console.log(e, index);
    }
    onUnlockFightPos(posId: number) {
        if (UserData.instance.fightPosUnlock[posId] == false) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-cf2f4369f3e13570", () => {
                UserData.instance.fightPosUnlock[posId] = true;
                UserData.instance.onDataChange("fightPosUnlock");
                this.updateFightPosState();
            });
        }
    }
    fightPosUnlock(posId: number) {
        return UserData.instance.fightPosUnlock[posId];
    }
    judgeUnlockFightPos() {
        if (UserData.instance.fightPosUnlock[1] == false) {
            let dayDiff = TimeTool.dayDiff(UserData.instance.registerTimestamp, Date.now());
            if (dayDiff >= 1) {
                UserData.instance.fightPosUnlock[1] = true;
                UserData.instance.onDataChange("fightPosUnlock");
            }
        }
    }
    otherAllEmpty(curIndex: number) {
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            let fightCard = UserData.instance.fightCardList[i];
            if (i != curIndex && fightCard) return false;
        }
        return true;
    }
    static haveRed() {
        if (UserData.instance.fightCardList.indexOf(null) == -1) return false;
        let haveUnlockEmptyPos = false;
        for (let i = 0; i < UserData.instance.fightCardList.length; i++) {
            if (UserData.instance.fightPosUnlock[i] && !UserData.instance.fightCardList[i]) {
                haveUnlockEmptyPos = true;
                break;
            }
        }
        if (!haveUnlockEmptyPos) return false;
        let cardDataList = [...UserData.instance.cardList];
        cardDataList.sort((a, b) => {
            let dataList = GameDataManager.Instance.getMonsterData([a.monsterId, b.monsterId]);
            if (a.monsterId >= 120 || b.monsterId >= 120) {
                if (dataList[0].level == dataList[1].level) {
                    return b.monsterId - a.monsterId;
                } else {
                    return dataList[1].level - dataList[0].level;
                }
            } else {
                if (a.monsterId == b.monsterId) {
                    if (a.curHp == b.curHp) {
                        return dataList[1].atk - dataList[0].atk;
                    } else {
                        return b.curHp - a.curHp;
                    }
                } else {
                    return b.monsterId - a.monsterId;
                }
            }
        });
        let dataList: CardData[] = [];
        for (let i = 0; i < 3; i++) {
            if (i < cardDataList.length) {
                dataList.push(cardDataList[i]);
            } else {
                dataList.push(null);
            }
        }
        for (let i = 0; i < dataList.length; i++) {
            if (dataList[i]) {
                let have = false;
                for (let j = 0; j < UserData.instance.fightCardList.length; j++) {
                    if (UserData.instance.fightCardList[j]) {
                        if (dataList[i].cardId == UserData.instance.fightCardList[j].cardId) {
                            have = true;
                            break;
                        }
                    }
                }
                if (!have) {
                    return true;
                }
            }
        }
        return false;
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        let lockBoxList = [this.lockBox1, this.lockBox2, this.lockBox3];
        for (let i = 0; i < lockBoxList.length; i++) {
            let videoTag = lockBoxList[i].getChildByName("videoTag") as Laya.Sprite;
            videoTag.visible = isShowVideoTag;
        }
    }
}