import { EventType } from "../gamelogic/GEnum";
import { TongYiEventManager } from "../gamelogic/TongYiEventManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import { GuideManager } from "../gframe-work/manager/GuideManager";
import LayaTool from "../gframe-work/tool/LayaTool";
import { ui } from "../ui/layaMaxUI";

export default class ChooseOneRuntime extends ui.pages.chooseOneUI {
    chooseMonsterIdList = [1, 2, 3, 7, 4, 8, 6, 9];
    runTime: number = 0;
    moveSpeed: number = 5;
    curMoveSpeed: number = 0;
    rewardMonsterIdList = [9, 3, 4, 6];
    isMoving = false;
    monsterImageList: Laya.Image[] = [];
    monsterIdList: number[] = [];
    centerX: number = 0;
    gap: number = 0;
    selectIndex: number = -1;
    isPick: boolean = false;
    onAwake() {
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.randomBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.allBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        Laya.timer.frameLoop(1, this, this.onFrameLoop);
        this.createMonsterImageList();
        this.startMove();
        AllPlatformAdMgr.Instance.sendVideoData = { page_id: 3, button_id: 3, ad_id: 1, ad_type: 1 };
        TongYiEventManager.sendEvent(10002, AllPlatformAdMgr.Instance.sendVideoData);
        this.randomBtn.visible = false;
        Laya.timer.once(AllPlatformAdMgr.Instance.delayTime, this, () => {
            this.randomBtn.visible = true;
        });
        this.setBtnVideoTag();
    }
    createMonsterImageList() {
        this.curMoveSpeed = this.moveSpeed;
        let centerIndex = (this.chooseMonsterIdList.length - 1) / 2;
        this.centerX = Laya.stage.width / 2;
        this.gap = Laya.stage.width / 2;
        for (let i = 0; i < this.chooseMonsterIdList.length; i++) {
            let image = new Laya.Image(`monster/Ui_Monster${this.chooseMonsterIdList[i]}.png`);
            image.anchorX = 0.5;
            image.anchorY = 1;
            image.y = this.monsterBox.height;
            image.x = (i - centerIndex) * this.gap + this.centerX;
            this.monsterImageList.push(image);
            this.monsterBox.addChild(image);
            image.scaleX = image.scaleY = this.getRealScale(image.x);
        }
        this.monsterIdList = [...this.chooseMonsterIdList];
    }
    onBtnClick(e: Laya.Event) {
        if (this.isPick) return;
        if (e.target == this.randomBtn) {
            this.isPick = true;
            let index = this.getIndex();
            this.stopMove(index);
            this.rewardMonsterIdList = [this.chooseMonsterIdList[index]];
            GuideManager.finishInnerTask("点击选择初始怪按钮");
        } else if (e.target == this.allBtn) {
            AllPlatformAdMgr.Instance.sendVideoData = { page_id: 3, button_id: 3, ad_id: 2, ad_type: 1 };
            AllPlatformAdMgr.Instance.showVideoAd("adunit-0cad7f4a75069fbe", () => {
                GuideManager.finishInnerTask("点击选择初始怪按钮");
                this.isPick = true;
                let index = this.getIndex();
                this.rewardMonsterIdList = [this.chooseMonsterIdList[index]].concat(this.rewardMonsterIdList);
                this.stopMove(index);
            });
        }
    }
    onDestroy() {
        Laya.timer.clear(this, this.onFrameLoop);
    }
    onFrameLoop() {
        let deltaTime = Laya.timer.delta / 1000;
        this.runTime += deltaTime;
        if (this.isMoving) {
            if (this.selectIndex != -1) {
                this.curMoveSpeed -= 0.03;
                this.curMoveSpeed = this.curMoveSpeed > 2 ? this.curMoveSpeed : 2;
            }
            let moveX = this.curMoveSpeed * deltaTime * 100;
            let newIndex = this.monsterIdList.indexOf(this.chooseMonsterIdList[this.selectIndex]);
            if (this.selectIndex != -1 &&
                this.monsterImageList[newIndex].x < this.centerX &&
                this.monsterImageList[newIndex].x + moveX >= this.centerX) {
                moveX = this.centerX - this.monsterImageList[newIndex].x;
                this.isMoving = false;
                Laya.timer.clear(this, this.onFrameLoop);
                LayaTool.openScene("pages/getCard.scene", true, true, Laya.Handler.create(this, () => {
                    EventManager.event(EventType.ChooseOneFinish);
                }), [2, this.rewardMonsterIdList, 1]);
            }
            for (let i = 0; i < this.monsterImageList.length; i++) {
                let monsterImage = this.monsterImageList[i];
                monsterImage.x += moveX;
                if (monsterImage.x > this.centerX + this.gap * 4) {
                    let image = this.monsterImageList.pop();
                    image.x = this.monsterImageList[0].x - this.gap;
                    this.monsterImageList = [image].concat(this.monsterImageList);
                    let id = this.monsterIdList.pop();
                    this.monsterIdList = [id].concat(this.monsterIdList);
                }
                monsterImage.scaleX = monsterImage.scaleY = this.getRealScale(monsterImage.x);
            }
        }
    }
    getIndex() {
        let maxScaleX = 0;
        let monsterId = -1;
        let index = -1;
        for (let i = 0; i < this.monsterImageList.length; i++) {
            let monsterImage = this.monsterImageList[i];
            if (monsterImage.scaleX > maxScaleX) {
                monsterId = this.monsterIdList[i];
                maxScaleX = monsterImage.scaleX;
                index = i;
            }
        }
        let newIndex = index - 2;
        newIndex = newIndex > 0 ? newIndex : 0;
        return this.chooseMonsterIdList.indexOf(this.monsterIdList[newIndex]);
    }

    startMove() {
        this.isMoving = true;
    }
    stopMove(index: number) {
        this.selectIndex = index;
    }
    getRealScale(x: number) {
        if (x > this.centerX) {
            x = x < Laya.stage.width ? x : Laya.stage.width;
            return 1 - 0.3 * (x - this.centerX) / (Laya.stage.width - this.centerX);
        } else if (x == this.centerX) {
            return 1;
        } else {
            x = x < 0 ? 0 : x;
            return 1 - 0.3 * (x - this.centerX) / (0 - this.centerX);
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}