import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class LevelCellRuntime extends ui.pages.levelCellUI {
    data: any;
    onAwake() {
        this.levelLabel.filters = [CatchRuntime.blackColorFilter];
    }
    set dataSource(data: any) {
        this.data = data;
        if (!data) return;
        let parseLevel1 = (data.bigLevel - 1) * 10 + data.smallLevel;
        let parseLevel2 = (UserData.instance.challengeLevel[0] - 1) * 10 + UserData.instance.challengeLevel[1];
        if (parseLevel1 < parseLevel2) {
            this.setCellState(0);
        } else if (parseLevel1 == parseLevel2) {
            if (UserData.instance.allPassChallenge) {
                this.setCellState(0);
            } else {
                this.setCellState(1);
            }
        } else {
            this.setCellState(2);
        }
    }
    setCellState(state: number) {
        this.curLight.visible = state == 1;
        this.cover.visible = state == 2;
        this.rewardIcon1.visible = (state == 1 || state == 2);
        this.rewardLabel1.visible = (state == 1 || state == 2);
        this.levelLabel.visible = (state == 1 || state == 2);
        this.rewardIcon2.visible = state == 1;
        this.rewardLabel2.visible = state == 1;
        if (state == 0) {
            //已经通关
            this.bg.skin = "newSystem/Ui_Guan1.png";
            this.width = 120;
        } else if (state == 1) {
            //正在通关
            this.bg.skin = "newSystem/Ui_Guan2.png";
            this.width = 185;
            this.levelLabel.text = `${this.data.bigLevel}-${this.data.smallLevel}`;
            this.rewardIcon1.x = 51;
            this.rewardIcon1.y = 70;
            this.rewardLabel1.centerX = -10;
            this.rewardIcon2.x = 148;
            this.rewardIcon2.y = 61;
            this.setReward(1, this.data.passReward[0][0], this.data.passReward[0][1]);
            this.setReward(2, this.data.firstPassReward[0][0], this.data.firstPassReward[0][1]);
        } else if (state == 2) {
            //未开始通关
            this.bg.skin = "newSystem/Ui_Guan3.png";
            this.width = 120;
            this.levelLabel.text = `${this.data.bigLevel}-${this.data.smallLevel}`;
            this.rewardIcon1.x = 42;
            this.rewardIcon1.y = 70;
            this.rewardLabel1.centerX = 13;
            this.setReward(1, this.data.passReward[0][0], this.data.passReward[0][1]);
        }
    }
    setReward(pos: number, rewardType: number, rewardNum: number) {
        let rewardUiList = [{ icon: this.rewardIcon1, label: this.rewardLabel1 }, { icon: this.rewardIcon2, label: this.rewardLabel2 }];
        let rewardUi = rewardUiList[pos - 1];
        if (rewardType == 1) {
            rewardUi.icon.skin = "main/Ui_Money.png";
            rewardUi.icon.width = 17;
            rewardUi.icon.height = 21;
        } else if (rewardType == 5) {
            rewardUi.icon.skin = "newSystem/Ui_Badge.png";
            rewardUi.icon.width = 20;
            rewardUi.icon.height = 20;
        }
        rewardUi.label.text = "X" + rewardNum;
    }
}