import LayaTool from "../tool/LayaTool";

export default class Scene3DBase extends Laya.Script3D {
    protected scene3d: Laya.Scene3D;
    protected camera: Laya.Camera;
    protected clearNode: Laya.Sprite3D;
    protected cameraUrl: string = "MainCamera/Camera";
    onAwake() {
        this.scene3d = this.owner as Laya.Scene3D;
        this.camera = LayaTool.findNodeByUrl(this.scene3d, this.cameraUrl);
        this.camera.enableHDR = false;
        this.clearNode = new Laya.Sprite3D;
        this.clearNode.name = "clearNode";
        this.scene3d.addChild(this.clearNode);
    }
    onDestroy() {
        this.clearNode.destroy();
        this.scene3d.removeSelf();
    }
    /**3d坐标转2d坐标 */
    convert3DTo2DPosition(position: Laya.Vector3) {
        let out = new Laya.Vector4;
        this.camera.viewport.project(position, this.camera.projectionViewMatrix, out);
        return new Laya.Vector2(out.x / Laya.stage.clientScaleX, out.y / Laya.stage.clientScaleY);
    }
}