import { ui } from "../ui/layaMaxUI";

export default class GetLotteryTimeRuntime extends ui.pages.getLotteryTimeUI {
    onAwake(): void {
        this.goShopBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.goShopBtn) {
            this.close("yes");
        }
    }
}