import { GameDataManager } from "../gamelogic/GameDataManager";
import { EventType } from "../gamelogic/GEnum";
import { WXPay } from "../gamelogic/WXPay";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import EventManager from "../gframe-work/manager/EventManager";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import CatchRuntime from "./CatchRuntime";

export default class GiftPackRuntime extends ui.pages.giftPackUI {
    data: any;
    onAwake(): void {
        this.hpLabel1.filters = [CatchRuntime.blackColorFilter];
        this.atkLabel1.filters = [CatchRuntime.blackColorFilter];
        this.hpLabel2.filters = [CatchRuntime.blackColorFilter];
        this.atkLabel2.filters = [CatchRuntime.blackColorFilter];
        this.hpLabel3.filters = [CatchRuntime.blackColorFilter];
        this.atkLabel3.filters = [CatchRuntime.blackColorFilter];
        this.buyBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
    }
    public set dataSource(data: any) {
        this.data = data;
        if (!data) return;
        let descList = ["新手礼包A", "新手礼包B", "新手礼包C", "新手礼包D", "成长礼包", "资深礼包A", "资深礼包B", "资深礼包C"];
        let bgSizeList = [[566, 156], [566, 158], [566, 152], [566, 152], [566, 152], [566, 211], [566, 211], [566, 329]];
        let labelList = [[this.hpLabel1, this.atkLabel1], [this.hpLabel2, this.atkLabel2], [this.hpLabel3, this.atkLabel3]];
        let btnSkinList = ["pay/Btn_Money1.png", "pay/Btn_Money2.png", "pay/Btn_Money2.png", "pay/Btn_Money3.png", "pay/Btn_Money1.png", "pay/Btn_Money4.png", "pay/Btn_Money5.png", "pay/Btn_Money6.png"];
        let posList = [
            [[242, 77], [324, 77], [436, 53]],
            [[242, 75], [324, 75], [438, 52]],
            [[241, 73], [323, 73], [438, 53]],
            [[241, 76], [323, 76], [437, 52]],
            [[0, 0], [0, 0], [435, 53]],
            [[44, 124], [126, 124], [318, 78], [398, 78], [463, 71]],
            [[43, 125], [125, 125], [323, 62], [405, 62], [462, 74]],
            [[66, 224], [148, 224], [86, 73], [168, 73], [388, 100], [470, 100], [443, 152]],
        ];
        let labelShowNumList = [1, 1, 1, 1, 0, 2, 2, 3];

        let index = descList.indexOf(data.desc);
        this.width = bgSizeList[index][0];
        this.height = bgSizeList[index][1];
        this.bg.skin = `pay/Ui_Gift${index + 1}.png`;

        let showNum = labelShowNumList[index];
        for (let j = 0; j < labelList.length; j++) {
            if (j < showNum) {
                labelList[j][0].visible = true;
                labelList[j][1].visible = true;
            } else {
                labelList[j][0].visible = false;
                labelList[j][1].visible = false;
            }
        }

        let pos = posList[index];
        let posIndex = 0;
        for (let j = 0; j < showNum; j++) {
            let label = labelList[j];
            label[0].x = pos[posIndex][0];
            label[0].y = pos[posIndex][1];
            posIndex++;
            label[1].x = pos[posIndex][0];
            label[1].y = pos[posIndex][1];
            posIndex++;
        }
        this.updateMonsterData();
        this.buyBtn.x = pos[pos.length - 1][0];
        this.buyBtn.y = pos[pos.length - 1][1];
        this.buyBtn.skin = btnSkinList[index];
        let buyTime = GameDataManager.Instance.getGiftPackBuyTime(this.data.productId);
        if (this.data.limit > 0) {
            this.limitBg.visible = true;
            this.limitLabel.text = `${buyTime}/${this.data.limit}`;
        } else {
            this.limitBg.visible = false;
        }
        if (this.data.desc == "成长礼包" && buyTime >= 1) {
            this.buyMask.visible = true;
        } else {
            this.buyMask.visible = false;
        }
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.buyBtn) {
            let canBuy = this.getCanBuy(this.data);
            if (!canBuy) return;
            if (typeof wx == "undefined") {
                this.buyGiftPackSuccess();
            } else {
                WXPay.buyItem(this.data).then(() => {
                    this.buyGiftPackSuccess();
                });
            }
        }
    }
    buyGiftPackSuccess() {
        this.getSpecialReward(this.data);
        this.updateForceDataObj();
        GameDataManager.Instance.receiveReward(this.data.goods);
        GameDataManager.Instance.forceDataObj = {};
        if (!UserData.instance.giftPackBuyRecord[this.data.productId]) {
            UserData.instance.giftPackBuyRecord[this.data.productId] = 0;
        }
        UserData.instance.giftPackBuyRecord[this.data.productId]++;
        UserData.instance.onDataChange("giftPackBuyRecord");
        EventManager.event(EventType.RefreshGiftPackList);
        this.addLotteryTime(this.data.costNum);
    }
    getCanBuy(data: any) {
        let canBuy = true;
        let buyTime = GameDataManager.Instance.getGiftPackBuyTime(data.productId);
        if (data.desc == "新手礼包A") {
            if (buyTime >= 1) {
                canBuy = false;
            }
        } else if (data.desc == "新手礼包B") {
            let buyTimeA = GameDataManager.Instance.getGiftPackBuyTime(data.productId - 1);
            if (buyTimeA == 0 || buyTime >= 1) {
                canBuy = false;
            }
            if (buyTimeA == 0) EventManager.event(EventType.PopTip, "购买前置礼包");
        } else if (data.desc == "新手礼包C") {
            let buyTimeB = GameDataManager.Instance.getGiftPackBuyTime(data.productId - 1);
            if (buyTimeB == 0 || buyTime >= 1) {
                canBuy = false;
            }
            if (buyTimeB == 0) EventManager.event(EventType.PopTip, "购买前置礼包");
        } else if (data.desc == "成长礼包") {
            if (buyTime >= 1) {
                canBuy = false;
            }
        }
        return canBuy;
    }
    getSpecialReward(data: any) {
        if (data.desc == "资深礼包A") {
            UserData.instance.gameRank = 7;
            UserData.instance.eliteGameRank = 7;
            UserData.instance.onDataChange("gameRank");
            UserData.instance.onDataChange("eliteGameRank");
        } else if (data.desc == "资深礼包B") {
            UserData.instance.speedBuff = 2;
            UserData.instance.onDataChange("speedBuff");
        } else if (data.desc == "资深礼包C") {
            this.openAllCollectMonster();
            AllPlatformAdMgr.Instance.noAd = true;
            UserData.instance.noAd = true;
            UserData.instance.freeAutoFight = true;
            UserData.instance.freeCure = true;
            UserData.instance.onDataChange("noAd");
            UserData.instance.onDataChange("freeAutoFight");
            UserData.instance.onDataChange("freeCure");
        }
    }
    //图鉴全开
    openAllCollectMonster() {
        for (const key in GameDataManager.Instance.monsterData) {
            let monsterId = Number(key);
            if (UserData.instance.collectMonsterIdList.indexOf(monsterId) == -1) {
                UserData.instance.collectMonsterIdList.push(monsterId);
            }
        }
        UserData.instance.onDataChange("collectMonsterIdList");
    }
    addLotteryTime(rmb: number) {
        let addTime = rmb * 3;
        UserData.instance.freeLotteryTimeLeft += addTime;
        UserData.instance.onDataChange("freeLotteryTimeLeft");
    }
    updateMonsterData() {
        let labelList = [[this.hpLabel1, this.atkLabel1], [this.hpLabel2, this.atkLabel2], [this.hpLabel3, this.atkLabel3]];
        for (let i = 0; i < this.data.goods.length; i++) {
            let item = this.data.goods[i];
            if (item.type == "monster") {
                let labelGroup = labelList[i];
                let cardData = GameDataManager.Instance.getOneCardData(item.num);
                if (item.star) GameDataManager.Instance.upMonsterDataToSpecifyStar(cardData, item.star);
                labelGroup[0].text = cardData.hp + "";
                labelGroup[1].text = cardData.atk + "";
            }
        }
    }
    updateForceDataObj() {
        let labelList = [[this.hpLabel1, this.atkLabel1], [this.hpLabel2, this.atkLabel2], [this.hpLabel3, this.atkLabel3]];
        for (let i = 0; i < this.data.goods.length; i++) {
            let item = this.data.goods[i];
            if (item.type == "monster") {
                let labelGroup = labelList[i];
                let hp = Number(labelGroup[0].text);
                let atk = Number(labelGroup[1].text);
                GameDataManager.Instance.forceDataObj[item.num] = { hp: hp, atk: atk, star: item.star };
            }
        }
    }
}