import { FightType } from "../gamelogic/FightType";
import { GameDataManager } from "../gamelogic/GameDataManager";
import { AllPlatformAdMgr } from "../gframe-work/advert/AllPlatformAdMgr";
import UserData from "../gframe-work/manager/UserData";
import { ui } from "../ui/layaMaxUI";
import DivisionIconRuntime from "./DivisionIconRuntime";

export default class PvpRankRewardCellRuntime extends ui.pages.PvpRankRewardCellUI {
    data: any;
    rewardDataList: any[];
    rewardDataExtraList: any[];
    onAwake(): void {
        this.receiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        this.extraReceiveBtn.on(Laya.Event.CLICK, this, this.onBtnClick);
        (<DivisionIconRuntime>this.divisionIcon).setShowType(true, true);
        this.setBtnVideoTag();
    }
    onBtnClick(e: Laya.Event) {
        if (e.target == this.receiveBtn) {
            GameDataManager.Instance.receiveReward(this.rewardDataList);
            UserData.instance.divisionRewardState[this.data.index][0] = 1;
            UserData.instance.onDataChange("divisionRewardState");
            this.dataSource = this.data;
        } else if (e.target == this.extraReceiveBtn) {
            AllPlatformAdMgr.Instance.showVideoAd("adunit-9de3143facfc2b2d", () => {
                GameDataManager.Instance.receiveReward(this.rewardDataExtraList);
                UserData.instance.divisionRewardState[this.data.index][1] = 1;
                UserData.instance.onDataChange("divisionRewardState");
                this.dataSource = this.data;
            });
        }
    }
    set dataSource(data: any) {
        this.data = data;
        if (!this.data) return;
        (<DivisionIconRuntime>this.divisionIcon).setDivision(this.data.level);
        (<DivisionIconRuntime>this.divisionIcon).setStarNum(this.data.star);
        let rewardList = GameDataManager.Instance.parseRewardData(this.data.levelReward);
        this.rewardDataList = GameDataManager.Instance.countRewardList(rewardList, FightType.Pvp);
        let rewardUiDataList = GameDataManager.Instance.getRewardUiDataList(this.rewardDataList);
        let rewardExtraList = GameDataManager.Instance.parseRewardData(this.data.levelExtraReward);
        this.rewardDataExtraList = GameDataManager.Instance.countRewardList(rewardExtraList, FightType.Pvp);
        let rewardUiDataExtraList = GameDataManager.Instance.getRewardUiDataList(this.rewardDataExtraList);
        this.rewardList.array = rewardUiDataList;
        this.rewardList.refresh();
        this.extraRewardList.array = rewardUiDataExtraList;
        this.extraRewardList.refresh();
        this.setRewardState();
    }
    setRewardState() {
        let curDivision = GameDataManager.Instance.getPvpDivisionDataByScore(UserData.instance.winCount);
        let targetDivision = [this.data.level, this.data.star];
        let compare = GameDataManager.Instance.compareDivision(curDivision, targetDivision);
        if (compare == 0 || compare == 1) {
            let state = UserData.instance.divisionRewardState[this.data.index];
            if (state[0] == 0) {
                this.receiveBtn.visible = true;
            } else {
                this.receiveBtn.visible = false;
            }
            if (state[1] == 0) {
                this.extraReceiveBtn.visible = true;
            } else {
                this.extraReceiveBtn.visible = false;
            }
        } else {
            this.receiveBtn.visible = false;
            this.extraReceiveBtn.visible = false;
        }
    }
    setBtnVideoTag() {
        let isShowVideoTag = AllPlatformAdMgr.Instance.videoMode == 1;
        this.videoTag.visible = isShowVideoTag;
    }
}