import { ui } from "../ui/layaMaxUI";

export default class ChallengeRankCellRuntime extends ui.pages.challengeRankCellUI {
    data: any;
    static rankImagePosConfig = [{ x: -11, y: -12 }, { x: 7, y: 1 }, { x: 14, y: -1 }];
    set dataSource(data: any) {
        this.data = data;
        if (!this.data) return;
        this.nameLabel.text = this.data.UserName;
        this.headImage.skin = this.data.avatarIP;
        this.setRank(data.index);
        let smallLevel = data.RankValue % 10;
        let bigLevel = Math.ceil(data.RankValue / 10);
        this.levelLabel.text = `${bigLevel}-${smallLevel}`;
        this.setMe(data.isMe);
    }
    setRank(rank: number) {
        if (rank <= 3) {
            this.rankImage.visible = true;
            this.rankLabel.visible = false;
            this.rankImage.skin = `challengeRank/Ui_No${rank}.png`;
            this.rankImage.x = ChallengeRankCellRuntime.rankImagePosConfig[rank - 1].x;
            this.rankImage.y = ChallengeRankCellRuntime.rankImagePosConfig[rank - 1].y;
        } else {
            this.rankImage.visible = false;
            this.rankLabel.visible = true;
            this.rankLabel.text = rank + "";
        }
    }
    setMe(isMe: boolean) {
        this.bg.skin = isMe ? "challengeRank/Ui_FrameRank3.png" : "challengeRank/Ui_FrameRank2.png";
    }
}